try {
	Notification.requestPermission();
} catch {}
const sendNotification = (title: string, work: Work) => {
	const options = {
		body: work.description,
		// icon: 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAHElEQVQI12P4//8/w38GIAXDIBKE0DHxgljNBAAO9TXL0Y4OHwAAAABJRU5ErkJggg==',
		icon: NewTaskIcon,
		dir: "ltr",
		requireInteraction: true,
		tag: work.id,
	};
	const n = new Notification(title, options);
	n.onclick = (e) => {
		window.focus();
		n.close();
	};
};

const useGet = (url: string, callback?: Function) => {
	const [loading, setLoading] = useState<boolean>(true);
	const [data, setData] = useState<any>([]);
	const [updatedData, setUpdatedData] = useState<any>([]);
	const [error, setError] = useState<string>("");
	const timer = useRef<Timeout>(null);
	const timerUpdate = useRef<Timeout>(null);
	const datas = useRef<any>(data);
	const updateHistory = useRef<Array<Array<number>>>([]);
	datas.current = data;
	const fetchData = (
		clear: boolean = false,
		postAction: (() => {}) | null = null
	) => {
		clearTimeout(timer.current);
		setLoading(true);
		setError("");
		clear && setData([]);

		axiosInstance
			.get(url)
			.then((res) => {
				res.data?.workList?.sort((w1: Work, w2: Work) =>
					(w2.createDate || w2.modifyDate).localeCompare(
						w1.createDate || w1.modifyDate
					)
				);

				if (!_.isEqual(res.data, datas.current)) {
					if (datas.current?.workList?.length) {
						const diff = _.differenceWith(
							res.data?.workList,
							datas.current?.workList,
							_.isEqual
						);
						setUpdatedData(diff);
						clearTimeout(timerUpdate.current);
						const index = updateHistory.current.length;
						updateHistory.current.push(diff.map((w) => w.id));
						timerUpdate.current = setTimeout(
							() =>
								setUpdatedData((list: Array<Work>) =>
									list.filter(
										(w) =>
											updateHistory.current.findLastIndex((h: Array<number>) =>
												h.includes(w.id)
											) > index
									)
								),
							5555
						);
					}

					setData(res.data || []); // true
					callback && callback(res);
				}
			})
			.catch((e) => setError(e))
			.finally(() => {
				setLoading(false);
				timer.current = setTimeout(fetchData, 11000);
				postAction?.();
			});
	};
	useEffect(() => {
		fetchData();
		return () => {
			clearTimeout(timer.current);
			clearTimeout(timerUpdate.current);
		};
	}, []);

	return { data, loading, error, fetchData, updatedData };
};

const status = {
	0: "all",
	1: "pending",
	2: "doing",
	3: "done",
	4: "pause",
} as any;

export const statusColor = {
	0: "#B72136",
	[TaskStatus.pending]: "#B72136",
	[TaskStatus.doing]: "#3366FF",
	[TaskStatus.done]: "#00AB55",
	[TaskStatus.pause]: "#919EAB",
};

const TaskList: FC = () => {
	const { t } = useTranslation("task");
	const gt = useTranslation().t;

	const [statusFilterState, setStatusFilter] = useState<number>(
		+localStorage.taskStatusFilter || 0
	);

	const { enqueueSnackbar } = useSnackbar();
	const _user = getTokenUser();
	const isSender = _user?.userId == -1;
	// const isSender = !location.search.includes("user");

	const [search, setSearch] = useState<string>("");

	const getWorks = useGet("/work");
	const getUsers = useGet("/ticket/ticketUserList");

	const showLoading =
		(!getWorks.data?.workList || !getUsers.data?.userList) &&
		(getWorks.loading || getUsers.loading);
	const data = (getWorks.data?.workList || []).filter(
		(work: Work) =>
			!search ||
			Object.values(work).some(
				(p) => p && (p + "").toLowerCase().includes(search.toLowerCase())
			)
	);
	const times = getWorks.data?.times || {};
	console.log(getWorks.updatedData);

	const statusCounts = {
		all: data.length,
		pending: data.filter((r: any) => r.status === TaskStatus.pending).length,
		doing: data.filter((r: any) => r.status === TaskStatus.doing).length,
		done: data.filter((r: any) => r.status === TaskStatus.done).length,
		pause: data.filter((r: any) => r.status === TaskStatus.pause).length,
	} as any;

	const statusFilter = statusCounts[status[statusFilterState]]
		? statusFilterState
		: 0;

	//paging
	const [page, setPage] = useState<number>(+localStorage.taskPage || 0);
	const [rowsPerPage, setRowsPerPage] = useState<number>(
		+localStorage.taskRowsPerPage || 10
	);

	const [showDialog, setShowDialog] = useState<boolean>(false);
	const filteredData = data
		.filter((r: any) => !statusFilter || r.status === statusFilter)
		.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage);

	const filteredCount = data?.filter(
		(r: any) => !statusFilter || r.status === statusFilter
	)?.length;

	if (
		page * rowsPerPage &&
		page * rowsPerPage >= statusCounts[status[statusFilterState]]
	) {
		setPage(0);
	}

	useEffect(() => {
		localStorage.taskRowsPerPage = rowsPerPage;
		localStorage.taskPage = page;
		localStorage.taskStatusFilter = statusFilterState;
	}, [rowsPerPage, page, statusFilterState]);

	const lastDataRef = useRef([]);

	useEffect(() => {
		//for notif
		const lastData = lastDataRef.current;
		lastDataRef.current = data;

		if (data?.length > lastData.length && lastData?.length > 0) {
			const lastWork = data.slice(-1)[0];
			if (!lastWork) return;
			if (!isSender && !lastData.find((w: Work) => w.id === lastWork.id)) {
				sendNotification("تسک جدید:‌ " + lastWork.subject, lastWork);
			}
		}

		if (data?.length == lastData.length && lastData?.length > 0) {
			const changeList = [];
			lastData.forEach((work) => {
				const w = data.find((w: Work) => w.id == work.id);
				if (!_.isEqual(work, w)) changeList.push(w);
			});

			if (changeList.length > 0) {
				sendNotification(
					"تسک‌های شما به روز شدند." +
						"\n" +
						changeList.map((w) => "#" + w.id).join("\n"),
					changeList[0]
				);
			}
		}
	}, [data]);

	// useEffect(() => {
	// 	if (page)
	// 		document.documentElement.scrollTop =
	// 			document.documentElement.scrollHeight;
	// }, [page]);

	return (
		// <Page title={t('TicketList')}>
		<Container maxWidth="lg" sx={{ py: 8 }}>
			<TaskListHeaderToolbar
				isSender={isSender}
				state={[showDialog, setShowDialog]}
			/>

			<DebouncedTextField
				state={[search, setSearch]}
				placeholder={t("Search")}
				InputProps={{
					"aria-label": "search",
					startAdornment: (
						<InputAdornment position="start">
							<SearchIcon />
						</InputAdornment>
					),
				}}
			/>

			<TaskListToobar
				statusFilter={statusFilter}
				setStatusFilter={setStatusFilter}
				statusCounts={statusCounts}
			/>

			{!showLoading && (
				<Box>
					{filteredData.length ? (
						<>
							<Box
								sx={{
									display: "flex",
									flexDirection: "column",
									gap: "10px",
									py: 1,
								}}
							>
								{filteredData.map((work: Work) => (
									<TaskCard
										key={work.id}
										item={work}
										users={getUsers.data?.userList}
										statusFilter={statusFilter}
										workPauseTime={times[work.id]}
										warn={getWorks.updatedData?.some(
											(w: Work) => w.id == work.id
										)}
									/>
								))}
							</Box>
							<Card
								sx={{
									boxShadow:
										"0px 0px 2px rgba(145, 158, 171, 0.2), 0px 12px 24px -4px rgba(145, 158, 171, 0.12)",
									borderRadius: "16px",
									border: "none",
									mt: "10px",
									p: "4px 4px 4px 4px",
								}}
							>
								<TablePagination
									component="div"
									count={filteredCount}
									page={page}
									showLastButton={true}
									onPageChange={(e, newPage) => setPage(newPage)}
									rowsPerPage={rowsPerPage}
									onRowsPerPageChange={(e) => setRowsPerPage(+e.target.value)}
									labelRowsPerPage={gt("RowsPerPage")}
									rowsPerPageOptions={[5, 10, 25, 50, 100]}
									labelDisplayedRows={({ from, to, count }) =>
										`${from}–${to} ${gt("From")} ${
											count !== -1 ? count : t("MoreThan") + " " + to
										}`
									}
									sx={{
										"& .MuiToolbar-root": { pl: { xs: 0, md: "16px" } },
										"& .MuiInputBase-root": { mr: { xs: 0, md: "32px" } },
										"& .MuiTablePagination-actions": {
											ml: { xs: 0, md: "20px" },
										},
									}}
								/>
							</Card>
						</>
					) : (
						<Box
							sx={{
								display: "flex",
								flexDirection: "column",
								alignItems: "center",
								justifyContent: "center",
								gap: 2,
								py: 4,
							}}
						>
							<EmptyIcon />
							<Typography
								color="secondary.dimgray.light1"
								sx={{ fontSize: "14px" }}
							>
								{search ? gt("SearchNoResult") : t("EmptyData")}
							</Typography>
						</Box>
					)}
				</Box>
			)}

			{showLoading && (
				<Stack spacing={2} sx={{ mt: 4 }}>
					<Skeleton variant="rounded" width="100%" height={145} />
					<Skeleton variant="rounded" width="100%" height={145} />
					<Skeleton variant="rounded" width="100%" height={145} />
					<Skeleton variant="rounded" width="100%" height={145} />
				</Stack>
			)}

			{isSender && showDialog && (
				<TaskAddDialog
					state={[showDialog, setShowDialog]}
					getWorks={getWorks}
					onAdd={() => {
						if (!statusFilter || statusFilter == TaskStatus.pending) {
							setPage(0);
							// setPage(parseInt(filteredCount / rowsPerPage));
							// setTimeout(
							// 	() =>
							// 		(document.documentElement.scrollTop =
							// 			document.documentElement.scrollHeight),
							// 	100
							// );
						}
					}}
				/>
			)}
		</Container>
		// </Page>
	);
};

export type WorkPauseTime = {
	date: string;
	pauseTime: number;
};

const TaskCard = memo<{
	item: Work;
	users: Array<User>;
	statusFilter: number;
	workPauseTime: WorkPauseTime;
	warn: boolean;
}>(({ item, users, statusFilter, workPauseTime, warn }) => {
	const { t } = useTranslation("task");
	const user = users?.find((u) => u.id == item.supporterId);
	const userName = user ? user.name + " " + user.lastName : t("NoName");
	const twoLine = item.status === TaskStatus.done;
	const showTag = Boolean(!statusFilter);

	const _user = getTokenUser();
	const isSender = _user?.userId == -1;
	// const isSender = !location.search.includes("user");

	//time
	var stopTimed = getNow();
	if (workPauseTime?.date) {
		stopTimed = new Date(workPauseTime?.date);
	}
	if (item.status == TaskStatus.done) {
		stopTimed = new Date(item.endDate);
	}

	const pauseTime = workPauseTime?.pauseTime;

	let passedTime = (stopTimed - new Date(item.startDate)) / 1000;
	passedTime -= pauseTime || 0;
	const remainTime = item.finalTime - passedTime;

	// // @ts-ignore
	// <Card sx={isSingle ? { flexShrink: 0, p: 3, boxShadow: 'none', borderBottom: `1px dashed ${theme.palette.secondary.slate.light1}` } : { transition: 'height 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms', display: 'block', boxShadow: '0px 0px 2px rgba(145, 158, 171, 0.2), 0px 12px 24px -4px rgba(145, 158, 171, 0.12)', borderRadius: '16px', border: '1px solid transparent', backgroundColor: ticket.status === 3 ? theme.palette.secondary.slate.muted : '#FFFFFF', px: 3, py: 2, '&:hover': {
	//   // @ts-ignore
	//   borderColor: theme.palette.info.blue
	// } }} to={`/ticket/${ticket.id}`} component={!isSingle ? RouterLink : 'div'}>

	return (
		<Button
			sx={{
				padding: "16px 24px 16px 24px",
				boxShadow: "0px 5px 12px -4px rgba(145, 158, 171, 0.31)",
				borderRadius: "8px",
				background: "#FFFFFF",
				flexDirection: "column",
				justifyContent: "center",
				gap: "12px",
				display: "flex",
				transition: "box-shadow, border 333ms cubic-bezier(0.4, 0, 0.2, 1) 0ms",
				alignItems: "normal",
				border: "1px solid transparent",
				"&:hover": {
					border: "1px solid #3366FF44",
				},
				textTransform: "none",
				animation: warn
					? "pulseCard 0.88s ease-in-out alternate-reverse infinite"
					: "",
			}}
			to={`/task/${item.id}`}
			component={RouterLink}
			target="_blank"
		>
			<div style={{ display: "flex" }}>
				<Tooltip
					color="secondary.gray.main"
					sx={{ fontSize: "14px" }}
					title={t("Work Id")}
				>
					<Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
						{/* <HashtagIcon /> */}
						<Typography color="error.dark">#{item.id}</Typography>
					</Box>
				</Tooltip>
				<p
					style={{
						color: "#212B36",
						fontSize: "14px",
						flex: 1,
						fontWeight: 500,
						marginRight: "8px",
					}}
				>
					{item.subject}
				</p>
				{Boolean(item.ticketId) && (
					<Link
						href={"/ticket/" + item.ticketId}
						sx={{ color: "#3366FF", fontSize: "12px", lineHeight: "24px" }}
						target="_blank"
						onClick={(e) => e.stopPropagation()}
					>
						{t("ToTicket")}
					</Link>
				)}
				{!twoLine && item.status != TaskStatus.pending && (
					<Box display={"flex"} alignItems={"center"}>
						<ProgressBar work={item} workPauseTime={workPauseTime} />
					</Box>
				)}
				{!twoLine && showTag && (
					<Box
						sx={{
							background: statusColor[item.status],
							textAlign: "center",
							color: "white",
							fontSize: 12,
							fontWeight: 700,
							p: "2px 8px",
							borderRadius: "6px",
							ml: "24px",
							height: "24px",
						}}
					>
						{t(
							status[item.status].replace(
								/^./,
								status[item.status][0].toUpperCase()
							)
						)}
					</Box>
				)}

				{/* !twoLine */}
				{isSender && (
					<>
						<UserIcon style={{ marginRight: "12px" }} />

						<p
							style={{
								color: "#212B36",
								fontSize: "12px",
								fontWeight: 400,
								marginRight: "8px",
								lineHeight: "24px",
							}}
						>
							{userName || t("NoName")}
						</p>
					</>
				)}
			</div>

			{twoLine && (
				<div style={{ display: "flex", alignItems: "center" }}>
					{/* {isSender && (
						<>
							<UserIcon />

							<p
								style={{
									color: "#212B36",
									fontSize: "12px",
									fontWeight: 400,
									lineHeight: "24px",
									marginRight: "8px",
									marginLeft: "8px",
									minWidth: "64px",
								}}
							>
								{userName || t("NoName")}
							</p>
						</>
					)} */}

					<p
						style={{
							color: "#212B36",
							fontSize: "12px",
							flex: 1,
							fontWeight: 400,
							lineHeight: "24px",
							marginRight: "8px",
						}}
					>
						{t("TotalTime")} :{" "}
						<span style={{ color: "#3366FF" }}>
							{toTimeString(item.finalTime) + " "}
						</span>
						<Typography sx={{ ml: 2.5 }} component={"span"} />
						{t("StartDateString")} :{" "}
						<span style={{ color: "#3366FF" }}>
							{toDateTimeString(item.startDate)}
						</span>
						<p style={{ color: "#212B36" }}>
							{t("PassedTime")} :{" "}
							<span style={{ color: "#3366FF" }}>
								{toTimeString(passedTime) + " "}
							</span>
							<Typography sx={{ ml: 1 }} component={"span"} />
							{t("StartEndString")} :{" "}
							<Typography
								sx={{ fontSize: "12px", color: "#3366FF", mr: 1 }}
								component={"span"}
							>
								{toDateTimeString(item.endDate)}
							</Typography>{" "}
							{Boolean(pauseTime) &&
								" ( توقف:‌ " + toTimeString(pauseTime) + " )"}
						</p>
					</p>

					<div style={{ flex: 1 }}></div>

					{twoLine && item.status != TaskStatus.pending && (
						<Box display={"flex"} alignItems={"center"}>
							<ProgressBar work={item} workPauseTime={workPauseTime} />
						</Box>
					)}

					{showTag && (
						<Box
							sx={{
								background: statusColor[item.status],
								textAlign: "center",
								color: "white",
								fontSize: 12,
								fontWeight: 700,
								p: "2px 8px",
								borderRadius: "6px",
								ml: "24px",
								height: "24px",
								lineHeight: "22px",
							}}
						>
							{t(
								status[item.status].replace(
									/^./,
									status[item.status][0].toUpperCase()
								)
							)}
						</Box>
					)}
				</div>
			)}
		</Button>
	);
});

export const ProgressBar = memo<{
	work: Work;
	workPauseTime?: WorkPauseTime;
}>(({ work, workPauseTime }) => {
	const { t } = useTranslation("task");
	var stopTimed = getNow();
	if (workPauseTime?.date) {
		stopTimed = new Date(workPauseTime?.date);
	}
	if (work.status == TaskStatus.done) {
		stopTimed = new Date(work.endDate);
	}

	const disabled = work?.status == TaskStatus.pause;

	const pauseTime = workPauseTime?.pauseTime;

	let passedTime = (stopTimed - new Date(work.startDate)) / 1000;
	passedTime -= pauseTime || 0;
	const remainTime = work.finalTime - passedTime;
	const delayed = remainTime < 0;
	const percent = parseInt(delayed ? 100 : (remainTime / work.finalTime) * 100);

	var color = "#00AB55"; //green
	if (percent > 25 && percent < 75) color = "#3366FF"; //blue
	if (percent <= 25) color = "#B72136"; //red
	if (disabled) color = statusColor[TaskStatus.pause];
	var width = (136 * percent) / 100;

	const [timing, setTiming] = useState(0);
	useEffect(() => {
		const id = setInterval(() => {
			work?.status == TaskStatus.doing && setTiming((c) => c + 1);
		}, 1000);
		return () => {
			clearInterval(id);
		};
	}, [work, workPauseTime]);

	return (
		<>
			<p
				style={{
					color: "#212B36",
					fontSize: 12,
					fontWeight: 400,
					marginRight: "8px",
				}}
			>
				{toTimeString(Math.abs(remainTime), true)}
			</p>

			<p
				style={{
					fontSize: 12,
					color: delayed ? "#B72136" : "3366FF",
					marginRight: "8px",
				}}
			>
				{delayed ? t("Delay") : t("Remain")}
			</p>

			{!delayed && (
				<Tooltip title={percent + "%"}>
					<Box
						sx={{
							width: "136px",
							background: color + "44",
							borderRadius: 2,
							overflow: "hidden",
							display: "flex",
							height: 6,
							ml: "8px",
							flexDirection: "row-reverse",
							"&:hover": { cursor: "help" },
						}}
					>
						<div
							style={{
								width: width,
								height: 6,
								background: color,
								borderRadius: 2,
							}}
						/>
					</Box>
				</Tooltip>
			)}
		</>
	);
});

export const DebouncedTextField = memo<{
	state: [string, Dispatch<SetStateAction<string>>];
	debounce?: number;
	props: TextFieldProps;
}>(({ state, debounce = 444, ...props }) => {
	const [debouncedRenderMe, setDebouncedRenderMe] = state;
	const [renderMe, setRenderMe] = useState(debouncedRenderMe);

	const timeoutRef = useRef();

	const handleChange = (e: any) => {
		clearTimeout(timeoutRef.current);
		timeoutRef.current = setTimeout(
			() => setDebouncedRenderMe(e.target.value),
			debounce
		);
		setRenderMe(e.target.value);
	};

	//for direct change state
	useEffect(() => {
		setRenderMe(debouncedRenderMe);
	}, [debouncedRenderMe]);

	return <TextField value={renderMe} onChange={handleChange} {...props} />;
});

export default TaskList;
