import { FC, useCallback, useEffect, useState } from "react"
import { styled, alpha } from '@mui/material/styles';
import { Box, SwipeableDrawer, IconButton, InputBase, Link, List, ListItem, ListSubheader, Popper, Stack, useMediaQuery } from "@mui/material"
import { NavLink as RouterLink, useLocation } from 'react-router-dom';
import axiosInstance from "../utils/axios-instance";
import { categories } from "../utils/help";
import _ from "lodash";
import useResponsive from "../hooks/useResponsive";
import { useTranslation } from "react-i18next";
import useClickStore from "../utils/store";

import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowLeftIcon from '@mui/icons-material/KeyboardArrowLeft';
import ClearIcon from '@mui/icons-material/Clear';
import { ReactComponent as MenuIcon } from '../images/menu.svg'

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  border: '1px solid #919EAB',
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    // paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    paddingLeft: '12px',
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));

// import navItems from '../assets/data/menu-data.json';
type MenuItem = {
  id: string | number
  path: string
  title: string
  description?: string
  children?: MenuItem[]
}
// children?: {
//   id: number
//   path: string
//   title: string
//   description?: string
// }[]
type MenuDesktopItemProps = {
  isOpen: boolean,
  onClose: () => void,
  imidateClose: () => void,
  onOpen: (event: any, isClick?: boolean) => void,
  onPopperEnter: () => void,
  onPopperClose: () => void,
  item: MenuItem,
  anchorEl: null | HTMLElement
};

function IconBullet({ type = 'item' }: { type: 'item' | 'subheader' }) {
  return (
    <Box sx={{ width: 24, height: 16, display: 'flex', alignItems: 'center' }}>
      <Box
        component="span"
        sx={{
          ml: '2px',
          width: 4,
          height: 4,
          borderRadius: '50%',
          bgcolor: 'currentColor',
          ...(type !== 'item' && { ml: 0, width: 8, height: 2, borderRadius: 2 }),
        }}
      />
    </Box>
  );
}

const MenuDesktopItem: FC<MenuDesktopItemProps> = ({ item, anchorEl, isOpen, onOpen, onClose, onPopperEnter, onPopperClose, imidateClose }) => {
  const { title, path, children } = item;
  const [showChildItem, setShowChildItem] = useState<string | number>(children?.[0]?.id || '');
  const [search, setSearch] = useState<string>('');
  const [searchInput, setSearchInput] = useState<string>('');
  const handleSearchChange = _.debounce(async (event: { target: { value: string } }) => {
    setSearch(event.target.value);
  }, 1000)
  const getFilterdItems = useCallback((items: MenuItem[], searchValue: string) => items.filter(i => (i?.children ? i.children.filter(j => j.title.trim().includes(searchValue.trim())) : []).length || i.title.trim().includes(searchValue.trim())), [search])
  const mdMatches = useMediaQuery((theme: any) => theme.breakpoints.up('md'));
  if (children && mdMatches) {
    return (
      <>
        <Link
          // onClick={(event) => onOpen(event, true)}
          href={path}
          onMouseEnter={onOpen}
          // onMouseLeave={onClose}
          className="font-medium text-base lg:text-lg hover:text-[#2D99FF]"
          sx={{
            marginRight: {
              lg: 5,
              xs: 2
            },
            textDecoration: 'none',
            display: 'flex',
            cursor: 'pointer',
            alignItems: 'center',
            color: '#212B36',
            zIndex: 1,
            position: 'relative',
            borderBottom: '2px solid transparent',
            ...(isOpen && { color: '#2D99FF', borderBottomColor: '#2D99FF' }),
            '&:hover': {
              borderBottomColor: '#2D99FF',
              color: '#2D99FF'
            }
          }}
        >
          {title}
          <KeyboardArrowDownIcon className="text-xs hidden md:inline-block" onMouseOver={onPopperEnter} />
        </Link>
        {isOpen && <Box className="fixed inset-0 z-0" onClick={imidateClose} sx={{ backgroundColor: 'rgba(0, 0, 0, 0.06)' }}></Box>}
        <Popper
          onMouseEnter={onPopperEnter}
          open={isOpen}
          anchorEl={anchorEl}
          placement="bottom"
          popperOptions={{
            placement: 'bottom',
            strategy: 'fixed'
          }}
          sx={{
            borderRadius: '0 0 16px 16px',
            width: '100%',
            maxWidth: '1200px',
            minHeight: '300px',
            maxHeight: '100%',
            boxShadow: '0 24px 48px 0 rgba(145, 158, 171, 0.16)',
            display: 'flex',
            borderTop: '2px solid #2D99FF',
            backgroundColor: '#fff',
            zIndex: 9999,
            marginTop: '24px !important',
            overflow: 'hidden',
            right: '50% !important',
            transform: 'translate(50%, 59px) !important',
            // left: {
            //   xl: 'calc(50% + 32px) !important'
            // }
          }}
        // anchorReference="anchorPosition"
        // anchorPosition={{ top: 80, left: 0 }}
        // anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
        // transformOrigin={{ vertical: 'top', horizontal: 'center' }}
        // disableEnforceFocus={true}
        // onClose={onClose}
        // PaperProps={{
        //   sx: {
        //     px: 4,
        //     py: 6,
        //     pt: 3,
        //     pb: 3,
        //     left: 16,
        //     m: 'auto',
        //     borderRadius: '0 0 16px 16px',
        //     width: '100%',
        //     maxWidth: '1200px',
        //     minHeight: '300px',
        //     maxHeight: '100%',
        //     boxShadow: '0 24px 48px 0 rgba(145, 158, 171, 0.16)',
        //     display: 'flex',
        //     borderTop: '2px solid #2D99FF',
        //   },
        // }}
        >
          <Box className="flex flex-col basis-full md:basis-1/5 gap-1" sx={{
            px: 4,
            py: 6,
            pt: 3,
            pb: 3,
          }}>
            <div className="flex flex-col">
              <div>
                <List disablePadding className="flex flex-col">
                  <ListSubheader
                    disableSticky
                    disableGutters
                    sx={{
                      display: 'flex',
                      lineHeight: 'unset',
                      alignItems: 'center',
                      color: 'text.primary',
                      typography: 'overline',
                    }}
                  >
                    <h6 className="text-sm leading-8 font-medium text-[#637381]">راهنما و آموزش نرم افزار</h6>
                  </ListSubheader>
                </List>
              </div>
            </div>
            <div className="flex flex-col py-2">
              <div>
                <List disablePadding className="flex flex-col">
                  <ListSubheader
                    disableSticky
                    disableGutters
                    sx={{
                      display: 'flex',
                      lineHeight: 'unset',
                      alignItems: 'center',
                      color: 'text.primary',
                      typography: 'overline',
                    }}
                  >
                    <Search>
                      {/* <SearchIconWrapper>
                        <SearchIcon />
                      </SearchIconWrapper> */}
                      <StyledInputBase
                        value={searchInput}
                        placeholder="جستجو..."
                        inputProps={{ 'aria-label': 'search' }}
                        onChange={(event: { target: any; }) => { setSearchInput(event.target.value); handleSearchChange(event) }}
                        fullWidth
                        classes={{ input: 'flex-grow' }}
                        endAdornment={!!search.length && (
                          <IconButton onClick={() => { setSearchInput(''); setSearch('') }} aria-label="clear-search">
                            <ClearIcon />
                          </IconButton>
                        )}
                      />
                    </Search>
                  </ListSubheader>
                </List>
              </div>
            </div>
            {(search && children ? getFilterdItems(children, search) : children)?.map((childItem, cIndex: number) => {
              return (
                <div className="flex flex-col" key={String(childItem.id) + String(cIndex)}>
                  <div>
                    <List disablePadding className="flex flex-col">
                      <ListSubheader
                        to={childItem.path}
                        component={RouterLink}
                        disableSticky
                        disableGutters
                        onMouseOver={() => setShowChildItem(childItem.id)}
                        sx={{
                          display: 'flex',
                          lineHeight: 'unset',
                          alignItems: 'center',
                          color: 'text.primary',
                          typography: 'overline',
                        }}
                      >
                        <h6 className={`text-sm leading-8 font-normal text-[#212b36] ${showChildItem === childItem.id ? 'text-[#2d99ff]' : ''}`}>{childItem.title}</h6>
                        <KeyboardArrowLeftIcon className={`text-base mr-2 ${showChildItem === childItem.id ? 'text-[#2d99ff]' : ''}`} />
                      </ListSubheader>
                    </List>
                  </div>
                  <Box sx={{
                    width: '80%',
                    position: 'absolute',
                    top: 0,
                    right: 0,
                    bottom: 0,
                    py: 3,
                    px: 2,
                    zIndex: 1,
                    backgroundColor: '#F6F7F8',
                    display: showChildItem !== childItem.id ? 'none' : '',
                  }}>
                    <List className="flex flex-col flex-wrap w-full h-full content-start">
                      {(search && childItem?.children ? getFilterdItems(childItem?.children, search) : childItem?.children)?.map((itemChild: MenuItem, index: number) => (
                        <ListItem
                          className="w-full md:w-1/4 pl-0"
                          key={String(itemChild.id) + String(index)}
                          to={itemChild.path}
                          component={RouterLink}
                          sx={{
                            borderLeft: index > 8 ? '1px solid #E2E8F0' : 'none',
                            '&.active': {
                              color: 'text.primary',
                              typography: 'subtitle2',
                            },
                          }}
                        >
                          {/* <IconBullet type={"item"} /> */}
                          <span className="text-sm text-[#212B36] hover:text-[#2D99FF]">{itemChild.title}</span>
                        </ListItem>
                      ))}
                    </List>
                  </Box>
                </div>
              );
            })}
          </Box>
        </Popper>
      </>
    );
  }
  return (
    <Link
      href={path}
      className="font-medium text-base lg:text-lg transition-colors"
      sx={{
        color: '#212B36',
        marginRight: {
          lg: 5,
          xs: 2
        },
        borderBottom: '2px solid transparent',
        textDecoration: 'none',
        '&.active': {
          color: 'primary.main',
        },
        '&:hover': {
          borderBottomColor: '#2D99FF',
          color: '#2D99FF'
        }
      }}
    >
      {title}
    </Link>
  );
}

const MainMenu: FC = () => {
  const { t } = useTranslation('help')
  const { pathname } = useLocation();
  const [open, setOpen] = useState(false);
  const [popperEnter, setPopperEnter] = useState(false);
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [mobileMenuIsOpen, setMobileMenuIsOpen] = useState<boolean>(false)
  const [menuItems, setMenuItems] = useState<{
    id: number | string
    title: string
    path: string
    external?: boolean
    children?: {
      id: number | string
      title: string
      path: string
      children?: {
        id: number | string
        title: string
        path: string
      }[]
    }[]
  }[]>([
    {
      "id": "features",
      "title": "امکانات و ویژگی‌ها",
      "path": "/features",
      "external": false
    },
    {
      "id": "prices",
      "title": "قیمت‌ها",
      "path": "/plans",
      "external": false
    },
    {
      "id": "doc",
      "title": "راهنما",
      "path": "/help",
      "external": false,
      // "children": []
    },
  
    {
      "id": "aboutus",
      "title": "درباره‌ما",
      "path": "/about-us",
      "external": true
    },
    {
      "id": "contactus",
      "title": "ارتباط ما",
      "path": "/contact-us",
      "external": true
    }
  ])
  const isMobile = useResponsive('down', 'md');

  useEffect(() => {
    if (open) {
      handleClose();
    }
  }, [pathname]);

  const handleOpen = (event: any, isClickMode: boolean) => {
    if (window.screen.width > 959 || isClickMode) {
      if (event) {
        setAnchorEl(event.currentTarget)
      }
      setOpen(true);
    }
    if (isClickMode) {
      setPopperEnter(true)
    }
  };

  const handleClose = _.debounce(async () => {
    if (!popperEnter) {
      setOpen(false);
    }
  }, 3000)

  useEffect(() => {
    axiosInstance.get('/Help/menu').then(res => {
      const menutItemsWithHelps = [...menuItems]
      const helpChildren = categories.map(i => ({
        id: i.id,
        title: t(i.id),
        path: `/helpList/${i.id?.toLowerCase()?.replace('help', '')}`,
        children: res.data.filter((x: { category: string }) => x.category === i.id || x.category === t(i.id)).map((x: { id: any; title: string; }) => ({
          id: x.id,
          title: t(x.title),
          path: `/${x.title.split(' ').join('-')}`
        }))
      })).filter(i => i?.children?.length)
      // menutItemsWithHelps[1].children = helpChildren
      useClickStore.setState({ helpMenu: helpChildren })
      // setMenuItems(menutItemsWithHelps)
    })
  }, [])
  return !isMobile ? (
    <Stack direction="row" sx={{ flexWrap: 'wrap' }}>
      {menuItems.map((link: MenuItem) => (
        <MenuDesktopItem
          key={link.id}
          item={link}
          isOpen={open}
          imidateClose={() => { setPopperEnter(false); setOpen(false); }}
          // @ts-ignore
          onOpen={handleOpen}
          onClose={handleClose}
          anchorEl={anchorEl}
          onPopperEnter={() => setPopperEnter(true)}
          onPopperClose={() => {
            setPopperEnter(false);
            setOpen(false);
          }}
        />
      ))}
    </Stack>
  ) : (
    <div>
      <IconButton onClick={() => setMobileMenuIsOpen(true)}>
        <MenuIcon />
      </IconButton>
      <SwipeableDrawer
        anchor="left"
        open={mobileMenuIsOpen}
        onOpen={() => setMobileMenuIsOpen(true)}
        onClose={() => setMobileMenuIsOpen(false)}
      >
        <div className="w-[256px] max-w-full flex flex-col pt-8 px-4 pb-1 gap-4">
          {
            menuItems.map((link: MenuItem) => (
              <Link
                href={link.path}
                className="font-normal text-base transition-colors"
                sx={{
                  color: '#212B36',
                  marginRight: 5,
                  borderBottom: '2px solid transparent',
                  textDecoration: 'none',
                  '&.active': {
                    color: 'primary.main',
                  },
                  '&:hover': {
                    borderBottomColor: '#2D99FF',
                    color: '#2D99FF'
                  }
                }}
              >
                {link.title}
              </Link>
            ))
          }
        </div>
      </SwipeableDrawer>
    </div>
  )
}
export default MainMenu;