import { Box, Button, Card, Tooltip, Typography } from "@mui/material";
import { Dispatch, FC, SetStateAction, useEffect, useMemo, useState } from "react";
import axiosInstance from "../../utils/axios-instance";
import { getFullDate } from "../../utils/formatNumber";
import { useSnackbar } from "notistack";

// icons
import { ReactComponent as EmptyIcon } from '../../images/ic_empty1.svg';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import { InfoJSON, Ticket } from "@/types";
import { ReactComponent as CompanyIcon } from '../../images/ic_company.svg'
import { ReactComponent as UserIcon } from '../../images/ic_user.svg'

const statusColor = {
  1: '#00AB55',
  2: '#B72136',
  3: '#212B36'
}

const ticketStatus: { [key: number]: string } = {
  1: 'در انتظار پاسخ پشتیبانی',
  2: 'در انتظار پاسخ کاربر',
  3: 'بسته شده'
}

const TicketList: FC<{ setLoades: Dispatch<SetStateAction<string[]>> }> = ({ setLoades }) => {
  const { enqueueSnackbar } = useSnackbar();
  const [listLoading, setListLoading] = useState<boolean>(false)
  const [tickets, setTickets] = useState<Ticket[]>([])
  const [lastTickets, setLastTickets] = useState<Ticket[]>([])
  const unreadTickets = useMemo(() => tickets.filter(i => !(i?.infoJson as InfoJSON)?.supporterLastSeenChat)?.length || 0, [tickets])

  useEffect(() => {
    document.getElementById('_loading')!.style.display = 'flex'
    setListLoading(false)
    axiosInstance({
      baseURL: 'https://clickmis.net/',
      url: '/api/ticket',
      method: "get",
    })
      .then((res) => {
        const data = res.data?.map((i: Ticket) => ({
          ...i,
          infoJson: i?.infoJson ? JSON.parse(i?.infoJson as string) : {},
          createTime: i?.createDate
            ? new Date(i.createDate)
              .toLocaleTimeString('fa')
              .split(':')
              .slice(0, 2)
              .join(':')
            : null,
          createDate: i?.createDate ? getFullDate(new Date(i.createDate).toLocaleDateString('fa')) : null,
          createTimeStamp: i?.createDate ? +new Date(i?.createDate) : null,
        })).sort((x: { createTimeStamp: number; }, y: { createTimeStamp: number; }) => y.createTimeStamp - x.createTimeStamp)
        setTickets(data);
        setLastTickets(data.slice(0, 4))
      })
      .catch((e) => {
        console.log(e);
        if (e.code === "ERR_NETWORK") {
          console.log("خطای دسترسی به شبکه");
          enqueueSnackbar("خطای دسترسی به شبکه", {
            variant: "error",
          });
        } else {
          enqueueSnackbar("خطا در دریافت اطلاعات", {
            variant: "error",
          });
        }
      })
      .finally(() => {
        setListLoading(false)
        setLoades((i: string[]) => [...i, 'ticketGet'])
      });
  }, [])
  return (
    <Box sx={{ width: 0, flexBasis: { xs: '100%', lg: '30%' }, px: '12px', py: '20px' }}>
      <Card sx={{ display: 'flex', flexDirection: 'column', boxShadow: '0px 12px 24px -4px rgba(145, 158, 171, 0.12), 0px 0px 2px 0px rgba(145, 158, 171, 0.20)', borderRadius: '16px', pt: 3, px: 3, flexGrow: 1 }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 1, pb: 3 }}>
          <Typography variant="h3" sx={{ fontSize: '16px', fontWeight: 700 }}>تیکت‌ها</Typography>
          <Typography variant="body2" sx={{ fontSize: '14px', fontWeight: 400 }} color="#637381">{unreadTickets} تیکت خوانده نشده دارید</Typography>
        </Box>
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'stretch', justifyContent: 'flex-start', flexGrow: 1, gap: 1 }}>
          {
            lastTickets.length && !listLoading ? lastTickets.map((i, j: number) => (
              <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'stretch', justifyContent: 'center', gap: 1, borderTop: '1px dashed #e5e8eb', py: 2 }} key={i.id}>
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                  <Typography color="#919EAB" sx={{ fontSize: '12px', fontWeight: 400 }}>{i.createTime} {i.createDate}</Typography>
                  <Typography color="#212B36" sx={{ fontSize: '12px', borderRadius: '6px', color: '#fff', backgroundColor: statusColor[i.status as 1 | 2 | 3], px: 1, py: '2px' }}>{ticketStatus[i.status] as string}</Typography>
                </Box>
                <Box>
                  <Typography noWrap color="#212B36" sx={{ fontSize: '12px', fontWeight: 400 }}>{i.subject}</Typography>
                </Box>
                <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                  <Tooltip color="secondary.gray.main" sx={{ fontSize: '14px' }} title={(
                    <Box sx={{ textAlign: 'center' }}>
                      <p>شرکت: {i.companyName}</p>
                      {(i?.infoJson as InfoJSON)?.branch && <p>شعبه: {(i?.infoJson as InfoJSON).branch}</p>}
                      <p>{(i?.infoJson as InfoJSON)?.domain ? (i?.infoJson as InfoJSON)?.domain : ''}</p>
                    </Box>
                  )}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <CompanyIcon />
                      <Typography onClick={(event: any) => (i?.infoJson as InfoJSON)?.domain ? event.stopPropagation() : null} component={!!(i?.infoJson as InfoJSON)?.domain ? 'a' : 'p'} href={(i?.infoJson as InfoJSON)?.domain} target="_blank" color="dimgray.main" sx={{ fontSize: '12px' }}>{(i?.infoJson as InfoJSON)?.companyDescripiton || i.companyName}</Typography>
                    </Box>
                  </Tooltip>
                  <Tooltip color="secondary.gray.main" sx={{ fontSize: '14px' }} title="کاربر">
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <UserIcon />
                      <Typography color="dimgray.main" sx={{ fontSize: '12px' }}>{(i?.infoJson as InfoJSON)?.fullName} ({i.mobile})</Typography>
                    </Box>
                  </Tooltip>
                </Box>
              </Box>
            )) : (
              <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', gap: 3 }}>
                <EmptyIcon />
                <Typography variant="body2" color="#2E3947">هنوز تیکتی ثبت نشده است</Typography>
              </Box>
            )
          }
        </Box>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'flex-end', borderTop: '1px solid #919EAB3D', py: 1, gap: 1 }}>
          <Button component="a" href="/tickets" target="_blank">
            <Typography color="#212B36" sx={{ fontSize: '14px', fontWeight: 700 }}>نمایش همه</Typography>
            <ChevronLeftIcon />
          </Button>
        </Box>
      </Card>
    </Box>
  )
}
export default TicketList