import { create } from 'zustand'

interface ClickStore {
  helpMenu: {
    id: number | string
    title: string
    path: string
    children?: {
      id: number | string
      title: string
      path: string
    }[]
  }[]
  setHelpMenu: (value: {
    id: number | string
    title: string
    path: string
    children?: {
      id: number | string
      title: string
      path: string
    }[]
  }[]) => void
}

const useClickStore = create<ClickStore>((set) => ({
  helpMenu: [],
  setHelpMenu: () => set((value: any) => ({ helpMenu: value }))
}))

export default useClickStore