import { FC, useMemo } from "react";
import Box from "@mui/material/Box";
import TabView from "../components/Tab";
import Header from "../components/Header";

import type { OnChangeSetting, Setting, TabData } from "../types";
import { useEffect, useState } from "react";
import _ from "lodash";
import { KeyboardEvent } from "react";
import axiosInstance, { userGuid, _userGuid } from "../utils/axios-instance";
import { Grid, LinearProgress, Skeleton } from "@mui/material";
import { Container } from "@mui/system";
import { useSnackbar } from "notistack";
import config from "../assets/data/setting";
import axios from "axios";

const SettingView: FC = () => {
	const [data, setData] = useState<Setting[] | []>();
	const [settingLoading, setSettingLoading] = useState<boolean>(true);
	const [filteredData, setFilteredData] = useState<TabData | {}>();
	const [settingChanges, setSettingChanges] = useState<Setting[]>([]);
	const [saveLoading, setSaveLoading] = useState<boolean>(false);
	const [branchList, setBranchList] = useState<{ name: string, value: string }[]>([])
	const {enqueueSnackbar, closeSnackbar} = useSnackbar();
	const configDateList = useMemo(() => config ? Object.keys(config)?.filter((i: any) => config[i].type === 'date') : [], [config])
	// const configDateList = Object.keys(config)?.filter((i: any) => i.type === 'date').map((i: any) => i.Name)
	const save = () => {
		if (settingChanges.length) {
			setSaveLoading(true);

			axios
				.post("/api/Setting/BatchEdit?UserGuid=" + userGuid, settingChanges)
				.then((res) => {
					if (res.status === 200) {
						enqueueSnackbar("تنظیمات با موفقیت ذخیره شد", {
							variant: "success",
						});
						setSettingChanges([]);
					}
				})
				.catch((e) => {
					console.log(e);
					if (e.code === "ERR_NETWORK") {
						console.log("خطای دسترسی به شبکه");
						enqueueSnackbar("خطای دسترسی به شبکه", {
							variant: "error",
						});
					}else {
						enqueueSnackbar("در ذخیره‌سازی تنظیمات خطایی رخ داد!", {
							variant: "error",
						});
					}
				})
				.finally(() => setSaveLoading(false));
		}
	};
	const getCategorized = (data: Setting[]) => {
		const result: TabData = _.groupBy(data, "Category") as unknown as TabData;
		Object.keys(result).forEach(
			(i: string) =>
				(result[i] = _.groupBy(result[i], "FormId") as unknown as {
					[key: string]: Setting[];
				})
		);
		return result;
	};
	const fetchJson = () => {
		if(!_userGuid) return enqueueSnackbar("کاربر یافت نشد!", {
			variant: "error",
		});

		setSettingLoading(true);
		axios
			.get("/api/Setting/List?userGuid=" + userGuid)
			.then((res) => {
				let { data } = res;
				data = data.Data;
				setData(data);
				const result = getCategorized(data);
				setFilteredData(result);
			})
			.catch((e: Error) => {
				console.log(e.message);
				enqueueSnackbar("خطای در دریافت اطلاعات!", {
					variant: "error",
				});
			})
			.finally(() => setSettingLoading(false));
			
			axios.get(`/api/Branch/List?userGuid=${userGuid}`).then(res => {
			if (res.status === 200) {
				if (res?.data?.Data) {
					const branchList = res?.data?.Data.map((i: { Code: string, Title: string, Guid: string }) => ({
						name: i.Code + (i.Title ? ' - ' + i.Title : ''),
						value: i.Guid
					}))
					setBranchList(branchList);
				}
			}
			}).catch(e => console.log(e))
		// }).finally(() => setSettingLoading(true));
	};
	const handleSearchKeyDown: (
		e: KeyboardEvent<HTMLElement & HTMLInputElement>
	) => TabData = (event) => {
		let result = {};
		const filteredList = data?.length
			? data.filter(
					(i: Setting) =>
						i.FormName.indexOf(
							(event.target as HTMLInputElement).value.trim()
						) !== -1 ||
						i.Name.toLowerCase().indexOf(
							(event.target as HTMLInputElement).value.toLowerCase().trim()
						) !== -1
			  )
			: [];
		if (filteredList) {
			const categorizedList = getCategorized(
				filteredList
			) as unknown as Setting[];
			if (categorizedList) {
				result = categorizedList;
				// setFilteredData(categorizedList);
			}
		}
		return result;
	};
	const getNamedValue = (val: any) =>
		String(val)[0].toUpperCase() + String(val).slice(1);
		
	const handleOnChangeSettingForm: OnChangeSetting = (event, setting) => {
		console.log(event, setting);
		// Save New Changes
		const settingChangesList: Setting[] = [...settingChanges];
		const settingIndex = settingChangesList.findIndex(
			(i: Setting) => i.Name === setting.Name
		);
		let newValue = setting.IsCheckBox
			? getNamedValue(event.target?.checked)
			: (configDateList.includes(setting.Name) ? event.target?.value.replace(new RegExp('/', "g"), '') : event.target?.value);
		if (settingIndex > -1) {
			settingChangesList[settingIndex].Value = newValue;
		} else {
			settingChangesList.push({
				...setting,
				Value: newValue,
			});
		}
		// Save new changes in view data
		const dataView: Setting[] | [] = [...(data as Setting[] | [])];
		const itemViewIndex = dataView.findIndex(
			(i: Setting) => i.Name === setting.Name
		);
		if (itemViewIndex > -1) {
			dataView[itemViewIndex].Value = newValue;
			setData(dataView);
			setFilteredData(getCategorized(dataView));
		}
		setSettingChanges(settingChangesList);
		// console.log(settingChangesList)
	};
	useEffect(() => {
		fetchJson();
	}, []);
	return (
		<Container maxWidth="xl">
			{settingLoading ? (
				<>
					{/* <LinearProgress /> */}
					<Box sx={{ px: 2 }}>
						<Skeleton animation="wave" height={72} width={600} />
					</Box>
					<Box
						sx={{ display: "flex", alignItems: "flex-start", gap: 3, px: 2 }}
					>
						{[...Array(8)].map((i: any, j: number) => (
							<Skeleton
								key={j}
								variant="text"
								animation="wave"
								width={88}
								height={48}
							/>
						))}
					</Box>
					<Box sx={{ display: "flex", alignItems: "flex-start" }}>
						<Box
							sx={{
								display: "flex",
								flexDirection: "column",
								alignItems: "flex-start",
								gap: 2,
								mt: 3,
								pl: 2,
							}}
						>
							{[...Array(6)].map((i: any, j: number) => (
								<Skeleton
									key={j}
									variant="text"
									animation="wave"
									width={88}
									height={48}
								/>
							))}
						</Box>
						<Box
							sx={{
								display: "flex",
								flexDirection: "column",
								flexGrow: 1,
								py: 1,
							}}
						>
							<Grid container m={0} sx={{ flexGrow: 1, pl: 1 }}>
								<Grid
									item
									xs={12}
									md={6}
									pt={"0 !important"}
									px={2}
									sx={{ display: "flex", alignItems: "center", gap: 1 }}
								>
									<Skeleton
										variant="rounded"
										width={40}
										height={20}
										sx={{ borderRadius: "16px" }}
									/>
									<Skeleton
										variant="text"
										animation="wave"
										height={20}
										sx={{ fontSize: "1rem", flexGrow: 1 }}
									/>
								</Grid>
								<Grid item xs={12} md={6} pt={"0 !important"} px={2}>
									<Skeleton animation="wave" height={64} />
								</Grid>
								<Grid item xs={12} md={6} pt={"0 !important"} px={2}>
									<Skeleton animation="wave" height={64} />
								</Grid>
								<Grid
									item
									xs={12}
									md={6}
									pt={"0 !important"}
									px={2}
									sx={{ display: "flex", alignItems: "center", gap: 1 }}
								>
									<Skeleton
										variant="rounded"
										width={40}
										height={20}
										sx={{ borderRadius: "16px" }}
									/>
									<Skeleton
										variant="text"
										animation="wave"
										height={20}
										sx={{ fontSize: "1rem", flexGrow: 1 }}
									/>
								</Grid>
								<Grid item xs={12} md={6} pt={"0 !important"} px={2}>
									<Skeleton animation="wave" height={64} />
								</Grid>
								<Grid item xs={12} md={6} pt={"0 !important"} px={2}>
									<Skeleton animation="wave" height={64} />
								</Grid>
								<Grid
									item
									xs={12}
									md={6}
									py={3}
									px={2}
									sx={{ display: "flex", alignItems: "center", gap: 1 }}
								>
									<Skeleton
										variant="rounded"
										width={40}
										height={20}
										sx={{ borderRadius: "16px" }}
									/>
									<Skeleton
										variant="text"
										animation="wave"
										height={20}
										sx={{ fontSize: "1rem", flexGrow: 1 }}
									/>
								</Grid>
								<Grid
									item
									xs={12}
									md={6}
									py={3}
									px={2}
									sx={{ display: "flex", alignItems: "center", gap: 1 }}
								>
									<Skeleton
										variant="rounded"
										width={40}
										height={20}
										sx={{ borderRadius: "16px" }}
									/>
									<Skeleton
										variant="text"
										animation="wave"
										height={20}
										sx={{ fontSize: "1rem", flexGrow: 1 }}
									/>
								</Grid>
								{/* <Grid item xs={12} pt={"0 !important"} px={2}>
									<Skeleton animation="wave" height={64} />
								</Grid> */}
								{/* <Grid
									item
									xs={12}
									py={3}
									px={2}
									sx={{ display: "flex", alignItems: "center", gap: 1 }}
								>
									<Skeleton
										variant="rounded"
										width={40}
										height={20}
										sx={{ borderRadius: "16px" }}
									/>
									<Skeleton
										variant="text"
										animation="wave"
										height={20}
										sx={{ fontSize: "1rem", flexGrow: 1 }}
									/>
								</Grid> */}
							</Grid>
						</Box>
					</Box>
				</>
			) : (
				<Box sx={{ pt: 2 }}>
					<Box sx={{ flexGrow: 1 }}>
						<Header
							handleSearchKeyDown={handleSearchKeyDown}
							onChange={handleOnChangeSettingForm}
							save={save}
							saveLoading={saveLoading}
							saveButtonDisabled={!settingChanges.length}
						/>
					</Box>
					<TabView
						data={filteredData as TabData}
						loading={settingLoading}
						branchList={branchList}
						onChange={handleOnChangeSettingForm}
					/>
				</Box>
			)}
		</Container>
	);
};
export default SettingView;
