import { DayValue } from "@amir04lm26/react-modern-calendar-date-picker";
import numeral from "numeral";

// ----------------------------------------------------------------------
export const getViewDateFormat: (data: string) => string = (date) =>
	`${date.slice(0, 4)}/${date.slice(4, 6)}/${date.slice(6, 8)}`;

export function fCurrency(number: any) {
	return numeral(number).format(Number.isInteger(number) ? "$0,0" : "$0,0.00");
}

export function fPercent(number: any) {
	return numeral(number / 100).format("0.0%");
}

export function fNumber(number: any) {
	return numeral(number).format();
}

export function fShortenNumber(number: any) {
	return numeral(number).format("0.00a").replace(".00", "");
}

export function fData(number: any) {
	return numeral(number).format("0.0 b");
}

export function getFormatedDatePicker(data: string) {
	const dateValue = data?.replaceAll("/", "");
	return dateValue
		? {
				day: +dateValue.slice(6, 8),
				month: +dateValue.slice(4, 6),
				year: +dateValue.slice(0, 4),
		  }
		: null;
}

export const getFormatedDate = (data: string) => {
	const date = getFormatedDatePicker(data);
	return `${date?.year}/${date?.month}/${date?.day}`;
};

export const getFormatedDatePickerExport = (val: DayValue) =>
	`${val?.year}/${String(val?.month).padStart(2, "0")}/${String(
		val?.day
	).padStart(2, "0")}`;
export const getFormatedAPIDate = (val: DayValue) =>
	`${val?.year}${String(val?.month).padStart(2, "0")}${String(
		val?.day
	).padStart(2, "0")}`;

export const getRawNumberFormat: (number: any) => number = (number) => {
	if (number) {
		if (typeof number === "string") {
			return +number.replace(new RegExp(",", "g"), "");
		} else if (typeof number === "number") {
			return number;
		}
	}
	return 0;
};

export const getPriceFormat = (value: string | number) => {
	return new Intl.NumberFormat("fa-IR", {
		// style: 'currency',
		currency: "IRT",

		// These options are needed to round to whole numbers if that's what you want.
		//minimumFractionDigits: 0, // (this suffices for whole numbers, but will print 2500.10 as $2,500.1)
		//maximumFractionDigits: 0, // (causes 2500.99 to be printed as $2,501)
	}).format(+value);
};

export function toEnDigit(s: any) {
	return s?.replace(/[\u0660-\u0669\u06f0-\u06f9]/g, function (a: any) {
		return a.charCodeAt(0) & 15;
	});
}

export const getFullDate = (date: string) =>
	date
		.split("/")
		.map((i) => toEnDigit(i).padStart(2, "0"))
		.join("/");

export const getTicketTypeFromFileName = (name: string) => {
	if (name.match(/\.(jpg|jpeg|png|gif|svg|webg)$/i)) {
		return 3;
	} else if (name.match(/\.(mp3|webm|wav|wma)$/i)) {
		return 2;
	} else {
		return 4;
	}
};
