import {
	Dispatch,
	FC,
	SetStateAction,
	useEffect,
	useMemo,
	useState,
} from "react";
import { Link as RouterLink } from "react-router-dom";
import axiosInstance from "../../utils/axios-instance";
import { UserInfo } from "@/types";

// @mui
import {
	Avatar,
	Box,
	Drawer,
	List,
	ListItem,
	ListItemButton,
	ListItemIcon,
	ListItemText,
	Typography,
	useMediaQuery,
} from "@mui/material";

import Logo from "../../images/logo.png";
import Solemanipor from "../../images/solemanipor.png";

// icons
import iconTicketList from "../../images/ic_ticket.svg";
import iconHelps from "../../images/ic_clipboard-text.svg";
import iconUserList from "../../images/ic_user.svg";
import iconDashboard from "../../images/ic_dashboard.svg";
import iconAddItem from "../../images/ic_additem.svg";
import iconMenuBoard from "../../images/menu-board.svg";
import { ReactComponent as LogoutIcon } from "../../images/logout.svg";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import { useSnackbar } from "notistack";
const routes: {
	name: string;
	link: string;
	target?: string;
	icon: any;
}[] = [
	{
		name: "داشبورد",
		link: "dashboard",
		icon: iconDashboard,
	},
	{
		name: "لیست کاربران",
		link: "userlist",
		target: "_blank",
		icon: iconUserList,
	},
	{
		name: "افزودن خودآموز",
		link: "helpForm",
		target: "_blank",
		icon: iconAddItem,
	},
	{
		name: "لیست خودآموز‌ها",
		link: "helpList",
		target: "_blank",
		icon: iconHelps,
	},
	{
		name: "افزودن بلاگ",
		link: "blogForm",
		target: "_blank",
		icon: iconAddItem,
	},
	{
		name: "لیست بلاگ‌ها",
		link: "blog",
		target: "_blank",
		icon: iconHelps,
	},
	{
		name: "افزودن پرسش متداول",
		link: "faqForm",
		target: "_blank",
		icon: iconAddItem,
	},
	{
		name: "پرسش‌های متداول",
		link: "faq",
		target: "_blank",
		icon: iconHelps,
	},
	{
		name: "لیست تیکت‌ها",
		link: "tickets",
		target: "_blank",
		icon: iconTicketList,
	},
	{
		name: "لیست تسک‌ها",
		link: "taskList",
		target: "_blank",
		icon: iconMenuBoard,
	},
];

const drawerWidth = 260;

const DashboardNavbar: FC<{
	setLoades?: Dispatch<SetStateAction<string[]>>;
}> = ({ setLoades }) => {
	const { enqueueSnackbar } = useSnackbar();
	const [userInfo, setUserInfo] = useState<UserInfo | null>(null);
	const currentPage: string = useMemo(
		() => location.pathname.slice(1),
		[location]
	);
	const matches = useMediaQuery("(min-width:960px)");
	const logout = () => {
		window.localStorage.removeItem("item");
		window.open("/Login", "_self");
	};
	useEffect(() => {
		axiosInstance
			.get("/User")
			.then((res) => {
				console.log(res);
				setUserInfo({
					...res.data.user,
					fullName: `${res.data.user.name} ${res.data.user.lastName}`,
				});
			})
			.catch((e) => {
				console.log(e);
				if (e.code === "ERR_NETWORK") {
					console.log("خطای دسترسی به شبکه");
					enqueueSnackbar("خطای دسترسی به شبکه", {
						variant: "error",
					});
				} else {
					enqueueSnackbar("خطا در دریافت اطلاعات", {
						variant: "error",
					});
				}
			})
			.finally(() => {
				setLoades?.((i: string[]) => [...i, "userGet"]);
			});
	}, []);
	return (
		<Drawer
			variant={matches ? "permanent" : "temporary"}
			anchor="left"
			sx={{
				width: drawerWidth,
				flexShrink: 0,
				"& .MuiDrawer-paper": {
					width: drawerWidth,
					boxSizing: "border-box",
					borderRight: "1px dashed rgba(145, 158, 171, 0.2)",
				},
				borderRight: "1px dashed rgba(145, 158, 171, 0.2)",
			}}
		>
			<Box sx={{ p: "24px 16px" }} display={"flex"} alignItems={"center"}>
				<img
					src={Logo}
					alt="نرم افزار حسابداری آنلاین و ابری کلیک"
					style={{ height: "36px" }}
				/>

				<Typography sx={{ fontSize: "14px", ml: 2 }}>
					نرم افزار حسابداری آنلاین کلیک
				</Typography>
			</Box>

			<Box
				sx={{
					display: "flex",
					flexDirection: "column",
					justifyContent: "center",
					background: "rgba(145, 158, 171, 0.08)",
					borderRadius: "12px",
					mx: 3,
					gap: 2,
					p: 2,
				}}
			>
				<Box sx={{ display: "flex", align: "center" }}>
					<Avatar alt={userInfo?.fullName} src={Solemanipor} sx={{ mr: 2 }} />
					<Box>
						<Typography
							color="#212B36"
							sx={{ fontSize: "14px", fontWeight: 500 }}
						>
							{userInfo?.fullName}
						</Typography>
						<Typography
							color="#637381"
							sx={{ fontSize: "14px", fontWeight: 400 }}
						>
							مدیر
						</Typography>
					</Box>
				</Box>
				<Box
					sx={{ width: "100%", height: "2px", backgroundColor: "#dee2e6" }}
				></Box>
				<Box
					onClick={logout}
					sx={{
						display: "flex",
						alignItems: "center",
						gap: 2,
						cursor: "pointer",
					}}
				>
					<LogoutIcon />
					<Typography
						color="#B72136"
						sx={{ fontSize: "14px", fontWeight: 400 }}
					>
						خروج
					</Typography>
				</Box>
			</Box>

			<Typography sx={{ fontSize: "14px", ml: 2, mt: 2, fontWeight: 700 }}>
				مدیریت
			</Typography>

			<List sx={{ px: 2 }}>
				{routes.map((route, index: number) => (
					<ListItem key={index} disablePadding>
						<ListItemButton
							component={RouterLink}
							to={"/" + route.link}
							target={route?.target}
							sx={{
								color: "#637381",
								"&:hover": {
									backgroundColor: "#EDFDFF",
									"& .menu-title": { color: "#3366FF" },
								},
								borderRadius: "8px",
								backgroundColor: route?.link === currentPage ? "#EDFDFF" : "",
							}}
						>
							<ListItemIcon>
								<img
									style={{ width: "24px", height: "24px" }}
									src={route.icon}
								/>
							</ListItemIcon>
							<ListItemText
								className="menu-title"
								sx={{
									"& .MuiTypography-root": {
										fontSize: "14px",
										fontWeight: route?.link === currentPage ? 500 : 400,
										color: route?.link === currentPage ? "#3366FF" : "#637381",
									},
								}}
								primary={route.name}
							/>
							<ChevronLeftIcon
								sx={{
									color: route?.link === currentPage ? "#3366FF" : "#637381",
								}}
							/>
						</ListItemButton>
					</ListItem>
				))}
			</List>
		</Drawer>
	);
};
export default DashboardNavbar;
