import { FC, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import axiosInstance from '../utils/axios-instance'
import { Plan, Period } from 'types'

// @mui
import { Box, Container, Skeleton, Stack, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@mui/material'

// component
import Header from '../layouts/Header'
import Footer from '../components/layout/Footer'

// icons
import { ReactComponent as CheckIcon } from '../images/icons/ic_checkmark.svg'
import { ReactComponent as NoCheckIcon } from '../images/icons/ic_close.svg'

const Features: FC = () => {
  const { t } = useTranslation('features')
  const [ rows, setRows ] = useState<{
    id: number | string,
    name: string,
    isCategory?: boolean
  }[]>([])
  const [ heads, setHeads ] = useState<string[]>([])
  const [selectedPlans, setSelectedPlans] = useState<string[]>([])
  const [loading, setLoading] = useState<boolean>(true)

  const togglePlan = (plan: string) => {
    const newSelectedPlans = [...selectedPlans]
    const planIndex = newSelectedPlans.indexOf(plan)
    if (planIndex > -1) {
      newSelectedPlans.splice(planIndex, 1)
    } else {
      newSelectedPlans.push(plan)
    }
    setSelectedPlans(newSelectedPlans)
  }

  useEffect(() => {
    axiosInstance({
      baseURL: 'https://clickmis.net/',
      url: '/api/Plan',
      method: 'get'
    }).then(res => {
        if (res.status === 200) {
          const planList = JSON.parse(res.data.planList).map((i: Plan, j: number) => ({ ...i, planIndex:  j }))
          const activePlanList = planList.filter((x: { disableList: any[] }) => !x?.disableList?.length)
          const groupList = JSON.parse(res.data.groupList)
          const systemItems = groupList.map((i: { name: string; subgroups: { planStatus: boolean[][] }[] }, j: number) => ({
            id: `s${j}`,
            name: i.name,
            ...(Object.fromEntries(activePlanList.map((x: { name: any, planIndex: number }) => {
              return [x.name, i.subgroups.some((y: { planStatus: boolean[][] }) => y.planStatus[5][x.planIndex])]
            })))
          }))
          const subgroups = groupList.map((i: { subgroups: any }) => i.subgroups).flat().map((i: { name: any; planStatus: boolean[][] }, j: any) => {
            return {
              id: `E${j}`,
              name: i.name,
              ...(Object.fromEntries(activePlanList.map((x: { name: string, planIndex: number }) => {
                return [x.name, i.planStatus[5][x.planIndex]]
              })))
            }
          })
          setRows([
            {
              id: 1,
              name: 'سیستم',
              isCategory: true
            },
            ...systemItems,
            {
              id: 2,
              name: 'امکانات ویژه',
              isCategory: true
            },
            ...subgroups,
            {
              id: 3,
              name: 'خدمات',
              isCategory: true,
            },
            {
              "id": 'k1',
              "name": "اپلیکیشن موبایلی",
              "استاندارد": false,
              "پیشرفته": true,
              "برنزی": true,
              "نقره ای(حرفه‌ای)": true,
              "طلایی(پلاس)": true
            },
            {
              "id": 'k2',
              "name": "سرویس OTP",
              "استاندارد": false,
              "پیشرفته": false,
              "برنزی": false,
              "نقره ای(حرفه‌ای)": true,
              "طلایی(پلاس)": true
            },
            {
              "id": 'k3',
              "name": "چندزبانه",
              "استاندارد": false,
              "پیشرفته": false,
              "برنزی": false,
              "نقره ای(حرفه‌ای)": true,
              "طلایی(پلاس)": true
            },
            {
              "id": 'k4',
              "name": "شعبه پذیری",
              "استاندارد": false,
              "پیشرفته": false,
              "برنزی": false,
              "نقره ای(حرفه‌ای)": true,
              "طلایی(پلاس)": true
            },
            {
              "id": 'k5',
              "name": "تعداد کاربر",
              "استاندارد": 1,
              "پیشرفته": 2,
              "برنزی": 3,
              "نقره ای(حرفه‌ای)": 4,
              "طلایی(پلاس)": 5
            },
          ])
          setHeads(['امکانات و ویژگی‌ها', ...activePlanList.map((i: { name: string }) => i.name)])
        }
    }).catch(console.log).finally(() => setLoading(false))
  }, [])
  return (
    <Box sx={{ backgroundColor: '#fff' }}>
      <Header />

      <Container maxWidth="lg" sx={{ pt: 11, pb: 1 }}>
        <Stack direction="column" alignItems="center" justifyContent="center" gap={2} sx={{ mt: { xs: '56px', md: '88px' } }}>
          <Typography variant="h1" sx={{ color: '#212B36', fontSize: '44px', fontWeight: 700 }}>{t('FacilitiesAndFeatures')}</Typography>
          <Typography variant="h2" sx={{ color: '#637381', fontSize: '18px' }}>{t('ComparingFeaturesAndFeaturesOfPlans')}</Typography>
        </Stack>
        <Stack sx={{ pt: 7 }}>
          {!loading ? (<TableContainer>
            <Table sx={{ minWidth: 700, '& .MuiTableCell-root': { border: 'none' } }} aria-label="customized table">
              <TableHead sx={{ backgroundColor: '#0154A4', borderRadius: '16px', '& .MuiTableCell-root': { color: '#fff' } }}>
                <TableRow>
                  {heads.map((i, j) => <TableCell align="center" key={i} sx={{ width: { xs: j === 0 ? 118 : 71, md: 220 }, p: 1, borderRadius: j === 0 ? '16px 0 0 16px' : j === heads.length - 1 ? '0 16px 16px 0' : '' }}>
                    <Typography variant="h3" onClick={() => togglePlan(i)} sx={{ fontWeight: 600, fontSize: { xs: '14px', md: '16px' }, cursor: 'pointer', transition: 'background-color 0.4s ease-in-out', borderRadius: '8px', p: 1, backgroundColor: selectedPlans.includes(i) ? '#00376D' : '' }}>{i}</Typography>
                  </TableCell>)}
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map((row, index) => (
                  <TableRow key={row.id}>
                    <TableCell align="center" sx={{ width: { xs: 118, md: 220 }, pt: index === 0 ? { xs: '4px', md: 1 } : 0, pb: index === rows.length-1 ? { xs: '4px', md: 1 } : 0, px: 0 }}>
                      <Box sx={{ height: index === 0 ? { xs: '54px', md: '63px' } : index === rows.length - 1 ? { xs: 54, md: 71 } : 46, display: 'flex', p: index === 0 ? { xs: '12px 0px 4px 12px', md: '24px 0px 8px 24px' } : index === rows.length -1 ? { xs: '4px 0px 12px 12px', md: '8px 0px 24px 24px' } : { xs: '4px 0px 4px 12px', md: '8px 0px 8px 24px' }, ...(index === 0 ? { border: '1px solid #0154A4', borderWidth: '1px 0 0 1px', borderRadius: '16px 0 0 0' } : {  borderLeft: '1px solid #0154A4' }), ...(index === rows.length - 1 ? { borderBottom: '1px solid #0154A4', borderRadius: '0 0 0 8px' } : {}) }}>
                        <Box sx={{ height: '38px', flexGrow: 1, display: 'flex', alignItems: 'center', backgroundColor: !row.isCategory ? '#F6F7F8' : 'transparent', borderRadius: '8px 0 0 8px' }}>
                          <Typography variant={row.isCategory ? 'h2' : 'h3'} noWrap sx={{ lineHeight: 1.6, fontSize: { xs: '12px', md: '14px' }, fontWeight: row.isCategory ? 700 : 400, width: { xs: 71, md: '220px' }, height: '38px', flexGrow: 1, p: 1, borderRadius: '8px', ...(row.isCategory ? { backgroundColor: '#00AB55', color: '#fff', } : { backgroundColor: '#EDEFF2' })}} title={row.name}> {row.name} </Typography>
                        </Box>
                      </Box>
                    </TableCell>
                    {!row.isCategory ? heads.slice(1).map((x, y) =><TableCell align="center" key={x} sx={{ width: { xs: 71, md: 220 }, verticalAlign: index === rows.length - 1 && y === heads.length - 2 ? 'text-top' : '', p: 0 }}>
                      <Box sx={{ p: { xs: y === heads.length - 2 ? '4px 12px 0 0' : '4px 0 0 0', md: y === heads.length - 2 ? '8px 24px 0 0' : index === rows.length - 1 ? 0 : '8px 0 0 0' }, height: index === rows.length - 1 ? y === heads.length - 2 ? { xs: 54, md: 71 } : { xs: 50, md: 63 } : 46, display: 'flex', ...(y === heads.length - 2 ? { borderRight: '1px solid #0154A4', borderRadius: index === rows.length - 1 ? '0px 0px 8px 0px' : '' } : {}), ...( index === rows.length - 1 ? { borderBottom: '1px solid #0154A4' } : {} ) }}>
                        <Box sx={{ height: '38px', backgroundColor: '#F6F7F8', flexGrow: 1, p: 1, display: 'flex', ...(y === heads.length - 2 ? { borderRadius: '0 8px 8px 0' } : {}) }}>
                          <Box sx={{ borderRadius: '8px', transition: 'background-color 0.4s ease-in-out', backgroundColor: selectedPlans.includes(x) ? '#DFE3E8' : '', flexGrow: 1, display: 'flex', alignItems: 'center', justifyContent: 'center', p: 1 }}>
                            {typeof row?.[x] === 'boolean' ? row?.[x] ? <CheckIcon /> : <NoCheckIcon /> : row?.[x]}
                          </Box>
                        </Box>
                      </Box>
                    </TableCell>) : (
                      <TableCell align="center" colSpan={heads.length - 2} sx={{ pt: index === 0 ? { xs: '4px', md: 1 } : 0, pb: index === rows.length-1 ? { xs: '4px', md: 1 } : 0, px: 0 }}>
                        <Box sx={{ height: index === 0 ? { xs: '54px', md: '63px' } : 46, display: 'flex', alignItems: 'center', justifyContent: 'center', ...(index === 0 ? { borderTop: '1px solid #0154A4' } : {})}}></Box>
                      </TableCell>
                    )}
                    {row.isCategory && (<TableCell align="center" sx={{ width: { xs: 71, md: 220 }, pt: index === 0 ? { xs: '4px', md: 1 } : 0, pb: index === rows.length-1 ? { xs: '4px', md: 1 } : 0, px: 0 }}>
                      <Box sx={{ height: index === 0 ? { xs: '54px', md: '63px' } : 46, display: 'flex', alignItems: 'center', justifyContent: 'center', ...(index === 0 ? { border: '1px solid #0154A4', borderWidth: '1px 1px 0 0', borderRadius: '0 16px 0 0' } : { borderRight: '1px solid #0154A4' })}}></Box>
                    </TableCell>)}
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>) : (
            <Stack direction="column" gap={1} sx={{ mb: 2 }}>
              <Skeleton variant="rounded" width="100%" height={50} />
              <Skeleton variant="rounded" width="100%" height={800} />
            </Stack>
          )}
        </Stack>
      </Container>

      <Footer />
    </Box>
  )
}
export default Features