import { Link } from "react-router-dom";
import { FC, useState, useEffect, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { useFormik } from "formik";
import { object, string, number } from "yup";
import axios from "../utils/axios-instance";
import {
	TextField,
	Alert,
	Box,
	Select,
	MenuItem,
	Container,
	Button,
	FormControl,
	FormHelperText,
	IconButton,
	InputAdornment,
	InputLabel,
	OutlinedInput,
	CircularProgress,
	LinearProgress,
	FormControlLabel,
	Checkbox,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";
import InfoIcon from "@mui/icons-material/Info";

import Logo from "../images/logo.svg";
import AuthBannerBg from "../images/auth-banner-bg.jpg";
import AuthBanner from "../images/auth-banner.png";
import LanguageSelector from "../components/LanguageSelector";
import { useTranslation } from "react-i18next";
import { VisibilityOff, Visibility, CheckBox } from "@mui/icons-material";
import { setScreenLoading } from "../utils";
import axiosInstance from "../utils/axios-instance";
//@ts-ignore
import jalaali, { jalaaliToDateObject } from "jalaali-js";
import { User } from "../pages/UserList";
import utils from "../utils/utils";

type UserFormProps = {
	currentUser?: User;
	plans?: any;
	callbacks?: any;
};

const UserForm: FC<UserFormProps> = ({ currentUser, plans, callbacks }) => {
	const { t } = useTranslation();
	const [loading, setLoading] = useState(false);
	const navigate = useNavigate();
	const { enqueueSnackbar } = useSnackbar();
	const [error, setError] = useState("");
	const isEdit = Boolean(currentUser);

	const initialValues = useMemo(() => {
		const _initialValues = {
			name: "",
			lastName: "",
			mobile: "",
			support: false,
			//nullable
			planName: null,
			planNumber: 0,
			planIndex: 0,
			email: null,
			password: null,
			meta: null,
			otp: null,
			planId: null,
			paymentStatus: null,
			siteName: null,
			companyName: null,
			planStartDate: currentUser?.planStartDate as string,
			planEndDate: currentUser?.planEndDate as string,
			planList: null,
		};
		Object.assign(_initialValues, currentUser);
		if (isEdit) {
			_initialValues.planStartDate = _initialValues.planStartDate
				? utils.toDateString(_initialValues.planStartDate)
				: _initialValues.planStartDate;
			_initialValues.planEndDate = _initialValues.planEndDate
				? utils.toDateString(_initialValues.planEndDate)
				: _initialValues.planEndDate;
				_initialValues.planNumber = _initialValues.planNumber || 0
				_initialValues.planIndex = _initialValues.planIndex || 0
				//@ts-ignore
				_initialValues.support = _initialValues.support && _initialValues.support == "true"
		}

		return _initialValues;
	}, [currentUser]);

	const userForm = useFormik({
		initialValues: initialValues,
		validate: (values) => {
			if (
				(values.planIndex === 0 && values.planNumber !== 0) ||
				(values.planIndex !== 0 && values.planNumber === 0)
			) {
				return {
					planIndex: "شماره پلن معتبر نمی باشد!",
					planNumber: "مدت پلن معتر نمی باشد!",
				};
			}
			return {};
		},
		validationSchema: object({
			name: string().required(t("insertName") as string),
			lastName: string().required(t("insertFamily") as string),
			planIndex: number().required("شماره پلن را وارد نمایید!"),
			planNumber: number().required("مدت پلن را وارد نمایید"),

			planStartDate: string()
				.nullable()
				.matches(/^\d{4}\/\d{2}\/\d{2}$/, "تاریخ صحیح نمی‌باشد!"),
			planEndDate: string()
				.nullable()
				.matches(/^\d{4}\/\d{2}\/\d{2}$/, "تاریخ صحیح نمی‌باشد!"),
			mobile: string()
				.required(t("mobileRequiredError") as string)
				.matches(/^(0)?([9][0-9]{9})$/, t("mobileValidationError") as string),
			email: string()
				.nullable()
				.matches(/(.+)@(.+){2,}\.(.+){2,}/, "ایمیل صحیح نمی‌باشد!"),
		}),
		onSubmit: (values) => {
			if (loading) return;
			setLoading(true);
			const getDate = (d: any) => {
				if (!d) return d;
				const ds = d.split("/");
				return jalaaliToDateObject(+ds[0], +ds[1], +ds[2], 12, 0, 0);
			};

			const input = { ...values } as any;
			input.planStartDate = getDate(input.planStartDate);
			input.planEndDate = getDate(input.planEndDate);
			input.planNumber = input.planNumber || null;
			input.planIndex = input.planIndex || null;
			input.planName = input.planIndex
				? planList[input.planIndex - 1]?.name
				: null;
			const promise = axios.put("User", input);
			promise
				.then((response) => {
					if (response.data.result === "ok") {
						enqueueSnackbar("عملیات انجام شد.", {
							variant: "success",
						});
					} else {
						response.data.error && setError(response.data.error);
					}

					return response.data?.user;
				})
				.catch((error) => {
					if (!error._handled) {
						enqueueSnackbar(
							error.response?.data?.error || "خطایی در عملیات روی داد",
							{
								variant: "error",
							}
						);
					}

					setError(t("errorTryAgain") as string);
				})
				.finally(() => setLoading(false));

			return promise;
		},
	});

	const [showPassword, setShowPassword] = useState(false);

	const handleClickShowPassword = () => setShowPassword((show) => !show);

	const handleMouseDownPassword = (
		event: React.MouseEvent<HTMLButtonElement>
	) => {
		event.preventDefault();
	};

	const [planList, setPlanList] = useState(plans);

	useEffect(() => {
		let mounted = true;
		!planList &&
			axiosInstance
				.get("/Plan")
				.then((response) => {
					if (response.data?.result === "ok") {
						response.data?.planList &&
							setPlanList(JSON.parse(response.data?.planList));
						// response.data?.planList &&
						// 	setGroupList(JSON.parse(response.data?.groupList));

						return JSON.parse(response.data?.planList);
					}
				})
				.catch((error) => {
					if (!error._handled) {
						enqueueSnackbar(
							error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
							{
								variant: "error",
							}
						);
					}
				})
				.finally(() => setScreenLoading(false));

		return () => {
			mounted = false;
		};
	}, []);

	callbacks &&
		(callbacks.userFormSubmit = () => {
			if (!userForm.isValid) {
				enqueueSnackbar(
					<span style={{ whiteSpace: "pre" }}>
						{"خطایی در عملیات رخداد: \n" +
							Object.values(userForm.errors)
								.filter((e) => e)
								.join("\n")}
					</span>,
					{
						variant: "error",
						autoHideDuration: 10000,
					}
				);
			}
			return userForm.submitForm();
		});

	return (
		<div className="flex max-w-[480px] pt-4 lg:shrink-0 lg:items-center">
			<div className="w-full">
				{error && (
					<Alert severity="error" className="mb-6 w-full">
						{error}
					</Alert>
				)}

				<form onSubmit={userForm.handleSubmit} autoComplete="off">
					<div className="mb-5 flex flex-col gap-2 lg:flex-row">
						<TextField
							autoFocus
							label={t("name")}
							className="mb-5 flex-grow lg:ml-0 lg:mb-0"
							error={userForm.touched.name && Boolean(userForm.errors.name)}
							helperText={userForm.touched.name && userForm.errors.name}
							{...userForm.getFieldProps("name")}
						/>

						<TextField
							label={t("family")}
							className="flex-grow"
							error={
								userForm.touched.lastName && Boolean(userForm.errors.lastName)
							}
							helperText={userForm.touched.lastName && userForm.errors.lastName}
							{...userForm.getFieldProps("lastName")}
						/>
					</div>

					<div className="mb-5 flex flex-col gap-2 lg:flex-row">
						<TextField
							label="دامنه"
							className="mb-5 flex-grow lg:ml-0 lg:mb-0"
							error={
								userForm.touched.siteName && Boolean(userForm.errors.siteName)
							}
							helperText={userForm.touched.siteName && userForm.errors.siteName}
							{...userForm.getFieldProps("siteName")}
						/>

						<TextField
							label="نام شرکت"
							className="flex-grow"
							error={
								userForm.touched.companyName &&
								Boolean(userForm.errors.companyName)
							}
							helperText={
								userForm.touched.companyName && userForm.errors.companyName
							}
							{...userForm.getFieldProps("companyName")}
						/>
					</div>

					<Box display={"flex"}>
						<FormControlLabel
							label="کاربر پشتیبان می باشد"
							sx={{ flex: 1 }}
							control={<Checkbox checked={userForm.values.support} {...userForm.getFieldProps("support")} />}
						/>

						<TextField
							autoComplete="off"
							inputProps={{ name: "email", autoComplete: "off" }}
							fullWidth
							label={<label>ایمیل</label>}
							className="mb-5"
							type="email"
							sx={{ flex: 1 }}
							error={userForm.touched.email && Boolean(userForm.errors.email)}
							helperText={userForm.touched.email && userForm.errors.email}
							{...userForm.getFieldProps("email")}
						/>
					</Box>

					<TextField
						fullWidth
						label={<label>{t("mobile")}</label>}
						className="mb-5"
						autoComplete="off"
						inputProps={{ name: "mobile", autoComplete: "off" }}
						error={userForm.touched.mobile && Boolean(userForm.errors.mobile)}
						helperText={userForm.touched.mobile && userForm.errors.mobile}
						{...userForm.getFieldProps("mobile")}
					/>

					<FormControl
						fullWidth
						className="mb-5"
						variant="outlined"
						error={
							userForm.touched.password && Boolean(userForm.errors.password)
						}
					>
						<InputLabel htmlFor="outlined-adornment-password">
							{t("password")}
						</InputLabel>
						<OutlinedInput
							{...userForm.getFieldProps("password")}
							id="outlined-adornment-password"
							type={showPassword ? "text" : "password"}
							autoComplete="new-password"
							name="password"
							endAdornment={
								<InputAdornment position="end">
									<IconButton
										aria-label="toggle password visibility"
										onClick={handleClickShowPassword}
										onMouseDown={handleMouseDownPassword}
										edge="end"
									>
										{showPassword ? <VisibilityOff /> : <Visibility />}
									</IconButton>
								</InputAdornment>
							}
							label={t("password")}
						/>

						<FormHelperText id="outlined-weight-helper-text">
							{userForm.touched.password && userForm.errors.password}
						</FormHelperText>
					</FormControl>

					<div className="mb-5 flex flex-col gap-2 lg:flex-row">
						<FormControl
							fullWidth
							error={
								userForm.touched.planIndex && Boolean(userForm.errors.planIndex)
							}
						>
							<InputLabel>شماره پلن‌(برای دسترسی‌ها) </InputLabel>
							<Select
								{...userForm.getFieldProps("planIndex")}
								id="planIndex"
								name="planIndex"
								label="شماره پلن‌(برای دسترسی‌ها)  "
								fullWidth
							>
								<MenuItem
									value={0}
									// disabled={Boolean(userForm.values.planNumber)}
								>
									ندارد
								</MenuItem>
								{(planList as any)?.map((plan: any, index: any) => (
									<MenuItem value={index + 1} key={index}>
										{plan.name}
									</MenuItem>
								))}
							</Select>
							{!planList && (
								<LinearProgress sx={{ mx: 1, borderRadius: 100 }} />
							)}
						</FormControl>

						<FormControl
							fullWidth
							error={
								userForm.touched.planNumber &&
								Boolean(userForm.errors.planNumber)
							}
						>
							<InputLabel>مدت زمان پلن </InputLabel>
							<Select
								{...userForm.getFieldProps("planNumber")}
								id="planNumber"
								name="planNumber"
								label="مدت زمان پلن"
							>
								<MenuItem
									value={0}
									disabled={Boolean(userForm.values.planIndex)}
								>
									ندارد
								</MenuItem>
								{/* <MenuItem value={1}>یک ماه</MenuItem> */}
								<MenuItem value={6}>  دو ماهه </MenuItem>
								{/* <MenuItem value={2}>سه ماه</MenuItem> */}
								{/* <MenuItem value={3}>شش ماه</MenuItem> */}
								<MenuItem value={4}> یک سال </MenuItem>
								<MenuItem value={5}> مادام العمر </MenuItem>
							</Select>
						</FormControl>
					</div>

					<div className="mb-5 flex flex-col gap-2 lg:flex-row">
						<TextField
							label="تاریخ آغاز پلن"
							className="mb-5 flex-grow lg:ml-0 lg:mb-0"
							placeholder="__/__/____"
							error={
								userForm.touched.planStartDate &&
								Boolean(userForm.errors.planStartDate)
							}
							helperText={
								userForm.touched.planStartDate && userForm.errors.planStartDate
							}
							{...userForm.getFieldProps("planStartDate")}
						/>

						<TextField
							label="تاریخ اتمام(انقضا) پلن"
							className="flex-grow"
							placeholder="__/__/____"
							error={
								userForm.touched.planEndDate &&
								Boolean(userForm.errors.planEndDate)
							}
							helperText={
								userForm.touched.planEndDate && userForm.errors.planEndDate
							}
							{...userForm.getFieldProps("planEndDate")}
						/>
					</div>
					{/* 
					<LoadingButton
						fullWidth
						disableElevation
						variant="contained"
						size="large"
						type="submit"
						className="bg-blue-700 hover:bg-blue-900"
						loading={loading}
						onClick={() => console.log(userForm.errors)}
						sx={{
							"&:hover": {
								boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
							},
						}}
					>
						{isEdit ? "ویرایش کاربر" : "افزودن"}
					</LoadingButton> */}
				</form>

				<div className="flex justify-center lg:hidden"></div>
			</div>
		</div>
	);
};

export default UserForm;
