import { Dispatch, FC, SetStateAction, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link as RouterLink } from "react-router-dom";
import { UserInfo } from "@/types";

// @mui
import {
	Button,
	Box,
	List,
	ListItem,
	Alert,
	Typography,
	IconButton,
} from "@mui/material";
import { useTheme } from "@mui/material/styles";

// components
import HeaderBreadcrumbs from "../HeaderBreadcrumbs";

// icons
import AddIcon from "@mui/icons-material/Add"; // icons
import KeyboardBackspaceIcon from "@mui/icons-material/KeyboardBackspace"; // icons
import { ReactComponent as InfoIcon } from "../../images/ic_info.svg";

const TicketHeaderToolbar: FC<{
	id: any;
}> = ({ id }) => {
	const { t } = useTranslation("task");
	const gt = useTranslation().t;
	const theme = useTheme();
	const [showPageInfo, setShowPageInfo] = useState<boolean>(
		localStorage.showPageInfo === "true"
	);
	const showPageInfoHandleChange = (val: boolean) => {
		setShowPageInfo(val);
		localStorage.setItem("showPageInfo", JSON.stringify(val));
	};

	const points = [];

	return (
		<Box>
			<Box
				sx={{
					width: "100%",
					display: "flex",
					alignItems: "center",
					justifyContent: "space-between",
				}}
			>
				<HeaderBreadcrumbs
					heading={t("TicketList")}
					links={[
						{ name: gt("Dashboard"), href: "/dashboard" },
						{ name: t("Tasks"), href: "/taskList" },
						{ name: t("Task") + " " + id },
					]}
				/>
				<Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
					<IconButton onClick={() => showPageInfoHandleChange(!showPageInfo)}>
						<InfoIcon />
					</IconButton>
					{/* @ts-ignore */}

					<Button
						to="/taskList"
						component={RouterLink}
						variant="outlined"
						// @ts-ignore
						endIcon={
							<KeyboardBackspaceIcon
								sx={{ color: theme.palette.secondary.dimgray.main }}
							/>
						}
						sx={{
							// @ts-ignore
							color: theme.palette.secondary.dimgray.main,
							fontWeight: 700,
							// @ts-ignore
							borderColor: theme.palette.secondary.gray.light3,
							"&:hover": {
								// @ts-ignore
								borderColor: theme.palette.secondary.gray.light3,
								backgroundColor: "rgba(145, 158, 171, 0.08)",
							},
						}}
					>
						{t("Back")}
					</Button>
				</Box>
			</Box>

			{/* @ts-ignore */}
			{showPageInfo && (
				<Alert
					severity="info"
					icon={<InfoIcon />}
					sx={{ backgroundColor: theme.palette.info.pale, mb: 3 }}
				>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							justifyContent: "center",
						}}
					>
						{/* @ts-ignore */}
						<Typography
							variant="h3"
							sx={{
								fontSize: "16px",
								fontWeight: 700,
								color: theme.palette.secondary.primary.muted,
							}}
						>
							{t("TicketTitle")}
						</Typography>
						<List
							sx={{
								listStyleType: "disc",
								listStylePosition: "inside",
								// @ts-ignore
								color: theme.palette.secondary.primary.muted,
								p: 0,
							}}
						>
							{points.map((i) => (
								<ListItem key={i} sx={{ display: "list-item" }}>
									{t(i)}
								</ListItem>
							))}
						</List>
					</Box>
				</Alert>
			)}
		</Box>
	);
};
export default TicketHeaderToolbar;
