import { Dispatch, FC, SetStateAction, useEffect, useMemo, useState, memo } from 'react';
import { useTranslation } from 'react-i18next';
import { Report, ItemInventory } from '@/types';
import Scrollbar from '../Scrollbar';
import { NumericFormat } from 'react-number-format';
import axiosInstance, { userGuid } from '../../utils/axios-instance';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';

// @mui
import { Paper, TableContainer, Table, TableHead, TableRow, TableCell, TableBody, TablePagination, Box, Typography, IconButton, TextField } from '@mui/material';
import { useTheme } from '@mui/material/styles';

// icons
import { ReactComponent as EditIcon } from '../../images/ic_edit-gray.svg'
import { ReactComponent as CheckIcon } from '../../images/ic_checkmark.svg'
import { ReactComponent as CloseIcon } from '../../images/ic_close.svg'
import { getPriceFormat } from '../../utils/formatNumber';

const ItemsTable: FC<{ rows: Report[], rowsPerPage: number, setRowsPerPage: Dispatch<SetStateAction<number>>, page: number, setPage: Dispatch<SetStateAction<number>>, itemCount: number, tableSettings: any[], setTableSettings: Dispatch<SetStateAction<any[]>> }> = ({ rows, rowsPerPage, setRowsPerPage, page, setPage, itemCount, tableSettings, setTableSettings }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
	const { enqueueSnackbar } = useSnackbar();
  const theme = useTheme();
  const [editModeRow, setEditModeRow] = useState<Report | null>(null)
  const tableHeads = useMemo(() => tableSettings?.filter(i => i.show) || [], [tableSettings])

  const handleChangeRowsPerPage = (event: { target: { value: string; }; }) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  function NumberFormatCustom(props: { [x: string]: any; name?: any; inputRef?: any; onChange?: any; }) {
    const { inputRef, onChange, ...other } = props;
  
    return (
      // @ts-ignore
      <NumericFormat
        {...other}
        getInputRef={inputRef}
        onValueChange={(values: { value: any, formattedValue: string }) => {
          onChange({
            target: {
              name: props.name,
              value: values.formattedValue
            }
          });
        }}
        thousandSeparator
        // isNumericString
      />
    );
  }

  const onChangeItemRow = (key: keyof ItemInventory, value: any) => {
    // @ts-ignore
    setEditModeRow(row => ({...((rows as Report[]) | {}), [key]: value}));
  }

  const editRow = () => {
    axiosInstance.put(`Item/Edit?userGuid=${userGuid}`, {
      itemGuid: editModeRow?.Guid,
      model: editModeRow
    }).then(res => {
      if (res.status === 200) {
        enqueueSnackbar(t('EditItemSuccess'), { variant: 'success' });
      } else {
        enqueueSnackbar(t('EditItemFailed'), { variant: 'error' });
      }
    }).catch(e => {
      console.log(e)
      enqueueSnackbar(t('EditItemFailed'), { variant: 'error' });
    })
    setEditModeRow(null)
  }

  return (
    <Box sx={{ px: 1 }}>
      <Scrollbar>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              {/* @ts-ignore */}
              <TableRow sx={{ backgroundColor: theme.palette.secondary.slate.deep, borderRadius: '8px', '& .MuiTableCell-root': {border: '0'} }}>
                {/* @ts-ignore */}
                {tableHeads.map(i => (<TableCell key={i.value}><Typography sx={{ color: theme.palette.secondary.gray.main, fontSize: '14px', fontWeight: 700 }}>{t(i.value === 'Action' ? '' : i.value)}</Typography></TableCell>))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)?.map((row, index: number) => (
                <TableRow
                  key={row.Id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  onClick={() => !editModeRow && navigate(`/DepotsItem/${row.Id}`)}
                >
                  {!!tableHeads && tableHeads.map((head: any) => {
                    if (head.editable && editModeRow?.Id === row.Id) {
                      return (<TableCell key={(row as any)?.[head.value]}>
                        <TextField
                          value={row?.[head.value as keyof Report]}
                          onChange={event => onChangeItemRow(head.value, event?.target.value)}
                          InputProps={{ inputComponent: head.type === 'price' ? NumberFormatCustom : undefined }}
                        />
                      </TableCell>)
                    }
                    let colValue = row?.[head.value as keyof Report]
                    switch (head.type) {
                      case 'price':
                        colValue = getPriceFormat(colValue as string)
                        break
                    }
                    switch (head.value) {
                      case 'Row':
                        return (<TableCell align="left" key={(row as any)?.[head.value]}>{page * rowsPerPage + index + 1}</TableCell>)
                      case 'Action':
                        return (<TableCell key={(row as any)?.[head.value]}>
                          <Box sx={{ display: 'flex', alignItems: 'center' }}>
                          { 
                            editModeRow?.Id === row.Id ? (<>
                              <IconButton onClick={editRow}><CheckIcon/></IconButton>
                              <IconButton onClick={(event) =>{ event.stopPropagation();setEditModeRow(null)}}><CloseIcon/></IconButton>
                            </>) : <IconButton onClick={(event) => {event.stopPropagation();setEditModeRow(row)}}><EditIcon/></IconButton>
                          }
                          </Box>
                        </TableCell>)
                      default:
                        return (<TableCell key={(row as any)?.[head.value]}>{colValue}</TableCell>)
                    }
                  })}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Scrollbar>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25, 50, 100, 200]}
        component="div"
        count={itemCount}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={(e, page) => setPage(page)}
        onRowsPerPageChange={handleChangeRowsPerPage}
        labelRowsPerPage={t("RowsPerPage")}
        showFirstButton={true}
        showLastButton={true}
        labelDisplayedRows={({ from, to, count }) =>
          `${from}–${to} ${t("From")} ${count !== -1 ? count : t('MoreThan') + " " + to
          }`
        }
      />
    </Box>
  )
}
export default memo(ItemsTable)
