import { FC } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { NavLink as RouterLink } from "react-router-dom";

// @mui
import {
	Box,
	Container,
	Grid,
	Stack,
	Typography,
	Button,
	IconButton,
} from "@mui/material";

// icons
import footerBg from "../../images/footer-bg.jpg";
import zaringpal from "../../images/zarinpal.png";
import nezamSenfi from "../../images/nezm_senfi.png";
import { ReactComponent as CallIcon } from "../../images/icons/ic_call.svg";
import { ReactComponent as EmailIcon } from "../../images/icons/ic_sms.svg";
import { ReactComponent as MapIcon } from "../../images/icons/ic_map.svg";
import { ReactComponent as FacebookIcon } from "../../images/ic_facebook.svg";
import { ReactComponent as InstagramIcon } from "../../images/ic_instagram.svg";
import { ReactComponent as LinkedinIcon } from "../../images/ic_linkedin.svg";

const Footer: FC<{ sx?: { [key: string]: any } }> = ({ sx = {} }) => {
	const { t } = useTranslation("help");
	return (
		<Box
			sx={{
				alignSelf: "stretch",
				backgroundImage: `url(${footerBg})`,
				backgroundRepeat: "no-repeat",
				backgroundSize: "cover",
				py: 6,
				...sx,
			}}
		>
			<Container maxWidth="lg">
				<Grid
					container
					sx={{
						flexDirection: { xs: "column", md: "row" },
						px: "4px",
						color: "#fff",
						"& a": { color: "#fff", fontSize: "16px", fontWeight: 400 },
					}}
				>
					<Grid item xs={12} sm={6} md={3} sx={{ px: 1 }}>
						<Typography>{t("ContactUs")}</Typography>
						<Stack direction="column" spacing={3} sx={{ py: 3 }}>
							<Stack direction="row" alignItems="center" spacing="12px">
								<Box component={CallIcon} sx={{ flexShrink: 0 }} />
								<Typography
									dir="ltr"
									sx={{ fontWeight: 400, fontSize: "16px" }}
								>
									051 384 786 30 - 5
								</Typography>
							</Stack>
							<Stack direction="row" alignItems="center" spacing="12px">
								<Box component={EmailIcon} sx={{ flexShrink: 0 }} />
								<Typography
									dir="ltr"
									sx={{ fontWeight: 400, fontSize: "16px" }}
								>
									info@clickmis.net
								</Typography>
							</Stack>
							<Stack direction="row" alignItems="center" spacing="12px">
								<Box component={MapIcon} sx={{ flexShrink: 0 }} />
								<Typography sx={{ fontWeight: 400, fontSize: "16px" }}>
									مشهد، احمدآباد 13(ابونصر)، پلاک 5، ساختمان چاری، طبقه اول،
									واحد 103
								</Typography>
							</Stack>
						</Stack>
					</Grid>
					<Grid
						item
						xs={12}
						sm={6}
						md={3}
						sx={{
							flexDirection: "column",
							display: { xs: "none", md: "flex" },
							px: 1,
						}}
					>
						<Typography>{t("Features")}</Typography>
						<Stack direction="column" spacing={3} sx={{ py: 3 }}>
							<a href="/help">{t("Help")}</a>
							<Link to="/blog">{t("Blog")}</Link>
							<Link to="/plans">{t("Prices")}</Link>
							<a href="/about-us#ourCustomers">{t("OurCustomers")}</a>
						</Stack>
					</Grid>
					<Grid
						item
						xs={12}
						sm={6}
						md={3}
						sx={{
							flexDirection: "column",
							display: { xs: "none", md: "flex" },
							px: 1,
						}}
					>
						<Typography>{t("QuickAccess")}</Typography>
						<Stack direction="column" spacing={3} sx={{ py: 3 }}>
							<Link to="/choose">{t("Login")}</Link>
							<a href="/about-us">{t("AboutUs")}</a>
							<a href="/contact-us">{t("ContactUs")}</a>
							<Link to="/faq">{t("FAQ")}</Link>
						</Stack>
					</Grid>
					<Grid item xs={12} sm={6} md={3} sx={{ px: 1 }}>
						<Stack direction="column" spacing={3} sx={{ py: 3 }}>
							<Button
								variant="contained"
								sx={{ backgroundColor: "#2D99FF" }}
								component={RouterLink}
								to="/choose"
							>
								۱۵ روز استفاده رایگان
							</Button>
							<Stack direction="row" alignItems="center" gap={2}>
								<img src={zaringpal} alt="zaringpal" />
								<img
									src={nezamSenfi}
									className="rounded-[5px] bg-white"
									alt="nezamSenfi"
								/>
							</Stack>
						</Stack>
					</Grid>
					<Box
						sx={{ width: "100%", height: "1px", backgroundColor: "#919EAB" }}
					></Box>
					<Stack
						direction={{ xs: "column-reverse", md: "row" }}
						alignItems="center"
						justifyContent="space-between"
						sx={{ width: "100%" }}
					>
						<Typography sx={{ fontSize: "14px", fontWeight: 400 }}>
							تمامی حقوق برای وب‌ سایت کلیک محفوظ می باشد.
						</Typography>
						<Stack direction="row" alignItems="center">
							<IconButton
								href="https://www.facebook.com/clickmisnet"
								component="a"
								target="_blank"
								rel="noopener"
							>
								<FacebookIcon />
							</IconButton>
							<IconButton
								href="https://instagram.com/clickmisnet"
								component="a"
								target="_blank"
								rel="noopener"
							>
								<InstagramIcon />
							</IconButton>
							<IconButton
								href="https://www.linkedin.com/in/clickmisnet"
								component="a"
								target="_blank"
								rel="noopener"
							>
								<LinkedinIcon />
							</IconButton>
						</Stack>
					</Stack>
				</Grid>
			</Container>
		</Box>
	);
};
export default Footer;
