import { useTranslation } from "react-i18next";
import { UserInfo } from "@/types";

// @mui
import {
	Autocomplete,
	Box,
	Button,
	Card,
	CircularProgress,
	Dialog,
	DialogActions,
	DialogTitle,
	FormControl,
	FormControlLabel,
	FormLabel,
	IconButton,
	Radio,
	RadioGroup,
	TextField,
	Typography,
} from "@mui/material";
import { useTheme, alpha } from "@mui/material/styles";
import CloseIcon from "@mui/icons-material/Close"; // icons
import axiosInstance from "../../utils/axios-instance";
import React from "react";
import setting from "@/settings";
import { setScreenLoading } from "@/utils";
import axios from "axios";
import { useFormik } from "formik";
import { number, object, string } from "yup";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";

import utils, {
	getNow,
	toDateString,
	toDateTimeString,
	toTimeString,
	getTokenUser,
} from "../../utils/utils";

const useGet = (url: string) => {
	const [loading, setLoading] = useState<boolean>(true);
	const [data, setData] = useState<any>([]);
	const [error, setError] = useState<string>("");
	const fetchData = () => {
		setLoading(true);
		setError("");

		axiosInstance
			.get(url)
			.then((res) => {
				setData(res.data || []);
			})
			.catch((e) => setError(e))
			.finally(() => setLoading(false));
	};
	useEffect(() => {
		fetchData();
	}, []);

	return { data, loading, error, fetchData };
};

const useSend = (url: string) => {
	const [loading, setLoading] = useState<boolean>(false);
	const [data, setData] = useState<any>(null);
	const [error, setError] = useState<string>("");

	const fetchData = (data: any, callback: Function) => {
		setLoading(true);
		setError("");

		axiosInstance
			.post(url, data)
			.then((res) => {
				setData(res.data || {});
				callback && callback(res.data);
			})
			.catch((e) => {
				setError(e);
				callback && callback(e);
			})
			.finally(() => setLoading(false));
	};

	return { data, loading, error, fetchData };
};

const TaskAddDialog: FC<{
	state: [boolean, Dispatch<SetStateAction<boolean>>];
	getWorks: {
		data: any;
		loading: boolean;
		error: string;
		fetchData: () => void;
	};
	onAdd: () => void;
}> = ({ state, getWorks, onAdd }) => {
	const { t } = useTranslation("task");
	const gt = useTranslation().t;
	const getUsers = useGet("/ticket/ticketUserList");
	const sendTask = useSend("/work");
	const getTickets = useGet("/ticket/SelectList");

	const userList =
		getUsers.data.userList?.map((r: any) => ({
			title: r.name + " " + r.lastName,
			id: r.id,
		})) || [];
	const [show, setShow] = state;
	const { enqueueSnackbar } = useSnackbar();

	const taskForm = useFormik({
		initialValues: {
			subject: "",
			supporter: null as any,
			supporterInput: "",
			description: "",
			suggestTime: 0,
			suggestType: "day",
			ticket: null,
		},
		validationSchema: object({
			subject: string().required(t("Required") as string),
			description: string().required(t("Required") as string),
			supporter: object()
				.nullable()
				.required(t("Required") as string),
			suggestTime: number()
				.required(t("Required") as string)
				.min(0.0001, t("TimeInvalid") as string),
		}),
		onSubmit: (values) => {
			const data = {
				SupporterId: values.supporter?.id,
				Description: values.description,
				Subject: values.subject,
				TicketId: values.ticket?.id,
				SuggestTime:
					values.suggestType == "day"
						? values.suggestTime * 440 * 60
						: values.suggestTime * 3600,
			};

			sendTask.fetchData(data, (res: any) => {
				if (res.result == "ok") {
					enqueueSnackbar(t("ToastAddTask"), {
						variant: "success",
					});
					getWorks.fetchData(false, onAdd);
					// getWorks.fetchData(false, onAdd);
					setShow(false);
				} else {
					enqueueSnackbar(t("ToastErrorTask") + "\n" + res, {
						variant: "error",
					});
				}
			});
		},
	});
	// handleChange
	// useEffect(() => {
	// 	if (!sendTask.data) return;

	// 	if (sendTask.data.result == "ok") {
	// 		enqueueSnackbar(t("ToastAddTask"), {
	// 			variant: "success",
	// 		});
	// 	}
	// }, [sendTask.data]);

	return (
		<Dialog
			open={show}
			onClose={() => {
				setShow(false);
			}}
			sx={{ p: 0 }}
		>
			<DialogTitle
				sx={{
					display: "flex",
					p: "24px",
					pr: "16px",
					color: "#212B36",
					alignItems: "center",
				}}
			>
				{t("NewTask")}
				<IconButton
					sx={{ ml: "auto" }}
					onClick={() => {
						setShow(false);
					}}
				>
					<CloseIcon />
				</IconButton>
			</DialogTitle>

			<Box sx={{ p: "24px", pt: 0, minWidth: "480px" }}>
				<TextField
					autoFocus
					fullWidth
					label={t("Subject")}
					error={taskForm.touched.subject && Boolean(taskForm.errors.subject)}
					helperText={taskForm.touched.subject && taskForm.errors.subject}
					{...taskForm.getFieldProps("subject")}
				/>

				<TextField
					sx={{ mt: "24px" }}
					fullWidth
					minRows={3}
					maxRows={10}
					label={t("Description")}
					error={
						taskForm.touched.description && Boolean(taskForm.errors.description)
					}
					helperText={
						taskForm.touched.description && taskForm.errors.description
					}
					{...taskForm.getFieldProps("description")}
					multiline={true}
				/>

				<Autocomplete
					disablePortal
					options={userList}
					sx={{ mt: "24px" }}
					loading={getUsers.loading}
					isOptionEqualToValue={(option, value) => option.title === value.title}
					getOptionLabel={(option) => option.title}
					// renderInput={(params) => (
					// 	<TextField {...params} label={t("AssignTo")} />
					// )}
					loadingText={t("LoadingUsers")}
					renderInput={(params) => (
						<TextField
							{...taskForm.getFieldProps("supporterInput")}
							{...params}
							label={t("AssignTo")}
							error={
								taskForm.touched.supporter && Boolean(taskForm.errors.supporter)
							}
							// helperText={
							// 	taskForm.touched.supporter && taskForm.errors.supporter
							// }
							InputProps={{
								...params.InputProps,
								endAdornment: (
									<React.Fragment>
										{getUsers.loading ? (
											<CircularProgress color="inherit" size={20} />
										) : null}
										{params.InputProps.endAdornment}
									</React.Fragment>
								),
							}}
						/>
					)}
					{...taskForm.getFieldProps("supporter")}
					onChange={(e, value) => taskForm.setFieldValue("supporter", value)}
				/>

				<Autocomplete
					disablePortal
					options={getTickets.data}
					sx={{ mt: "24px" }}
					loading={getTickets.loading}
					isOptionEqualToValue={(option, value) => option.id === value.id}
					getOptionLabel={(option) =>
						"#" + ` ${option.id} -  ` + option.subject
					}
					renderOption={(props, option, state) => {
						const info = JSON.parse(option.infoJson || "{}");
						return (
							<Button
								sx={{
									disply: "flex",
									alignItems: "normal",
									textAlign: "left",
									py: 2,
									fontSize: "12px",
								}}
								fullWidth
								{...props}
							>
								<Typography color="error.dark" sx={{ mr: 1 }}>
									#{option.id}
								</Typography>
								<span>{option.subject}</span>
								<span style={{ flex: 1 }}> </span>
								<Box sx={{ ml: 1 }}>
									<p style={{ color: "#3f3f3f", fontSize: "11px" }}>
										{info.fullName}
										{" - "}
										{toDateString(option.createDate)}
									</p>
									<p style={{ color: "#3f3f3f", fontSize: "11px" }}>
										{info.companyDescripiton || info.companyDescription}
									</p>
								</Box>
							</Button>
						);
					}}
					filterOptions={(list, state) =>
						list.filter((ticket) =>
							Object.values(ticket).some(
								(p) =>
									p &&
									(p + "")
										.toLowerCase()
										.includes(state.inputValue?.toLowerCase())
							)
						)
					}
					// renderInput={(params) => (
					// 	<TextField {...params} label={t("AssignTo")} />
					// )}
					loadingText={t("LoadingUsers")}
					renderInput={(params) => (
						<TextField
							{...params}
							label={t("AddTicket")}
							multiline={true}
							InputProps={{
								...params.InputProps,
								endAdornment: (
									<React.Fragment>
										{getTickets.loading ? (
											<CircularProgress color="inherit" size={20} />
										) : null}
										{params.InputProps.endAdornment}
									</React.Fragment>
								),
							}}
						/>
					)}
					value={taskForm.values.ticket}
					onChange={(e, v) => taskForm.setFieldValue("ticket", v)}
					noOptionsText={gt("NoOption")}
				/>

				<Box sx={{ display: "flex", mt: "24px", gap: 1 }}>
					<TextField
						label={t("SuggestTime")}
						type="number"
						error={
							taskForm.touched.suggestTime &&
							Boolean(taskForm.errors.suggestTime)
						}
						helperText={
							taskForm.touched.suggestTime && taskForm.errors.suggestTime
						}
						{...taskForm.getFieldProps("suggestTime")}
						sx={{ mr: 2 }}
					/>

					<RadioGroup
						sx={{ display: "flex", flexDirection: "row" }}
						onChange={(e) =>
							taskForm.setFieldValue("suggestType", e.target.value)
						}
						value={taskForm.values.suggestType}
					>
						<FormControlLabel
							value="day"
							sx={{}}
							control={<Radio color="success" />}
							label={t("TimeDay")}
						/>
						<FormControlLabel
							value="hour"
							sx={{}}
							control={<Radio color="success" />}
							label={t("TimeHour")}
						/>
					</RadioGroup>
				</Box>
			</Box>

			<DialogActions sx={{ pl: 16, pt: 2, pb: 3, pr: 3 }}>
				{/* @ts-ignore */}
				<Button
					variant="outlined"
					sx={{
						flexGrow: 1,
					}}
					onClick={() => {
						setShow(false);
					}}
				>
					{t("Cancel")}
				</Button>

				{/* @ts-ignore */}
				<LoadingButton
					color="success"
					variant="contained"
					sx={{
						color: "#fff",
						flexGrow: 1,
						boxShadow: "0px 8px 16px rgba(0, 171, 85, 0.24)",
					}}
					disabled={!taskForm.dirty || !taskForm.isValid}
					onClick={taskForm.handleSubmit}
					loading={sendTask.loading}
				>
					{t("CreateTask")}
				</LoadingButton>
			</DialogActions>
		</Dialog>
	);
};
export default TaskAddDialog;
