import { FC } from 'react';
import { Helmet } from 'react-helmet-async'

// @mui
import { Container } from '@mui/material';

// components
import Header from '../layouts/Header';
import Footer from '../components/layout/Footer';

const Help: FC = () => {
  return (
    <>
      <Helmet>
        <title>راهنمای خودآموز و مستندات | کلیــــک</title>
        <meta name="title" content="راهنمای خودآموز و مستندات | کلیــــک" />
        <meta name="description" content="اگر به دنبال آموزش آنلاین و حرفه‌ای در یادگیری نرم‌افزار حسابداری کلیک هستید، ما با استفاده از بهترین روش‌های آموزشی، به شما در این مسیر کمک خواهیم کرد. تیم مجرب و حرفه‌ای پشتیبانی ما همواره در کنار شماست تا با سرعتی بیشتر به موفقیت و رشد کسب و کار خود برسید." />
        <meta name="keywords" content="نرم افزار حسابداری کلیک, حسابداری, کلیک, آموزش, مستندات" />
      </Helmet>
      <Header />
      <Container className="fronted-page" maxWidth={false} sx={{ maxWidth: '1272px' }}></Container>
      <Footer />
    </>
  )
}
export default Help