import { Dispatch, FC, SetStateAction, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Item2 } from '@/types';
import Scrollbar from '../Scrollbar';
import { NumericFormat } from 'react-number-format';
import { useSnackbar } from 'notistack';
import axiosInstance, { userGuid } from '../../utils/axios-instance';
import { getPriceFormat } from '../../utils/formatNumber';

// @mui
import { Paper, TableContainer, Table, TableHead, TableRow, TableCell, TableBody, TablePagination, Box, Typography, IconButton, TextField } from '@mui/material';
import { useTheme } from '@mui/material/styles';

const tableHeads = [ 
  { title: 'ItemName', value: 'title' }, { title: 'SellCounts', value: 'value', type: 'price' }, { title: 'IncomeFromSell', value: 'IncomeFromSell' }, { title: 'InventoryItems', value: 'inventory' }
]

const ItemsCountIncomeTable: FC<{ rows: any[], rowsPerPage: number, setRowsPerPage: Dispatch<SetStateAction<number>>, page: number, setPage: Dispatch<SetStateAction<number>>, itemCount: number }> = ({ rows, rowsPerPage, setRowsPerPage, page, setPage, itemCount }) => {
  const { t } = useTranslation();
  const theme = useTheme();
	const { enqueueSnackbar } = useSnackbar();
  const [editModeRow, setEditModeRow] = useState<Item2 | null>(null)// @ts-ignore

  const handleChangeRowsPerPage = (event: { target: { value: string; }; }) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  function NumberFormatCustom(props: { [x: string]: any; name?: any; inputRef?: any; onChange?: any; }) {
    const { inputRef, onChange, ...other } = props;
  
    return (
      // @ts-ignore
      <NumericFormat
        {...other}
        getInputRef={inputRef}
        onValueChange={(values: { value: any, formattedValue: string }) => {
          onChange({
            target: {
              name: props.name,
              value: values.formattedValue
            }
          });
        }}
        thousandSeparator
        // isNumericString
      />
    );
  }

  const onChangeItemRow = (key: keyof Item2, value: any) => {
    // @ts-ignore
    setEditModeRow(row => ({...((rows) | {}), [key]: value}));
  }

  const editRow = () => {
    
  }

  return (
    <Box sx={{ px: 1 }}>
      <Scrollbar>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              {/* @ts-ignore */}
              <TableRow sx={{ backgroundColor: theme.palette.secondary.slate.deep, borderRadius: '8px', '& .MuiTableCell-root': {border: '0'} }}>
                {/* @ts-ignore */}
                {tableHeads.map(i => (<TableCell key={i.value}><Typography sx={{ color: theme.palette.secondary.gray.main, fontSize: '14px', fontWeight: 700 }}>{t(i.title)}</Typography></TableCell>))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows?.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)?.map((row, index: number) => (
                <TableRow
                  key={row.Id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  {tableHeads.map((head: any) => {
                    if (head.editable && editModeRow?.Id === row.Id) {
                      return (<TableCell key={(row as any)?.[head.value]}>
                        <TextField
                          value={row?.[head.value as keyof Item2]}
                          onChange={event => onChangeItemRow(head.value, event?.target.value)}
                          InputProps={{ inputComponent: head.type === 'price' ? NumberFormatCustom : undefined }} 
                        />
                      </TableCell>)
                    }
                    let colValue = row?.[(head.value || head.title) as keyof Item2]
                    switch (head.type) {
                      case 'price':
                        colValue = getPriceFormat(colValue as string)
                        break
                    }
                    switch (head.value) {
                      case 'title':
                        return (<TableCell key={(row as any)?.[head.value]}>
                          <Box component="span" sx={{ width: '12px', height: '12px', backgroundColor: row.color, borderRadius: '100%', display: 'inline-block', verticalAlign: 'middle' }}></Box> {row.title}
                        </TableCell>)
                      case 'IncomeFromSell':
                        return (<TableCell key={(row as any)?.[head.value]}>{getPriceFormat(row.SalePrice * row.value)}</TableCell>)
                      default:
                        return (<TableCell key={(row as any)?.[head.value]}>{colValue}</TableCell>)
                    }
                  })}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Scrollbar>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25, 50, 100, 200]}
        component="div"
        count={itemCount}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={(e, page) => setPage(page)}
        onRowsPerPageChange={handleChangeRowsPerPage}
        labelRowsPerPage={t("RowsPerPage")}
        showFirstButton={true}
        showLastButton={true}
        labelDisplayedRows={({ from, to, count }) =>
          `${from}–${to} ${t("From")} ${count !== -1 ? count : t('MoreThan') + " " + to
          }`
        }
      />
    </Box>
  )
}
export default ItemsCountIncomeTable
