import { FC, useEffect, useRef, useState } from 'react'


import { Box, Stack, Typography } from '@mui/material'
let isMounted: boolean = false
const CLKSwitch: FC<{ options: { title: string, value: any, disabled?: boolean }[], value: any, onChange: (value: any) => void }> = ({ options, value, onChange }) => {
  const wrapperRef = useRef<HTMLDivElement>()
  const [widthLeftSpacing, setWidthLeftSpacing] = useState<{
    left: number,
    width: number
  }>({
    left: 0,
    width: 0
  })
  useEffect(() => {
    setTimeout(() => {
      const optionIndex = options.findIndex(i => i.value === value)
      const optionElement = document.getElementById(`clk-switch-opiton-${optionIndex}`)
      // @ts-ignore
      setWidthLeftSpacing({
        left: optionElement ? optionElement?.offsetLeft : 0,
        width: optionElement ? optionElement?.offsetWidth : 0,
      })
    }, !isMounted ? 1000 : 0)
    if (!isMounted) {
      isMounted = true
    }
  }, [value])
  return (
    <Stack direction="row" alignItems="center" sx={{ position: 'relative', backgroundColor: '#EDEFF2', p: '4px', borderRadius: '100px', transition: 'all .3s ease-out' }} ref={wrapperRef}>
      <Box sx={{ width: widthLeftSpacing.width, height: 32, backgroundColor: '#fff', borderRadius: '100px', boxShadow: '0px 8px 8px 0px rgba(18, 17, 39, 0.10)', position: 'absolute', right: 0, top: '4px', transition: 'all .3s ease-out', transform: `translateX(${widthLeftSpacing.left * -1}px)` }}></Box>
      {options.map((i, j) => <Typography sx={{ fontSize: '16px', fontWeight: 600, transition: 'color .3s ease-out', color: value === i.value ? '#212B36' : '#637381', zIndex: 0, cursor: !i.disabled ? 'pointer' : '', py: '4px', px: 2 }} key={i.value} id={`clk-switch-opiton-${j}`} onClick={() => !i.disabled && onChange(i?.value)}>{i.title}</Typography>)}
    </Stack>
  )
}
export default CLKSwitch