const _getUserStatus = (user: User) => {
	if (user && user.siteName) {
		return "سیستم راه‌اندازی شده";
	} else if (user && user.planName && user.paymentStatus === "ok") {
		return "پرداخت شده";
	}
	return "معمولی";
};

const _getUserStatusInfo = (user: User) => {
	const statusTooltip = user.status;
	if (user.planEndDate && user.planStartDate) {
		const remainDays =
			parseInt((new Date(user.planEndDate) - new Date()) / (1000 * 3600 * 24)) +
			"";
		if (user && remainDays && remainDays !== "-") {
			if (remainDays === 0 || remainDays < 0) {
				return {
					tooltip: `غیرفعال: ${statusTooltip}`,
					icon: ExpirIcon,
					code: 4,
				};
			}
			if (remainDays < 15) {
				return {
					tooltip: `درحال اتمام: ${statusTooltip}`,
					icon: ExpirSoonIcon,
					code: 3,
				};
			} else {
				return { tooltip: `فعال: ${statusTooltip}`, icon: ActiveIcon, code: 1 };
			}
		}
	}
	return { tooltip: `دمو: ${statusTooltip}`, icon: DemoIcon, code: 2 };
};

export const mapUserView = (user: User, index?: number) => {
	let _user = { ...user } as any;
	_user.fullname = user.name + " " + user.lastName;
	(_user.createDate = utils.toDateString(_user.createDate)),
		(_user.planEndDate = utils.toDateString(_user.planEndDate)),
		(_user.status = _getUserStatus(user));
	_user.statusColor =
		(!user.siteName && (user.paymentStatus === "ok" ? "warning" : "info")) ||
		"success";
	_user.statusInfo = _getUserStatusInfo({ ...user, status: _user.status });
	_user.planDescription = user.planName
		? planUtils.getPlanDescription(user.planName, user.planNumber)
		: "-";

	if (user.planEndDate && user.planStartDate) {
		_user.remainDays =
			parseInt((new Date(user.planEndDate) - new Date()) / (1000 * 3600 * 24)) +
			"";
	}

	if (_user.remainDays === 0 || _user.remainDays < 0) {
		_user.remainDays = "منقضی شده";
	}

	_user.remainDays = _user.remainDays || "-";
	if (~index) {
		_user.rowNumber = index + 1;
	}

	_user.support = _user.support == "true" ? "پشتیبان" : "کاربر";
	return _user;
};

