import { FC, SetStateAction, useEffect, useState, useMemo, Dispatch } from 'react';
import { CustomerTab } from '@/types';
import axiosInstance from '../../utils/axios-instance';

// @mui
import { Box, Card, IconButton, InputAdornment, Tab, Table, TableContainer, Tabs } from '@mui/material';

// components
import InputStyle from '../InputStyle';
import Iconify from '../Iconify';
import Scrollbar from '../Scrollbar';
import CustomerTable from './CustomerTable';
import { mapUserView } from '../../pages/UserList';
import { useSnackbar } from 'notistack';

const tabTitles: CustomerTab[] = [
  {
    id: 0,
    name: 'همه'
  },
  {
    id: 1,
    name: 'فعال'
  },
  {
    id: 2,
    name: 'مشترکین دمو'
  },
  {
    id: 3,
    name: 'اشتراک‌های درحال اتمام'
  },
  {
    id: 4,
    name: 'غیر فعال'
  }
]

const tabCountColors = [
	{
		background: '#d2e7f9 !important',
		color: '#1939b7 !important',
	},
	{
		background: '#ddf4e5 !important',
		color: '#007B55 !important',
	},
	{
		background: '#f7e1db !important',
		color: '#B82E06 !important',
	},
	{
		background: '#e3e0f8 !important',
		color: '#826AF9 !important',
	},
	{
		background: '#f7dbdb !important',
		color: '#FF4842 !important',
	},
];

const CustomerList: FC<{ setLoades: Dispatch<SetStateAction<string[]>> }> = ({ setLoades }) => {
	const { enqueueSnackbar } = useSnackbar();
  const [currentTab, setCurrentTab] = useState<string | number>(
		tabTitles[0].id
	);
  const [filterName, setFilterName] = useState<string>('')
  const [userList, setUserList] = useState<any[]>([]);
	const [listLoading, setListLoading] = useState<boolean>(true);

  const statusCouts = useMemo(
		() => ({
			0: userList.length,
			1: userList.filter((i) => i.statusInfo.code === 1).length,
			2: userList.filter((i) => i.statusInfo.code === 2).length,
			3: userList.filter((i) => i.statusInfo.code === 3).length,
			4: userList.filter((i) => i.statusInfo.code === 4).length,
		}),
		[userList]
	)

  useEffect(() => {
		let mounted = true;
    setListLoading(true)
		axiosInstance
			.get('/User/list')
			.then((response) => {
				if (response.data?.result === 'ok') {
          const result = response.data.userList.filter((i: { planEndDate?: any; }) => i.planEndDate).map(mapUserView)
          console.log(result)
					mounted && setUserList(result);
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || 'خطایی در دریافت اطلاعات روی داد!',
						{
							variant: 'error',
						}
					);
				}
			})
			.finally(() => {
        setListLoading(false)
        setLoades((i: string[]) => [...i, 'userListGet'])
      });

		return () => {
			mounted = false;
		};
	}, []);
  return (
    <Box sx={{ width: 0, flexBasis: { xs: '100%', lg: '70%' }, px: '12px', py: '20px' }}>
      <Card sx={{ boxShadow: '0px 12px 24px -4px rgba(145, 158, 171, 0.12), 0px 0px 2px 0px rgba(145, 158, 171, 0.20)', borderRadius: '16px', border: 'none' }}>
        <Tabs
          value={currentTab}
          scrollButtons="auto"
          variant="scrollable"
          indicatorColor="secondary"
          allowScrollButtonsMobile
          onChange={(e, value) => setCurrentTab(value)}
          sx={{ backgroundColor: '#f4f6f8', borderRadius: '16px 16px 0 0', '& .MuiTabs-indicator': { backgroundColor: '#00AB55' } }}
        >
          {tabTitles.map((tab: CustomerTab, j: number) => (
            <Tab
              disableRipple
              key={j}
              label={tab.name}
              disabled={statusCouts[tab.id as 0|1|2|3|4] === 0}
              icon={<Box sx={tabCountColors[j]}>{statusCouts[tab.id as 0|1|2|3|4]}</Box>}
              iconPosition="start"
              value={tab.id}
              sx={{
                '& .MuiTab-iconWrapper': {
                  height: '24px',
                  minWidth: '22px',
                  lineHeight: '0',
                  borderRadius: '6px',
                  cursor: 'default',
                  alignItems: 'center',
                  whiteSpace: 'nowrap',
                  display: 'inline-flex',
                  justifyContent: 'center',
                  textTransform: 'capitalize',
                  padding: '0px 8px',
                  color: '#006c9c',
                  fontSize: '0.75rem',
                  fontFamily: '"EstedadVFFa", Public Sans, sans-serif',
                  backgroundColor: 'rgba(0, 184, 217, 0.16)',
                  fontWeight: '700',
                  marginRight: '8px',
                },
              }}
            />
          ))}
        </Tabs>
        <Box sx={{ display: 'flex', flexDirection: { xs: 'column', md: 'row' }, alignItems: { xs: 'stretch', md: 'center' }, justifyContent: 'space-between', gap: 1, p: 2 }}>
          {/* @ts-ignore */}
          <InputStyle
          // @ts-ignore
            stretchStart={240}
            value={filterName}
            onChange={(event: { target: { value: SetStateAction<string>; }; }) => setFilterName(event.target.value)}
            placeholder="جستجوی مشترک"
            sx={{ '& .MuiOutlinedInput-root': { width: { xs: '100%', md: '240px' }, '&.Mui-focused': { width: { xs: '100%', md: '300px' } } } }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Iconify icon={'eva:search-fill'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
                </InputAdornment>
              ),
              endAdornment: (
                <InputAdornment position="end">
                  {filterName?.trim()?.length > 0 && (
                    <IconButton onClick={(event: any) => {
                      event.stopPropagation()
                      setFilterName('')
                    }}>
                      <Iconify icon={'fluent-mdl2:clear'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
                    </IconButton>
                  )}
                </InputAdornment>
              )
            }}
          />
        </Box>
        <CustomerTable rows={userList} setRows={setUserList} loading={false} filterName={filterName} currentTab={+currentTab} />
      </Card>
    </Box>
  )
}
export default CustomerList
