import { FC, useState } from "react";
import "@amir04lm26/react-modern-calendar-date-picker/lib/DatePicker.css";
import DatePicker from "@amir04lm26/react-modern-calendar-date-picker";
import { GetTitle, OnChangeSetting, Setting, SettingConfig } from "@/types";
import { useMemo } from "react";
import TextField from "@mui/material/TextField";
import { getFormatedDatePickerExport } from "../../utils/formatNumber";

const DatePickerCLK: FC<{
	config: SettingConfig;
	data: Setting;
	onChange: OnChangeSetting;
	getTitle: GetTitle;
}> = (props) => {
	const { data, onChange, getTitle } = props;
	const [dateValue, setDateValue] = useState<string>(`${data.Value.slice(0, 4)}/${data.Value.slice(4, 6)}/${data.Value.slice(6, 8)}` || "");
	const selectedDay = useMemo(() => {
		if (data?.Value) {
			if (data.Value.includes("/")) {
				setDateValue(data.Value);
				return {
					day: +data.Value.split("/")[2],
					month: +data.Value.split("/")[1],
					year: +data.Value.split("/")[0],
				};
			} else {
				const dataValueFormat = {
					day: +data.Value.slice(6, 8),
					month: +data.Value.slice(4, 6),
					year: +data.Value.slice(0, 4),
				};
				setDateValue(
					`${dataValueFormat.year}/${dataValueFormat.month}/${dataValueFormat.day}`
				);
				return dataValueFormat;
			}
		}
		return null;
	}, [data.Value]);
	return (
		// <TextField
		// 			value={dateValue}
		// 			fullWidth
		// 			name={data.Name}
		// 			onChange={(event) => {
		// 				setDateValue(event.target.value)
		// 				onChange(event, data);
		// 			}}
		// 			variant="outlined"
		// 			label={getTitle(data.FormName)}
		// 		/>
		<DatePicker
			wrapperClassName="w-full"
			value={selectedDay as any}
			onChange={(val) => onChange(
				{ target: { value: getFormatedDatePickerExport(val)} },
				data
			)}
			shouldHighlightWeekends
			locale="fa"
			colorPrimary="#0093FF"
			renderInput={({ ref }) => (
				<TextField
					ref={ref as any}
					fullWidth
					name={data.Name}
					value={dateValue}
					variant="outlined"
					label={getTitle(data.FormName)}
				/>
			)}
		/>
	);
};
export default DatePickerCLK;
