import { Link } from "react-router-dom";

import Logo from "../../images/logo.svg";

import AuthBannerBg from "../../images/auth-banner-bg.jpg";

import AuthBanner from "../../images/auth-banner.png";
import { useNavigate } from "react-router-dom";
import { useFormik } from "formik";
import { object, string } from "yup";
import { TextField, Alert } from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useState } from "react";
import axios from "../../utils/axios-instance";
import LanguageSelector from "../../components/LanguageSelector";
import { useTranslation } from "react-i18next";

const ResetPassword = () => {
	const navigate = useNavigate();
	const [loading, setLoading] = useState(false);
	const [error, setError] = useState("");
	const { t } = useTranslation();
	const loginForm = useFormik({
		initialValues: {
			mobile: "",
		},
		validationSchema: object({
			mobile: string()
				.required(t('mobileRequiredError') as string)
				.matches(/^(0)?([9][0-9]{9})$/, t('mobileValidationError') as string),
		}),
		onSubmit: (values) => {
			setLoading(true);

			axios
				.post("User/otp", {
					mobilePhone: values.mobile,
				})
				.then((response) => {
					if (response.data.result === "ok") {
						navigate("/verify", { state: { mobile: values.mobile, resetPassword: true } });
					} else {
						setError(response.data.error);
					}
				})
				.catch((error) => {
					if (error._handled) setError(t('errorTryAgain') as string);
					else setError(error.response?.data?.error || ('errorTryAgain') as string);
				})
				.finally(() => setLoading(false));
		},
	});

	return (
		<main className="flex">
			<div className="mx-auto flex min-h-screen w-[480px] px-4 pt-32 lg:shrink-0 lg:items-center lg:px-16 lg:pt-0">
				<div className="w-full">
					<div className="flex flex-col items-center gap-1 mb-4">
						<Link to="/" className="mb-10">
							<img
								src={Logo}
								alt="نرم افزار حسابداری آنلاین و ابری کلیک"
								className="h-32"
							/>
						</Link>
						<h3 className="mb-4 text-xl font-extrabold text-[#212B36] lg:text-2xl">
							{ t('resetPasswordTitle') }
						</h3>
					</div>

					{error && (
						<Alert severity="error" className="mb-6 w-full">
							{error}
						</Alert>
					)}

					<form onSubmit={loginForm.handleSubmit}>
						<TextField
							fullWidth
							label={t('mobile')}
							className="mb-5"
							error={
								loginForm.touched.mobile && Boolean(loginForm.errors.mobile)
							}
							helperText={loginForm.touched.mobile && loginForm.errors.mobile}
							{...loginForm.getFieldProps("mobile")}
						/>
						<LoadingButton
							disableElevation
							fullWidth
							variant="contained"
							size="large"
							type="submit"
							className="bg-blue-700 hover:bg-blue-900"
							loading={loading}
							sx={{
								"&:hover": { boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0" },
							}}
						>
							{t('sendPassword')}
						</LoadingButton>
					</form>
					<div className="mt-6 flex text-base justify-center">
						<p className="ml-1 mr-1 text-[#919EAB]">
							{ t('rememberPassword') }
						</p>
						<Link
							to="/login"
							className="font-normal text-[#3366FF] hover:underline"
						>
							{ t('enter') }
						</Link>
					</div>
					<div className="mt-4 flex text-base justify-center">
						<p className="ml-1 mr-1 text-[#919EAB]">{ t('notRegiser') }</p>
						<Link
							to="/register"
							className="font-normal text-[#3366FF] hover:underline"
						>
							{ t('doRegister') }
						</Link>
					</div>
					<div className="flex justify-center">
						<LanguageSelector className="lg:absolute z-[1] top-4 lg:left-4" />
					</div>
				</div>
			</div>

			<div className="relative hidden grow flex-col items-center justify-center overflow-hidden lg:flex pt-[100px]">
				<h1 className="mb-5 text-3xl font-bold text-gray-800">
					{ t('resetPasswordDetailTitle') }
				</h1>

				<h2 className="mb-20 text-xl text-gray-500">
					{ t('resetPasswordDetailSubtitle') }
				</h2>

				<img
					src={AuthBanner}
					alt="نرم افزار حسابداری آنلاین و ابری کلیک"
					className="w-full object-cover"
					style={{ maxWidth: "720px" }}
				/>

				<div
					className="absolute inset-0 -z-10 -scale-x-100 bg-cover bg-center bg-no-repeat"
					style={{
						backgroundImage: `linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)), url(${AuthBannerBg})`,
					}}
				></div>
			</div>
		</main>
	);
};

export default ResetPassword;
