import React from "react";
import { Button, StepIconProps } from "@mui/material";
import { useSnackbar } from "notistack";
import { useNavigate, useLocation } from "react-router-dom";
import { TextField, CircularProgress, Alert } from "@mui/material";
import axiosInstance from "../../utils/axios-instance";
import settings from "../../settings";
import { Box, Container, Typography, Divider, Grid } from "@mui/material";
import Page from "../../components/Page";

import planUtils from "../../utils/planUtils";
import { LoadingButton } from "@mui/lab";

import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import StepConnector, {
	stepConnectorClasses,
} from "@mui/material/StepConnector";
import { stepIconClasses } from "@mui/material/StepIcon";
import { styled } from "@mui/material/styles";
import { Check } from "@mui/icons-material";
// import LanguageSelector from "../../components/LanguageSelector";
import Logo from "../../images/logo.svg";

let _controller = new AbortController();

type _plans = {
	[key: string]: string;
};

const QontoConnector = styled(StepConnector)(({ theme }) => ({
	[`&.${stepConnectorClasses.alternativeLabel}`]: {
		top: 10,
		left: "calc(-50% + 16px)",
		right: "calc(50% + 16px)",
	},
	[`&.${stepConnectorClasses.active}`]: {
		[`& .${stepConnectorClasses.line}`]: {
			borderColor: "#00AB55",
		},
	},
	[`&.${stepConnectorClasses.completed}`]: {
		[`& .${stepConnectorClasses.line}`]: {
			borderColor: "#00AB55",
		},
	},
	[`& .${stepConnectorClasses.line}`]: {
		borderColor:
			theme.palette.mode === "dark" ? theme.palette.grey[800] : "#eaeaf0",
		borderTopWidth: 3,
		borderRadius: 1,
	},
}));

function QontoStepIcon(props: StepIconProps) {
	const { active, completed, className } = props;

	return (
		<div className={className}>
			{completed ? (
				<Check className="QontoStepIcon-completedIcon" />
			) : (
				<div className="QontoStepIcon-circle" />
			)}
		</div>
	);
}

const addDays = function (days: number) {
	var date = new Date();
	date.setDate(date.getDate() + days);
	return date.toLocaleDateString("fa-ir");
};

const Choose = () => {
	const navigate = useNavigate();
	const { enqueueSnackbar } = useSnackbar();
	const location = useLocation();

	const [demoLoading, setDemoLoading] = React.useState(false);
	const [loading, setLoading] = React.useState(true);
	const [user, setUser] = React.useState<any>({});
	const [domain, setDomain] = React.useState("");
	const [screenLoading, setScreenLoading] = React.useState(false);

	enum UserStatus {
		normal, //nothing
		payment, //choose plan but need to pay
		site, //buy plan and create site
	}

	const [status, setStatus] = React.useState<UserStatus>(UserStatus.normal);
	const [planDescription, setPlanDescription] = React.useState<string>("");
	const [price, setPrice] = React.useState<number>(0);
	const [expireDate, setExpireDate] = React.useState<string>("");
	const [isExpire, setIsExpire] = React.useState<boolean>(false);
	const [isPermanent, setIsPermanent] = React.useState<boolean>(false);
	const [days, setDays] = React.useState<number>(0);
	const [totalDays, setTotalDays] = React.useState<number>(30);

	const onDemoClick = () => {
		setDemoLoading(true);

		_controller = new AbortController();
		fetch("https://clickmis.net/api/User/RegisterToSite", {
			headers: {
				authorization: `Bearer ${window.localStorage.token}`,
				"content-type": "application/json",
			},
			body: "{}",
			method: "POST",
			mode: "cors",
			credentials: "include",
			signal: _controller.signal,
		})
			// axiosInstance
			// 	.post(
			// 		"/User/RegisterToSite",
			// 		{},
			// 		{
			// 			signal: _controller.signal,
			// 			baseURL: "https://clickmis.net/api/",
			// 			withCredentials: true,
			// 		}
			// 	)
			.then((response) => {
				return response.json();
			})
			.then((data) => {
				if (data.result === "ok") {
					if (data.redirect) {
						// window.document.cookie =
						// 	response.data.cookie + ";domain=clickmis.net";

						setScreenLoading(true);
						return window.location.assign(data.redirect);
					}
				}

				enqueueSnackbar("در ساخت حساب دموی شما خطایی رخ داد!", {
					variant: "error",
				});

				return window.location.assign(data?.redirect || settings.demoUrl);
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar("در ساخت حساب دموی شما خطایی رخ داد!", {
						variant: "error",
					});
				}
			})
			.finally(() => setDemoLoading(false));
	};

	React.useEffect(() => {
		axiosInstance
			.get("/User/Detail")
			.then((response) => {
				if (response.data?.result === "ok") {
					const user = response.data.user;
					user &&
						setPlanDescription(
							planUtils.getPlanDescription(user.planName, user.planNumber - 1)
						);

					setPrice((response.data.payment?.price ?? 0) * 1);
					setExpireDate(addDays(response.data.days));

					setIsExpire(response.data.days <= 0);
					setDays(response.data.days);

					var _totalDays = 0;
					if (user.planNumber == 1) _totalDays = 30;
					if (user.planNumber == 2) _totalDays = 90;
					if (user.planNumber == 3) _totalDays = 180;
					if (user.planNumber == 4) _totalDays = 360;
					if (user.planNumber == 5) {
						_totalDays = 360;
						setIsPermanent(true);
					}
					setTotalDays(_totalDays);

					//check user status
					if (user && user.siteName) {
						//user had site
						setStatus(UserStatus.site);
						setDomain(settings.siteUrl.replace("{domain}", user.siteName));

						// need
						// window.location.assign(
						// 	`https://${user.siteName}.clickmis.net`
						// );
					} else if (user && user.planName && user.paymentStatus === "ok") {
						//if had payment go create site
						setStatus(UserStatus.payment);

						//need
						// navigate("/payment-result", { state: { user } });
					}

					setStatus(UserStatus.normal);
					return setUser(user);
				}

				response.data?.error &&
					enqueueSnackbar(response.data?.error, {
						variant: "error",
					});
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(error.data?.error || "خطایی روی داد!", {
						variant: "error",
					});
				}
			})
			.finally(() => setLoading(false));

		return () => {
			_controller && _controller.abort();
		};
	}, []);

	const validateloading = false;

	const steps = [
		"ثبت نام در سایت",
		"خرید اشتراک کلیک ",
		"راه اندازی نرم‌افزار",
	];

	return (
		<Page
			title="جزئیات حساب"
			className="column flex h-full min-h-screen items-center justify-center bg-white"
		>
			<Box
				className="absolute flex w-full "
				sx={{
					top: "28px",
					px: { xs: 2, md: 12 },
					justifyContent: "space-between",
				}}
			>
				<div className="flex items-center pl-8 lg:pl-16">
					<a href="/" className="flex-shrink-0">
						<img
							src={Logo}
							alt="نرم افزار حسابداری آنلاین و ابری کلیک"
							className="h-12"
						/>
					</a>
					{/* <a href="/" className="hidden sm:block"> */}
					<a href="/" className="">
						<Typography
							variant="h6"
							className="pr-2 text-xl font-medium"
							sx={{ color: "#212B36", mx: 1 }}
						>
							حسابداری آنلاین کلیک
						</Typography>
					</a>
				</div>

				{/* <div className="flex justify-center" style={{ visibility: "hidden" }}>
					<LanguageSelector className="" />
				</div> */}
			</Box>

			<Container className="pt-32 pb-20">
				<Grid container spacing={3} className="center justify-center">
					<Grid item xs={12} md={6}>
						<Box className="flex flex-row items-center justify-center rounded-2xl sm:p-10">
							{validateloading ? (
								<div className="lds-ripple">
									<div></div>

									<div></div>
								</div>
							) : (
								<div className="flex flex-col items-center">
									<Stepper
										connector={<QontoConnector />}
										activeStep={
											{
												[UserStatus.normal]: 1,
												[UserStatus.payment]: 2,
												[UserStatus.site]: 3,
											}[status]
										}
										alternativeLabel
										className="mb-12"
										sx={{
											width: { sm: "100%" },
											[`& .${stepIconClasses.completed}`]: {
												color: "#00AB55 !important",
											},
										}}
									>
										{steps.map((label) => (
											<Step key={label}>
												<StepLabel>{label}</StepLabel>
											</Step>
										))}
									</Stepper>

									{
										{
											[UserStatus.normal]: (
												<Box>
													<h1
														className="font-weight-700 mb-4 text-center"
														style={{ color: "#212B36", fontSize: "32px" }}
													>
														کاربر “
														{(user.name || "") + " " + (user.lastName || "")}”
														خوش آمدید
													</h1>

													<h4
														className="font-weight-400 text-center"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														مجموعه کلیک، برای تجربه بهتر و جلب رضایت مشتریان
														خدمات،
														<span style={{ color: "rgb(25, 118, 210)" }}>
															{" "}
															۱۵ روز استفاده رایگان{" "}
														</span>
														از نرم‌افزار را فراهم کرده است.
														<span className="mt-8 block">
															برای خرید اشتراک از سرویس های کلیک دیدن کنید.
														</span>
													</h4>

													<Box className="mt-12 w-full text-center">
														<Divider
															sx={{
																maxWidth: 410,
																margin: "auto",
																marginBottom: 3,
															}}
														/>

														<Box className="flex w-full items-center justify-center">
															<Button
																onClick={onDemoClick}
																disableElevation
																color="inherit"
																size="large"
																variant="contained"
																disabled={demoLoading || loading}
																className="bg-white hover:shadow-[rgba(32,101,209,0.24)_0px_8px_16px_0px]"
																sx={{
																	maxWidth: "190px",
																	border:
																		"1px solid rgba(145, 158, 171, 0.32);",
																	borderRadius: "8px",
																	mr: 3,
																	flex: 1,
																}}
															>
																15 روز رایگان
																{(demoLoading || loading) && (
																	<CircularProgress
																		color="inherit"
																		size="24px"
																		sx={{ ml: 2 }}
																	/>
																)}
															</Button>

															<LoadingButton
																onClick={() => {
																	navigate("/plans", { state: { user } });
																}}
																size="large"
																disableElevation
																variant="contained"
																sx={{
																	"&:hover": {
																		boxShadow:
																			"rgba(32,101,209,0.24) 0 8px 16px 0",
																		backgroundColor: "#007B55",
																	},
																	backgroundColor: "#00AB55",
																	maxWidth: "190px",
																	flex: 1,
																}}
																className=""
																loading={loading}
															>
																<span>طرح‌های فروش</span>
															</LoadingButton>
														</Box>
													</Box>
												</Box>
											),

											[UserStatus.payment]: (
												<Box>
													<h1
														className="font-weight-700 mb-4 text-center"
														style={{ color: "#212B36", fontSize: "32px" }}
													>
														کاربر “
														{(user.name || "") + " " + (user.lastName || "")}”
													</h1>

													<h4
														className="font-weight-400 text-center"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														خرید شما با موفقیت ثبت شد.
														<span className="mt-4 block">
															برای تکمیل فرآیند خرید و راه‌اندازی نرم‌افزار
															اقدام کنید.
														</span>
													</h4>

													<Divider
														sx={{
															maxWidth: 410,
															margin: "auto",
															my: 2,
														}}
													/>

													<Box
														sx={{
															color: "#212B36",
															fontSize: "16px",
															lineHeight: "24px",
															display: "flex",
															alignItems: "center",
															justifyContent: "center",
														}}
													>
														<Box sx={{ mr: 3 }}>
															<Typography
																sx={{
																	mt: 1,
																	fontSize: "14px",
																	fontWeight: "700",
																	lineHeight: "26px",
																	color: "#212B36",
																}}
															>
																سرویس {planDescription}
															</Typography>

															<Typography
																sx={{
																	mt: 0.5,
																	fontSize: "14px",
																	color: !isExpire ? "#212B36" : "#FF4842",
																}}
															>
																{isExpire
																	? "منقضی شده در تاریخ "
																	: isPermanent
																	? " پشتیبانی تا تاریخ"
																	: " اعتبار تا تاریخ"}
																{" " + expireDate}
															</Typography>
														</Box>

														<Box
															className="flex items-center justify-center"
															sx={{
																width: 94,
																height: 94,
															}}
														>
															<svg
																style={{
																	width: 94,
																	height: "94px",
																	position: "absolute",
																}}
																xmlns="http://www.w3.org/2000/svg"
																viewBox="0 0 100 100"
															>
																<path
																	className="grey"
																	d="M40,90 A40,40 0 1,1 60,90"
																	style={{
																		fill: "none",
																		strokeLinecap: "round",
																		strokeWidth: 8,
																		stroke: "rgba(145, 158, 171, 0.16)",
																	}}
																/>
																<path
																	className="purple"
																	d="M40,90 A40,40 0 1,1 60,90"
																	style={{
																		fill: "none",
																		strokeLinecap: "round",
																		strokeWidth: 8,
																		stroke: isExpire
																			? "#FF4842"
																			: isPermanent
																			? "#FFC107"
																			: "#5BE584",
																		// strokeDasharray: "calc(40 * 3.142 * 1.85)", //= 232
																		strokeDasharray: 232,
																		strokeDashoffset: isExpire
																			? "231"
																			: 232 * (1 - days / totalDays),
																	}}
																/>
															</svg>

															<Typography
																sx={{
																	fontSize: "12px",
																	color: "#637381",
																	textAlign: "center",
																	fontWeight: 700,
																	lineHeight: "24px",
																}}
															>
																<span className="mt-2 block">باقی مانده</span>

																<Typography
																	component={"span"}
																	sx={{
																		fontSize: "12px",
																		fontWeight: 700,
																		lineHeight: "24px",
																		color: !isExpire ? "#212B36" : "#FF4842",
																	}}
																>
																	{isExpire ? "تمام شده!" : days + " روز"}
																</Typography>
															</Typography>
														</Box>
													</Box>

													{/* <h4
														className="font-weight-400 mt-8 flex"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														اشتراک خریداری شده:
														<span
															style={{ color: "#212B36", marginRight: "auto" }}
														>
															{planDescription}
														</span>
													</h4>

													<h4
														className="font-weight-400 mt-2 flex"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														مبلغ پرداخت شده:
														<span
															style={{ color: "#212B36", marginRight: "auto" }}
														>
															{Number(price).toLocaleString()} ریال
														</span>
													</h4>

													<h4
														className="font-weight-400 mt-2 flex"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														وضعیت اشتراک:
														<span
															style={{
																color: !isExpire ? "#212B36" : "#FF4842",
																marginRight: "auto",
															}}
														>
															{isExpire
																? "منقضی شده در تاریخ "
																: " اعتبار تا تاریخ"}
															{" " + expireDate}
														</span>
													</h4> */}

													{isExpire && (
														<Box
															className="font-weight-400 mt-2 flex"
															sx={{
																color: "#637381",
																fontSize: "16px",
																lineHeight: "24px",
															}}
														>
															{/* <Box
																sx={{
																	width: 94,
																	height: "94px",
																	margin: "auto 12px",
																	background: "rgba(145, 158, 171, 0.16)",
																	borderRadius: "50px",
																}}
															>
																<Box
																	sx={{
																		// width: parseInt("30") + "%",
																		width: 94,
																		height: "94px",
																		background: "#FFC107",
																		borderRadius: "50px",
																	}}
																></Box>
															</Box> */}
														</Box>
													)}

													<Box className="mt-4 w-full text-center">
														<Divider
															sx={{
																maxWidth: 410,
																margin: "auto",
																marginBottom: 3,
															}}
														/>

														<Box className="flex w-full items-center justify-center">
															<LoadingButton
																onClick={() => {
																	isExpire &&
																		navigate("/plans", { state: { user } });
																	!isExpire &&
																		navigate("/payment-result", {
																			state: { user },
																		});
																}}
																size="large"
																disableElevation
																variant="contained"
																sx={{
																	"&:hover": {
																		boxShadow:
																			"rgba(32,101,209,0.24) 0 8px 16px 0",
																		backgroundColor: isExpire
																			? "#7A0C2E"
																			: "#007B55",
																	},
																	backgroundColor: isExpire
																		? "#B72136"
																		: "#00AB55",
																	maxWidth: "414px",
																	flex: 1,
																}}
																className=""
																loading={loading}
															>
																{!isExpire ? (
																	<span> راه‌اندازی نرم‌افزار </span>
																) : (
																	<span> خرید/تمدید اشتراک </span>
																)}
															</LoadingButton>
														</Box>
													</Box>
												</Box>
											),

											[UserStatus.site]: (
												<Box>
													<h1
														className="font-weight-700 mb-4 text-center"
														style={{ color: "#212B36", fontSize: "32px" }}
													>
														کاربر “
														{(user.name || "") + " " + (user.lastName || "")}”
													</h1>

													<h4
														className="font-weight-400 text-center"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														خرید شما با موفقیت ثبت شد.
														<span className="mt-4 block">
															برای تکمیل فرآیند خرید و راه‌اندازی نرم‌افزار
															اقدام کنید.
														</span>
													</h4>

													<Divider
														sx={{
															maxWidth: 410,
															margin: "auto",
															my: 2,
														}}
													/>

													<Box
														sx={{
															color: "#212B36",
															fontSize: "16px",
															lineHeight: "24px",
															display: "flex",
															alignItems: "center",
															justifyContent: "center",
														}}
													>
														<Box sx={{ mr: 3 }}>
															<Typography
																sx={{
																	mt: 1,
																	fontSize: "14px",
																	fontWeight: "700",
																	lineHeight: "26px",
																	color: "#212B36",
																}}
															>
																سرویس {planDescription}
															</Typography>

															<Typography
																sx={{
																	mt: 0.5,
																	fontSize: "14px",
																	color: !isExpire ? "#212B36" : "#FF4842",
																}}
															>
																{isExpire
																	? "منقضی شده در تاریخ "
																	: isPermanent
																	? " پشتیبانی تا تاریخ"
																	: " اعتبار تا تاریخ"}
																{" " + expireDate}
															</Typography>
														</Box>

														<Box
															className="flex items-center justify-center"
															sx={{
																width: 94,
																height: 94,
															}}
														>
															<svg
																style={{
																	width: 94,
																	height: "94px",
																	position: "absolute",
																}}
																xmlns="http://www.w3.org/2000/svg"
																viewBox="0 0 100 100"
															>
																<path
																	className="grey"
																	d="M40,90 A40,40 0 1,1 60,90"
																	style={{
																		fill: "none",
																		strokeLinecap: "round",
																		strokeWidth: 8,
																		stroke: "rgba(145, 158, 171, 0.16)",
																	}}
																/>
																<path
																	className="purple"
																	d="M40,90 A40,40 0 1,1 60,90"
																	style={{
																		fill: "none",
																		strokeLinecap: "round",
																		strokeWidth: 8,
																		stroke: isExpire
																			? "#FF4842"
																			: isPermanent
																			? "#FFC107"
																			: "#5BE584",
																		// strokeDasharray: "calc(40 * 3.142 * 1.85)", //= 232
																		strokeDasharray: 232,
																		strokeDashoffset: isExpire
																			? "231"
																			: 232 * (1 - days / totalDays),
																	}}
																/>
															</svg>

															<Typography
																sx={{
																	fontSize: "12px",
																	color: "#637381",
																	textAlign: "center",
																	fontWeight: 700,
																	lineHeight: "24px",
																}}
															>
																<span className="mt-2 block">باقی مانده</span>

																<Typography
																	component={"span"}
																	sx={{
																		fontSize: "12px",
																		fontWeight: 700,
																		lineHeight: "24px",
																		color: !isExpire ? "#212B36" : "#FF4842",
																	}}
																>
																	{isExpire ? "تمام شده!" : days + " روز"}
																</Typography>
															</Typography>
														</Box>
													</Box>

													{/* <h4
														className="font-weight-400 mt-8 flex"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														اشتراک خریداری شده:
														<span
															style={{ color: "#212B36", marginRight: "auto" }}
														>
															{planDescription}
														</span>
													</h4>

													<h4
														className="font-weight-400 mt-2 flex"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														مبلغ پرداخت شده:
														<span
															style={{ color: "#212B36", marginRight: "auto" }}
														>
															{Number(price).toLocaleString()} ریال
														</span>
													</h4>

													<h4
														className="font-weight-400 mt-2 flex"
														style={{
															color: "#637381",
															fontSize: "16px",
															lineHeight: "24px",
														}}
													>
														وضعیت اشتراک:
														<span
															style={{
																color: !isExpire ? "#212B36" : "#FF4842",
																marginRight: "auto",
															}}
														>
															{isExpire
																? "منقضی شده در تاریخ "
																: " اعتبار تا تاریخ"}
															{" " + expireDate}
														</span>
													</h4> */}

													{isExpire && (
														<Box
															className="font-weight-400 mt-2 flex"
															sx={{
																color: "#637381",
																fontSize: "16px",
																lineHeight: "24px",
															}}
														>
															{/* <Box
																sx={{
																	width: 94,
																	height: "94px",
																	margin: "auto 12px",
																	background: "rgba(145, 158, 171, 0.16)",
																	borderRadius: "50px",
																}}
															>
																<Box
																	sx={{
																		// width: parseInt("30") + "%",
																		width: 94,
																		height: "94px",
																		background: "#FFC107",
																		borderRadius: "50px",
																	}}
																></Box>
															</Box> */}
														</Box>
													)}

													<Box className="mt-4 w-full text-center ">
														<Divider
															sx={{
																maxWidth: 410,
																margin: "auto",
																marginBottom: 3,
															}}
														/>

														<Box className="flex w-full items-center justify-center">
															<LoadingButton
																onClick={() => {
																	!isExpire && (window.location.href = domain);
																	isExpire &&
																		navigate("/plans", {
																			state: { user },
																		});
																}}
																size="large"
																disableElevation
																variant="contained"
																sx={{
																	"&:hover": {
																		boxShadow:
																			"rgba(32,101,209,0.24) 0 8px 16px 0",
																		backgroundColor: isExpire
																			? "#7A0C2E"
																			: "#007B55",
																	},
																	backgroundColor: isExpire
																		? "#B72136"
																		: "#00AB55",
																	maxWidth: "414px",
																	flex: 1,
																}}
																className=""
																loading={loading}
															>
																{!isExpire ? (
																	<span> ورود به پنل نرم‌افزار</span>
																) : (
																	<span> خرید/تمدید اشتراک </span>
																)}
															</LoadingButton>
														</Box>
													</Box>
												</Box>
											),
										}[status]
									}
								</div>
							)}
						</Box>
					</Grid>
				</Grid>
			</Container>
		</Page>
	);
};

export default Choose;
