import { FC } from "react";
import { useTranslation } from "react-i18next";
import { UserInfo } from "@/types";

// @mui
import { Box, Card, Typography } from "@mui/material";
import { useTheme, alpha } from "@mui/material/styles";

// icons
import { ReactComponent as Folder1Icon } from "../../../images/more-square.svg";
import { ReactComponent as Folder2Icon } from "../../../images/alert_icon.svg";
import { ReactComponent as Folder3Icon } from "../../../images/task-doing.svg";
import { ReactComponent as Folder4Icon } from "../../../images/task-done.svg";
import { ReactComponent as Folder5Icon } from "../../../images/task-pause.svg";

const currentUser = "kaze";

const status = {
	0: "all",
	1: "pending",
	2: "doing",
	3: "done",
	4: "pause",
} as any;

const TaskListToobar: FC<{
	statusFilter: number;
	setStatusFilter: (txt: number) => void;
	statusCounts: { [key: string]: number };
	userInfo?: UserInfo;
}> = ({ statusFilter, setStatusFilter, statusCounts, userInfo }) => {
	const { t } = useTranslation("task");
	const theme = useTheme();

	const handleClick = (tab: number) => {
		const check = status[tab] || "all";
		if (!statusCounts[check]) return;
		setStatusFilter(tab);
	};


	return (
		<Box sx={{ mt: 3, mb: 4 }}>
			<Card
				sx={{
					boxShadow:
						"0px 0px 2px rgba(145, 158, 171, 0.2), 0px 12px 24px -4px rgba(145, 158, 171, 0.12)",
					borderRadius: "16px",
					p: 1,
				}}
			>
				<Box
					sx={{
						display: "flex",
						alignItems: "center",
						flexWrap: "nowrap",
						userSelect: "none",
						overflowX: "auto",
					}}
				>
					{/* @ts-ignore */}
					<Box
						onClick={() => handleClick(0)}
						sx={{
							backgroundColor:
								statusFilter === 0 ? alpha(theme.palette.note.purple, 0.1) : "",
							cursor: "pointer",
							display: "flex",
							alignItems: "center",
							justifyContent: "center",
							gap: 2,
							flex: 1,
							borderRadius: "16px",
							p: 2,
						}}
					>
						<Folder1Icon />
						<Box>
							<Typography variant="h5" sx={{ fontSize: "16px !important" }}>
								{t("AllTasks") as string}
							</Typography>
							<Typography
								variant="subtitle1"
								color="secondary.gray.main"
								sx={{
									display: "flex",
									alignItems: "center",
									gap: "4px",
									fontSize: "14px",
								}}
							>
								<Typography
									color="secondary.dimgray.main"
									sx={{ fontWeight: 700, fontSize: "14px" }}
								>
									{statusCounts["all"]}
								</Typography>
								{t("Task")}
							</Typography>
						</Box>
					</Box>
					{/* @ts-ignore */}
					<Box
						onClick={() => handleClick(1)}
						sx={{
							backgroundColor:
								statusFilter === 1 ? alpha(theme.palette.error.red, 0.1) : "",
							cursor: "pointer",
							display: "flex",
							alignItems: "center",
							justifyContent: "center",
							gap: 2,
							flex: 1,
							borderRadius: "16px",
							p: 2,
							opacity: statusCounts["pending"] ? 1 : 0.5,
						}}
					>
						<Folder2Icon />
						<Box>
							<Typography variant="h5" sx={{ fontSize: "16px !important" }}>
								{t("Pending") as string}
							</Typography>
							<Typography
								variant="subtitle1"
								color="secondary.gray.main"
								sx={{
									display: "flex",
									alignItems: "center",
									gap: "4px",
									fontSize: "14px",
								}}
							>
								<Typography
									color="secondary.dimgray.main"
									sx={{ fontWeight: 700, fontSize: "14px" }}
								>
									{statusCounts["pending"]}
								</Typography>
								{t("Task")}
							</Typography>
						</Box>
					</Box>

					{/* @ts-ignore */}
					<Box
						onClick={() => handleClick(2)}
						sx={{
							backgroundColor:
								statusFilter === 2
									? alpha(theme.palette.primary.main, 0.1)
									: "",
							cursor: "pointer",
							display: "flex",
							alignItems: "center",
							justifyContent: "center",
							gap: 2,
							flex: 1,
							borderRadius: "16px",
							p: 2,
							opacity: statusCounts["doing"] ? 1 : 0.5,
						}}
					>
						<Folder3Icon />
						<Box>
							<Typography variant="h5" sx={{ fontSize: "16px !important" }}>
								{t("Doing") as string}
							</Typography>
							<Typography
								variant="subtitle1"
								color="secondary.gray.main"
								sx={{
									display: "flex",
									alignItems: "center",
									gap: "4px",
									fontSize: "14px",
								}}
							>
								<Typography
									color="secondary.dimgray.main"
									sx={{ fontWeight: 700, fontSize: "14px" }}
								>
									{statusCounts["doing"]}
								</Typography>
								{t("Task")}
							</Typography>
						</Box>
					</Box>

					{/* @ts-ignore */}
					<Box
						onClick={() => handleClick(3)}
						sx={{
							backgroundColor:
								statusFilter === 3
									? alpha(theme.palette.success.main, 0.1)
									: "",
							cursor: "pointer",
							display: "flex",
							alignItems: "center",
							justifyContent: "center",
							gap: 2,
							flex: 1,
							borderRadius: "16px",
							p: 2,
							opacity: statusCounts["done"] ? 1 : 0.5,
						}}
					>
						<Folder4Icon />
						<Box>
							<Typography variant="h5" sx={{ fontSize: "16px !important" }}>
								{t("Done") as string}
							</Typography>
							<Typography
								variant="subtitle1"
								color="secondary.gray.main"
								sx={{
									display: "flex",
									alignItems: "center",
									gap: "4px",
									fontSize: "14px",
								}}
							>
								<Typography
									color="secondary.dimgray.main"
									sx={{ fontWeight: 700, fontSize: "14px" }}
								>
									{statusCounts["done"]}
								</Typography>
								{t("Task")}
							</Typography>
						</Box>
					</Box>
					{/* @ts-ignore */}
					<Box
						onClick={() => handleClick(4)}
						sx={{
							backgroundColor:
								statusFilter === 4 ? alpha(theme.palette.gray.muted, 0.1) : "",
							cursor: "pointer",
							display: "flex",
							alignItems: "center",
							justifyContent: "center",
							gap: 2,
							flex: 1,
							borderRadius: "16px",
							p: 2,
							opacity: statusCounts["pause"] ? 1 : 0.5,
						}}
					>
						<Folder5Icon />
						<Box>
							<Typography variant="h5" sx={{ fontSize: "16px !important" }}>
								{t("Pause") as string}
							</Typography>
							<Typography
								variant="subtitle1"
								color="secondary.gray.main"
								sx={{
									display: "flex",
									alignItems: "center",
									gap: "4px",
									fontSize: "14px",
								}}
							>
								<Typography
									color="secondary.dimgray.main"
									sx={{ fontWeight: 700, fontSize: "14px" }}
								>
									{statusCounts["pause"]}
								</Typography>
								{t("Task")}
							</Typography>
						</Box>
					</Box>
				</Box>
			</Card>
		</Box>
	);
};
export default TaskListToobar;
