import { FC, useMemo, useState, MouseEvent } from "react"
import { useTranslation } from "react-i18next";
import { useTheme } from '@mui/material/styles';
import merge from 'lodash/merge';
import ReactApexChart from 'react-apexcharts';
import { Link as RouterLink } from 'react-router-dom';
import { DataItemType } from "@/types";
import { BaseOptionChart } from '../chart';

// utils
import { fNumber } from '../../utils/formatNumber';

// @mui
import { Box, Typography, Button, styled, Card, Menu, MenuItem } from "@mui/material"

// icons
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

const PieChart: FC<{ items: { id: number, name: string, link: string, data: DataItemType[] }[], hideShowAll?: boolean, width?: number, height?: number, wrapperHeight?: number }> = ({ items, hideShowAll, width, height, wrapperHeight = 204 }) => {
	const { t } = useTranslation();
  const theme = useTheme();
  const [selectedItem, setSelectedItem] = useState<number>(items?.[0]?.id || 1)
  // @ts-ignore
  const menuList = useMemo(() => items?.filter(i => i.id !== selectedItem).map(i => ({ ...i, name: i?.Title || i.name, id: i?.Id || i.id })) || [], [items, selectedItem])
  // @ts-ignore
  const { name, link, data, charts } = useMemo(() => items?.find(i => i.id === selectedItem) || {}, [items, selectedItem])
  const legendtHeight = useMemo(() => wrapperHeight + ((data?.length || 0) * 18), [data])
  const ChartWrapperStyle = styled('div')(({ theme }) => ({
    width: '100%',
    height: legendtHeight,
    marginTop: theme.spacing(1),
    '& .apexcharts-canvas, & .apexcharts-canvas svg': { width: '100%', height: 'fit-content !important' },
    // '& .apexcharts-canvas svg': { height: wrapperHeight },
    '& .apexcharts-canvas svg,.apexcharts-canvas foreignObject': {
      overflow: 'visible',
      width: '100%'
    },
    '& .apexcharts-legend': {
      // minHeight: LEGEND_HEIGHT,
      alignContent: 'center',
      // position: 'relative !important',
      borderTop: `solid 1px ${theme.palette.divider}`,
      flexDirection: 'column !important',
      flexWrap: 'nowrap !important',
      paddingTop: '12px',
      marginTop: '56px',
      display: charts ? 'none' : 'flex',
      paddingLeft: '24px',
      paddingRight: '24px',
      bottom: '-16px !important'
    },
    '& .apexcharts-slices': {
      width: '150px',
      height: '150px'
    },
    '& .apexcharts-legend-text': {
      paddingLeft: '8px',
      paddingRight: 0
    },
    '& .apexcharts-tooltip-marker': {
      marginRight: '6px',
      marginLeft: 0,
    },
    // '& .apexcharts-inner': {
    //   transform: `translateX(${hideShowAll ? '8' : '-8'}px)`,
    // }
  }));
  // BaseOptionChart()
  const chartOptions: any = merge(BaseOptionChart(), {
    colors: data?.map((i: DataItemType) => i.color),
    labels: data?.map((i: DataItemType) => t(i.title)),
    stroke: { colors: [theme.palette.background.paper] },
    legend: {
      show: true,
      fontSize: String(12),
      position: 'bottom',
      horizontalAlign: 'center',
      markers: {
        radius: 12,
      },
      fontWeight: 400,
      offsetY: 180,
      itemMargin: { horizontal: 0, vertical: 0 },
      labels: {
        // @ts-ignore
        colors: theme.palette.secondary.gray.main,
      },
    },
    dataLabels: {
      formatter: (val: number) => val.toFixed(1)
    },
    tooltip: {
      fillSeriesColor: false,
      y: {
        formatter: (seriesName: any) => fNumber(seriesName),
        title: {
          formatter: (seriesName: any) => `${seriesName}`,
        },
      },
    },
    plotOptions: {
      pie: { donut: { labels: { show: false } } },
    },
  });

  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const handleClick = (event: MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const openItem = (id: number) => {
    setSelectedItem(id)
    handleClose()
  }

  return (
    <Box sx={{ p: '12px', flexGrow: 1 }}>
      <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'flex-start', boxShadow: '0px 5px 12px -4px rgba(145, 158, 171, 0.31)' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 3 }}>
          {items.length > 1 ? (<div>
            <Button
              id="basic-button"
              variant="text"
              aria-controls={open ? 'basic-menu' : undefined}
              aria-haspopup="true"
              aria-expanded={open ? 'true' : undefined}
              onClick={handleClick}
              endIcon={<KeyboardArrowDownIcon />}
            >
              <Typography color="secondary.dimgray.main" sx={{ fontWeight: 700, fontSize: '16px' }}>{t(name)}</Typography>
            </Button>
            <Menu
              id="basic-menu"
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              MenuListProps={{
                'aria-labelledby': 'basic-button',
              }}
            >
              {menuList.map(i => <MenuItem onClick={() => openItem(i.id)} key={i.id}>{t(i.name)}</MenuItem>)}
            </Menu>
          </div>) : <Typography color="secondary.dimgray.main" sx={{ fontWeight: 700, fontSize: '16px' }}>{t(name)}</Typography>}
          {/* @ts-ignore */}
          {link && !hideShowAll && <Button variant="outlined" to={link} size="small" component={RouterLink} sx={{ color: theme.palette.secondary.dimgray.main, borderColor: theme.palette.secondary.gray.pastel }}>{t('ShowAll')}</Button> }
        </Box>
        <Box>
          <ChartWrapperStyle dir="ltr">
            {!!data?.length && <ReactApexChart width={width || undefined} height={height || undefined} type="pie" series={data?.map((i: DataItemType) => i.value) as number[]} options={chartOptions} dir="rtl" />}
          </ChartWrapperStyle>
        </Box>
        {!!charts?.length && (<Box sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', borderTop: `solid 1px ${theme.palette.divider}`, px: 3, pt: '12px', pb: '20px' }}>
          {
            charts.map((i: any) => (
              // @ts-ignore
              <Typography key={i.Id} color="secondary.gray.main" sx={{ fontSize: '12px', display: 'flex', alignItems: 'center', '&:before': { content: '""', width: '12px', height: '12px', backgroundColor: theme.palette.error.red, borderRadius: '100%', mr: 1 } }}>
                {i.Title}
              </Typography>
            ))
          }
        </Box>)}
      </Card>
    </Box>
  )
}
export default PieChart
