import { Dispatch, FC, SetStateAction, useMemo, useState } from "react";
import { Attachment, File } from "@/types";

// @mui
import { Box, Tooltip, Typography } from "@mui/material";

// icons
import { ReactComponent as FileIcon } from "../../images/ic_file.svg";
import FallBackImage from "../../images/ic_file.svg";

const TicketFile: FC<{
	file: File;
	openFile: (file: File) => void;
	setNotLoadedImages: Dispatch<SetStateAction<number[] | []>>;
}> = ({ openFile, file, setNotLoadedImages }) => {
	// const file = useMemo(() => files.find((i) => i.id === id), [id, files]);
	// const fileIndex = useMemo(
	// 	() => files.findIndex((i) => i.id === id),
	// 	[id, files]
	// );
	const [error, setError] = useState(false);
	return (
		<Box
			key={file.id}
			onClick={() => openFile(file)}
			sx={{
				display: "flex",
				alignItems: "center",
				justifyContent: "flex-end",
				gap: 2,
			}}
		>
			{error && (
				<Box
					sx={{
						flexGrow: 1,
						display: "flex",
						flexDirection: "column",
						alignItems: "stretch",
						direction: "rtl",
						maxWidth: "77%",
					}}
				>
					<Tooltip title={file?.name}>
						<Typography
							color="secondary.dimgray.main"
							noWrap
							sx={{
								fontSize: "14px",
								maxWidth: { md: "128px" },
								direction: "rtl",
								textAlign: "right",
							}}
						>
							{file?.name}
						</Typography>
					</Tooltip>
					<Typography
						variant="body2"
						color="secondary.gray.main"
						noWrap
						sx={{
							fontSize: "14px",
							fontFamily: "Public Sans,sans-serif",
							direction: "rtl",
							textAlign: "right",
							mr: 0,
							ml: "auto",
						}}
					>
						{file?.size ? parseInt(file.size / 1000) : ""} KB
					</Typography>
				</Box>
			)}
			<Tooltip
				title={
					<Box sx={{ whiteSpace: "pre-line" }}>
						{file?.name + "\n" + parseInt(file.size / 1000) + " KB"}
					</Box>
				}
			>
				{!!file?.url ? (
					<Box
						component="img"
						src={"/" + file.url}
						onError={(event: { target: HTMLImageElement; onerror: any }) => {
							event.target.src = FallBackImage;
							setNotLoadedImages((j) => [...j, file.id]);
							setError(true);
							event.onerror = null;
						}}
						minWidth={48}
						minHeight={48}
						maxWidth={312}
						maxHeight={312}
						style={{ borderRadius: "8px", objectFit: "cover" }}
					/>
				) : (
					<FileIcon style={{ fontSize: "48px" }} />
				)}
			</Tooltip>
		</Box>
	);
};

export default TicketFile;
