import * as React from "react";
import { useState, useEffect } from "react";
import {
	Link as RouterLink,
	Navigate,
	useLocation,
	useNavigate,
} from "react-router-dom";
// @mui
import { useTheme } from "@mui/material/styles";
import {
	Box,
	Card,
	Table,
	Avatar,
	Button,
	Checkbox,
	TableRow,
	TableBody,
	TableCell,
	Container,
	Typography,
	TableContainer,
	TablePagination,
	LinearProgress,
	DialogActions,
	DialogContent,
	DialogContentText,
	DialogTitle,
	Alert,
	Divider,
	Grid,
	TextField,
	TableHead,
	Paper,
} from "@mui/material";
// components
import Page from "../components/Page";
import Label from "../components/Label";
import Iconify from "../components/Iconify";
import Scrollbar from "../components/Scrollbar";
import SearchNotFound from "../components/SearchNotFound";
import HeaderBreadcrumbs from "../components/HeaderBreadcrumbs";
// sections
import {
	UserListHead,
	UserListToolbar,
	UserMoreMenu,
} from "../sections/@dashboard/user/list";

import axiosInstance from "../utils/axios-instance";
import settings from "../settings";
import planUtils from "../utils/planUtils";
import { useSnackbar } from "notistack";
import { useConfirm } from "material-ui-confirm";
import { LoadingButton } from "@mui/lab";
import ScreenLoading from "../components/ScreenLoading";

const termName = ["1 ماهه", "3 ماهه", "6 ماهه", "سالیانه", "دائمی", "دو ماهه"];

let _controller = new AbortController();

export default function Payment() {
	// const UserList = () => {
	const theme = useTheme();

	const [userList, setUserViewList] = useState([] as any[]);
	const { enqueueSnackbar } = useSnackbar();
	const navigate = useNavigate();
	const location = useLocation();

	const plan = location.state?.plan;
	useEffect(() => {
		if (!plan) {
			navigate("/plans");
		}
		return () => {};
	}, []);

	if (!plan) {
		return <ScreenLoading />;
	}

	const user = location.state?.user;
	const selectedTab = (location.state?.selectedTab || 0) as number;
	const validateloading = false;

	const description = `پلن ${plan.name} ${termName[selectedTab]} حسابداری کلیک`;
	const price = +plan.priceList[selectedTab] * 10;

	const getPrice = () => {
		const realPrice =
			plan.priceList[0] * ({ 1: 3, 2: 6, 3: 12, 4: 120 }[selectedTab] || 1);
		return realPrice;
	};

	const realPrice = getPrice() * 10;
	const discountPercent = parseInt(100 - (price * 100) / realPrice + "");

	const [buyLoading, setBuyLoading] = React.useState(false);

	const buyPlan = (plan: any) => {
		if (_controller) {
			_controller.abort();
			_controller = new AbortController();
		}

		setBuyLoading(true);

		const tomanPrice = price / 10;

		axiosInstance
			.post(
				user.planId ? "Payment/ExtendPlan" : "Payment",
				{
					planNumber: selectedTab + 1,
					price: tomanPrice + parseInt((tomanPrice / 100) * 9 + ""),
					description: `خرید پلن ${plan.name} ${termName[selectedTab]} حسابداری کلیک`,
					planName: plan.name,
					planIndex: plan.planIndex,
				},
				{ signal: _controller.signal }
			)
			.then((response) => {
				if (response.data.result === "ok") {
					window.location.assign(response.data.redirect);
				} else {
					enqueueSnackbar("خطایی رخ داد مجدد تلاش نمایید!", {
						variant: "error",
					});
				}
			})
			.catch((error) => {
				//TODO ERROR
				if (!error._handled && error.code !== "ERR_CANCELED") {
					// setError(
					// 	error.response?.data?.error || "خطایی رخ داد مجدد تلاش نمایید!"
					// );

					enqueueSnackbar(
						error.response?.data?.error || "خطایی رخ داد مجدد تلاش نمایید!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => setBuyLoading(false));
	};

	return (
		<Page title="پرداخت خدمات">
			<Container className="pt-32 pb-20">
				<Grid container spacing={3} className="justify-center">
					<Grid item xs={12} md={6}>
						<Paper className="flex flex-row items-center justify-center rounded-2xl  p-10">
							{validateloading ? (
								<div className="lds-ripple">
									<div></div>

									<div></div>
								</div>
							) : (
								<div className="flex flex-col items-center">
									<h1 className="mb-4 text-xl font-bold text-green-800 ">
										پیش فاکتور خدمات
									</h1>

									<Grid container className="w-100 m-8">
										<Grid item md={6} xs={6}>
											<Typography className="mb-4 text-gray-500">
												فروشنده
											</Typography>
											<Typography>رایان پردازش کلیک</Typography>

											<Typography className="mb-4 mt-6 text-gray-500">
												تاریخ
											</Typography>
											<Typography>
												{new Date().toLocaleDateString("fa-ir")}
											</Typography>
										</Grid>
										<Grid item md={6} xs={6}>
											<Typography className="mb-4 text-gray-500">
												خریدار
											</Typography>
											<Typography>
												{(user.name || "") + " " + (user.lastName || "")}
											</Typography>
										</Grid>
									</Grid>

									{/* <h2 className="mb-4 text-center text-sm text-gray-500 lg:text-base">
										{description}
									</h2> */}

									<TableContainer>
										<Table>
											<TableHead sx={{}}>
												<TableRow>
													<TableCell sx={{}}>#</TableCell>
													<TableCell colSpan={2} sx={{ minWidth: 168 }}>
														نام خدمات
													</TableCell>
													<TableCell sx={{}}>مدت</TableCell>
													<TableCell sx={{}}>قیمت(ریال)</TableCell>
												</TableRow>
											</TableHead>
											<TableBody>
												<TableRow>
													<TableCell sx={{}}> 1 </TableCell>
													<TableCell colSpan={2} sx={{}}>
														{plan.name}
													</TableCell>
													<TableCell sx={{}}>{termName[selectedTab]}</TableCell>
													<TableCell sx={{}}>
														{Number(realPrice).toLocaleString()} ریال
													</TableCell>
												</TableRow>
												<TableRow
													sx={{
														"& > *": {
															borderBottom: "unset !important",
														},
														"& > td": {
															padding: "8px 16px !important",
														},
													}}
												>
													<TableCell sx={{}}></TableCell>
													<TableCell colSpan={2} sx={{}}></TableCell>
													<TableCell sx={{}}> تخفیف </TableCell>
													<TableCell sx={{}} className="text-green-500">
														- {Number(realPrice - price).toLocaleString()} ریال
													</TableCell>
												</TableRow>
												<TableRow
													sx={{
														"& > *": {
															borderBottom: "unset !important",
														},
														"& > td": {
															padding: "8px 16px !important",
														},
													}}
												>
													<TableCell sx={{}}></TableCell>
													<TableCell colSpan={2} sx={{}}></TableCell>
													<TableCell sx={{}}> مالیات </TableCell>
													<TableCell sx={{}} className="text-red-500">
														+ {Number((price * 9) / 100).toLocaleString()} ریال
													</TableCell>
												</TableRow>
												<TableRow
													sx={{
														"& > *": {
															borderBottom: "unset !important",
														},
														"& > td": {
															padding: "8px 16px !important",
														},
													}}
												>
													<TableCell sx={{}}></TableCell>
													<TableCell colSpan={2} sx={{}}></TableCell>
													<TableCell sx={{}}> جمع کل پرداخت </TableCell>
													<TableCell sx={{}} className="font-bold">
														{Number(price + (price * 9) / 100).toLocaleString()}{" "}
														ریال
													</TableCell>
												</TableRow>

												<TableRow
													sx={{
														"& > *": {
															borderBottom: "unset !important",
														},
														"& > td": {
															padding: "8px 16px !important",
														},
													}}
												>
													<TableCell sx={{}}></TableCell>
													<TableCell colSpan={2} sx={{}}></TableCell>
													<TableCell sx={{}}>
														<Button
															onClick={() => {
																navigate("/plans");
															}}
															fullWidth
															variant="outlined"
															size="small"
															className="mt-4"
														>
															<span>بارگشت</span>
														</Button>
													</TableCell>
													<TableCell sx={{}}>
														<LoadingButton
															onClick={() => {
																buyPlan(plan);
															}}
															disableElevation
															fullWidth
															variant="contained"
															size="small"
															sx={{
																"&:hover": {
																	boxShadow:
																		"rgba(32,101,209,0.24) 0 8px 16px 0",
																	backgroundColor: "#007B55",
																},
																backgroundColor: "#00AB55",
															}}
															className="mt-4"
															loading={buyLoading}
														>
															<span>پرداخت</span>
														</LoadingButton>
													</TableCell>
												</TableRow>
											</TableBody>
										</Table>
									</TableContainer>
								</div>
							)}
						</Paper>
					</Grid>
				</Grid>
			</Container>
		</Page>
	);
}
