import { FC, useMemo } from "react";
// @mui
import { Box, Card, Avatar, CardContent, Typography, Stack, Link, IconButton } from "@mui/material";
import { Link as RouterLink } from "react-router-dom";
import { useTranslation } from "react-i18next";
import type { Post, PostType } from "../../../types";
import AccessTimeIcon from "@mui/icons-material/AccessTime";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import Image from "../Image";
import TextMaxLine from "../TextMaxLine";

import axiosInstance from "../../utils/axios-instance";

import { setScreenLoading } from "../../utils";

// const postType = location.pathname.toLowerCase().includes('help') ? 'help' : 'blog';
const canEdit = window.localStorage.isAdmin === 'true';

const PostCard: FC<{ post: Post, docType?: PostType }> = ({ post, docType }) => {
	const { t } = useTranslation('help')
	const postType = useMemo(() => [1, 2].includes(post.type) ? 'help' : 'blog', [post.type])
	const link = ['/helplist', '/helpList'].includes(location.pathname)
		? `/${post.title.split(' ').join('-')}`
		: /*@ts-ignore */
		`/${post.isHelp ? "help" : postType}/${post.id}`;

	const editLink = `/${postType}form/${post.id}`;

	const onDelete = (id: number) => {
		const conf = confirm("پاک شود؟!");
		if (!conf) return;

		setScreenLoading(true);
		axiosInstance
			.delete(`help`, { data: { id } })
			.then((res: any) => {
				window.location.reload();
			})
			.catch((e) => {
				console.log(e);
			})
			.finally(() => setScreenLoading(false));
	};

	return (
		<Card>
			<Box sx={{ position: "relative" }}>
				<Box
					component="span"
					sx={{
						display: "inline-block",
						bgcolor: "currentColor",
						WebkitMask: `url(https://minimal-assets-api.vercel.app/assets/icons/shape-avatar.svg) no-repeat center / contain`,
						mask: `url(https://minimal-assets-api.vercel.app/assets/icons/shape-avatar.svg) no-repeat center / contain`,
						width: 80,
						height: 36,
						zIndex: 9,
						bottom: -15,
						position: "absolute",
						color: "background.paper",
					}}
				/>
				<Avatar
					alt={t(post.categoryTitle) as string}
					src={post.categoryIcon}
					sx={{
						left: 24,
						zIndex: 9,
						width: 32,
						height: 32,
						bottom: -16,
						position: "absolute",
					}}
				/>

				<Link
					to={link}
					color="inherit"
					component={RouterLink}
					sx={{
						margin: 0,
						textDecoration: "none",
					}}
				>
					{
						(post.videoUrl && docType === 1) ? (
							<Box dangerouslySetInnerHTML={{ __html: post.videoUrl }}></Box>
						) : (
							<Image
								className="post-cover"
								alt="cover"
								// @ts-ignore
								src={post.featured_media || post.imageUrl}
								ratio="4/3"
								sx={{
									background:
										"#f4f6f8 url(https://old.clickmis.net/wp-content/uploads/2021/06/background-postsimage.svg) no-repeat top 28px center !important",
								}}
							/>
						)
					}

				</Link>
			</Box>
			<CardContent
				sx={{
					pt: 4.5,
					width: 1,
					minHeight: "215px",
					"&:last-child": {
						pb: 0,
					},
				}}
			>
				<div className="flex items-center">
					<Typography
						gutterBottom
						variant="caption"
						component="div"
						sx={{
							color: "#2D99FF",
						}}
					>
						<Typography
							variant="caption"
							sx={{
								color: "text.disabled",
							}}
						>
							دسته بندی:{" "}
						</Typography>
						{t(post.categoryTitle)}
					</Typography>
					<span className="flex-grow"></span>
					<Typography
						gutterBottom
						variant="caption"
						component="div"
						sx={{
							color: "text.disabled",
						}}
					>
						{post.date}
					</Typography>
				</div>

				<Link
					to={link}
					color="inherit"
					component={RouterLink}
					sx={{
						margin: 0,
						textDecoration: "none",
					}}
				>
					{/* @ts-ignore */}
					<TextMaxLine
						variant={"h6"}
						line={1}
						persistent
						sx={{
							mb: 1,
							"&:hover": { color: "#2D99FF" },
						}}
					>
						{post.title}
					</TextMaxLine>
				</Link>
				{/* @ts-ignore */}
				<TextMaxLine variant={"body2"} line={3} persistent align="justify">
					{post.desciption}
				</TextMaxLine>
				{!!post.readTime && (
					<Stack
						flexWrap="wrap"
						direction="row"
						justifyContent="flex-start"
						sx={{
							mt: 3,
							color: "text.disabled",
							gap: 1,
						}}
					>
						<Stack
							direction="row"
							alignItems="center"
							sx={{
								typography: "body2",
							}}
						>
							<AccessTimeIcon sx={{ width: 16, height: 16, mr: 0.5 }} />
							مدت زمان مطالعه: {post.readTime}
						</Stack>

						{canEdit && (
							<>
								<IconButton
									aria-label="Edit"
									sx={{ ml: "auto" }}
									to={editLink}
									component={RouterLink}
								>
									<EditIcon sx={{ width: 16, height: 16 }} />
								</IconButton>
								<IconButton aria-label="Edit" onClick={() => onDelete(post.id)}>
									<DeleteIcon sx={{ width: 16, height: 16 }} />
								</IconButton>
							</>
						)}
					</Stack>
				)}

				{canEdit && !post.readTime && (
					<>
						<IconButton aria-label="Edit" to={editLink} component={RouterLink}>
							<EditIcon sx={{ width: 16, height: 16 }} />
						</IconButton>
						<IconButton aria-label="Edit" onClick={() => onDelete(post.id)}>
							<DeleteIcon sx={{ width: 16, height: 16 }} />
						</IconButton>
					</>
				)}
			</CardContent>
		</Card>
	);
};
export default PostCard;
