import { Dispatch, FC, SetStateAction, useEffect, useState } from 'react';
import { useSnackbar } from 'notistack';

// @mui
import { Box, Card, CircularProgress, Grid, Tooltip, Typography, useTheme } from '@mui/material';
import axiosInstance from '../../utils/axios-instance';
import { StatusItem } from '@/types';

// icons
import { ReactComponent as DataBaseIcon } from '../../images/database.svg'
import { ReactComponent as UsersIcon } from '../../images/user-tick.svg'
import { ReactComponent as ItemsIcon } from '../../images/items.svg'
import { ReactComponent as DepotioIcon } from '../../images/depotio.svg'
import { ReactComponent as UserSearchIcon } from '../../images/user-search.svg'
import { getPriceFormat } from '../../utils/formatNumber';

interface InfoType {
	invoiceCount: number;
	itemCount: number;
	userCount: number;
	databaseCount: number;
	date: Date;
	clickUsersCount: number;
}

const DashboardHeader: FC<{ setLoades: Dispatch<SetStateAction<string[]>>}> = ({ setLoades }) => {
	const { enqueueSnackbar } = useSnackbar();
	const theme = useTheme();
	const [info, setInfo] = useState<InfoType | null>(null);
	const [statusList, setStatusList] = useState<{ [key: string]: StatusItem }>();
	const [listLoading, setListLoading] = useState(true);

	useEffect(() => {
		let mounted = true;
		axiosInstance
			.get('/click/info')
			.then((response) => {
				if (response.data) {
					mounted && setInfo(response.data[0]);
					const info = response.data[0];
					setStatusList({
						database: {
							id: 1,
							name: "پایگاه‌داده",
							color: "#3366FF",
							value: info?.databaseCount,
							unit: 'عدد',
							icon: DataBaseIcon
						},
						user: {
							id: 2,
							name: "کاربران",
							color: "#00AB55",
							value: info?.userCount,
							unit: 'عدد',
							icon: UsersIcon
						},
						item: {
							id: 3,
							name: "کالاها",
							color: "#FFC107",
							value: info?.itemCount,
							unit: 'فاکتور',
							icon: ItemsIcon
						},
						invoice: {
							id: 4,
							name: "ورودی/خروجی انبار",
							color: "#FF4842",
							value: info?.invoiceCount,
							unit: 'فاکتور',
							icon: DepotioIcon
						},
						clickUsersCount: {
							id: 5,
							name: "کاربران کلیک",
							color: "#637381",
							value: info?.clickUsersCount,
							unit: 'فاکتور',
							icon: UserSearchIcon
						},
					});
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => {
        setLoades((i: string[]) => [...i, 'clickInfoGet'])
				setListLoading(false)
			});
	}, [])
	return (
		<Box sx={{ py: '20px', px: '12px' }}>
			<Card sx={{ display: 'flex', alignItems: 'center', boxShadow: '0px 12px 24px -4px rgba(145, 158, 171, 0.12), 0px 0px 2px 0px rgba(145, 158, 171, 0.20)', borderRadius: '16px' }}>
				<Grid container className="flex flex-wrap py-4">
					{!!statusList && Object.keys(statusList).map((i: string, j: number) => {
						const item: StatusItem = statusList[i]
						const Icon = item.icon;
						return (
							<Grid
								item
								xs={12} sm={6} md={4} lg={12/5}
								key={item.id}
								sx={{
									position: "relative",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
									width: "100%",
									minWidth: "200px",
									py: 2,
									opacity: item.value ? 1 : 0.4,
									borderRight: Object.keys(statusList).length-1 > j ? '1px dashed #e5e8eb' : 'none'
								}}
							>
								{/* <Tooltip title={`${(+item.value).toFixed(1)}%`}> */}
									<Box
										sx={{
											width: 56,
											height: 56,
											position: "relative",
											display: "flex",
											alignItems: "center",
											justifyContent: "center",
										}}
									>
										<Icon sx={{ color: item.color }} />
										<CircularProgress
											variant="determinate"
											sx={{
												color: "rgba(145, 158, 171, 0.16)",
												position: "absolute",
												top: 0,
												left: 0,
											}}
											size={56}
											thickness={4}
											value={100}
										/>
										<CircularProgress
											variant="determinate"
											size={56}
											value={+item.value as number}
											sx={{
												color: item.color,
												opacity: 0.48,
												position: "absolute",
												top: 0,
												left: 0,
												zIndex: 1,
											}}
										/>
									</Box>
								{/* </Tooltip> */}
								<Box sx={{ display: 'flex', flexDirection: 'column', gap: '2px', ml: 2, minWidth: "140px" }}>
									<Typography
										variant="h6"
										sx={{ fontWeight: 600, fontSize: "1.125rem" }}
									>
										{item.name}
									</Typography>
									<Typography
										variant="h6"
										color="#212B36"
										sx={{ fontSize: '14px', display: 'flex', alignItems: 'center', gap: '4px' }}
									>
										{/* @ts-ignore */}
										{getPriceFormat(item.value)} <Typography variant="body2" color="#637381">{item.unit}</Typography>
									</Typography>
								</Box>
								{/* {j < Object.keys(statusList).length - 1 && (
											<Divider
												orientation="vertical"
												variant="middle"
												flexItem
												sx={{
													margin: "0px",
													flexShrink: 0,
													borderWidth: "0px thin 0px 0px",
													borderColor: "rgba(145, 158, 171, 0.24)",
													// height: 'auto',
													alignSelf: "stretch",
													borderStyle: "dashed",
													position: "absolute",
													top: 0,
													right: 0,
													height: "100%",
												}}
											/>
										)} */}
							</Grid>
						)
					})}
				</Grid>
			</Card>
		</Box>
	)
}
export default DashboardHeader