import { FC } from 'react';
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import IconButton from '@mui/material/IconButton';
import RefreshIcon from '@mui/icons-material/Refresh';
import { OnChangeSetting, Setting, SettingConfig } from '@/types';
import Tooltip from '@mui/material/Tooltip';
import axiosInstance from '../../utils/axios-instance';

const CodeGenrator: FC<{ config: SettingConfig, data: Setting, onChange: OnChangeSetting }> = (props) => {
  const { data, onChange } = props;
  const getCode = () => {
    axiosInstance.post('/Common/ComponentProfiles/GenerateApiKey').then((res: { data: string }) => {
      console.log(res)
      onChange({ target: { value: res.data } }, data)
    }).catch(e => {
      console.log(e)
    })
  };
  return (
    <div>
      <div className="mb-2">{data.FormName}</div>
      <Paper
        component="form"
        sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', maxWidth: '100%' }}
      >
        <InputBase
          value={data.Value}
          name={data.Name}
          sx={{ ml: 1, flex: 1 }}
          inputProps={{ 'aria-label': 'code genrator' }}
        />
        <Tooltip title="تولید کلید">
          <IconButton onClick={getCode} color="primary" sx={{ p: '10px' }} aria-label="directions">
            <RefreshIcon />
          </IconButton>
        </Tooltip>
      </Paper>
    </div>
  )
}
export default CodeGenrator