import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';

// @mui
import { Box, Container, Typography, TextField, FormControl, FormControlLabel, Radio, RadioGroup, Autocomplete, Skeleton, styled, Switch } from '@mui/material';

import axiosInstance from '../utils/axios-instance';
import { useSnackbar } from 'notistack';
import { useTranslation } from 'react-i18next';

import '@amir04lm26/react-modern-calendar-date-picker/lib/DatePicker.css';
import DatePicker from '@amir04lm26/react-modern-calendar-date-picker';

// components
import Page from '../components/Page';
import { setScreenLoading } from '../utils';
import utils from '../utils/utils';
import Logo from '../images/logo.png';
import FileUploadLogo from '../images/fileUpload.svg';
import './HelpForm.css';
import { useFormik } from 'formik';
import { object } from 'yup';
import { LoadingButton } from '@mui/lab';
// import MISEditor from '../components/MISEditor';
import DashboardNavbar from '../components/dashboard/DashboardNavbar';
import { categories } from '../utils/help';

const MISEditor = React.lazy(() => import('../components/MISEditor'));

const Android12Switch = styled(Switch)(({ theme }) => ({
  padding: 8,
  '& .MuiSwitch-track': {
    borderRadius: 22 / 2,
    '&:before, &:after': {
      content: '""',
      position: 'absolute',
      top: '50%',
      transform: 'translateY(-50%)',
      width: 16,
      height: 16,
    },
    '&:before': {
      backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
        theme.palette.getContrastText(theme.palette.primary.main),
      )}" d="M21,7L9,19L3.5,13.5L4.91,12.09L9,16.17L19.59,5.59L21,7Z"/></svg>')`,
      left: 12,
    },
    '&:after': {
      backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="16" width="16" viewBox="0 0 24 24"><path fill="${encodeURIComponent(
        theme.palette.getContrastText(theme.palette.primary.main),
      )}" d="M19,13H5V11H19V13Z" /></svg>')`,
      right: 12,
    },
  },
  '& .MuiSwitch-thumb': {
    boxShadow: 'none',
    width: 16,
    height: 16,
    margin: 2,
  },
}));

export default function HelpForm() {
	const { t } = useTranslation('help')
	const routeParams = useParams();
	const { enqueueSnackbar } = useSnackbar();
	const navigate = useNavigate();
	//customize
	const [listLoading, setListLoading] = useState(false);
	const [helpId, setHelpId] = useState(false);
	const [isArchive, setIsArchive] = useState<boolean>(false);

	const themeStretch = true;

	const form = useFormik({
		initialValues: {
			summary: "",
			title: "",
			tags: [],
			category: "",
			date: "" + utils.getToday(),
			readTime: 60,
			type: "2",
			image: null as any,
			text: "",
			url: "",
			address: "",
		},
		validationSchema: object({}),
		onSubmit: (values) => {
			setListLoading(true);
			console.log(values);

			// const url = window.URL.createObjectURL(
			// 	new Blob([JSON.stringify(values, null, "\t")], {
			// 		type: "text/plain;charset=utf-8;",
			// 	})
			// );
			// const a = document.createElement("a");
			// a.style.display = "none";
			// a.href = url;
			// // the filename you want
			// a.download = (values.title || "file") + ".json";
			// document.body.appendChild(a);
			// a.click();
			// window.URL.revokeObjectURL(url);

			var formDate = new FormData();
			values.image && formDate.append("imageFile", values.image);

			const data = {
				id: routeParams.id || helpId,
				summary: values.summary?.trim() || '',
				title: values.title?.trim() || '',
				tagsArray: JSON.stringify(values.tags),
				category: values.category,
				date: utils.jalaliToGregorian(values.date),
				readTime: values.readTime,
				type: isArchive ? +values.type + 400 : values.type,
				textHtml: values.text,
				videoUrl: values.url?.trim() || '',
				address: values.address?.trim() || '',
			};

			formDate.append("helpJson", JSON.stringify(data));
			axiosInstance
				.post("/help", formDate)
				.then((response) => {
					if (response.data?.result === "ok") {
						if (routeParams.id || helpId) {
							enqueueSnackbar("مطلب مورد نظر ویرایش گردید.", { variant: 'success' });
						} else {
							enqueueSnackbar("مطلب مورد نظر درج گردید.", { variant: 'success' });
							navigate(`/helpForm/${response.data?.help?.id}`)
						}
						setHelpId(response.data?.help?.id);
					}
				})
				.catch((error) => {
					if (!error._handled) {
						enqueueSnackbar(
							error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
							{
								variant: "error",
							}
						);
					}
				})
				.finally(() => setListLoading(false));
		},
	});

	const [fileUrl, setFileUrl] = useState("");

	const inputRef = React.useRef<any>(null);
	const handleFileChange = (event: any) => {
		const fileObj = event.target.files && event.target.files[0];
		if (!fileObj) {
			return;
		}

		console.log("fileObj is", fileObj);

		// 👇️ reset file input
		event.target.value = null;

		// 👇️ is now empty
		console.log(event.target.files);

		// 👇️ can still access file object here
		console.log(fileObj);
		console.log(fileObj.name);
		setFileUrl(URL.createObjectURL(fileObj));
		form.setFieldValue("image", fileObj);
	};

	const editor = React.useMemo(
		() => (
			<MISEditor
				value={form.values.text}
				onChange={(text) => form.setFieldValue("text", text)}
			/>
		),
		[form.values.text]
	);

	useEffect(() => {
		if (routeParams.id) {
			setScreenLoading(true);
			const postType = location.pathname.includes("help") ? "docs" : "posts";
			axiosInstance
				.get(`help/${routeParams.id}`)
				.then((res: any) => {
					let type = res.data.help.type
					if (type > 400) { 
						type = type - 400
						setIsArchive(true)
					}
					const result = {
						...res.data.help,
						tags: JSON.parse(res.data.help.tagsArray || "[]"),
						text: res.data.help.textHtml,
						date: utils.toDateString(res.data.help.date),
						url: res.data.help.videoUrl,
						type
					}
					form.setValues(result);
				})
				.catch((e) => {
					console.log(e);
				})
				.finally(() => setScreenLoading(false));
		}
	}, [routeParams.id]);

	return (
		<Page title={routeParams?.id ? 'ویرایش راهنما'  : 'افزودن راهنمای جدید'}>
			<Box sx={{ display: "flex" }}>
				<DashboardNavbar />
				<Container maxWidth={themeStretch ? false : "lg"}>
					<Box sx={{ p: "60px 30px 30px 30px", display: "flex" }}>
						<Typography
							component={"h3"}
							sx={{ color: "#212B36", fontWeight: 700, fontSize: "24px" }}
						>
							پنل مدیریت - افزودن راهنمای جدید
						</Typography>
						<div style={{ flex: 1 }}></div>
						<Link to="/" className="">
							<img
								src={Logo}
								alt="نرم افزار حسابداری آنلاین و ابری کلیک"
								style={{ height: "48px" }}
							/>
						</Link>
					</Box>

					<Box sx={{ display: "flex", p: "0 40px 40px 40px" }}>
						<Box className="HelpForm__Panel">
							<Typography
								component={"h4"}
								sx={{ color: "#212B36", fontWeight: 700, fontSize: "16px" }}
							>
								کاور آموزش
							</Typography>

							<form onSubmit={form.handleSubmit}>
								<FormControl fullWidth>
									<RadioGroup
										aria-labelledby="demo-radio-buttons-group-label"
										defaultValue="1"
										sx={{
											flexDirection: "row",
											justifyContent: "space-between",
											mt: 0,
											pl: 1,
										}}
										{...form.getFieldProps("type")}
									>
										<FormControlLabel
											value="1"
											control={<Radio />}
											label="آموزش ویدئویی"
										/>
										<FormControlLabel
											value="2"
											control={<Radio />}
											label="مستندات آموزشی"
										/>
									</RadioGroup>
								</FormControl>
								{/* @ts-ignore */}
								{(form.values.type === "1" || form.values.type === 1) && (
									<TextField
										autoFocus
										fullWidth
										label={"آی فریم آپارت"}
										sx={{ mt: 2 }}
										multiline={true}
										rows={3}
										error={form.touched.url && Boolean(form.errors.url)}
										helperText={form.touched.url && form.errors.url}
										{...form.getFieldProps("url")}
									/>
								)}

								<TextField
									autoFocus
									fullWidth
									label={"عنوان"}
									sx={{ mt: 2 }}
									error={form.touched.title && Boolean(form.errors.title)}
									helperText={form.touched.title && form.errors.title}
									{...form.getFieldProps("title")}
								/>

								<Autocomplete
									sx={{ mt: 2 }}
									// freeSolo
									options={categories}
									isOptionEqualToValue={(option, value) => (option?.id || option) === value}
									renderOption={(props, option) => (
										<li {...props} value={option?.id || option}>
											{t(option?.id || option)}
										</li>
									)}
									noOptionsText="موردی یافت نشد!"
									getOptionLabel={(option) => typeof option === 'object' ? t(option?.id) : t(option)}
									renderInput={(params) => (
										<TextField {...params} label="دسته بندی" />
									)}
									{...form.getFieldProps("category")}
									selectOnFocus
									onBlur={(event: any) => {
										form.setFieldValue("category", event.target.value);
									}}
									onChange={(event, newValue) => {
										if (typeof newValue === "string") {
											form.setFieldValue("category", newValue);
										} else if (newValue && newValue.inputValue) {
											// Create a new value from the user input
											form.setFieldValue("category", newValue.inputValue);
										} else {
											form.setFieldValue("category", newValue);
										}
									}}
								/>

								<Autocomplete
									freeSolo
									sx={{ mt: 2 }}
									multiple
									limitTags={2}
									options={[]}
									renderInput={(params) => (
										<TextField {...params} label="تگ‌ها" placeholder="تگ" />
									)}
									{...form.getFieldProps("tags")}
									noOptionsText="موردی یافت نشد!"
									onChange={(event, newValue) => {
										form.setFieldValue("tags", newValue);
									}}
								/>

								<Box sx={{ width: "100%", mt: 2 }}>
									<DatePicker
										wrapperClassName="HelpForm__Date"
										shouldHighlightWeekends
										locale="fa"
										colorPrimary="#0093FF"
										onChange={(date) => {
											console.log("data", date);
											form.setFieldValue(
												"date",
												`${date?.year}/${date?.month}/${date?.day}`
											);
										}}
										renderInput={({ ref }) => (
											<TextField
												ref={ref as any}
												fullWidth
												variant="outlined"
												label={"تاریخ"}
												{...form.getFieldProps("date")}
											/>
										)}
									/>
								</Box>

								<TextField
									autoFocus
									fullWidth
									label={"مدت زمان مطالعه (ثانیه) "}
									sx={{ mt: 2 }}
									type="number"
									error={form.touched.readTime && Boolean(form.errors.readTime)}
									helperText={form.touched.readTime && form.errors.readTime}
									{...form.getFieldProps("readTime")}
								/>

								<TextField
									fullWidth
									label={"آدرس در سایت"}
									sx={{ mt: 2 }}
									error={form.touched.address && Boolean(form.errors.address)}
									helperText={form.touched.address && form.errors.address}
									{...form.getFieldProps("address")}
								/>

								<TextField
									autoFocus
									fullWidth
									multiline={true}
									rows={3}
									label={"توضیح کوتاه"}
									sx={{ mt: 2 }}
									type="number"
									error={form.touched.readTime && Boolean(form.errors.readTime)}
									helperText={form.touched.readTime && form.errors.readTime}
									{...form.getFieldProps("summary")}
								/>

								<FormControlLabel
									control={<Android12Switch checked={isArchive} onChange={(event: React.ChangeEvent<HTMLInputElement>) => setIsArchive(event.target.checked)} inputProps={{ 'aria-label': 'آرشیو' }} />}
									label="آرشیو"
									sx={{ my: 2 }}
								/>

								<LoadingButton
									disableElevation
									fullWidth
									variant="contained"
									size="large"
									type="submit"
									loading={listLoading}
									// onClick={validationCheck}
									sx={{
										mt: 2,
										"&:hover": {
											boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
										},
									}}
								>
									{"ذخیره اطلاعات"}
								</LoadingButton>
							</form>
						</Box>

						<Box
							className="HelpForm__Panel"
							sx={{ ml: 3, flex: 1, width: "auto" }}
						>
							<Typography
								component={"h4"}
								sx={{ color: "#212B36", fontWeight: 700, fontSize: "16px" }}
							>
								محتوای آموزش
							</Typography>

							<React.Suspense
								fallback={<Skeleton animation="wave" height={320} />}
							>
								{editor}
							</React.Suspense>

							<Typography
								component={"h4"}
								sx={{ color: "#212B36", fontWeight: 700, fontSize: "16px" }}
							>
								تصویر کاور
							</Typography>

							<Box
								className="HelpForm__UploadBox"
								onClick={() => inputRef?.current?.click()}
							>
								<input
									ref={inputRef}
									style={{ display: "none" }}
									type="file"
									accept="image/*,.svg"
									onChange={handleFileChange}
								/>

								<Box
									sx={{
										display: "flex",
										width: "364px",
										alignItems: "center",
										justifyContent: fileUrl ? "center" : "end",
										mr: 6,
									}}
								>
									{!fileUrl && <img src={FileUploadLogo} alt="آپلود عکس" />}
									{fileUrl && (
										<img
											style={{ maxHeight: "100%" }}
											src={fileUrl}
											alt="عکسه آپلود شده"
										/>
									)}
								</Box>
								<Box
									sx={{
										display: "flex",
										flex: 1,
										alignItems: "center",
										justifyContent: "start",
									}}
								>
									<Box>
										<Typography
											component={"h4"}
											sx={{
												color: "#212B36",
												fontWeight: 700,
												fontSize: "20px",
											}}
										>
											انتخاب فایل
										</Typography>

										<Typography
											component={"h5"}
											sx={{
												color: "#637381",
												fontWeight: 400,
												fontSize: "14px",
												mt: 2,
											}}
										>
											فایل خود را جهت بارگذاری
											<span
												style={{
													color: "#00AB55",
													fontWeight: 700,
													textDecoration: "underline",
													fontSize: "16px",
												}}
											>
												{" "}
												انتخاب کنید{" "}
											</span>
											و یا در اینجا رها کنید.
										</Typography>
									</Box>
								</Box>
							</Box>
						</Box>
					</Box>
				</Container>
			</Box>
		</Page>
	);
}
