import { FC, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link as RouterLink } from 'react-router-dom';
import axiosInstance, { userGuid } from '../utils/axios-instance';
import { ItemInventory, Report } from '@/types';

// @mui
import { Box, Button, Container, IconButton, Card } from '@mui/material';
import { useTheme } from '@mui/material/styles';

// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import PieChart from '../components/items/PieChart';
import DashboardNavbar from '../components/dashboard/DashboardNavbar';
import ItemsTableListView from '../components/items/ItemsTableListView';

// icon
import { ReactComponent as PrintIcon } from '../images/ic_printer.svg'
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';


const ItemsPriceInventory: FC = () => {
  const { t } = useTranslation();
  const theme = useTheme();
  const [loadeds, setLoades] = useState<string[]>([])
  const [unsellItems, setUnsellItems] = useState<ItemInventory[]>([])
  const [reportList, setReportList] = useState<Report[]>([])
  const [itemDashboard, setItemDashboard] = useState({})
  // @ts-ignore
  const chartColors = [ theme.palette.info.blue, theme.palette.secondary.success.deep, theme.palette.warning.yellow, theme.palette.note.purple, theme.palette.error.red ]

  const [items, setItems] = useState<{
    id: number
    items: {
      id: number
      name: string
      link: string
      charts?: any[]
      data: {
        id: number
        color: string
        title: string
        value: number
      }[]
    }[]
  }[]>([
    {
      id: 1,
      items: [
        {
          id: 1,
          name: 'BestSellers',
          link: '/ItemsPriceInventory/BestSellers',
          data: []
        }
      ]
    },
    {
      id: 2,
      items: [
        {
          id: 1,
          name: 'LeastSold',
          link: '/ItemsPriceInventory/LeastSold',
          data: []
        }
      ]
    },
    {
      id: 3,
      items: [
        {
          id: 1,
          name: 'StagnantItems',
          link: '/ItemsPriceInventory/Stagnant',
          charts: [],
          data: []
        },
        {
          id: 2,
          name: 'DepotExclusiveItems',
          link: '/ItemsPriceInventory/DepotExclusiveItems',
          data: []
        }
      ]
    },
  ])

  const getChartData = (items: any, type: string, list: Report[] = []) => {
    return items?.[type]?.map((i: { total: number; itemGuid: string; }, index: number) => {
      const itemDetail = list?.find((j: { Guid: string; }) => (i.itemGuid || i) === j.Guid)
      return {
        id: itemDetail?.Id || index + 1,
        color: chartColors[index],
        title: itemDetail?.Title || '?',
        value: i.total || items?.[type]?.length
      }
    })
  }

  useEffect(() => {
    axiosInstance.get('/Item/ReportList').then(res => {
      if (res.status === 200) {
        setReportList(res.data)
        axiosInstance.get(`/Item/ReportInventory?userGuid=${userGuid}`).then(resi => {
          if (resi.status === 200) {
            const result = resi.data
            setItemDashboard(result)
            const newItems = [...items]
            newItems[0].items[0].data = getChartData(result, 'top5', res.data)
            newItems[1].items[0].data = getChartData(result, 'bottom5', res.data)
            if (!newItems[2].items[0].charts) {
              newItems[2].items[0].charts = []
            }
            newItems[2].items[0].charts = result.unsellItems.slice(0, 5).map((i: string) => res.data.find((j: Report) => j.Guid === i))
            newItems[2].items[0].data = [
              {
                id: 1,
                color: chartColors[4],
                title: 'StagnantItems',
                value: result.unsellItems.length
              },
              {
                id: 2,
                color: chartColors[1],
                title: 'OtherItems',
                value: res.data.length
              }
            ]
            setItems(newItems)
          }
        })
        // const newItems = [...items]
        // @ts-ignore
      }
    })
    // axiosInstance.get(`/Item/Unsells?userGuid=${userGuid}`).then(res => {
    //   if (res.status === 200) {
    //     setUnsellItems(res.data)
    //     const newItems = [...items]
    //     if (!newItems[2].items[0].charts) {
    //       newItems[2].items[0].charts = []
    //     }
    //     newItems[2].items[0].charts = res.data.slice(0, 4)
    //     newItems[2].items[0].data[0] = {
    //       id: 1,
    //       color: chartColors[4],
    //       title: 'StagnantItems',
    //       value: res.data.length
    //     }
    //     setItems(newItems)
    //   }
    // })
    
  }, [])

  return (
    <Page title={t('ItemsPriceInventory')}>
      <Box sx={{ display: 'flex' }}>
        <DashboardNavbar setLoades={setLoades} />
        <Container sx={{ maxWidth: '1340px !important' ,display: 'flex', flexDirection: 'column', py: 3 }}>
          <HeaderBreadcrumbs
						sx={{ mt: 2 }}
						heading={t('ItemsPriceInventory')}
            // @ts-ignore
            headingSx={{ fontSize: '24px', fontWeight: 700, color: theme.palette.secondary.dimgray.main }}
						links={[
              { name: t('Dashboard'), href: '/' },
              { name: t('BuyAndSell') }
            ]}
						action={
							<Box sx={{ display: 'flex', alignItems: 'center' }}>
                <IconButton><PrintIcon /></IconButton> 
                <Button 
                  to="/dashboard" 
                  component={RouterLink} 
                  variant="outlined"
                  // @ts-ignore
                  endIcon={<KeyboardBackspaceIcon sx={{ color: theme.palette.secondary.dimgray.main }} />} 
                  sx={{
                    // @ts-ignore
                    color: theme.palette.secondary.dimgray.main, 
                    fontWeight: 700,
                    // @ts-ignore
                    borderColor: theme.palette.secondary.gray.light3, 
                    '&:hover': {
                      // @ts-ignore
                      borderColor: theme.palette.secondary.gray.light3,
                      backgroundColor: 'rgba(145, 158, 171, 0.08)'
                    }
                  }}
                >
                  {t('Back')}
                </Button>
              </Box>
						}
					/>
          <Box sx={{ display: 'flex' }}>
            {items.map( (i, index) => <PieChart key={i.id} width={408} height={index === 2 ? 260 : 360} items={i?.items?.filter(j => j?.data?.length) || []} />)}
          </Box>
          <ItemsTableListView items={reportList} />
        </Container>
      </Box>
    </Page>
  )
}
export default ItemsPriceInventory