import * as React from "react";
import { useState, useEffect } from "react";
import { Link as RouterLink } from "react-router-dom";
// @mui
import { useTheme } from "@mui/material/styles";
// components
import Page from "../components/Page";
import Label from "../components/Label";
import "./AdminDashboard.css";
import Iconify from "../components/Iconify";
import Scrollbar from "../components/Scrollbar";
import SearchNotFound from "../components/SearchNotFound";
import HeaderBreadcrumbs from "../components/HeaderBreadcrumbs";
// sections
import {
	UserListHead,
	UserListToolbar,
	UserMoreMenu,
} from "../sections/@dashboard/user/list";

import axiosInstance from "../utils/axios-instance";
import settings from "../settings";
import planUtils from "../utils/planUtils";
import { useSnackbar } from "notistack";
import { useConfirm } from "material-ui-confirm";
import CheckIcon from "@mui/icons-material/Check";
import OpenInNewIcon from "@mui/icons-material/OpenInNew";
import WarningIcon from "@mui/icons-material/Warning";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import CheckCircleIcon from "@mui/icons-material/CheckCircle";
import CircleIcon from "@mui/icons-material/Circle";
// import { DatePicker } from "@mui/x-date-pickers/DatePicker";
// import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
// import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";

import "@amir04lm26/react-modern-calendar-date-picker/lib/DatePicker.css";
import DatePicker from "@amir04lm26/react-modern-calendar-date-picker";
import { setScreenLoading } from "../utils";
import utils from "../utils/utils";
import UserForm from "./UserForm";
import {
	Container,
	Typography,
	Tooltip,
	CircularProgress,
	Divider,
	Box,
	Drawer,
	Toolbar,
	List,
	ListItem,
	ListItemButton,
	ListItemIcon,
	ListItemText,
} from "@mui/material";

import Logo from "../images/logo.png";

import iconTicketList from "../images/icons/ic_convert-card.svg";
import iconHelps from "../images/icons/ic_bank.svg";
import iconUserList from "../images/icons/ic_box.svg";
import iconDashboard from "../images/icons/ic_dashboard.svg";
import iconAddItem from "../images/ic_additem.svg";

interface InfoType {
	invoiceCount: number;
	itemCount: number;
	userCount: number;
	databaseCount: number;
	date: Date;
	clickUsersCount: number;
}

const routes = [
	{
		name: "لیست کاربران",
		link: "userlist",
		icon: iconUserList,
	},

	{
		name: "افزودن خودآموز",
		link: "helpForm",
		icon: iconAddItem,
	},

	{
		name: "لیست خودآموز‌ها",
		link: "helpList",
		icon: iconHelps,
	},
	{
		name: "افزودن بلاگ",
		link: "blogForm",
		target: "_blank",
		icon: iconAddItem,
	},
	{
		name: "لیست بلاگ‌ها",
		link: "blog",
		target: "_blank",
		icon: iconHelps,
	},
	
	{
		name: "افزودن پرسش متداول",
		link: "faqForm",
		target: "_blank",
		icon: iconAddItem,
	},
	{
		name: "پرسش‌های متداول",
		link: "faq",
		target: "_blank",
		icon: iconHelps,
	},
	{
		name: "لیست تیکت‌ها",
		link: "tickets",
		icon: iconTicketList,
	},
];

export default function AdminDashboard() {
	// const UserList = () => {
	const theme = useTheme();

	const [info, setInfo] = useState<InfoType | null>(null);
	const [siteList, setSiteList] = useState<any>(null);
	const [userModelList, setUserModelList] = useState([] as any[]);
	const [page, setPage] = useState(0);
	const [order, setOrder] = useState("asc");
	const [selected, setSelected] = useState([] as any[]);
	const [orderBy, setOrderBy] = useState("name");
	const [filterName, setFilterName] = useState("");
	const [rowsPerPage, setRowsPerPage] = useState(25);
	const [planList, setPlanList] = useState(null);
	const [planListLoading, setPlanListLoading] = useState(true);

	//customize
	const [listLoading, setListLoading] = useState(true);
	const { enqueueSnackbar } = useSnackbar();
	const confirm = useConfirm();

	const themeStretch = true;

	const [statusList, setStatusList] = useState<any>(null);

	useEffect(() => {
		let mounted = true;
		axiosInstance
			.get("/click/info")
			.then((response) => {
				if (response.data) {
					mounted && setInfo(response.data[0]);
					const info = response.data[0];
					setStatusList({
						database: {
							id: 1,
							name: "پایگاه‌داده",
							color: "#3366FF",
							value: info?.databaseCount,
						},
						user: {
							id: 2,
							name: "کاربران",
							color: "#00AB55",
							value: info?.userCount,
						},
						item: {
							id: 3,
							name: "کالاها",
							color: "#FFC107",
							value: info?.itemCount,
						},
						invoice: {
							id: 4,
							name: "ورودی/خروجی انبار",
							color: "#FF4842",
							value: info?.invoiceCount,
						},
						clickUsersCount: {
							id: 5,
							name: "کاربران کلیک",
							color: "#637381",
							value: info?.clickUsersCount,
						},
					});
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => setListLoading(false));

		axiosInstance
			.get("/click/sites")
			.then((response) => {
				if (response.data) {
					response.data?.sort((a: any, b: any) => {
						if (a.name > b.name) {
							return 1;
						}
						if (a.name < b.name) {
							return -1;
						}
						return 0;
					});

					mounted && setSiteList(response.data);
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => setListLoading(false));

		return () => {
			mounted = false;
		};
	}, []);

	const drawerWidth = 260;

	return (
		<Page title="عجب داشبوردی 😀">
			<Box sx={{ pl: "250px" }}>
				<Box className="dash__statistic">
					<Box display={"flex"}>
						{Object.keys(statusList || {}).map((i, j: number) => {
							const item = statusList[i];
							const Icon = item.icon;
							return (
								<Box
									className=""
									key={item.id}
									sx={{
										position: "relative",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
										py: 1,
										mr: 2,
									}}
								>
									<Tooltip title={``}>
										<Box
											sx={{
												width: 56,
												height: 56,
												position: "relative",
												display: "flex",
												alignItems: "center",
												justifyContent: "center",
											}}
										>
											<WarningIcon sx={{ color: item.color }} />
											<CircularProgress
												variant="determinate"
												sx={{
													color: "rgba(145, 158, 171, 0.16)",
													position: "absolute",
													top: 0,
													left: 0,
												}}
												size={56}
												thickness={4}
												value={100}
											/>
											<CircularProgress
												variant="determinate"
												size={56}
												value={80}
												sx={{
													color: item.color,
													opacity: 0.48,
													position: "absolute",
													top: 0,
													left: 0,
													zIndex: 1,
												}}
											/>
										</Box>
									</Tooltip>
									<Box sx={{ flex: 1, ml: 4 }}>
										<Typography
											variant="h6"
											sx={{ fontWeight: 600, fontSize: "1.125rem" }}
										>
											{item.name}
										</Typography>

										<Tooltip title={`عدد`}>
											<Typography
												variant="h6"
												sx={{ color: item.color, fontSize: "0.875rem" }}
											>
												{Number(item.value).toLocaleString()} عدد
											</Typography>
										</Tooltip>
									</Box>

									{j != 4 && <Box className="dash__statistic__divider"></Box>}
								</Box>
							);
						})}
					</Box>
				</Box>
				{/* <Typography
					component={"h2"}
					sx={{
						ml: 4,
						mt: 4,
						color: "#212B36",
						fontWeight: 700,
						fontSize: "22px",
					}}
				>
					عجب داشبوردی 😀
				</Typography> */}

				<Box sx={{ p: 4, display: "flex", gap: "32px" }}>
					<Box className="HelpForm__Panel" sx={{ maxHeight: "548px" }}>
						<Typography
							component={"h4"}
							sx={{ color: "#212B36", fontWeight: 700, fontSize: "16px" }}
						>
							جزئیات کلیه کاربران:
						</Typography>

						<Box>
							<div className="flex flex-wrap py-4">
								{Object.keys(statusList || {}).map((i, j: number) => {
									const item = statusList[i];
									const Icon = item.icon;
									return (
										<Box
											className="basis-full"
											key={item.id}
											sx={{
												position: "relative",
												display: "flex",
												alignItems: "center",
												justifyContent: "center",
												width: "100%",
												minWidth: "200px",
												py: 2,
											}}
										>
											<Tooltip title={``}>
												<Box
													sx={{
														width: 56,
														height: 56,
														position: "relative",
														display: "flex",
														alignItems: "center",
														justifyContent: "center",
													}}
												>
													<WarningIcon sx={{ color: item.color }} />
													<CircularProgress
														variant="determinate"
														sx={{
															color: "rgba(145, 158, 171, 0.16)",
															position: "absolute",
															top: 0,
															left: 0,
														}}
														size={56}
														thickness={4}
														value={100}
													/>
													<CircularProgress
														variant="determinate"
														size={56}
														value={80}
														sx={{
															color: item.color,
															opacity: 0.48,
															position: "absolute",
															top: 0,
															left: 0,
															zIndex: 1,
														}}
													/>
												</Box>
											</Tooltip>
											<Box sx={{ flex: 1, minWidth: "140px", ml: 4 }}>
												<Typography
													variant="h6"
													sx={{ fontWeight: 600, fontSize: "1.125rem" }}
												>
													{item.name}
												</Typography>

												<Tooltip title={`عدد`}>
													<Typography
														variant="h6"
														sx={{ color: item.color, fontSize: "0.875rem" }}
													>
														{Number(item.value).toLocaleString()} عدد
													</Typography>
												</Tooltip>
											</Box>
										</Box>
									);
								})}
							</div>
						</Box>
					</Box>

					<Box
						className="HelpForm__Panel"
						sx={{ maxHeight: "548px", overflowY: "auto" }}
					>
						<Typography
							component={"h4"}
							sx={{ color: "#212B36", fontWeight: 700, fontSize: "16px" }}
						>
							لیست سایت ها:
						</Typography>

						<Box>
							{siteList?.map((site: any, i: number) => {
								return (
									<Box key={i}>
										<Box
											sx={{
												my: 1.25,
												width: "100%",
												display: "flex",
												justifyContent: "space-between",
												textAlign: "left",
												color: "#212B36",
												alignItems: "center",
											}}
										>
											<p>{i + 1}</p>
											<p style={{ flex: 1, margin: "0 16px" }}>
												<a
													style={{ color: "#212B36" }}
													href={"https://" + site.host}
												>
													{site.name}
												</a>
											</p>
											<p
												style={{
													color:
														site.state == "Started" ? "#00AB55" : "#637381aa",
													height: "24px",
												}}
											>
												{site.state == "Started" ? (
													<CheckCircleIcon />
												) : (
													<CircleIcon />
												)}
											</p>
										</Box>
										<Divider
											sx={{
												width: "100%",
											}}
										/>
									</Box>
								);
							})}
						</Box>
					</Box>
				</Box>

				<Drawer
					sx={{
						width: drawerWidth,
						flexShrink: 0,
						"& .MuiDrawer-paper": {
							width: drawerWidth,
							boxSizing: "border-box",
							borderRight: "1px dashed rgba(145, 158, 171, 0.2)",
						},
						borderRight: "1px dashed rgba(145, 158, 171, 0.2)",
					}}
					variant="permanent"
					anchor="left"
				>
					<Box sx={{ p: "24px 16px" }} display={"flex"} alignItems={"center"}>
						<img
							src={Logo}
							alt="نرم افزار حسابداری آنلاین و ابری کلیک"
							style={{ height: "36px" }}
						/>

						<Typography sx={{ fontSize: "14px", ml: 2 }}>
							نرم افزار حسابداری آنلاین کلیک
						</Typography>
					</Box>

					<Typography sx={{ fontSize: "14px", ml: 2, mt: 2, fontWeight: 700 }}>
						مدیریت
					</Typography>

					<List>
						{routes.map((route, index) => (
							<ListItem key={index} disablePadding>
								<ListItemButton
									component={RouterLink}
									to={"/" + route.link}
									target="_blank"
									sx={{ color: "#637381" }}
								>
									<ListItemIcon>
										<img
											style={{ width: "24px", height: "24px" }}
											src={route.icon}
										/>
									</ListItemIcon>
									<ListItemText sx={{ fontWeight: 400 }} primary={route.name} />
									<ChevronLeftIcon />
								</ListItemButton>
							</ListItem>
						))}
					</List>
				</Drawer>
			</Box>
		</Page>
	);
}
