import { Dispatch, FC, SetStateAction, useMemo, useState } from "react";
import Scrollbar from "../Scrollbar";
import { setScreenLoading } from "../../utils";

// @mui
import { Avatar, Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, FormControl, InputLabel, LinearProgress, MenuItem, Select, TextField, Tooltip, Typography } from "@mui/material";
import { DataGrid, GridColDef, GridRenderCellParams } from '@mui/x-data-grid';
import { UserMoreMenu } from "../../sections/@dashboard/user/list";
import { useConfirm } from "material-ui-confirm";
import axiosInstance from "../../utils/axios-instance";
import { useSnackbar } from "notistack";
import { mapUserView } from "../../pages/UserList";
import UserForm from "../../pages/UserForm";
import { rowSelectionStateInitializer } from "@mui/x-data-grid/internals";

const avatarUrl = "https://api-prod-minimal-v4.vercel.app/assets/images/avatars/avatar_11.jpg";

const CustomerTable: FC<{ rows: any[], setRows: Dispatch<SetStateAction<any[]>>, loading: boolean, filterName: string, currentTab: number }> = ({ rows, setRows, loading, filterName, currentTab }) => {
	const confirm = useConfirm();
	const { enqueueSnackbar } = useSnackbar();
	const [planListLoading, setPlanListLoading] = useState<boolean>(true);
	const [planList, setPlanList] = useState(null);
	const [editUser, setEditUser] = useState(null);
	const [open, setOpen] = useState<boolean>(false);
	const [userFormLoading, setUserFormLoading] = useState(false);
  const handleClickOpen = () => {
		setOpen(true);
	};
  const [headCells, setHeadCells] = useState<GridColDef[]>([
		// @ts-ignore
    { field: 'rowNumber', headerName: 'ردیف', width: 104, filterable: false, editable: false, flex: 0 },
    { field: 'companyName', headerName: 'شرکت', width: 300, renderCell: (params: GridRenderCellParams) => (
      <>
        <Avatar
          alt={params.value}
          src={avatarUrl}
          sx={{ mr: 2 }}
        />
        <Typography variant="subtitle2" noWrap>
          {params.value}
        </Typography>
      </>
    ) },
    { field: 'siteName', headerName: 'نام سیستم', width: 144 },
    { field: 'planEndDate', headerName: 'تاریخ انقضا', width: 128 },
    { field: 'remainDays', headerName: 'مانده اشتراک', width: 144 },
    {
      field: 'statusInfo', headerName: 'وضعیت', width: 100, renderCell: (params: GridRenderCellParams) => {
        const Icon = params.value.icon
        return (
          <Tooltip title={params.value.tooltip}>
            <Icon />
          </Tooltip>
        )
      }
    },
    {
      field: 'action', headerName: 'عملیات', width: 88, sortable: false, filterable: false, renderCell: (params: GridRenderCellParams) => {
        const { row } = params
        return (
          <UserMoreMenu
            onDelete={() => handleDeleteUser(row)}
            // @ts-ignore
            onAddExistSite={
              !row.siteName &&
              (() => handleAddExistSite(row))
            }
            onEditDays={
              row.siteName && (() => handleChangeDays(row))
            }
            onEditPlan={
              row.planName && (() => handleChangePlan(row))
            }
            onUserDialog={() => handleUserDialog(row)}
            userName={String(row.id)}
          />
        )
      }
    }
  ]);

  const handleUserDialog = (user: any) => {
		// const userModel = rows.find((u) => u.id == user.id);
		setEditUser(user);
		handleClickOpen();
	};

  const filteredRows = useMemo(() => {
    let newList = [...rows]
    const searchPhrase = filterName?.trim().toLocaleLowerCase()
    if (searchPhrase) {
      newList = newList.filter(i => {
        return i.companyName?.trim().toLocaleLowerCase().includes(searchPhrase) ||
        i.fullname?.trim().toLocaleLowerCase().includes(searchPhrase) ||
        i.status?.trim().toLocaleLowerCase().includes(searchPhrase) ||
        i.planDescription?.trim().toLocaleLowerCase().includes(searchPhrase)
      })
    }
    newList = currentTab > 0 ? newList.filter(i => i.statusInfo.code === currentTab) : newList
    return newList
  }, [rows, currentTab, filterName])

  const handleDeleteUser = (user: any) => {
		confirm({
			title: "",
			description: `کاربر مورد نظر ${
				user.siteName ? "(به همراه سیستم ساخته شده)" : ""
			}  حذف شود؟`,
			confirmationText: "بله حذف شود",
			cancellationText: "خیر",
		}).then(() => {
			axiosInstance
				.delete("/User/delete", { params: { id: user.id } })
				.then((response) => {
					if (response.data?.result === "ok") {
						const deleteUser = rows.filter((u: { id: any; }) => u.id !== user.id);
						setRows(deleteUser);

						return enqueueSnackbar("کاربر «" + user.fullname + "» حذف شد.", {
							variant: "success",
						});
					}

					enqueueSnackbar(
						response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				})
				.catch((error) => {
					if (!error._handled) {
						enqueueSnackbar(
							error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
							{
								variant: "error",
							}
						);
					}
				})
				// .finally(() => setListLoading(false));
		});
	};

  const handleChangeDays = (user: any) => {
		const userModel = rows.find((u) => u.id == user.id);
		const remainDays = +user.remainDays || 0;

		confirm({
			title: "",
			description: `افزودن سایت به کاربر مورد نظر`,
			confirmationText: "بله تغییر کند",
			cancellationText: "خیر",
			content: (
				<Box>
					<TextField
						autoFocus
						margin="dense"
						id="confirmDays"
						name="confirmDays"
						label="روزهای باقی‌مانده"
						type="text"
						fullWidth
						variant="standard"
						defaultValue={remainDays}
					/>
				</Box>
			),
		}).then(() => {
			const confirmDays = (document.getElementById('confirmDays') as HTMLInputElement).value;
			var date = remainDays < 1 ? new Date() : new Date(userModel.planEndDate);
			date.setDate(date.getDate() + (+confirmDays - remainDays));
			remainDays < 1 && date.setTime(date.getTime() + 300 * 60 * 1000);

			axiosInstance
				.post("/User/editUser", { id: user.id, planEndDate: date })
				.then((response) => {
					if (response.data?.result === "ok") {
						Object.assign(user, mapUserView(response.data?.addUser));
						Object.assign(userModel, response.data?.addUser);
						// setUserViewList(userList.slice());
						// setUserModelList(userModelList.slice());

						return enqueueSnackbar("انجام شد.", {
							variant: "success",
						});
					}

					enqueueSnackbar(
						response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				})
				.catch((error) => {
					if (!error._handled) {
						enqueueSnackbar(
							error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
							{
								variant: "error",
							}
						);
					}
				})
				// .finally(() => setListLoading(false));
		});
	};

  const getPlans = () => {
    // @ts-ignore
		if (!planListLoading) return new Promise();
		setScreenLoading(true);
		return axiosInstance
			.get("/Plan")
			.then((response) => {
				if (response.data?.result === "ok") {
					response.data?.planList &&
						setPlanList(JSON.parse(response.data?.planList));
					// response.data?.planList &&
					// 	setGroupList(JSON.parse(response.data?.groupList));

					return JSON.parse(response.data?.planList);
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => {setScreenLoading(false);setPlanListLoading(false)});
	};

  const handleChangePlan = (user: any, _planList?: any[]) => {
		if (!_planList && planListLoading) {
      // @ts-ignore
			getPlans().then((list) => handleChangePlan(user, list || []));
		}
		_planList = planList || _planList || [];

		const userModel = rows.find((u) => u.id == user.id);

		confirm({
			title: '',
			description: `افزودن سایت به کاربر مورد نظر`,
			confirmationText: "بله تغییر کند",
			cancellationText: "خیر",
			content: (
				<Box sx={{ display: "flex" }}>
					<FormControl sx={{ mx: 1, flex: 1 }}>
						<InputLabel>شماره پلن‌(برای دسترسی‌ها) </InputLabel>
						<Select
							id="confirmPlanIndex"
							name="confirmPlanIndex"
							label="انتخاب پلن"
							fullWidth
						>
							{_planList.map((plan, index) => (
								<MenuItem value={index + 1}> {plan.name} </MenuItem>
							))}

							{/* <MenuItem value={2}>پایه</MenuItem>
							<MenuItem value={3}>اقتصادی</MenuItem>
							<MenuItem value={4}> استاندارد </MenuItem>
							<MenuItem value={5}> پیشرفته</MenuItem>
							<MenuItem value={6}> برنزی</MenuItem>
							<MenuItem value={7}> نقره‌ای(حرفه‌ای)</MenuItem>
							<MenuItem value={8}> طلایی(پلاس)</MenuItem> */}
						</Select>
					</FormControl>

					<FormControl>
						<InputLabel>مدت زمان پلن </InputLabel>
						<Select
							id="confirmPlanNumber"
							name="confirmPlanNumber"
							label="مدت زمان پلن"
							sx={{ minWidth: 150 }}
						>
							<MenuItem value={1}>یک ماه</MenuItem>
							<MenuItem value={2}>سه ماه</MenuItem>
							<MenuItem value={3}>شش ماه</MenuItem>
							<MenuItem value={4}> یک سال </MenuItem>
							<MenuItem value={5}> مادام العمر </MenuItem>
						</Select>
					</FormControl>
				</Box>
			),
		}).then(() => {
      const confirmPlanNumber =
      +(document.querySelector('input[name="confirmPlanNumber"]') as HTMLInputElement)?.value ||
      undefined;
			const confirmPlanIndex =
      +(document.querySelector('input[name="confirmPlanIndex"]') as HTMLInputElement)?.value ||
      undefined;
      if (confirmPlanIndex) {
			  const confirmPlanName = _planList?.[confirmPlanIndex - 1]?.name;
        axiosInstance
          .post("/User/editUser", {
            id: user.id,
            planName: confirmPlanName,
            planNumber: confirmPlanNumber,
            planIndex: confirmPlanIndex,
          })
          .then((response) => {
            if (response.data?.result === "ok") {
              Object.assign(user, mapUserView(response.data?.addUser));
              Object.assign(userModel, response.data?.addUser);
              // setUserViewList(userList.slice());
              // setUserModelList(userModelList.slice());

              return enqueueSnackbar("انجام شد.", {
                variant: "success",
              });
            }

            enqueueSnackbar(
              response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
              {
                variant: "error",
              }
            );
          })
          .catch((error) => {
            if (!error._handled) {
              enqueueSnackbar(
                error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
                {
                  variant: "error",
                }
              );
            }
          })
          // .finally(() => setListLoading(false));
      }
		});
	};

  const handleAddExistSite = (user: any) => {
		const userModel = rows.find((u) => u.id == user.id);

		confirm({
			title: "",
			description: `افزودن سایت به کاربر مورد نظر`,
			confirmationText: "بله افزوده شود",
			cancellationText: "خیر",
			content: (
				<Box>
					<TextField
						autoFocus
						margin="dense"
						id="confirmSiteName"
						name="confirmSiteName"
						label="نام سایت (دامین) برای افزودن به کاربر"
						type="text"
						fullWidth
						variant="standard"
					/>

					<TextField
						autoFocus
						margin="dense"
						id="confirmPlanIndex"
						name="confirmPlanIndex"
						label="شماره پلن"
						type="tel"
						variant="standard"
					/>
				</Box>
			),
		}).then(() => {
			const siteName = (document.getElementById("confirmSiteName") as HTMLInputElement)?.value;
			const planIndex = (document.getElementById("confirmPlanIndex") as HTMLInputElement)?.value;

			axiosInstance
				.post("/User/addExistSite", { id: user.id, siteName, planIndex })
				.then((response) => {
					if (response.data?.result === "ok") {
						Object.assign(user, mapUserView(response.data?.addUser));
						Object.assign(userModel, response.data?.addUser);
						// setUserViewList(userList.slice());
						// setUserModelList(userModelList.slice());

						return enqueueSnackbar("انجام شد.", {
							variant: "success",
						});
					}

					enqueueSnackbar(
						response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				})
				.catch((error) => {
					if (!error._handled) {
						enqueueSnackbar(
							error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
							{
								variant: "error",
							}
						);
					}
				})
				// .finally(() => setListLoading(false));
		});
	};

  const handleClose = () => {
		setOpen(false);
	};

  const _callbacks = {
    userFormSubmit: () => {},
  };

  const handleSubmit = () => {
		const promise = _callbacks.userFormSubmit();
    // @ts-ignore
		if (promise) {
			setUserFormLoading(true);
			promise
        // @ts-ignore
				.then((res: { data: { user: any; }; }) => {
					const user = res?.data?.user || res;
					if (user) {
						handleClose();
            // @ts-ignore
						const u = rowSelectionStateInitializer?.find((u) => u.id == user.id);
						if (u) {
							Object.assign(u, user);
						} else {
              const newRows = [...rows]
							newRows.push(u);
              setRows(newRows)
						}
						// setUserModelList(userModelList.slice());
						// setUserViewList(userModelList.map(mapUserView).slice());
					}
				})
				.finally(() => {
					setUserFormLoading(false);
				});
		}
	};
  
  return (
    <Box>
      <Scrollbar sx={{}}>
        <DataGrid
          rows={filteredRows}
          columns={headCells}
          disableRowSelectionOnClick
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 5 },
            },
          }}
          pageSizeOptions={[5, 10, 20]}
          localeText={{
            MuiTablePagination: {
              labelDisplayedRows: ({ from, to, count }) =>
                `از– تا از ${count !== -1 ? count : 'بیشتر از' + ' ' + to}`,
            },
            columnMenuSortAsc: 'مرتب سازی صعودی',
            columnMenuSortDesc: 'مرتب سازی نزولی',
            columnMenuFilter: 'فیلتر',
            columnMenuHideColumn: 'مخفی کردن ستون',
            columnMenuUnsort: 'نامرتب کردن',
            columnMenuManageColumns: 'مدیریت ستون‌ها',
            columnMenuLabel: 'منو',
            filterPanelColumns: 'ستون‌ها',
            filterPanelOperator: 'اپراتور',
            filterPanelInputLabel: 'مقدار',
            filterPanelInputPlaceholder: 'مقدار فیلتر',
            filterOperatorContains: 'شامل',
            filterOperatorEquals: 'برابر',
            filterOperatorStartsWith: 'شروع با',
            filterOperatorEndsWith: 'پایین با',
            filterOperatorIs: 'هست',
            filterOperatorNot: 'نیست',
            filterOperatorAfter: 'هست بعد',
            filterOperatorOnOrAfter: 'نیست یا بعد',
            filterOperatorBefore: 'هست قبل',
            filterOperatorOnOrBefore: 'نیست یا قبل',
            filterOperatorIsEmpty: 'خالی هست',
            filterOperatorIsNotEmpty: 'خالی نیست',
            filterOperatorIsAnyOf: 'هست هریک از',

            // Columns panel text
            columnsPanelTextFieldLabel: 'پیدا کردن ستون',
            columnsPanelTextFieldPlaceholder: 'عنوان ستون',
            columnsPanelDragIconLabel: 'ترتیب مجدد ستون',
            columnsPanelShowAllButton: 'نمایش همه',
            columnsPanelHideAllButton: 'مخفی کردن همه',
          }}
          slotProps={{
            pagination: {
              labelRowsPerPage: 'ردیف در هر صفحه'
            }
          }}
          sx={{ 
            border: 'none', 
            '& .MuiDataGrid-columnHeaders': { borderRadius: '8px', backgroundColor: '#F6F7F8', border: 'none !important', margin: '8px' },
            '& .MuiDataGrid-virtualScrollerContent': { px: 1 }
          }}
        />
      </Scrollbar>
      <Dialog open={open} onClose={handleClose}>
				<DialogTitle>
					{editUser ? "فرم ویرایش کاربر" : "فرم افزودن کاربر"}
				</DialogTitle>
				<DialogContent>
					<DialogContentText>
						<UserForm
							currentUser={editUser!}
							plans={planList}
							callbacks={_callbacks}
						/>
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<Button onClick={handleClose}>بستن </Button>
					<Button onClick={handleSubmit}>
						{editUser ? "ویرایش کاربر" : "افزودن کاربر"}
						{userFormLoading && (
							<Box sx={{ position: "absolute", bottom: 0, width: "100%" }}>
								<LinearProgress />
							</Box>
						)}
					</Button>
				</DialogActions>
			</Dialog>
    </Box>
  )
}
export default CustomerTable
