import { FC, useMemo, useState } from 'react'
import { Link as RouterLink } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { setScreenLoading } from "../../utils";
import axiosInstance from '../../utils/axios-instance';
import { Post } from 'types'

// @mui
import { Box, Card, CardContent, CardMedia, IconButton, Stack, Typography } from '@mui/material'

// icons
import { ReactComponent as TimerIcons } from '../../images/icons/ic_timer-start.svg'
import imgEmpty from '../../images/icons/ic_image.svg'
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";

const canEdit = window.localStorage.isAdmin === 'true'
// const postType = location.pathname.toLowerCase().includes('help') ? 'help' : 'blog'

const PostCard: FC<{ post: Post }> = ({ post }) => {
  const { t } = useTranslation('blog')
  const [imageLoadError, setImageLoadError] = useState<boolean>(false)
  const postType = useMemo(() => [1, 2].includes(post.type) ? 'help' : 'blog', [post.type])
  const onDelete = (id: number) => {
    const conf = confirm("پاک شود؟!");
    if (!conf) return;

    setScreenLoading(true);
    axiosInstance
      .delete(`help`, { data: { id } })
      .then((res: any) => {
        window.location.reload();
      })
      .catch((e) => {
        console.log(e);
      })
      .finally(() => setScreenLoading(false));
  };
  
  return (
    <Card sx={{ display: 'flex', alignItems: 'flex-end', position: 'relative', borderRadius: '16px', flexDirection: 'column', '&:before': { content: '""', width: 106, height: 90, backgroundImage: `url(${imgEmpty})`, display: post.imageUrl && !imageLoadError ? 'none' : 'block', position: 'absolute', top: '22%', left: '50%', transform: 'translate(-50%, -50%)', zIndex: 2 } }}>
      <Box
        component="img"
        sx={{ flexShrink: 0, width: '100%', height: 230, position: 'relative', backgroundColor: '#D9F0FD' }}
        src={post.imageUrl}
        title={post.title}
        alt={post.title}
        loading="lazy"
        onError={ e => { setImageLoadError(true); (e.target as HTMLImageElement).onerror = null }}
      />
      <CardContent sx={{ width: '100%', gap: 3, px: 3, pb: '16px !important' }}>
        <Stack direction="row" alignItems="center" justifyContent="space-between" sx={{ py: 1 }}>
          <Typography sx={{ fontSize: 14 }}>{t('Grouping')}: <Typography component="span" sx={{ color: '#3366FF' }}>{t(post.categoryTitle)}</Typography></Typography>
          <Typography variant="caption" sx={{ color: '#637381', fontSize: 14 }}>{post.date}</Typography>
        </Stack>
        <Stack direction="column" alignSelf="flex-end" gap={2} sx={{ position: 'relative', color: '#fff', py: 2 }}>
          <Typography component={RouterLink} to={`${postType === 'blog' ? '/blog' : ''}/${post.title?.trim()?.replace(/\s+/g, '-')}`} variant="h3" noWrap sx={{ fontSize: 22, fontWeight: 500, color: '#212B36', '&:hover': { color: '#2D99FF' } }} title={post.title}>{post.title}</Typography>
          <Typography variant="subtitle1" sx={{ color: '#637381', fontSize: 14, height: 72, textAlign: 'justify', overflow: 'hidden' }}>{post.summary}</Typography>
        </Stack>
        <Stack direction="row" alignItems="center" justifyContent="space-between" sx={{ fontSize: '14px', borderTop: '1px dashed rgba(145, 158, 171, 0.24)', pb: 1, pt: '12px' }}>
          <Stack direction="row" alignItems="center" gap={1}>
            <TimerIcons />
            <Typography sx={{ color: '#637381', fontSize: 14 }}>{t('StudyTime')}:</Typography>
            <Typography sx={{ color: '#212B36', fontSize: 14 }}>{post.readTime}</Typography>
          </Stack>
          {canEdit && (
            <Stack direction="row" alignItems="center">
              <IconButton
                aria-label="Edit"
                sx={{ ml: "auto" }}
                to={`/${postType}Form/${post.id}`}
                component={RouterLink}
              >
                <EditIcon sx={{ width: 16, height: 16 }} />
              </IconButton>
              <IconButton aria-label="Edit" onClick={() => onDelete(post.id)}>
                <DeleteIcon sx={{ width: 16, height: 16 }} />
              </IconButton>
            </Stack>
          )}
        </Stack>
      </CardContent>
    </Card>
  )
}
export default PostCard