import { FC, useEffect, useState, memo, useMemo } from "react";
import { useTranslation } from "react-i18next";
import { Link as RouterLink, useParams } from 'react-router-dom';
import ReactApexChart from 'react-apexcharts';
import merge from 'lodash/merge';
import axiosInstance, { userGuid } from "../utils/axios-instance";
import _ from "lodash";
import { Report } from '@/types';

// @mui
import { Box, Button, Container, IconButton, Card, Typography, styled } from "@mui/material";
import { useTheme } from '@mui/material/styles';

// components
import Page from '../components/Page';
import HeaderBreadcrumbs from '../components/HeaderBreadcrumbs';
import DashboardNavbar from '../components/dashboard/DashboardNavbar';
import { BaseOptionChart } from "../components/chart";
import PieChart from "../components/items/PieChart";
import ItemsCountIncomeTableListView from "../components/items/ItemsCountIncomeTableListView";
import ItemsPageFilter from "../components/items/ItemsPageFilter";

// icon
import { ReactComponent as PrintIcon } from '../images/ic_printer.svg'
import KeyboardBackspaceIcon from '@mui/icons-material/KeyboardBackspace';

const typeToApi = {
  BestSellers: 'top',
  LeastSold: 'bottom'
}

type ItemApi = { count: number, itemGuid: string, total: number }
type HoldingsApi = { ItemGuid?: string, total: number, holding1: number, holding2: number }

const LeaderDetails: FC = () => {
  const { t } = useTranslation();
  const routeParams = useParams();
  const theme = useTheme();
  const [loadeds, setLoades] = useState<string[]>([])
  const pageTitle = t(`${routeParams?.type}Items` || '')
  const [items, setItems] = useState<any>([])
  const [fullItemsList, setFullitemsList] = useState<any[]>([])
  const [reportsDetail, setReportsDetail] = useState<any>([])
  const [reportList, setReportList] = useState<Report[]>([])
  const [reportInventory, setReportInventory] = useState<{ bottom5: ItemApi[], top5: ItemApi[], holdings: HoldingsApi[], unsellItems: ItemApi[] }>()
  
  // @ts-ignore
  const chartColors = [theme.palette.info.blue, theme.palette.secondary.success.deep, theme.palette.warning.yellow, theme.palette.note.purple, theme.palette.error.red]

  const chartOptions: any = merge(BaseOptionChart(), {
    xaxis: {
      categories: ['فروردین', 'اردیبهشت', 'خرداد', 'تیر', 'مرداد', 'شهریور', 'مهر', 'آبان', 'آذر', 'دی', 'بهمن', 'اسفند'],
    },
    legend: {
      show: false
    }
  });

  const ChartWrapperStyle = styled('div')(() => ({
    '& .apexcharts-legend-text': {
      paddingLeft: '8px',
      paddingRight: 0
    },
    '& .apexcharts-tooltip-marker': {
      marginRight: '6px',
      marginLeft: 0,
    },
    '& .apexcharts-tooltip': {
      alignItems: 'flex-start'
    },
    '& .apexcharts-legend': {
      flexDirection: 'column'
    },
    '& .apexcharts-legend-series': {
      display: 'flex !important'
    }
  }));
  useEffect(() => {
    axiosInstance.get('/Item/ReportList').then(resa => {
      if (resa.status === 200) {
        setReportList(resa.data)
        axiosInstance.get(`/Item/ReportInventory?userGuid=${userGuid}`).then(res => {
          if (res.status === 200) {
            const result = res.data
            setReportInventory(result)
            if (routeParams.type === 'Stagnant') {
              setFullitemsList(result.unsellItems.map((i: string, index: number) => {
                const itemDetail = resa.data.find((j: Report) => j.Guid === i)
                const holding = result.holdings.find((j: { ItemGuid: any; }) => j.ItemGuid === i)
                return {
                  ...itemDetail,
                  id: itemDetail.Id,
                  color: '#FF4842',
                  title: itemDetail.Title,
                  value: 0,
                  IncomeFromSell: 0,
                  inventory: holding?.holding2 || holding?.holding1 || 0
                }
              }))
            }
            setItems(routeParams.type !== 'Stagnant' ? [
              {
                id: 1,
                name: routeParams?.type || 'BestSellers',
                // @ts-ignore
                data: result?.[`${typeToApi[routeParams?.type]}5`]?.map((i: { total: number; itemGuid: string; }, index: number) => {
                  // @ts-ignore
                  const itemDetail = resa.data?.find((j: { Guid: string; }) => i.itemGuid === j.Guid)
                  const holding = result.holdings.find((j: { ItemGuid: any; }) => j.ItemGuid === i.itemGuid)
                  return {
                    ...itemDetail,
                    id: itemDetail.Id,
                    color: chartColors[index] || ("#" + Math.floor(Math.random() * 16777215).toString(16)),
                    title: itemDetail.Title,
                    value: i.total,
                    inventory: holding?.holding2 || holding?.holding1
                  }
                })
              }
            ] : [
              {
                id: 1,
                name: 'StagnantItems',
                charts: result.unsellItems.slice(0, 5).map((i: string) => resa.data.find((j: Report) => j.Guid === i)),
                data: [
                  {
                    id: 1,
                    color: chartColors[4],
                    title: t('StagnantItems'),
                    value: result.unsellItems.length
                  },
                  {
                    id: 2,
                    color: chartColors[1],
                    title: t('OtherItems'),
                    value: resa.data.length
                  }
                ]
              }
            ])
          }
        })
      }
    })
  }, [])
  useEffect(() => {
    if (items?.[0]?.data) {
      const queryItems = items[0].data.map((i: { Guid: any; }) => `itemGuidList=${i.Guid}`).join('&')
      axiosInstance.get(`/Item/ReportInventoryDetails?userGuid=${userGuid}&${queryItems}`).then(res => {
        if (res.status === 200) {
          if (routeParams.type !== 'Stagnant') {
            setFullitemsList(res.data.SellItems.sort((a: ItemApi, b: ItemApi) => routeParams.type === 'LeastSold' ? (a.total - b.total) : (b.total - a.total)).map((i: ItemApi, index: number) => {
              const itemDetail = reportList.find((j: Report) => j.Guid === i.itemGuid)
              if (itemDetail) {
                const holding = reportInventory?.holdings?.find((j) => j.ItemGuid === i.itemGuid)
                return {
                  ...itemDetail,
                  id: itemDetail.Id,
                  color: chartColors[index] || ("#" + Math.floor(Math.random() * 16777215).toString(16)),
                  title: itemDetail.Title,
                  value: i.total,
                  inventory: holding?.holding2 || holding?.holding1
                }
              } else {
                return {}
              }
            }))
          }
          setReportsDetail(items[0].data.map((i: any) => ({
            name: i.title,
            // @ts-ignore
            data: [...Array(12).keys()].map(x => res.data.ItemsDetails.find(j => j.itemGuid === i.Guid && String(x + 1).padStart(2, '0') === j.Date.slice(4, 6))?.total || 0)
          })))
        }
      })
    }
  }, [items, reportInventory])
  return (
    <Page title={pageTitle}>
      <Box sx={{ display: 'flex' }}>
        <DashboardNavbar setLoades={setLoades} />
        <Container sx={{ maxWidth: '1340px !important', display: 'flex', flexDirection: 'column', py: 3 }}>
          <HeaderBreadcrumbs
            sx={{ mt: 2, px: '12px' }}
            heading={pageTitle}
            // @ts-ignore
            headingSx={{ fontSize: '24px', fontWeight: 700, color: theme.palette.secondary.dimgray.main }}
            links={[
              { name: t('Dashboard'), href: '/' },
              { name: t('BuyAndSell') }
            ]}
            action={
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <IconButton><PrintIcon /></IconButton>
                {/* <ItemsPageFilter /> */}
                <Button
                  to="/ItemsPriceInventory"
                  component={RouterLink}
                  variant="outlined"
                  // @ts-ignore
                  endIcon={<KeyboardBackspaceIcon sx={{ color: theme.palette.secondary.dimgray.main }} />}
                  sx={{
                    // @ts-ignore
                    color: theme.palette.secondary.dimgray.main,
                    fontWeight: 700,
                    // @ts-ignore
                    borderColor: theme.palette.secondary.gray.light3,
                    '&:hover': {
                      // @ts-ignore
                      borderColor: theme.palette.secondary.gray.light3,
                      backgroundColor: 'rgba(145, 158, 171, 0.08)'
                    }
                  }}
                >
                  {t('Back')}
                </Button>
              </Box>
            }
          />
          
          <Box sx={{ display: 'flex' }}>
            <Box sx={{ flexGrow: 1, p: '12px' }}>
              <Card sx={{ p: 2 }}>
                <Typography variant="subtitle1" sx={{ fontWeight: 700, mb: 2 }}>{`${t('DetailedReports')} ${pageTitle}`}</Typography>
                <ChartWrapperStyle>
                  <ReactApexChart height={340} type="line" series={reportsDetail} options={chartOptions} />
                </ChartWrapperStyle>
              </Card>
            </Box>
            <Box sx={{ width: '344px' }}>
              <PieChart items={items} height={routeParams.type !== 'Stagnant' ? 350 : 250} wrapperHeight={routeParams.type !== 'Stagnant' ? 262 : 192} hideShowAll />
            </Box>
          </Box>
          <Box sx={{ p: '12px' }}>
            <ItemsCountIncomeTableListView items={fullItemsList} />
          </Box>
        </Container>
      </Box>
    </Page>
  )
}

export default LeaderDetails