import { FC, useEffect, useState } from 'react';

// @mui
import { Box, Container } from '@mui/material';

// components
import DashboardHeader from '../components/dashboard/DashboardHeader';
import DashboardContent from '../components/dashboard/DashboardContent';
import DashboardNavbar from '../components/dashboard/DashboardNavbar';
import Page from '../components/Page';

const Dashboard: FC = () => {
  const [loadeds, setLoades] = useState<string[]>([])

  useEffect(() => {
    document.getElementById('_loading')!.style.display = 'flex'
  }, [])

  useEffect(() => {
    if (loadeds.length < 4) {
      document.getElementById('_loading')!.style.display = 'flex'
    } else {
      document.getElementById('_loading')!.style.display = 'none'
    }
  }, [loadeds])
  return (
    <Page title="داشبورد">
      <Box sx={{ display: 'flex' }}>
        <DashboardNavbar setLoades={setLoades} />
        <Container maxWidth="xl" sx={{ display: 'flex', flexDirection: 'column', py: 3 }}>
          <DashboardHeader setLoades={setLoades} />
          <DashboardContent setLoades={setLoades} />
        </Container>
      </Box>
    </Page>
  )
}
export default Dashboard