import React, { useMemo } from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import { useTranslation } from 'react-i18next';
import { FC } from 'react';
import type { Setting, TabVerticalComponent } from "../types";
import SettingForm from './SettingForm';
import { useTheme } from "@mui/material/styles";
import useMediaQuery from '@mui/material/useMediaQuery';
import { FormGroup } from "@mui/material";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`setting-tabpanel-${index}`}
      aria-labelledby={`setting-tab-${index}`}
      className="grow"
      {...other}
    >
      {value === index && (
        <Box sx={{ px: 2 }}>
          {/* , overflowY: 'auto', maxHeight: 'calc(100vh - 111px)', pb: 8 */}
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

const TabVertical: FC<TabVerticalComponent> = props => {
  const { data, onChange, branchList } = props;
  const { t } = useTranslation();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };
  const settingList = useMemo(() => {
    const list: any = {...data}
    Object.keys(list).forEach((i) => {
      list[i] = {
        1: list[i].filter((j: Setting) => j.IsCheckBox && !j.IsSystem),
        2: list[i].filter((j: Setting) => j.IsCheckBox && j.IsSystem),
        3: list[i].filter((j: Setting) => !j.IsCheckBox && !j.IsSystem),
        4: list[i].filter((j: Setting) => !j.IsCheckBox && j.IsSystem),
      };
    })
    return list
  }, [data])
  const isMd = useMediaQuery(theme.breakpoints.up('md'))
  return (
    <Box
      sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex' }}
      className="flex-col md:flex-row"
    >
      <Tabs
        orientation={isMd ? 'vertical' : 'horizontal'}
        value={value}
        onChange={handleChange}
        sx={{ borderRight: 1, borderColor: 'divider', minWidth: isMd ? 'max-content' : 'initial' }}
        aria-label="basic tabs example"
        variant="scrollable"
        visibleScrollbar={true}
      >
        {
          settingList && Object.keys(settingList).map((i: string, j: number) => (
            <Tab label={t(i)} id={i} aria-controls={i} key={j} />
          ))
        }
      </Tabs>
      {
        settingList && Object.keys(settingList).map((i: string, j: number) => (
          <TabPanel value={value} index={j} key={j}>
            <div>
              <FormGroup>
                <div className="flex flex-row flex-wrap self-stretch">
                    {
                      Object.keys(settingList[i]).map((x: any) => <SettingForm data={settingList[i][x]} branchList={branchList} onChange={onChange} />) 
                    }
                </div>
              </FormGroup>
            </div>
          </TabPanel>
        ))
      }
    </Box>
  );
}

export default TabVertical;
