import { FC, JSXElementConstructor, Key, ReactElement, ReactFragment, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import axiosInstance from '../utils/axios-instance'
import { Link as RouterLink, useNavigate } from "react-router-dom";
import { getPriceFormat } from '../utils/formatNumber';
import { useSnackbar } from "notistack";
import { Plan, Period } from 'types'

// @mui
import { Box, Card, Container, Grid, Stack, Typography, Button, Dialog, IconButton, Accordion, AccordionSummary, AccordionDetails, Skeleton } from '@mui/material'

// components
import Header from '../layouts/Header'
import Footer from '../components/layout/Footer'

// icons
import sectionBg from '../images/ic_plan-bg.svg'
import { ReactComponent as SignArrow } from '../images/sign-arrow.svg'
import { ReactComponent as CheckmarkIcon } from '../images/ic_checkmark-r.svg'
import { ReactComponent as ChevronLeftIcon } from '../images/ic_chevron-left-r.svg'
import { ReactComponent as CloseIcon } from '../images/ic_close-r.svg'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

// components
import CLKSwitch from '../components/CLKSwitch'

const periodList: { title: string, value: Period }[] = [
  {
    title: 'دو ماهه',
    value: 'twoMonths'
  },
  {
    title: 'سالیانه',
    value: 'annual'
  },
  {
    title: 'مادام العمر',
    value: 'lifetime'
  }
]

const periodIndex: { [k: Period]: number } = {
  twoMonths: 5,
  annual: 3,
  lifetime: 4
}

const planLabel = {
  3: 'کسب و کار کوچک',
  4: 'فروشگاه اینترنتی  و شرکتی',
  5: 'کسب و کار متوسط و تولیدی',
  6: 'کسب و کار بزرگ و کارخانجات'
}

const NewPlans: FC = () => {
  const { t } = useTranslation('plans')
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();
  const [period, setPeriod] = useState<Period>('twoMonths')
  const [planList, setPlanList] = useState<Plan[]>([])
  const [allGroupList, setAllGroupList] = useState<{ name: string, commandName: string, planStatus: { [pr: number]: number[], textList: string[], checkList: (null | boolean)[] } }[]>([])
  const [selectedPlan, setSelectedPlan] = useState<Plan | null>(null)
  const [planGroupList, setPlanGroupList] = useState<any[]>([])
  const [expandedPlanDialogAccordion, setExpandedPlanDialogAccordion] = useState<number | null>(0)
  const [plansLoading, setPlansLoading] = useState<boolean>(true)
	const [user, setUser] = useState(null);

  const getGroupingArray = (groupList: any[]) => {
    const group = [];
    let groupCounts = 1
    if (window.screen.width >= 1200) {
      groupCounts = 4
    } else if (window.screen.width >= 900) {
      groupCounts = 3
    } else if (window.screen.width >= 600) {
      groupCounts = 2
    }
    for (let i = 0; i < groupList.length; i += groupCounts) {
      group.push(groupList.slice(i, i + groupCounts));
    }
    return group
  }

  const payPlan = (plan: Plan) => {
		if (user) navigate("/payment", { state: { plan, selectedTab: periodIndex[period] as number, user } });
		else {
			enqueueSnackbar("برای سفارش ابتدا وارد حساب کاربری خود شوید.");
			navigate("/login", { state: { plan, selectedTab: periodIndex[period] as number, user } });
		}
	};
  const getFeaturesAsPeriod = (groupList: any[], planIndex: number, periodIndex: number) => {
    // groupList.filter((x: { name: string, subgroups: { planStatus: any[][] }[] }) => x.subgroups.some((o: { planStatus: any[][] }) => o.planStatus[periodIndex][planIndex])).map((x: { name: any }) => {

    //   return { name: x.name, has: x?.checkList?.length ? x.checkList[i.planIndex] : i?.features?.[period]?.includes(x.name) }
    // })
    return groupList.map(x => ({
      name: x.name,
      textList: x.textList,
      has: x?.checkList?.length ? x.checkList[planIndex] : x.subgroups.some((o: { planStatus: any[][] }) => o.planStatus[periodIndex][planIndex])
    }))
  }

  useEffect(() => {
    axiosInstance({
      baseURL: 'https://clickmis.net/',
      url: '/api/Plan',
      method: 'get'
    }).then((res: { status: number; data: { planList: string, groupList: string, user: any } }) => {
      if (res.status === 200) {
        res.data?.user && setUser(res.data?.user)
        const planList = JSON.parse(res.data.planList).map((i: any, j: number) => ({...i, planIndex: j }))
        const groupList = JSON.parse(res.data.groupList)
        setAllGroupList(groupList)
        setPlanGroupList(getGroupingArray(groupList))
        setPlanList(planList.filter((i: { disableList: number[] }) => !i.disableList.length).map((i: any, j: number) => ({
          name: i.name,
          priceList: i.priceList,
          status: i.status,
          planIndex: i.planIndex,
          features: {
            twoMonths: getFeaturesAsPeriod(groupList, i.planIndex, 5),
            annual: getFeaturesAsPeriod(groupList, i.planIndex, 3),
            lifetime: getFeaturesAsPeriod(groupList, i.planIndex, 4)
          }
        })))
      }
    }).finally(() => setPlansLoading(false))
  }, [])

  return (
    <Box sx={{ backgroundColor: '#fff', backgroundImage: `url(${sectionBg})`, backgroundPosition: 'top center', backgroundSize: 'contain', backgroundRepeat: 'no-repeat' }}>
      <Header />

      <Container maxWidth="lg" sx={{ py: 11 }}>
        <Stack direction="column" alignItems="center" justifyContent="center" gap={2} sx={{ mt: { xs: '56px', md: '88px' } }}>
          <Typography variant="h1" sx={{ color: '#212B36', fontSize: '44px', fontWeight: 700 }}>{t('Plans')}</Typography>
          <Typography variant="h2" sx={{ color: '#637381', fontSize: '18px' }}>{t('PlansDescription')}</Typography>
        </Stack>
        <Stack direction="row" alignItems="center" justifyContent="center" sx={{ pt: 7 }}>
          <Stack sx={{ position: 'relative', pb: '56px' }}>
            <Stack direction="row" sx={{ position: 'absolute', left: { xs: '48px', md: '-65%' }, top: { xs: '30%', md: '4%' }, flexDirection: { xs: 'row-reverse', md: 'row' } }}>
              <Typography sx={{ color: '#4220E9', fontSize: '18px', fontWeight: 500, pt: '56px' }}>اقتصادی</Typography>
              <Box component={SignArrow} sx={{ transform: { xs: 'rotate(160deg) scaleY(-1)', md: 'initial' } }}></Box>
            </Stack>
            <CLKSwitch options={periodList} value={period} onChange={setPeriod} />
          </Stack>
        </Stack>
        <Grid container sx={{ py: 8 }}>
          {!plansLoading ? planList.map(i => (
            <Grid item xs={12} sm={6} md={3} sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', p: '12px' }} key={i.name}>
              <Card sx={{ minHeight: i.status === 1 ? 606 : 560, backgroundColor: i.status === 1 ? '#0154A4' : '#fff', display: 'flex', flexDirection: 'column', gap: 2, borderRadius: '16px', boxShadow: '0px 5px 12px -4px rgba(145, 158, 171, 0.31)', p: 3 }}>
                {i.status === 1 && (<Stack direction="row">
                  <Typography sx={{ fontSize: '12px', fontWeight: 700, backgroundColor: '#fff', color: '#3366FF', borderRadius: '6px', px: 1, py: '4px' }}>محبوب ترین</Typography>
                </Stack>)}
                <Stack direction="row">
                  <Typography sx={{ fontSize: '12px', fontWeight: 700, backgroundColor: i.status === 1 ? '#fff' : '#3366FF', color: i.status === 1 ? '#3366FF' : '#fff', borderRadius: '6px', px: 1, py: '4px' }}>{planLabel[i.planIndex]}</Typography>
                </Stack>
                <Stack direction="column">
                  <Typography sx={{ color: i.status === 1 ? '#fff' : '#919EAB', fontSize: '12px', fontWeight: 700 }}>{i.name}</Typography>
                  <Typography sx={{ fontSize: '24px', fontWeight: 700, color: i.status === 1 ? '#fff' : '#212B36' }}>{getPriceFormat(i.priceList[periodIndex[period]])} <Typography component="span" sx={{ fontSize: '14px', fontWeight: 700, color: i.status === 1 ? '#fff' : '#212B36' }}>تومان</Typography></Typography>
                </Stack>
                <Stack direction="column" gap={2}>
                  {i?.features?.[period]?.map((x: { name: string; has: boolean, textList: { [x: string]: string; }; }) => (
                    <Stack direction="row" alignItems="center" gap="12px" key={x.name}>
                      <Box component={ x.has ? CheckmarkIcon : CloseIcon} className="svg-inline-color" sx={{ color: x.has ? i.status === 1 ? '#fff' : '#3366FF' : '#FF4842' }} />
                      <Typography sx={{ fontSize: '14px', color: i.status === 1 ? '#fff' : '#212B36' }}>{x.name} {!!x?.textList?.[i.planIndex] ? `: ${x.textList[i.planIndex]}` : ''}</Typography>
                    </Stack>
                  ))}
                </Stack>
                <Box sx={{ flexGrow: 1 }} />
                <Stack direction="row" alignItems="center" justifyContent="flex-end">
                  <Typography onClick={() => setSelectedPlan(i)} sx={{ cursor: 'pointer', fontSize: '14px', display: 'flex', alignItems: 'center', gap: 1, '&, & svg': { color: i.status === 1 ? '#fff' : '#637381' } }}>{t('ShowAll')} <Box component={ChevronLeftIcon} className="svg-inline-color" /></Typography>
                </Stack>
                <Stack direction="row" alignItems="center" justifyContent="flex-end">
                  <Button onClick={() => payPlan(i)} variant={i.status === 1 ? 'contained' : 'outlined'} sx={{ flexGrow: 1, color: i.status === 1 ? '#3366FF' : '#3366FF', borderColor: '#3366FF', backgroundColor: '#fff', '&:hover': { backgroundColor: '#e5eaf6' } }}>{t('Order')}</Button>
                </Stack>
              </Card>
            </Grid>
          )) : (
            <Grid container sx={{ py: 8 }}>
              <Grid item xs={12} sm={6} md={3} sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', p: '12px' }}>
                <Skeleton variant="rounded" width="100%" height={560} />
              </Grid>
              <Grid item xs={12} sm={6} md={3} sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', p: '12px' }}>
                <Skeleton variant="rounded" width="100%" height={606} />
              </Grid>
              <Grid item xs={12} sm={6} md={3} sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', p: '12px' }}>
                <Skeleton variant="rounded" width="100%" height={560} />
              </Grid>
              <Grid item xs={12} sm={6} md={3} sx={{ display: 'flex', flexDirection: 'column', justifyContent: 'flex-end', p: '12px' }}>
                <Skeleton variant="rounded" width="100%" height={560} />
              </Grid>
            </Grid>
          )}
        </Grid>
        <Stack direction="column" alignItems="center" justifyContent="center" gap={3}>
          <Stack direction="column" alignItems="center" justifyContent="center" gap={1}>
            <Typography sx={{ color: '#212B36', fontSize: '18px' }}>{t('FreeStartToTransformation')}</Typography>
            <Typography sx={{ color: '#212B36', fontSize: '16px' }}>{t('WelcomeToOnlineFinancialManagement')}</Typography>
          </Stack>
          <Stack direction="row" alignItems="center" gap={2}>
            <Button component={RouterLink} to="/login" variant="contained" sx={{ backgroundColor: '#0154A4', color: '#fff', fontSize: '16px', fontWeight: 700, borderRadius: '8px', boxShadow: '0px 2px 16px 0px rgba(52, 97, 255, 0.24)' }}>{t('15DaysFreeUsing')}</Button>
            <Button component={RouterLink} to="/features" variant="outlined" sx={{ borderColor: '#0154A4', color: '#0154A4', fontSize: '16px', fontWeight: 700, borderRadius: '8px', boxShadow: '0px 2px 16px 0px rgba(52, 97, 255, 0.24)' }}>{t('Features')}</Button>
          </Stack>
        </Stack>
      </Container>

      <Footer />
      <Dialog
        maxWidth={false}
        fullWidth
        sx={{ maxWidth: 1068, m: '0 auto', '& .MuiDialog-paper, & .MuiDialog-paper > div': { height: '100%' } }}
        open={!!selectedPlan}
        onClose={() => setSelectedPlan(null)}
        aria-labelledby="plan-dialog"
        aria-describedby="plan-dialog-details"
      >
        {!!selectedPlan && (<Stack direction="column">
          <Stack direction="row" sx={{ px: 3, pt: 3 }}>
            <Stack direction="column">
              <Typography sx={{ color: '#919EAB', fontSize: '12px', fontWeight: 700 }}>{selectedPlan.name}</Typography>
              <Typography sx={{ fontSize: '24px', fontWeight: 700, color: '#212B36' }}>{getPriceFormat(selectedPlan.priceList[periodIndex[period]])} <Typography component="span" sx={{ fontSize: '14px', fontWeight: 700, color: '#212B36' }}>تومان</Typography></Typography>
            </Stack>
            <Box sx={{ flexGrow: 1 }} />
            <Stack direction="row" alignItems="center" gap={3} sx={{ flexShrink: 0 }}>
              <Button component={RouterLink} to="/login" variant="contained" sx={{ backgroundColor: '#0154A4', color: '#fff' }}>{t('Order')}</Button>
              <IconButton onClick={() => setSelectedPlan(null)}><Box component={CloseIcon} className="svg-inline-color" sx={{ color: '#637381' }} /></IconButton>
            </Stack>
          </Stack>
          <Box className="click-scrollbar" sx={{ flexGrow: 1, overflow: 'auto', px: 3, pb: 3 }}>
            <Stack direction="column" gap={1} sx={{ '& .MuiAccordionSummary-content': { my: 1 }, '& .MuiAccordionSummary-root': { minHeight: 40 }, '& .MuiAccordion-root': { boxShadow: 'none', backgroundColor: '#EDEFF2', borderRadius: '8px', '&:before': { display: 'none' }, '&.Mui-expanded': { backgroundColor: '#F6F7F8' } } }}>
              {planGroupList.map((i, j) => (<Accordion expanded={expandedPlanDialogAccordion === j} onChange={() => setExpandedPlanDialogAccordion(a => a === j ? null : j)} key={i.name}>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls={i.name}
                  id={`plan-accordion-${j}`}
                >
                  <Grid container>
                    {i.map((x: { name: string; }) => {
                      const has = selectedPlan.features?.[period]?.find((a: { name: string; }) => a.name === x.name)?.has
                      return (<Grid item xs={12} sm={6} md={4} lg={3} key={x.name}>
                        <Stack direction="row" alignItems="center" gap="12px">
                          <Box component={has ? CheckmarkIcon : CloseIcon} className="svg-inline-color" sx={{ color: has ? '#0154A4' : '#FF4842' }} />
                          <Typography sx={{ fontSize: '14px', color: '#212B36' }}>{x.name}</Typography>
                        </Stack>
                      </Grid>)
                    })}
                  </Grid>
                </AccordionSummary>
                <AccordionDetails>
                  <Grid container>
                    {i.map((x: { name: string, subgroups: any[] }) => (<Grid item xs={12} sm={6} md={4} lg={3} key={x.name}>
                      <Stack direction="column" gap="12px" sx={{ pl: 3 }}>
                        {x.subgroups.map((y: { name: string, planStatus: boolean[][] }) => {
                          const has = periodIndex?.[period] && selectedPlan?.planIndex && y.planStatus[periodIndex[period]][selectedPlan?.planIndex]
                          return (
                            <Stack direction="row" alignItems="center" gap="12px" key={y.name}>
                              <Box component={has ? CheckmarkIcon : CloseIcon} className="svg-inline-color" sx={{ color: has ? '#0154A4' : '#FF4842' }} />
                              <Typography sx={{ fontSize: '14px', color: has ? '#212B36' : '#B72136', textDecoration: has ? '' : 'line-through' }}>{y.name}</Typography>
                            </Stack>
                          )
                        })}
                      </Stack>
                    </Grid>))}
                  </Grid>
                </AccordionDetails>
              </Accordion>))}
            </Stack>
          </Box>
        </Stack>)}
      </Dialog>
    </Box>
  )
}
export default NewPlans