import * as React from "react";

import { Link } from "react-router-dom";
import { ChangeEvent, FormEvent, useState } from "react";
import {
	Tabs,
	Tab,
	Input,
	InputBase,
	Switch,
	Tooltip,
	Button,
	ButtonProps,
	Skeleton,
	Chip,
	Divider,
} from "@mui/material";
import Logo from "../images/logo.png";
// mui
import {
	Container,
	TableContainer,
	Table,
	TableHead,
	TableRow,
	TableCell,
	TableBody,
	Paper,
	IconButton,
	Typography,
	Collapse,
	Box,
	Checkbox,
} from "@mui/material";
// icons
import {
	KeyboardArrowLeft,
	KeyboardArrowDown,
	CheckBoxRounded,
	DisabledByDefaultRounded,
} from "@mui/icons-material";

import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import MoreVertIcon from "@mui/icons-material/MoreVert";
// import AddIcon from "@mui/icons-material/Add";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";

import ListItemIcon from "@mui/material/ListItemIcon";
import DeleteIcon from "@mui/icons-material/Delete";
import CopyAllIcon from "@mui/icons-material/CopyAll";
import BlockIcon from "@mui/icons-material/Block";
import CheckIcon from "@mui/icons-material/Check";
import PlaylistAddIcon from "@mui/icons-material/PlaylistAdd";
import RemoveIcon from "@mui/icons-material/Remove";
import CloseIcon from "@mui/icons-material/Close";
import AddCardIcon from "@mui/icons-material/AddCard";
import SaveIcon from "@mui/icons-material/Save";
import ReplayIcon from "@mui/icons-material/Replay";
import ArrowForwardtIcon from "@mui/icons-material/ArrowForward";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import { useSnackbar } from "notistack";
import axiosInstance from "../utils/axios-instance";
import { styled } from "@mui/material/styles";
import { LoadingButton } from "@mui/lab";
import { useNavigate } from "react-router-dom";
import DoneAllIcon from "@mui/icons-material/DoneAll";
import RuleIcon from "@mui/icons-material/Rule";
import { TooltipProps, tooltipClasses } from "@mui/material/Tooltip";
import { Group, SubGroup, _groups, _planList } from "../utils/planUtils";
import Header from "../layouts/Header";

let _changeIndex = 0;
const _changeList: any[] = [];

let _useUndo = false;

const ColorButton = styled(LoadingButton)<ButtonProps>(({ theme }) => ({
	// color: theme.palette.getContrastText("#2CD9C5"),
	// color: "#fafafa",
	// backgroundColor: "#2CD9C5",
	// "&:hover": {
	// 	backgroundColor: "#008B7B",
	// },
	// "&:disabled": {
	// 	backgroundColor: "#008B7Baa",
	// },
}));

const LightTooltip = styled(({ className, ...props }: TooltipProps) => (
	<Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
	[`& .${tooltipClasses.tooltip}`]: {
		backgroundColor: "#3E4957",
		color: "#FFFFFF",
		boxShadow: theme.shadows[2],
		fontSize: 10,
		padding: "16px 32px 16px 16px",
	},
}));

let _controller = new AbortController();

const _clone = (input: any) => JSON.parse(JSON.stringify(input));

const Plans = () => {
	const [selectedTab, setSelectedTab] = useState(5);
	const [planList, setPlanListState] = useState(_planList);
	const [groupList, setGroupList] = useState(_groups);
	const [user, setUser] = useState(null);

	const [editMode, setEditMode] = useState(false);

	const handleChange = (event: any, selectedTab: any) => {
		// @ts-ignore
		setSelectedTab({ 0: 5, 1: 3, 2: 4 }[selectedTab]);
	};

	const setPlanList = (list: any[], dontAddToChangeList: boolean = false) => {
		if (!dontAddToChangeList) {
			_changeIndex++;
			_changeList.length = _changeIndex;
			_changeList.push(list.slice());
		}

		setPlanListState(list.slice());
	};

	// // Store current value in ref
	// React.useEffect(() => {
	// 	if (_useUndo) {
	// 		_useUndo = false;
	// 		return;
	// 	}
	// 	// console.log(planList);
	// 	_changeIndex++;
	// 	_changeList.length = _changeIndex;
	// 	_changeList.push(planList.slice());
	// 	//user change it
	// 	// setPlanList(planList.slice());
	// }, [planList]); // Only re-run if value changes

	const undo = () => {
		console.log(_changeList);
		_changeIndex--;
		if (_changeIndex < 1) _changeIndex = 1;
		const p = _changeList[_changeIndex];
		_useUndo = true;
		setPlanList(p.slice(), true);
	};

	const redo = () => {
		console.log(_changeList);
		_changeIndex++;
		if (_changeIndex >= _changeList.length)
			_changeIndex = _changeList.length - 1;
		const p = _changeList[_changeIndex];
		_useUndo = true;
		setPlanList(p.slice(), true);
	};

	//TODO Ctrl + z
	// // Store current value in ref
	React.useEffect(() => {
		const charList = ["ظ", "ك", "z", "Z"];
		const handleKeydown = (event: any) => {
			// if (event.shiftKey && charList.includes(event.key)) {
			// 	return redo();
			// }

			// if (event.ctrlKey && charList.includes(event.key)) {
			// 	return undo();
			// }

			if (event.ctrlKey && ["s", "S", "س", "ئ"].includes(event.key)) {
				//TODO SAVE
				event.preventDefault();
				return savePlan();
			}
		};

		document.addEventListener("keydown", handleKeydown);
		// setPlanList(planList);

		return () => {
			document.removeEventListener("keydown", handleKeydown);
		};
	}, []); // Only re-run if value changes

	const [listLoading, setListLoading] = useState(true);
	React.useEffect(() => {
		let mounted = true;
		reloadPlan();
		return () => {
			mounted = false;
		};
	}, []);

	// const tabList = ["یک ماهه", "سه ماهه", "شش ماهه", "سالیانه", "مادام العمر"];
	const tabList = ["دو ماهه", "سالیانه", "مادام العمر"];
	const getTab = (tabIndex: number) => {
		if (selectedTab === tabIndex)
			return (
				<Typography
					component={"span"}
					sx={{
						color: "#212B36",
						boxShadow: "0px 6px 8px rgba(18, 17, 39, 0.1)",
						width: "96px",
						background: "#ffffff",
						borderRadius: "100px",
						overflow: "hidden",
						fontWeight: 600,
						py: 1,
					}}
				>
					{tabList[{ 5: 0, 3: 1, 4: 2 }[tabIndex] || 0]}
				</Typography>
			);
		return (
			<span style={{ fontWeight: 600, color: "#637381" }}>
				{tabList[{ 5: 0, 3: 1, 4: 2 }[tabIndex] || 0]}
			</span>
		);
	};

	const onPlanNameChange = (
		plan: any,
		event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
	) => {
		plan.name = event.target.value;
		setPlanList([...planList]);
	};

	const onPlanPriceChange = (
		plan: any,
		event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
	) => {
		plan.priceList[selectedTab] = +event.target.value;
		setPlanList([...planList]);
	};

	const onPlanDelete = (plan: any) => {
		setPlanList([...planList.filter((p) => p !== plan)]);
	};

	//copy plan
	const onPlanDuplicate = (plan: any) => {
		planList.splice(planList.indexOf(plan), 0, { ..._clone(plan) });
		setPlanList([...planList]);
	};

	//copy plan
	const onPlanCopy = (plan: any) => {
		const planIndex = planList.indexOf(plan);

		groupList.forEach((group) => {
			group.subgroups.forEach((subGroup) => {
				const status = subGroup.planStatus[selectedTab]?.[planIndex];
				[0, 1, 2, 3, 4].forEach((i) => {
					//@ts-ignore
					subGroup.planStatus[i][planIndex] = status;
				});
			});
		});

		setGroupList(_clone(groupList));
	};

	//copy plan
	const onPlanStatus = (plan: any, status: number) => {
		planList.forEach((p) => {
			if (p.status === status) p.status = 0;
		});
		plan.status = status;

		setPlanList([...planList]);
	};
	//copy plan
	const onPlanSpecial = (plan: any) => {
		plan.special = !plan.special;
		setPlanList([...planList]);
		handleClose();
	};

	const onPlanAdd = () => {
		planList.push({
			name: "جدید ",
			priceList: [1, 1, 1, 1, 1],
			disableList: [],
			status: 0,
		});
		setPlanList([...planList]);
	};

	const onGroupAdd = () => {
		groupList.push({
			name: "جدید ",
			subgroups: [],
		});
		setGroupList([...groupList]);
	};

	//copy plan
	const onPlanEnable = (plan: any, enable: boolean) => {
		plan.disableList = plan.disableList.filter(
			(i: number) => i !== selectedTab
		);
		if (!enable) plan.disableList.push(selectedTab);
		setPlanList([...planList]);
	};

	const isPlanDisable = (plan: any) => {
		return plan.disableList.find((i: number) => i === selectedTab);
	};

	const getPLansName = (plan: any, index: number) => (
		<TableCell
			align="center"
			sx={{
				backgroundColor: "#F6F7F8",
				minWidth: 100,
				maxWidth: 100,
				padding: 0,
				display: plan.disableList.includes(selectedTab) ? "none" : "",
			}}
			key={index}
		>
			<div className="flex flex-col items-center justify-center">
				<Typography
					variant="body1"
					color="#212B36"
					component="div"
					sx={{
						mb: 1.5,
						fontSize: {
							xs: "0.9rem",
							md: "1rem",
						},
					}}
				>
					{editMode ? (
						<Input
							sx={{ width: "100px" }}
							value={plan.name}
							onChange={(event) => onPlanNameChange(plan, event)}
						/>
					) : (
						<b>{plan.name}</b>
					)}

					{editMode && (
						<>
							<IconButton
								className="shadow"
								onClick={(event) => handleMenuClick(event, plan)}
								sx={{
									ml: 1,
									p: 1,
									background: "#fafafa",
									transform: "scale(0.8)",
								}}
							>
								<MoreVertIcon />
							</IconButton>

							<Menu
								id="long-menu"
								MenuListProps={{
									"aria-labelledby": "long-button",
								}}
								anchorEl={anchorEl}
								open={plan === menuPlan}
								onClose={handleClose}
								PaperProps={{
									style: {
										maxHeight: 200,
									},
								}}
							>
								<MenuItem onClick={() => onPlanDelete(plan)}>
									<ListItemIcon>
										<DeleteIcon fontSize="small" />
									</ListItemIcon>
									<Typography variant="inherit"> حذف </Typography>
								</MenuItem>

								<MenuItem onClick={() => onPlanDuplicate(plan)}>
									<ListItemIcon>
										<CopyAllIcon fontSize="small" />
									</ListItemIcon>
									<Typography variant="inherit">کپی </Typography>
								</MenuItem>

								<MenuItem onClick={() => onPlanStatus(plan, 1)}>
									<ListItemIcon>
										<CheckIcon fontSize="small" />
									</ListItemIcon>
									<Typography variant="inherit"> مجبوب </Typography>
								</MenuItem>

								<MenuItem onClick={() => onPlanStatus(plan, 2)}>
									<ListItemIcon>
										<CheckIcon fontSize="small" />
									</ListItemIcon>
									<Typography variant="inherit"> اقتصادی </Typography>
								</MenuItem>

								<MenuItem onClick={() => onPlanCopy(plan)}>
									<ListItemIcon>
										<CopyAllIcon fontSize="small" />
									</ListItemIcon>
									<Typography variant="inherit">کپی در تمام دوره‌ها</Typography>
								</MenuItem>

								<MenuItem onClick={() => onPlanSpecial(plan)}>
									<ListItemIcon>
										{plan.special && <CheckIcon fontSize="small" />}
										{!plan.special && <CloseIcon fontSize="small" />}
									</ListItemIcon>
									<Typography variant="inherit"> مودیان </Typography>
								</MenuItem>

								{isPlanDisable(plan) ? (
									<MenuItem onClick={() => onPlanEnable(plan, true)}>
										<ListItemIcon>
											<CheckIcon fontSize="small" />
										</ListItemIcon>
										<Typography variant="inherit"> فعال </Typography>
									</MenuItem>
								) : (
									<MenuItem onClick={() => onPlanEnable(plan, false)}>
										<ListItemIcon>
											<BlockIcon fontSize="small" />
										</ListItemIcon>
										<Typography variant="inherit"> غیر فعال </Typography>
									</MenuItem>
								)}
							</Menu>
						</>
					)}
				</Typography>
				<Typography
					component="span"
					color="#212B36"
					sx={{
						fontSize: {
							xs: "0.8rem",
							md: "1rem",
						},
					}}
				>
					{/* {Number(plan.priceList[selectedTab]).toLocaleString()} تومان */}
					{editMode ? (
						<Input
							sx={{ width: "64px" }}
							type="tel"
							value={plan.priceList[selectedTab]}
							onChange={(event) => onPlanPriceChange(plan, event)}
						/>
					) : (
						<b>{Number(plan.priceList[selectedTab]).toLocaleString() + " "}</b>
					)}
					<small>تومان</small>
				</Typography>

				<ColorButton
					fullWidth
					size="small"
					variant="text"
					className="mt-2"
					sx={{
						width: 100,
					}}
					loading={buyLoading}
					onClick={() => {
						payPlan(plan);
					}}
				>
					<AddShoppingCartIcon sx={{ transform: "scale(0.75)" }} />
					<span className="mr-1">انتخاب</span>
					{/* «{plan.name}» */}
				</ColorButton>
			</div>
		</TableCell>
	);

	//copy Group
	const getSubGroupAllCheckStatus = (group: any, index: number) => {
		const list = group.subgroups.map(
			(s: any) => s.planStatus[selectedTab]?.[index]
		);
		if (list.every((b: any) => b)) return 1;
		if (list.every((b: any) => !b)) return 2;
		return 3;
	};

	const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
	const [menuPlan, setMenuPlan] = React.useState(null);
	const handleMenuClick = (event: React.MouseEvent<HTMLElement>, plan: any) => {
		setAnchorEl(event.currentTarget);
		setMenuPlan(plan);
	};
	const handleClose = () => {
		setAnchorEl(null);
		setMenuPlan(null);
	};

	const [openGroupList, setOpenGroupList] = useState<any[]>([]);
	const [menuGroup, setMenuGroup] = React.useState(null);
	const [groupAnchorEl, setGroupAnchorEl] = React.useState<null | HTMLElement>(
		null
	);
	const [subGroupAnchorEl, setSubGroupAnchorEl] =
		React.useState<null | HTMLElement>(null);
	const [menuSubGroup, setMenuSubGroup] = React.useState(null);

	const Row = (group: Group) => {
		const handleGroupMenuClick = (
			event: React.MouseEvent<HTMLElement>,
			plan: any
		) => {
			setGroupAnchorEl(event.currentTarget);
			setMenuGroup(plan);
		};
		const handleGroupClose = () => {
			setGroupAnchorEl(null);
			setMenuGroup(null);
		};

		const handleSubGroupMenuClick = (
			event: React.MouseEvent<HTMLElement>,
			subgroup: any
		) => {
			setSubGroupAnchorEl(event.currentTarget);
			setMenuSubGroup(subgroup);
		};
		const handleSubGroupClose = () => {
			setSubGroupAnchorEl(null);
			setMenuSubGroup(null);
		};

		const onGroupNameChange = (
			group: any,
			event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
		) => {
			group.name = event.target.value;
			setGroupList(groupList.slice());
		};

		const onGroupCommandNameChange = (
			group: any,
			event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
		) => {
			group.commandName = event.target.value;
			setGroupList(groupList.slice());
		};

		const isGroupDisable = (group: any) => {
			return group.disableList?.find((i: number) => i === selectedTab);
		};

		const open = openGroupList.includes(group);
		const toggle = () => {
			if (open) {
				return setOpenGroupList([...openGroupList.filter((g) => g !== group)]);
			}
			openGroupList.push(group);
			return setOpenGroupList([...openGroupList]);
		};

		const onGroupDelete = (grp: any) => {
			setGroupList([...groupList.filter((g) => g !== grp)]);
		};

		const onSubGroupAdd = (grp: any) => {
			grp.subgroups.push({ name: "جدید", planStatus: {} });
			setGroupList([...groupList]);
		};

		const onSubGroupDelete = (grp: any, subgrp: any) => {
			grp.subgroups = grp.subgroups.filter((s: any) => s !== subgrp);
			setGroupList([...groupList]);
			handleSubGroupClose();
		};

		//copy Group
		const onGroupDuplicate = (grp: any) => {
			groupList.splice(groupList.indexOf(grp), 0, { ..._clone(grp) });
			setGroupList([...groupList]);
		};

		//copy Group
		const onGroupEnable = (grp: any, enable: boolean) => {
			grp.disableList = grp.disableList.filter(
				(i: number) => i !== selectedTab
			);
			if (!enable) grp.disableList.push(groupList);
			setGroupList([...groupList]);
		};

		//copy Group
		const onSubGroupCheckChange = (
			event: any,
			subgroup: any,
			index: number
		) => {
			subgroup.planStatus[selectedTab][index] = event.target.checked;
			setGroupList([...groupList]);
		};

		//copy Group
		const onSubGroupCheckAllChange = (
			event: any,
			group: any,
			index: number
		) => {
			group.subgroups.forEach(
				(s: any, i: number) =>
					(s.planStatus[selectedTab][index] = event.target.checked)
			);
			setGroupList([...groupList]);
		};

		const index = groupList.indexOf(group);

		planList.forEach((plan: any, index: number) => {
			group.subgroups.forEach((sub: any) => {
				sub.planStatus[selectedTab] = sub.planStatus[selectedTab] || [];
				sub.planStatus[selectedTab][index] =
					sub.planStatus[selectedTab][index] || false;
				sub.planStatus[selectedTab].length = planList.length;
			});
		});

		group.textList = group.textList || [];
		group.checkList = group.checkList || [];

		return (
			<React.Fragment key={index}>
				<TableRow
					sx={{ cursor: "pointer", backgroundColor: "#F8fafc" }}
					onClick={toggle}
					key={index}
				>
					<TableCell
						sx={{
							minWidth: {
								xs: 164,
								md: 128,
							},
							maxWidth: {
								xs: 164,
								md: 128,
							},
							position: "sticky",
							zIndex: 1,
							left: 0,
							backgroundColor: "#F8fafc",
						}}
					>
						<Typography
							variant="body1"
							color="#212B36"
							component="span"
							sx={{
								mb: 1.5,
								fontSize: {
									xs: "0.7rem",
									md: "1rem",
								},
							}}
							onClick={(event) => {
								editMode && event.stopPropagation();
							}}
						>
							{editMode ? (
								<>
									<Input
										sx={{ width: "100px" }}
										value={group.name}
										onClick={(event) => event.stopPropagation()}
										onChange={(event) => onGroupNameChange(group, event)}
									/>
								</>
							) : (
								<span>
									{group.name}
									<IconButton size="small" onClick={toggle}>
										{group.subgroups.length ? (
											open ? (
												<KeyboardArrowDown fontSize="small" />
											) : (
												<KeyboardArrowLeft fontSize="small" />
											)
										) : (
											<RemoveIcon fontSize="small" />
										)}
									</IconButton>
								</span>
							)}

							{editMode && (
								<>
									<IconButton
										className="shadow"
										onClick={(event) => {
											event.stopPropagation();
											handleGroupMenuClick(event, group);
										}}
										sx={{
											ml: 1,
											p: 1,
											background: "#fafafa",
											transform: "scale(0.8)",
										}}
									>
										<MoreVertIcon />
									</IconButton>

									<Menu
										id="long-menu"
										MenuListProps={{
											"aria-labelledby": "long-button",
										}}
										anchorEl={groupAnchorEl}
										open={group === menuGroup}
										onClose={handleGroupClose}
										PaperProps={{
											style: {
												maxHeight: 200,
												width: "20ch",
											},
										}}
									>
										<MenuItem onClick={() => onGroupDelete(group)}>
											<ListItemIcon>
												<DeleteIcon fontSize="small" />
											</ListItemIcon>
											<Typography variant="inherit"> حذف </Typography>
										</MenuItem>

										<MenuItem onClick={() => onSubGroupAdd(group)}>
											<ListItemIcon>
												<AddCardIcon fontSize="small" />
											</ListItemIcon>
											<Typography variant="inherit"> افزودن ردیف </Typography>
										</MenuItem>

										<MenuItem onClick={() => onGroupDuplicate(group)}>
											<ListItemIcon>
												<CopyAllIcon fontSize="small" />
											</ListItemIcon>
											<Typography variant="inherit">کپی </Typography>
										</MenuItem>

										{isGroupDisable(group) ? (
											<MenuItem onClick={() => onGroupEnable(group, true)}>
												<ListItemIcon>
													<CheckIcon fontSize="small" />
												</ListItemIcon>
												<Typography variant="inherit"> فعال </Typography>
											</MenuItem>
										) : (
											<MenuItem onClick={() => onGroupEnable(group, false)}>
												<ListItemIcon>
													<BlockIcon fontSize="small" />
												</ListItemIcon>
												<Typography variant="inherit"> غیر فعال </Typography>
											</MenuItem>
										)}
									</Menu>
								</>
							)}
						</Typography>
					</TableCell>

					{/* <TableCell sx={{ backgroundColor: "#F6F7F8" }} />
					<TableCell sx={{ backgroundColor: "#F6F7F8" }} />
					<TableCell sx={{ backgroundColor: "#F6F7F8" }} /> */}

					{planList.map((plan: any, index: number) => (
						<TableCell
							key={index}
							align="center"
							sx={{
								minWidth: 100,
								maxWidth: 100,
								display: plan.disableList.includes(selectedTab) ? "none" : "",
							}}
						>
							{editMode ? (
								<>
									{group.subgroups.length === 0 ? (
										<Checkbox
											key={groupList.indexOf(group)}
											checked={group.checkList?.[index]}
											icon={<CloseIcon htmlColor="#F44336" />}
											checkedIcon={<CheckIcon className="text-green-500" />}
											// checked={subgroup.status[index]}
											onChange={(event) => {
												group.checkList !== undefined &&
													(group.checkList[index] = event.target.checked);
												setGroupList(_clone(groupList));
											}}
											onClick={(event) => {
												event.stopPropagation();
											}}
										/>
									) : (
										<Checkbox
											key={"uncontroll_" + groupList.indexOf(group)}
											defaultChecked={
												getSubGroupAllCheckStatus(group, index) === 1
											}
											icon={<CloseIcon htmlColor="#F44336" />}
											checkedIcon={<CheckIcon className="text-green-500" />}
											// checked={subgroup.status[index]}
											onChange={(event) =>
												onSubGroupCheckAllChange(event, group, index)
											}
											onClick={(event) => {
												event.stopPropagation();
											}}
										/>
									)}

									<Input
										value={
											group.textList !== undefined ? group.textList[index] : 0
										}
										onClick={(event) => {
											event.stopPropagation();
										}}
										onChange={(event) => {
											group.textList !== undefined &&
												(group.textList[index] = event.target.value);
											setGroupList(groupList.slice());
										}}
									/>
								</>
							) : (
								<Checkbox
									icon={<CloseIcon fontSize="small" htmlColor="#F44336" />}
									checkedIcon={
										<CheckIcon fontSize="small" className="text-green-500" />
									}
									defaultChecked={getSubGroupAllCheckStatus(group, index) === 1}
									sx={{
										padding: 0,
										display:
											open || getSubGroupAllCheckStatus(group, index) === 3
												? "none"
												: "",
									}}
									readOnly={true}
									disabled={true}
								/>
							)}
						</TableCell>
					))}
					{/* 
					<TableCell
						align="right"
						sx={{
							backgroundColor: "#F6F7F8",
						}}
					>
						<IconButton size="small" onClick={toggle}>
							{group.subgroups.length ? (
								open ? (
									<KeyboardArrowDown fontSize="large" />
								) : (
									<KeyboardArrowLeft fontSize="large" />
								)
							) : (
								<RemoveIcon fontSize="large" />
							)}
						</IconButton>
					</TableCell> */}
				</TableRow>
				<TableRow
					key={index + "_sub"}
					sx={{
						backgroundColor: "#F0F1F2",
					}}
				>
					<TableCell colSpan={planList.length + 2} sx={{ p: 0 }}>
						<Collapse in={open} timeout="auto" unmountOnExit>
							<Table>
								<TableBody>
									{group.subgroups.map((subgroup, index) => (
										<TableRow
											key={index}
											sx={{
												position: "sticky",
												zIndex: 1,
												top: 0,
												backgroundColor: "#F0F1F2",
												"&:nth-child(odd)": {
													backgroundColor: "#EDEEEF",
												},
											}}
										>
											<TableCell
												sx={{
													minWidth: 128,
													maxWidth: 128,
													p: 1,
													pl: 2,
													position: "sticky",
													zIndex: 1,
													left: 0,
													border: "none",
												}}
											>
												{/* <Typography
													variant="body1"
													color="#212B36"
													component="div"
												>
													{subgroup.name}
												</Typography> */}

												<Typography
													variant="body1"
													color="#212B36"
													component="span"
													sx={{ mb: 1.5 }}
													onClick={(event) => {
														event.stopPropagation();
													}}
												>
													{editMode ? (
														<>
															<Input
																sx={{ width: "100px" }}
																value={subgroup.name}
																onClick={(event) => event.stopPropagation()}
																onChange={(event) =>
																	onGroupNameChange(subgroup, event)
																}
															/>

															{showCommands && (
																<Input
																	sx={{ width: "100px" }}
																	//@ts-ignore
																	value={subgroup.commandName}
																	onClick={(event) => event.stopPropagation()}
																	onChange={(event) =>
																		onGroupCommandNameChange(subgroup, event)
																	}
																/>
															)}
														</>
													) : (
														<Typography
															component={"span"}
															sx={{ fontSize: "0.8125rem" }}
														>
															{subgroup.name}
														</Typography>
													)}

													{editMode && (
														<>
															<IconButton
																className="shadow"
																onClick={(event) => {
																	event.stopPropagation();
																	handleSubGroupMenuClick(event, subgroup);
																}}
																sx={{
																	ml: 1,
																	p: 1,
																	background: "#fafafa",
																	transform: "scale(0.8)",
																}}
															>
																<MoreVertIcon />
															</IconButton>

															<Menu
																id="long-menu"
																MenuListProps={{
																	"aria-labelledby": "long-button",
																}}
																anchorEl={subGroupAnchorEl}
																open={subgroup === menuSubGroup}
																onClose={handleSubGroupClose}
																PaperProps={{
																	style: {
																		maxHeight: 200,
																		width: "20ch",
																	},
																}}
															>
																<MenuItem
																	onClick={(event) =>
																		onSubGroupDelete(group, subgroup)
																	}
																>
																	<ListItemIcon>
																		<DeleteIcon fontSize="small" />
																	</ListItemIcon>
																	<Typography variant="inherit">حذف</Typography>
																</MenuItem>
															</Menu>
														</>
													)}
												</Typography>
											</TableCell>

											{planList.map((plan: any, index: number) => (
												<TableCell
													key={index}
													align="center"
													sx={{
														minWidth: 100,
														maxWidth: 100,
														p: 1,
														px: 2,
														border: "none",
														display: plan.disableList.includes(selectedTab)
															? "none"
															: "",
													}}
												>
													<Checkbox
														sx={{ p: 0 }}
														icon={
															<CloseIcon fontSize="small" htmlColor="#F44336" />
														}
														checkedIcon={
															<CheckIcon
																fontSize="small"
																className="text-green-500"
															/>
														}
														checked={subgroup.planStatus[selectedTab]?.[index]}
														onChange={(event) =>
															onSubGroupCheckChange(event, subgroup, index)
														}
														readOnly={!editMode}
														disabled={!editMode}
													/>

													{/* <Box
														sx={{
															display: "flex",
															justifyContent: "center",
														}}
													>
														{value ? (
															<CheckBoxRounded color="success" />
														) : (
															<DisabledByDefaultRounded color="error" />
														)}
													</Box> */}
												</TableCell>
											))}

											{/* <TableCell
												align="right"
												sx={{ backgroundColor: "#EEEDED" }}
											>
												<IconButton
													size="small"
													onClick={toggle}
													sx={{ visibility: "hidden" }}
												>
													<RemoveIcon fontSize="large" />
												</IconButton>
											</TableCell> */}
										</TableRow>
									))}
								</TableBody>
							</Table>
						</Collapse>
					</TableCell>
				</TableRow>
			</React.Fragment>
		);
	};

	const collapseAll = (collapse: boolean) => {
		if (!collapse) return setOpenGroupList([]);
		setOpenGroupList([...groupList]);
	};

	const { enqueueSnackbar } = useSnackbar();

	const savePlan = () => {
		axiosInstance
			.post("/Plan/save", {
				planList: JSON.stringify(planList),
				groupList: JSON.stringify(groupList),
			})
			.then((response) => {
				if (response.data?.result === "ok") {
					enqueueSnackbar("تغییرات ذخیره شد!", {
						variant: "success",
					});
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => setListLoading(false));

		//TODO replace name by commandName
		const planCommands: string[][] = [];
		[1, 2, 3, 4, 5, 6, 7, 8].forEach((planNumber) => {
			planCommands[planNumber - 1] = [];
			groupList.map((group) => [
				...group.subgroups.map((subgroup) => {
					//@ts-ignore
					if (subgroup.planStatus[0][planNumber - 1] && subgroup.commandName)
						planCommands[planNumber - 1]?.push(
							//@ts-ignore
							...subgroup.commandName.split(/[\-_,|;و،]/).map((i) => i.trim())
						);
				}),
			]);
		});

		console.log(planCommands);

		axiosInstance
			.post("/Plan/saveCommands", {
				planCommands,
			})
			.then((response) => {
				if (response.data?.result === "ok") {
					enqueueSnackbar("تغییرات دسترسی‌ها ذخیره شد!", {
						variant: "success",
					});
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				}
			});
	};

	const reloadPlan = () => {
		axiosInstance
			.get("/Plan")
			.then((response) => {
				if (response.data?.result === "ok") {
					response.data?.planList &&
						setPlanList(JSON.parse(response.data?.planList));
					response.data?.planList &&
						setGroupList(JSON.parse(response.data?.groupList));

					setEditMode(
						!location.search.includes("view") && response.data?.allowModify
					);

					response.data?.user && setUser(response.data?.user);
				}
			})
			.catch((error) => {
				if (!error._handled) {
					enqueueSnackbar(
						error.response?.data?.error || "خطایی در دریافت اطلاعات روی داد!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => setListLoading(false));
	};

	const [buyLoading, setBuyLoading] = React.useState(false);

	const buyPlan = (plan: any) => {
		if (_controller) {
			_controller.abort();
			_controller = new AbortController();
		}

		setBuyLoading(true);

		const termName = [
			"1 ماهه",
			"3 ماهه",
			"6 ماهه",
			"سالیانه",
			"دائمی",
			"دو ماهه",
		];
		const price = +plan.priceList[selectedTab];
		axiosInstance
			.post(
				"Payment",
				{
					planNumber: selectedTab + 1,
					price: price + parseInt((price / 100) * 9 + ""),
					description: `خرید پلن ${plan.name} ${termName[selectedTab]} حسابداری کلیک`,
					planName: plan.name,
				},
				{ signal: _controller.signal }
			)
			.then((response) => {
				if (response.data.result === "ok") {
					window.location.assign(response.data.redirect);
				} else {
					enqueueSnackbar("خطایی رخ داد مجدد تلاش نمایید!", {
						variant: "error",
					});
				}
			})
			.catch((error) => {
				//TODO ERROR
				if (!error._handled && error.code !== "ERR_CANCELED") {
					// setError(
					// 	error.response?.data?.error || "خطایی رخ داد مجدد تلاش نمایید!"
					// );

					enqueueSnackbar(
						error.response?.data?.error || "خطایی رخ داد مجدد تلاش نمایید!",
						{
							variant: "error",
						}
					);
				}
			})
			.finally(() => setBuyLoading(false));
	};

	const payPlan = (plan: any) => {
		plan.planIndex = planList.indexOf(plan) + 1;
		if (user) navigate("/payment", { state: { plan, selectedTab, user } });
		else {
			enqueueSnackbar("برای سفارش ابتدا وارد حساب کاربری خود شوید.");
			navigate("/login", { state: { plan, selectedTab, user } });
		}
	};

	const [openAll, setOpenAll] = React.useState(false);
	const [showCommands, setShowCommands] = React.useState(true);

	const navigate = useNavigate();
	const planScrollbarRef = React.useRef(null);
	const groupScrollbarRef = React.useRef(null);

	const PlanCard = (plan: any, index: number) => {
		const isPopular = plan.status > 0;
		const { name, status } = plan;
		const price = plan.priceList[selectedTab];
		const isUnlimited = selectedTab === 6;
		const support = selectedTab + 1;

		const checkIconVariants = {
			1: <DoneAllIcon fontSize="small" className="ml-2 text-green-500" />,
			2: <CloseIcon fontSize="small" className="ml-2 text-red-500" />,
			3: <CheckIcon fontSize="small" className="ml-2 text-green-500" />,
			4: (
				<DoneAllIcon
					fontSize="small"
					className="ml-2 text-sky-700"
					style={{ color: "#007B55" }}
				/>
			),
		};

		//get Checks
		const getSubGroupChecksCount = (
			group: any,
			index: number,
			checkStatus: number
		) => {
			if (checkStatus == 3)
				return (
					"  " +
					group.subgroups.filter((s: any) => s.planStatus[selectedTab]?.[index])
						.length +
					" مورد"
				);

			return {
				1: "  همه‌ موارد",
				2: "",
			}[checkStatus];
		};

		const getPrice = () => {
			const realPrice =
				plan.priceList[0] *
				({ 1: 3, 2: 6, 3: 12, 4: 120, 5: 2 }[selectedTab] || 1);
			const percent = parseInt(100 - (price * 100) / realPrice + "");
			return [Number(realPrice).toLocaleString(), percent];
		};

		return (
			<div
				className={`rounded-2xl bg-white p-8 px-4 pl-2 sm:px-10 sm:pl-2 ${
					isPopular ? "relative" : "border border-dashed border-blue-700/25"
				}`}
				style={{
					boxShadow: isPopular
						? "rgba(145, 158, 171, 0.26) 0px 24px 48px 0px"
						: "",
					display: plan.disableList.includes(selectedTab) ? "none" : "",
				}}
			>
				{isPopular && (
					<>
						{status === 1 && (
							<span className="absolute top-4 left-4 flex h-6 items-center rounded-md bg-cyan-500/20 px-2 text-xs font-bold text-sky-700">
								محبوب ترین
							</span>
						)}
						{status === 2 && (
							<span className="absolute top-4 left-4 flex h-6 items-center rounded-md bg-orange-500/20 px-2 text-xs font-bold text-amber-700">
								اقتصادی
							</span>
						)}
					</>
				)}

				<span className="mb-4 block font-bold text-gray-500">
					{name}
					<small
						className="mr-2 text-xs"
						style={{ color: "#4CAF50", opacity: 1 }}
					>
						{selectedTab !== 5 && getPrice()[1] + "% تخفیف"}
					</small>
				</span>

				{!isUnlimited && (
					<div className="mb-6 flex text-gray-800">
						<h2 className="ml-2 text-3xl font-extrabold">
							{Number(price).toLocaleString()}

							{selectedTab !== 5 && (
								<span
									style={{
										color: "rgb(145, 158, 171)",
										textDecoration: "line-through",
										fontWeight: 400,
									}}
									className="block text-xl"
								>
									{getPrice()[0]}
								</span>
							)}
						</h2>

						<h5 className="text-sm font-bold">تومان</h5>
					</div>
				)}

				<ul className="mb-6 space-y-2">
					{(selectedTab == 3 || selectedTab == 4) && plan.special && (
						<li className="flex items-center">
							{checkIconVariants[4]}
							<p
								className="flex h-6 items-center rounded-md bg-cyan-500/20 px-3 pr-9 text-sm text-xs font-bold text-sky-700"
								style={{
									marginRight: "-36px",
									background: "#CEE9DC",
									color: "#007B55",
								}}
							>
								قابلیت اتصال به سامانه مودیان
							</p>
						</li>
					)}

					{groupList.map((group) => {
						const checkStatus = getSubGroupAllCheckStatus(group, index);
						group.textList = group.textList || [];
						group.checkList = group.checkList || [];

						return (
							<li className="flex items-center">
								{group.subgroups.length === 0
									? checkIconVariants[group.checkList?.[index] ? 3 : 2]
									: checkIconVariants[checkStatus]}

								<p className="text-sm text-gray-800"> {group.name} </p>

								{group.textList[index] && (
									<p className="text-sm text-gray-800">
										{" "}
										: {group.textList[index]}{" "}
									</p>
								)}

								{checkStatus !== 2 && group.subgroups.length !== 0 && (
									<LightTooltip
										placement="left"
										title={
											<ul className="space-y-1">
												{group.subgroups
													.filter(
														(s: any) => s.planStatus[selectedTab]?.[index]
													)
													.map((subgroup, i) => {
														return (
															<li className="flex items-center" key={i}>
																<CheckIcon
																	fontSize="small"
																	className="ml-2 text-green-500"
																/>
																<p className="text-xs text-white">
																	{subgroup.name}
																</p>
															</li>
														);
													})}
											</ul>
										}
										arrow
									>
										<span className="mr-2 cursor-help rounded-md bg-gray-300/20 px-2 py-[2px] text-xs text-gray-400">
											{getSubGroupChecksCount(group, index, checkStatus)}
										</span>
									</LightTooltip>
								)}
							</li>
						);
					})}

					{/* <li className="flex items-center">
						<Button variant="text" className="text-xs text-gray-600">
							<KeyboardArrowDownIcon
								fontSize="small"
								className="ml-2 pr-0 text-gray-600"
							/>
							نمایش همه موارد
						</Button>
					</li> */}
				</ul>

				<LoadingButton
					onClick={() => {
						payPlan(plan);
					}}
					disableElevation
					variant="contained"
					sx={{
						"&:hover": {
							boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
							backgroundColor: "#007B55",
						},
						backgroundColor: "#00AB55",
						width: "calc(100% - 2rem)",
					}}
					className=""
					loading={buyLoading}
					disabled={selectedTab === 0 && window.location.search != "?sup"}
				>
					<AddShoppingCartIcon fontSize="small" className="ml-1" />
					<span>سفارش</span>
				</LoadingButton>
			</div>
		);
	};

	const [showDetail, setShowDetail] = React.useState(false);

	return (
		<>
			<Header />
			<header className="absolute flex w-full items-center justify-between px-4 pt-10 lg:px-6">
				{/* <Link to="/">
					<img
						src={Logo}
						alt="نرم افزار حسابداری آنلاین و ابری کلیک"
						className="h-16"
					/>
				</Link> */}

				<IconButton
					className="shadow"
					sx={{
						position: "absolute",
						left: {
							xs: "24px",
							sm: "32px",
						},
						top: "128px",
					}}
					onClick={(e) => navigate("/choose")}
				>
					<ArrowForwardtIcon color="action" />
				</IconButton>

				{/* <Link to="/choose" className="text-gray-800 hover:underline">
					بازگشت
				</Link> */}
			</header>

			<Box className="bg-white">
				<Box className="bg-white pt-32 pb-20">
					<h1 className="mb-4 text-center text-2xl font-bold text-gray-800 md:text-4xl">
						پلن های کلیک
					</h1>

					<p className="mb-10 text-center text-gray-500">
						یکی از پلن ها را انتخاب کرده و به جمع مشتریان ما بپیوندید
					</p>

					<Tabs
						value={selectedTab}
						className="mb-10"
						onChange={handleChange}
						variant="scrollable"
						scrollButtons="auto"
						sx={{
							"& .MuiTabs-flexContainer": {
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
								xs: { transform: "scale(0.6)", justifyContent: "center" },
								sm: { transform: "initial" },
							},
						}}
						TabIndicatorProps={{ sx: { display: "none" } }}
					>
						<Tab
							label={getTab(5)}
							sx={{
								backgroundColor: "rgba(18, 17, 39, 0.04)",
								borderRadius: "9999px 0 0 9999px",
								p: 0.5,
							}}
						/>

						<Tab
							label={getTab(3)}
							sx={{ backgroundColor: "rgba(18, 17, 39, 0.04)", p: 0.5 }}
						/>

						{/* <Tab
							label={getTab(2)}
							sx={{ backgroundColor: "rgba(18, 17, 39, 0.04)", p: 0.5 }}
						/>
						<Tab
							label={getTab(3)}
							sx={{ backgroundColor: "rgba(18, 17, 39, 0.04)", p: 0.5 }}
						/> */}

						<Tab
							label={getTab(4)}
							sx={{
								backgroundColor: "rgba(18, 17, 39, 0.04)",
								borderRadius: "0 9999px 9999px 0",
								p: 0.5,
							}}
						/>

						{editMode && (
							<Box>
								<IconButton
									className="shadow"
									sx={{ position: "absolute", right: "72px" }}
									onClick={savePlan}
								>
									<SaveIcon color="success" />
								</IconButton>
								<IconButton
									className="shadow"
									sx={{ position: "absolute", right: "24px" }}
									onClick={reloadPlan}
								>
									<ReplayIcon color="warning" />
								</IconButton>
							</Box>
						)}
					</Tabs>

					<Paper
						className="m-0 grid grid-cols-1 gap-6 p-8 md:m-6 md:grid-cols-2 xl:grid-cols-3 2xl:grid-cols-5"
						// className={
						// 	"m-0 grid grid-cols-1 gap-6 p-8 md:m-6 md:grid-cols-2 xl:grid-cols-5" +
						// 	// @ts-ignore
						// 	(planList.filter(
						// 			// @ts-ignore
						// 		(plan) => !plan.disableList.includes(selectedTab)
						// 	).length == 5
						// 		? " xl:grid-cols-5"
						// 		: " xl:grid-cols-4 ")
						// }
						elevation={0}
						sx={{
							m: 3,
							p: 2.5,
							borderRadius: 2,
							backgroundColor: "#FaFbFc",
							"@media screen and (min-width: 1350px)": {
								gridTemplateColumns: 'repeat(5, minmax(0, 1fr)) !important'
							},
						}}
					>
						{!listLoading
							? planList.map(PlanCard)
							: [1, 2, 3, 4].map((i) => (
									<Skeleton
										key={i}
										className={`rounded-2xl bg-white p-10 ${
											i === 1
												? "relative"
												: "border border-dashed border-blue-700/25"
										}`}
										height={628}
										style={{
											boxShadow:
												i === 1
													? "rgba(145, 158, 171, 0.16) 0px 24px 48px 0px"
													: "",
										}}
										variant="rounded"
										sx={{ m: 2, mt: 5, borderRadius: 2 }}
									/>
							  ))}
					</Paper>

					<Divider
						sx={{
							p: 0.5,
							px: 3,
							mt: 6,
						}}
					>
						<Chip
							onClick={() => setShowDetail(!showDetail)}
							sx={{ px: 2 }}
							label="مشاهده جزئیات پلن‌ها"
						/>
					</Divider>

					{!listLoading ? (
						showDetail && (
							<Paper elevation={0} sx={{ mt: 3, p: 2.5, borderRadius: 2 }}>
								<Paper
									elevation={0}
									sx={{
										borderRadius: 2.5,
										// borderColor: "#D8DDE1",
										overflow: "hidden",
										position: "sticky",
										zIndex: 3,
										top: 0,
									}}
								>
									<TableContainer
										ref={planScrollbarRef}
										onScroll={(event: any) => {
											if (groupScrollbarRef && groupScrollbarRef.current) {
												(groupScrollbarRef.current as any).scrollLeft =
													event.target.scrollLeft;
											}
										}}
									>
										<Table>
											<TableHead sx={{}}>
												<TableRow
													sx={{
														"& > *": {
															borderBottom: "unset !important",
														},
														"& .MuiTableCell-head": {
															paddingTop: "16px",
															paddingBottom: "16px",
														},
													}}
												>
													<TableCell
														sx={{
															backgroundColor: "#F6F7F8",
															minWidth: {
																xs: 130,
																md: 128,
															},
															maxWidth: {
																xs: 180,
																md: 128,
															},
															padding: {
																xs: "0 0 0 16px",
																md: "16px",
															},
														}}
													>
														<Typography
															variant="body1"
															color="#212B36"
															component="div"
															sx={{
																fontSize: {
																	xs: "0.7rem",
																	md: "1rem",
																},
															}}
														>
															<b>امکانات و ویژگی ها</b>
															{editMode && (
																<>
																	<IconButton
																		className="shadow"
																		onClick={onPlanAdd}
																		sx={{
																			ml: 1,
																			p: 1,
																			background: "#fafafa",
																			transform: "scale(0.8)",
																		}}
																	>
																		<AddCircleOutlineIcon />
																	</IconButton>
																	<IconButton
																		className="shadow"
																		onClick={onGroupAdd}
																		sx={{
																			ml: 1,
																			p: 1,
																			background: "#fafafa",
																			transform: "scale(0.8)",
																		}}
																	>
																		<PlaylistAddIcon />
																	</IconButton>
																</>
															)}

															<div>
																<small>
																	{openAll ? "بستن" : "باز کردن"} همه
																</small>
																<Tooltip title="باز/بستن همه" arrow>
																	<Switch
																		checked={openAll}
																		onChange={(event) => {
																			collapseAll(event.target.checked);
																			setOpenAll(event.target.checked);
																		}}
																	/>
																</Tooltip>
															</div>

															{editMode && (
																<div>
																	<small>
																		{showCommands ? "مخفی کردن" : "نمایش"}{" "}
																		دسترسی‌ها
																	</small>
																	<Tooltip title="مخفی/نمایش دسترسی‌ها" arrow>
																		<Switch
																			checked={showCommands}
																			onChange={(event) => {
																				setShowCommands(event.target.checked);
																			}}
																		/>
																	</Tooltip>
																</div>
															)}
														</Typography>
													</TableCell>

													{planList.map(getPLansName)}

													{/* <TableCell
													align="right"
													sx={{ backgroundColor: "#F6F7F8" }}
												>
													<IconButton
														size="small"
														sx={{ visibility: "hidden" }}
													>
														<RemoveIcon fontSize="large" />
													</IconButton>
												</TableCell> */}

													{/* 
											<TableCell
												align="center"
												sx={{
													backgroundColor: "#F6F7F8",
													minWidth: 180,
													maxWidth: 180,
												}}
											>
												<Typography
													variant="body1"
													color="#212B36"
													component="div"
													sx={{ mb: 1.5 }}
												>
													پایه
												</Typography>
												<Typography variant="h6" color="#212B36">
													{planProps().price.base} تومان
												</Typography>
											</TableCell>

											<TableCell
												align="center"
												sx={{
													backgroundColor: "#F6F7F8",
													minWidth: 180,
													maxWidth: 180,
												}}
											>
												<Typography
													variant="body1"
													color="#212B36"
													component="div"
													sx={{ mb: 1.5 }}
												>
													پیشرفته
												</Typography>
												<Typography variant="h6" color="#212B36">
													{planProps().price.advanced} تومان
												</Typography>
											</TableCell>

											<TableCell
												align="center"
												sx={{
													backgroundColor: "#F6F7F8",
													minWidth: 180,
													maxWidth: 180,
												}}
											>
												<Typography
													variant="body1"
													color="#212B36"
													component="div"
													sx={{ mb: 1.5 }}
												>
													حرفه ای
												</Typography>
												<Typography variant="h6" color="#212B36">
													{planProps().price.pro} تومان
												</Typography>
											</TableCell>

											<TableCell
												align="center"
												sx={{
													backgroundColor: "#F6F7F8",
													minWidth: 180,
													maxWidth: 180,
												}}
											>
												<Typography
													variant="body1"
													color="#212B36"
													component="div"
													sx={{ mb: 1.5 }}
												>
													کلاسیک
												</Typography>
												<Typography variant="h6" color="#212B36">
													{planProps().price.plus} تومان
												</Typography>
											</TableCell> */}
												</TableRow>
											</TableHead>
										</Table>
									</TableContainer>
								</Paper>

								<Paper
									elevation={0}
									sx={{
										mt: 2.5,
										borderRadius: 2.5,
										// borderColor: "#D8DDE1",
										overflow: "hidden",
									}}
								>
									<TableContainer
										ref={groupScrollbarRef}
										onScroll={(event: any) => {
											if (planScrollbarRef && planScrollbarRef.current) {
												(planScrollbarRef.current as any).scrollLeft =
													event.target.scrollLeft;
											}
										}}
									>
										<Table size="small">
											<TableBody>{groupList.map(Row)}</TableBody>
										</Table>
									</TableContainer>
								</Paper>
							</Paper>
						)
					) : (
						<>
							<Skeleton
								variant="rounded"
								height={128}
								sx={{ m: 2, mt: 5, borderRadius: 2 }}
							/>

							<Skeleton
								variant="rounded"
								height={186}
								animation="wave"
								sx={{ m: 2, borderRadius: 2 }}
							/>
						</>
					)}
				</Box>
			</Box>
		</>
	);
};

export default Plans;
