import { FC, ReactNode, useEffect, useState } from "react";
import type { SettingConfig, Setting, GetTitle } from "@/types";
import FormControl from "@mui/material/FormControl";
import Select, { SelectChangeEvent } from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import InputLabel from "@mui/material/InputLabel";
import FormHelperText from "@mui/material/FormHelperText";
import { useTranslation } from "react-i18next";

const SelectCLK: FC<{
	config: SettingConfig;
	data: Setting;
	getTitle: GetTitle;
	onChange: (
		event: SelectChangeEvent<"True" | "False">,
		child: ReactNode
	) => void;
}> = (props) => {
	const { config, data, onChange, getTitle } = props;
	const { t } = useTranslation();
	const [selectOptions, setSelectOptions] = useState<
		{ name: string; value: string }[]
	>([]);
	return (
		<FormControl fullWidth>
			<InputLabel id={`${data.Name}Label`}>
				{(config.label as string) || data.FormName}
			</InputLabel>
			<Select
				labelId={`${data.Name}Label`}
				id={data.Name}
				name={data.Name}
				value={data.Value}
				label={getTitle((config.label as string) || data.FormName)}
				onChange={onChange}
			>
				{(config?.options as any)?.map(
					(i: { name: string; value: string }, j: number) => (
						<MenuItem value={i.value} key={j}>
							{t(i.name)}
						</MenuItem>
					)
				)}
			</Select>
			<FormHelperText>{config?.hint as string}</FormHelperText>
		</FormControl>
	);
};

export default SelectCLK;
