import { FC } from 'react';
import { useTranslation } from 'react-i18next';
import { Item2 } from '@/types';
import Scrollbar from '../Scrollbar';
import { getPriceFormat } from '../../utils/formatNumber';

// @mui
import { Paper, TableContainer, Table, TableHead, TableRow, TableCell, TableBody, Box, Typography } from '@mui/material';
import { useTheme } from '@mui/material/styles';

const tableHeads = [ 
  { title: 'Row' }, { title: 'Depot', value: 'Title' }, { title: 'Inventory', value: 'DepotHoldings' }, { title: 'SellAverage', type: 'price' }, { title: 'BuyAverage', type: 'price' }
]

const DepotItemsTable: FC<{ rows: any[] }> = ({ rows }) => {
  const { t } = useTranslation();
  const theme = useTheme();
  return (
    <Box sx={{ px: 1 }}>
      <Scrollbar>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }} aria-label="simple table">
            <TableHead>
              {/* @ts-ignore */}
              <TableRow sx={{ backgroundColor: theme.palette.secondary.slate.deep, borderRadius: '8px', '& .MuiTableCell-root': {border: '0'} }}>
                {/* @ts-ignore */}
                {tableHeads.map(i => (<TableCell key={i.value}><Typography sx={{ color: theme.palette.secondary.gray.main, fontSize: '14px', fontWeight: 700 }}>{t(i.title)}</Typography></TableCell>))}
              </TableRow>
            </TableHead>
            <TableBody>
              {rows?.map((row, index: number) => (
                <TableRow
                  key={row.Id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  {tableHeads.map((head: any) => {
                    let colValue = row?.[(head.value || head.title) as keyof Item2]
                    switch (head.type) {
                      case 'price':
                        colValue = colValue !== '-' ? getPriceFormat(colValue as string) : '-'
                        break
                    }
                    switch (head.value || head.title) {
                      case 'Row':
                        return (<TableCell align="left" key={(row as any)?.[head.value]}>{ index + 1}</TableCell>)
                      case 'title':
                        return (<TableCell key={(row as any)?.[head.value]}>
                          <Box component="span" sx={{ width: '12px', height: '12px', backgroundColor: row.color, borderRadius: '100%', display: 'inline-block', verticalAlign: 'middle' }}></Box> {row.title}
                        </TableCell>)
                      case 'IncomeFromSell':
                        return (<TableCell key={(row as any)?.[head.value]}>{getPriceFormat(row.SalePrice * row.value)}</TableCell>)
                      default:
                        return (<TableCell key={(row as any)?.[head.value]}>{colValue}</TableCell>)
                    }
                  })}
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Scrollbar>
    </Box>
  )
}
export default DepotItemsTable
