import { FC, ChangeEventHandler } from "react";
import type {
	SettingConfig,
	Setting,
	OnChangeSetting,
	GetTitle,
} from "@/types";
import FormControl from "@mui/material/FormControl";
import TextField from "@mui/material/TextField";
import { useState, useEffect } from "react";
import { InputBaseComponentProps } from "@mui/material/InputBase";
// import InputLabel from '@mui/material/InputLabel';
// import FormHelperText from '@mui/material/FormHelperText';

const TextFieldCLK: FC<{
	type?: string;
	config: SettingConfig;
	data: Setting;
	onChange: OnChangeSetting;
	getTitle: GetTitle;
}> = (props) => {
	const { type, config, data, onChange, getTitle } = props;
	const [inputProps, setInputProps] = useState<InputBaseComponentProps>({
		dir: "auto",
	});
	useEffect(() => {
		const inputPropsList = { ...inputProps };
		if (type === "number") {
			inputPropsList.inputMode = "numeric";
			inputPropsList.pattern = "[0-9]*";
			inputPropsList.type = "number";
			if (config.min || config.min === 0) {
				inputPropsList.min = String(config.min);
			}
			if (config.max || config.max === 0) {
				inputPropsList.max = String(config.max);
			}
			setInputProps(inputPropsList);
		}
	}, [type]);
	return (
		<FormControl fullWidth>
			{/* className={config?.inputWrapperClass || 'w-full'} */}
			{/* <InputLabel id={`${data.name}Label`}>{data.FormName}</InputLabel> */}
			<TextField
				inputProps={inputProps}
				type={type || "text"}
				label={getTitle((config?.label || data.FormName) as string)}
				dir="auto"
				multiline={config?.type === "textarea"}
				rows={4}
				variant="outlined"
				helperText={
					(config?.hint && typeof config.hint === "function"
						? config.hint(data)
						: config?.hint || "") as string
				}
				defaultValue={data.Value}
				placeholder={config?.placeHolder}
				disabled={false}
				className="w-full"
				name={data.Name}
				onChange={
					onChange as ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement>
				}
			/>
			{/* <FormHelperText>{config?.info as string}</FormHelperText> */}
		</FormControl>
	);
};

export default TextFieldCLK;
