import { FC, useEffect, useMemo, useState } from "react";
import { Box, Card, useTheme, Typography, InputAdornment, IconButton, SwipeableDrawer, FormGroup, FormControlLabel, Checkbox } from "@mui/material";
import { useTranslation } from "react-i18next";
import InputStyle from '../InputStyle';
import { Report } from "@/types";

// componens
import ItemsTable from "./ItemsTable";

// icons
import Iconify from "../Iconify";
import { ReactComponent as EditIcon } from '../../images/ic_edit.svg'
import { ReactComponent as CalendarIcon } from '../../images/ic_calendar.svg'
import { ReactComponent as PrintIcon } from '../../images/ic_print.svg'
import { ReactComponent as DownloadIcon } from '../../images/ic_download.svg'
import _ from "lodash";

type TableSetting = { id: number, value: string, type?: 'price', editable?: true, show?: boolean }

const ItemsTableListView: FC<{ items: Report[] }> = ({ items }) => {
  const theme = useTheme();
	const { t } = useTranslation();
  const [searchPhrase, setSearchPhrase] = useState<string>('')
  const [rowsPerPage, setRowsPerPage] = useState<number>(5)
  const [page, setPage] = useState<number>(0)
  const [openTableSettings, setOpenTableSettings] = useState<boolean>(false)
  const [tableSettings, setTableSettings] = useState<TableSetting[]>([ 
    { id: 1, value: 'Row', show: true },
    { id: 2, value: 'Code', show: true },
    { id: 3, value: 'Title', editable: true, show: true },
    { id: 4, value: 'Barcode', show: true },
    { id: 5, value: 'TechnicalCode', show: true },
    { id: 6, value: 'ModelNo', show: true },
    { id: 7, value: 'WooCommerceProductId', show: false },
    { id: 8, value: 'WooCommerceVariableId', show: false },
    { id: 9, value: 'Unit1Description', show: true },
    { id: 10, value: 'Unit2Title', show: false },
    { id: 11, value: 'Quantity1', show: true },
    { id: 12, value: 'Quantity2', show: false },
    { id: 13, value: 'BuyPrice', type: 'price', editable: true, show: true },
    { id: 14, value: 'SalePrice', type: 'price', editable: true, show: true },
    { id: 15, value: 'LastBuyPrice', type: 'price', editable: true, show: false },
    { id: 16, value: 'LastSellPrice', type: 'price', editable: true, show: false },
    { id: 17, value: 'Action', show: true }
  ])
  const filteredItems = useMemo(() => {
    setPage(0);
    return items.filter(i => 
      String(i?.Code)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.Title)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.Barcode)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.TechnicalCode)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.ModelNo)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.WooCommerceProductId)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.WooCommerceVariableId)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.WooCommerceVariableId)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.Unit1Description)?.trim()?.includes(searchPhrase?.trim()) ||
      String(i?.Unit2Title)?.trim()?.includes(searchPhrase?.trim())
  )}, [items, searchPhrase, tableSettings])
  const onFilterName = _.debounce(async (search: string) => {
    setSearchPhrase(search);
  }, 1000)
  const tableSettingChange = (event: any, item: TableSetting) => {
    const newTableSettings = [...tableSettings]
    const itemIndex = newTableSettings.findIndex(i => item.id === i.id)
    if (itemIndex > -1) {
      newTableSettings[itemIndex].show = event.target.checked
      setTableSettings(newTableSettings)
    }
  }
  return (
    <Card>
      {/* @ts-ignore */}
      <Box sx={{ display: 'flex', alignItems: 'center', backgroundColor: theme?.palette.secondary.slate.deep, px: 3, py: '12px' }}>
        <Typography color="secondary.dimgray.main" sx={{ fontWeight: 700, fontSize: '14px' }}>{t('ItemsList')}</Typography>
      </Box>
      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 2 }}>
        {/* @ts-ignore */}
        <InputStyle
          stretchStart={240}
          value={searchPhrase}
          onChange={(event) => onFilterName(event.target.value)}
          placeholder={t('Search') as string}
          sx={{ '& .MuiOutlinedInput-root': { width: { xs: '100%', md: '240px' }, '&.Mui-focused': { width: { xs: '100%', md: '300px' } } } }}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <Iconify icon={'eva:search-fill'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
              </InputAdornment>
            ),
            endAdornment: (
              <InputAdornment position="end">
                {searchPhrase?.trim()?.length > 0 && (
                  <IconButton onClick={() => onFilterName('')}>
                    <Iconify icon={'fluent-mdl2:clear'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
                  </IconButton>
                )}
              </InputAdornment>
            )
          }}
        />
        {/* Todo: Actions */}
        {/* <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <IconButton onClick={() => setOpenTableSettings(true)}><EditIcon /></IconButton> 
          <IconButton><CalendarIcon /></IconButton>
          <IconButton><PrintIcon /></IconButton>
          <IconButton><DownloadIcon /></IconButton>
        </Box> */}
      </Box>
      <SwipeableDrawer
        anchor="right"
        open={openTableSettings}
        onClose={() => setOpenTableSettings(false)}
        onOpen={() => setOpenTableSettings(true)}
      >
        <Box sx={{ display: 'flex', flexDirection: 'column', py: 2, px: 3 }}>
          <FormGroup>
            { tableSettings.map(i => <FormControlLabel key={i.id} control={<Checkbox checked={i.show} onChange={(event) => tableSettingChange(event, i)} />} label={t(i.value)} />) }
          </FormGroup>
        </Box>
      </SwipeableDrawer>
      <ItemsTable 
        rows={filteredItems}
        rowsPerPage={rowsPerPage}
        setRowsPerPage={setRowsPerPage}
        page={page}
        setPage={setPage}
        itemCount={filteredItems.length}
        tableSettings={tableSettings}
        setTableSettings={setTableSettings}
      />
    </Card>
  )
}
export default ItemsTableListView