import { Link } from "react-router-dom";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useFormik } from "formik";
import { object, string } from "yup";
import axios from "../../utils/axios-instance";
import {
	TextField,
	Alert,
	Box,
	Select,
	MenuItem,
	Container,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";
import InfoIcon from "@mui/icons-material/Info";

import Logo from "../../images/logo.svg";
import AuthBannerBg from "../../images/auth-banner-bg.jpg";
import AuthBanner from "../../images/auth-banner.png";
import LanguageSelector from "../../components/LanguageSelector";
import { useTranslation } from "react-i18next";

const Register = () => {
	const { t } = useTranslation();
	const [loading, setLoading] = useState(false);
	const navigate = useNavigate();
	const { enqueueSnackbar } = useSnackbar();
	const [error, setError] = useState("");
	const direction = localStorage.getItem("i18nextLng")
		? ["fa", "sa"].includes(localStorage.getItem("i18nextLng") as string)
			? "rtl"
			: "ltr"
		: "rtl";
	const registerForm = useFormik({
		initialValues: {
			firstname: "",
			lastname: "",
			mobile: "",
		},
		validationSchema: object({
			firstname: string().required(t("insertName") as string),
			lastname: string().required(t("insertFamily") as string),
			mobile: string()
				.required(t("mobileRequiredError") as string)
				.matches(/^(0)?([9][0-9]{9})$/, t("mobileValidationError") as string),
		}),
		onSubmit: (values) => {
			setLoading(true);

			axios
				.post("User", {
					firstName: values.firstname,
					lastName: values.lastname,
					mobilePhone: values.mobile,
				})

				.then((response) => {
					if (response.data.result === "ok") {
					
						response.data?.update &&
							enqueueSnackbar(t("existError") as string, {
								variant: "warning",
							});

						navigate("/verify", {
							state: {
								firstName: values.firstname,
								lastName: values.lastname,
								mobile: values.mobile,
							},
						});
					} else {
						response.data.error && setError(response.data.error);
					}
				})
				.catch((error) => {
					if (!error._handled) {
						enqueueSnackbar(
							error.response?.data?.error || "خطایی در عملیات روی داد!",
							{
								variant: "error",
							}
						);
					}

					setError(t("errorTryAgain") as string);
				})
				.finally(() => setLoading(false));
		},
	});

	return (
		<Container className="flex min-h-screen flex-col items-center lg:items-stretch">
			<div className="hidden justify-end pt-7 lg:flex">
				<LanguageSelector />
			</div>
			<main className="flex grow justify-between">
				<div className="flex max-w-[480px] pt-32 lg:shrink-0 lg:items-center lg:pt-0">
					<div className="w-full">
						<div className="mb-4 flex flex-col items-center gap-1">
							<Link to="/" className="mb-10">
								<img
									src={Logo}
									alt="نرم افزار حسابداری آنلاین و ابری کلیک"
									className="h-[92px]"
								/>
							</Link>
							<h3 className="mb-4 text-xl font-extrabold text-[#212B36] lg:text-2xl">
								{t("registerTitle")}
							</h3>
							<p className="ml-1 mb-4 text-lg font-normal text-[#2E3947]">
								{t("registerSubtitle")}
							</p>
							<Alert
								severity="info"
								className="mb-6 w-full"
								color="info"
								classes={{
									message: "text-[#091A7A]",
									icon:
										"text-[#3366FF] pt-[5px]" +
										(direction === "rtl" ? " ml-3" : " mr-3"),
								}}
								icon={<InfoIcon sx={{ color: "#3366FF" }} />}
							>
								{t("registerAlert")}
							</Alert>
						</div>

						{error && (
							<Alert severity="error" className="mb-6 w-full">
								{error}
							</Alert>
						)}

						<form onSubmit={registerForm.handleSubmit}>
							<div className="mb-5 flex flex-col gap-2 lg:flex-row">
								<TextField
									autoFocus
									label={t("name")}
									className="mb-5 flex-grow lg:ml-0 lg:mb-0"
									error={
										registerForm.touched.firstname &&
										Boolean(registerForm.errors.firstname)
									}
									helperText={
										registerForm.touched.firstname &&
										registerForm.errors.firstname
									}
									{...registerForm.getFieldProps("firstname")}
								/>

								<TextField
									label={t("family")}
									className="flex-grow"
									error={
										registerForm.touched.lastname &&
										Boolean(registerForm.errors.lastname)
									}
									helperText={
										registerForm.touched.lastname &&
										registerForm.errors.lastname
									}
									{...registerForm.getFieldProps("lastname")}
								/>
							</div>

							<TextField
								fullWidth
								label={
									<label>
										{t("mobile")}
										{/* <span className="text-sm"> (برای ارسال رمز موقت) </span> */}
									</label>
								}
								className="mb-5"
								error={
									registerForm.touched.mobile &&
									Boolean(registerForm.errors.mobile)
								}
								helperText={
									registerForm.touched.mobile && registerForm.errors.mobile
								}
								{...registerForm.getFieldProps("mobile")}
							/>

							<LoadingButton
								disableElevation
								fullWidth
								variant="contained"
								size="large"
								type="submit"
								className="bg-blue-700 hover:bg-blue-900"
								loading={loading}
								sx={{
									"&:hover": {
										boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
									},
								}}
							>
								{t("register")}
							</LoadingButton>
						</form>
						<div className="my-6 flex justify-center gap-1 text-base">
							<p className="text-[#919EAB]">{t("alreadyRegistered")}</p>

							<Link
								to="/login"
								className="font-normal text-[#3366FF] hover:underline"
							>
								{t("enter")}
							</Link>
						</div>
						<div className="flex justify-center lg:hidden">
							<LanguageSelector />
						</div>
					</div>
				</div>

				<div className="relative hidden w-[490px] flex-col items-center justify-center overflow-hidden lg:flex">
					<h1 className="mb-5 text-3xl font-bold text-gray-800">
						{t("registerDetailTitle")}
					</h1>
					<h2 className="mb-14 text-lg text-gray-500">
						{t("registerDetailSubtitle")}
					</h2>
					<img
						src={AuthBanner}
						alt="نرم افزار حسابداری آنلاین و ابری کلیک"
						className="w-full object-cover"
						style={{ maxWidth: "490px" }}
					/>
				</div>

				{/* <div className="_loadingContainer">
				<img
					src={Logo}
					alt="نرم افزار حسابداری آنلاین و ابری کلیک"
					className="h-16"
				/>
				<div className="_loadingDiv1" />
				<div className="_loadingDiv2" />
			</div> */}
			</main>
		</Container>
	);
};

export default Register;
