import { FC, useState } from "react";
import { Card } from "@mui/material";
import { DataItemType } from "@/types";

// componens
import ItemsCountIncomeTable from "./ItemsCountIncomeTable";

const ItemsCountIncomeTableListView: FC<{ items: { id: number, name: string, link: string, data: DataItemType[] }[] }> = ({ items }) => {
  const [rowsPerPage, setRowsPerPage] = useState<number>(5)
  const [page, setPage] = useState<number>(0)
  return (
    <Card sx={{ py: 1 }}>
      <ItemsCountIncomeTable 
        rows={items} 
        rowsPerPage={rowsPerPage}
        setRowsPerPage={setRowsPerPage}
        page={page}
        setPage={setPage}
        itemCount={items?.length}
      />
    </Card>
  )
}
export default ItemsCountIncomeTableListView