import * as React from "react";
import { useState, useEffect, useRef } from "react";
import { Navigate } from "react-router-dom";
import _ from "lodash";

import axiosInstance from "../utils/axios-instance";

// components
import { setScreenLoading } from "../utils";
import { useParams } from "react-router-dom";
import { Timeout } from "react-number-format/types/types";

const useGet = (url: string, callback?: Function) => {
	const [loading, setLoading] = useState<boolean>(true);
	const [data, setData] = useState<any>([]);
	const [error, setError] = useState<string>("");
	const timer = useRef<Timeout>(null);
	const datas = useRef<any>(data);
	datas.current = data;
	const fetchData = () => {
		// @ts-ignore
		clearTimeout(timer.current);
		setLoading(true);
		setError("");

		axiosInstance
			.get(url)
			.then((res) => {
				if (!_.isEqual(res.data, datas.current)) {
					setData(res.data || []); // true
					callback && callback(res);
				}
			})
			.catch((e) => setError(e))
			.finally(() => {
				setLoading(false);
				// @ts-ignore
				timer.current = setTimeout(fetchData, 11000);
			});
	};
	useEffect(() => {
		fetchData();
		// @ts-ignore
		return () => clearTimeout(timer.current);
	}, []);

	return { data, loading, error, fetchData };
};

export default function HelpController() {
	const routeParams = useParams();
	const route = routeParams["*"];
	if (!route) return <Navigate to="/notFound" replace />;

	const getHelp = useGet(
		`/help/${route.includes(" ") ? "linkTitle" : "link"}?` + routeParams["*"]
	);
	setScreenLoading(getHelp.loading);
	if (getHelp.error) return <Navigate to="/notFound" replace />;

	if (!getHelp.loading && !getHelp.data?.id)
		return <Navigate to="/notFound" replace />;
	if (getHelp.data?.id)
		return <Navigate to={(getHelp.data?.type > 2 ? '/blog/' : '/newHelp/') + getHelp.data.id} replace />;
	return "";
}
