import React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import { useTranslation } from 'react-i18next';
import { FC } from 'react';
import TabVertical from './TabVertical';
import type { TabComponent } from "../types";

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ py: 1 }}>{children}</Box>
      )}
    </div>
  );
}

const TabView: FC<TabComponent> = props => {
  const { orientation, data, loading, onChange, branchList } = props;
  const { t } = useTranslation();
  const [value, setValue] = React.useState(0);
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };
  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
        <Tabs
          orientation={orientation || 'horizontal'}
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
          visibleScrollbar={true}
          variant="scrollable"
        >
          {
            data && Object.keys(data).map((i: string, j: number) => (
              <Tab label={t(i)} id={i} aria-controls={i} key={j} />
            ))
          }
        </Tabs>
      </Box>
      {
        data && Object.keys(data).length ? Object.keys(data).map((i: string, j: number) => (
          <TabPanel value={value} index={j} key={j}>
            <TabVertical orientation="vertical" data={data[i]} onChange={onChange} branchList={branchList} />
          </TabPanel>
        )) : loading ? (<div className="m-4">در حال بارگزاری...</div>) : (<div className="m-4">موردی یافت نشد!</div>)
      }
    </Box>
  );
}

export default TabView;
