import PropTypes from "prop-types";
import { paramCase } from "change-case";
import { useState } from "react";
import { Link as RouterLink } from "react-router-dom";
// @mui
import { MenuItem, IconButton } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../../../routes/paths";
// components
import Iconify from "../../../../components/Iconify";
import MenuPopover from "../../../../components/MenuPopover";

// ----------------------------------------------------------------------

UserMoreMenu.propTypes = {
	onDelete: PropTypes.func,
	onAddExistSite: PropTypes.func,
	onEditDays: PropTypes.func,
	onEditPlan: PropTypes.func,
	onUserDialog: PropTypes.func,
	userName: PropTypes.string,
};

export default function UserMoreMenu({
	onDelete,
	userName,
	onAddExistSite,
	onEditDays,
	onEditPlan,
	onUserDialog,
}) {
	const [open, setOpen] = useState(null);

	const handleOpen = (event) => {
		setOpen(event.currentTarget);
	};

	const handleClose = () => {
		setOpen(null);
	};

	const ICON = {
		mr: 2,
		width: 20,
		height: 20,
	};

	return (
		<>
			<IconButton onClick={handleOpen}>
				<Iconify icon={"eva:more-vertical-fill"} width={20} height={20} />
			</IconButton>

			<MenuPopover
				open={Boolean(open)}
				anchorEl={open}
				onClose={handleClose}
				anchorOrigin={{ vertical: "top", horizontal: "left" }}
				transformOrigin={{ vertical: "top", horizontal: "right" }}
				arrow="left-top"
				sx={{
					mt: -1,
					width: 160,
					"& .MuiMenuItem-root": {
						px: 1,
						typography: "body2",
						borderRadius: 0.75,
					},
				}}
			>
				<MenuItem onClick={onDelete} sx={{ color: "error.main" }}>
					<Iconify icon={"eva:trash-2-outline"} sx={{ ...ICON }} />
					حذف
				</MenuItem>

				<MenuItem onClick={onUserDialog ?? (() => {})} disabled={!onUserDialog}>
					<Iconify icon={"eva:add-fill"} sx={{ ...ICON }} />
					ویرایش کاربر
				</MenuItem>

				<MenuItem
					onClick={onAddExistSite ?? (() => {})}
					disabled={!onAddExistSite}
				>
					<Iconify icon={"eva:add-fill"} sx={{ ...ICON }} />
					افزودن سیستم موجود
				</MenuItem>

				<MenuItem onClick={onEditDays ?? (() => {})} disabled={!onEditDays}>
					<Iconify icon={"eva:add-fill"} sx={{ ...ICON }} />
					تغییر روزهای باقی مانده
				</MenuItem>

				<MenuItem onClick={onEditPlan ?? (() => {})} disabled={!onEditPlan}>
					<Iconify icon={"eva:add-fill"} sx={{ ...ICON }} />
					تغییر پلن
				</MenuItem>

				{/* <MenuItem component={RouterLink} to={`${PATH_DASHBOARD.user.root}/${paramCase(userName)}/edit`}>
          <Iconify icon={'eva:edit-fill'} sx={{ ...ICON }} />
          ویرایش
        </MenuItem> */}
			</MenuPopover>
		</>
	);
}
