//@mui
import { Box, IconButton, InputBase } from "@mui/material";
import LoadingButton from "@mui/lab/LoadingButton";
import { useTheme } from "@mui/material/styles";
import { makeStyles } from "@mui/styles";

//icons
import AttachFileIcon from "@mui/icons-material/AttachFile";
import { ReactComponent as SendIcon } from "../../images/ic_send.svg";
import { TaskContext } from "../../pages/TaskView";

import imageCompression from "browser-image-compression";

//components
// import TicketVoicePlayer from './TicketVoicePlayer';

const useStyles = makeStyles({
	wrapper: {
		position: "relative",
	},
	input: {
		display: "none",
	},
	button: {},
});

const TaskChatInput: FC = () => {
	const theme = useTheme();
	const { t } = useTranslation();
	const classes = useStyles();
	const { enqueueSnackbar } = useSnackbar();
	const recorderControls = useAudioRecorder({}, () =>
		enqueueSnackbar(t("MicError"), { variant: "error" })
	);

	const [submitLoading, setSubmitLoading] = useState<boolean>(false);
	const [attachments, setAttachments] = useState<Array<Attachment>>([]);
	const [message, setMessage] = useState<string>("");
	const [voiceRecorder, setVoiceRecorder] = useState<{
		url: string;
		blob: any;
	} | null>();
	const contentEditable = useRef();

	const { getWork } = useContext(TaskContext);
	if (getWork == null) return <></>;
	const { user, adminUser, chats, files, work } = getWork.data;

	const _user = getTokenUser();
	const isSender = _user?.userId == -1;

	const uploadFile = async (
		files: File[],
		doSilent: boolean,
		workChat: WorkChat
	) => {
		return new Promise<{ data: { files: { id: number }[] } }>(
			(resolve, reject) => {
				const fd = new FormData();

				[...files].forEach(async (file: File, index: number) => {
					let newFile: any = file;

					try {
						if (file.name.toLowerCase().includes(".svg")) throw "";
						const compressFile = await imageCompression(file, {
							initialQuality: 1,
							maxWidthOrHeight: 1980,
							maxSizeMB: 2,
							fileType: "image/jpeg",
						});
						fd.append("files", compressFile, file.name);
					} catch {
						fd.append("files", file, file.name);
					}

					// console.log(fd.getAll('files'))
					if (files.length === index + 1) {
						const additionalDataURL = `&referenceType=3&companyName=works/${work.id}`;
						console.log("Start axiosInstance");
						axiosInstance({
							url: `/uploadFile?ticketId=${workChat.id}${additionalDataURL}`,
							method: "post",
							data: fd,
							headers: {
								"Content-Type": "multipart/form-data",
							},
						})
							.then((res) => {
								console.log(res);
								if (!doSilent) {
									///submit("", res.data.files, true);
									enqueueSnackbar(t("FileUploadSuccessfuly"), {
										variant: "success",
									});
								}
								getWork.setData((data: any) => {
									return { ...data, files: [...data.files, ...res.data.files] };
								});
								resolve(res);
							})
							.catch((e) => {
								console.log(e);
								reject(e);
								if (e.code === "ERR_NETWORK") {
									console.log("خطای دسترسی به شبکه");
									enqueueSnackbar("خطای دسترسی به شبکه", {
										variant: "error",
									});
								} else {
									enqueueSnackbar("خطا در آپلود فایل‌ها", {
										variant: "error",
									});
								}
							});
					}
				});
			}
		);
	};

	const fileUploaderHandleChange = (e: { target: { files: any } }) => {
		submit("", e.target.files);
	};

	const fileUploaderHandleChange2 = (e: { target: { files: any } }) => {
		const files = e.target.files;
		[...files].forEach(async (file: any, index: number) => {
			if (file) {
				if (["image/gif", "image/jpeg", "image/png"].includes(file.type)) {
					var fr = new FileReader();
					fr.onload = function () {
						setAttachments((files: Attachment[]) => [
							...files,
							{
								id: files.length + 1,
								img: fr.result as string,
								name: file.name,
								size: file.size,
							} as Attachment,
						]);
					};
					fr.readAsDataURL(file);
				} else {
					setAttachments((files: string | any[]) => [
						...files,
						{
							id: files.length + 1,
							name: file.name,
							size: file.size,
						},
					]);
				}
			}
		});
		uploadFile(files);
	};

	const getLastReplyDuration = (ticket: Ticket) => {
		const userJSON = ticket?.userJson
			? JSON.parse(ticket?.userJson as string).sort(
					(a: { date: any; time: any }, b: { date: any; time: any }) =>
						+(a.date + a.time) - +(b.date + b.time)
			  )
			: null;
		const supporterJSON = ticket?.supporterJson
			? JSON.parse(ticket?.supporterJson as string).sort(
					(a, b) => +(a.date + a.time) - +(b.date + b.time)
			  )
			: null;
		if (userJSON && userJSON.length && supporterJSON && supporterJSON.length) {
			const lastUserChat = userJSON[userJSON?.length - 1];
			if (lastUserChat?.timestamp) {
				const chats = [
					...JSON.parse((ticket.userJson as string) || "[]").map((i: any) => ({
						...i,
						isSelf: true,
					})),
					...JSON.parse(
						ticket && (ticket?.supporterJson as string)
							? (ticket?.supporterJson as string)
							: "[]"
					).map((i: any) => ({ ...i, isSelf: false })),
				].sort((a, b) => +(a.date + a.time) - +(b.date + b.time));
				const chatIndex = chats.findIndex((i) => i.id === lastUserChat.id);
				if (chatIndex > -1) {
					const supportReplyChatIndex = chatIndex + 1;
					const supportReplyChat = chats[supportReplyChatIndex];
					if (supportReplyChat?.timestamp) {
						return getDiffTime(
							supportReplyChat.timestamp - lastUserChat.timestamp
						);
					}
				}
			}
		}
		return "";
	};

	const updateSeen = () => {
		// @ts-ignore
		const infoJson = { ...ticket.infoJson };
		if (
			currentUser === "support" &&
			(ticket?.userJson as UserJSON[])?.[
				(ticket?.userJson as UserJSON[])?.length - 1
			]?.id
		) {
			const lastChat = (ticket?.userJson as UserJSON[])?.[
				(ticket?.userJson as UserJSON[])?.length - 1
			]?.id;
			if (infoJson.supporterLastSeenChat === lastChat) {
				return;
			}
			infoJson.supporterLastSeenChat = lastChat;
		} else if (
			(ticket.supporterJson as UserJSON[])?.[
				(ticket?.supporterJson as UserJSON[])?.length - 1
			]?.id
		) {
			infoJson.userLastSeenChat = (ticket.supporterJson as UserJSON[])?.[
				(ticket?.supporterJson as UserJSON[])?.length - 1
			]?.id;
		}
		const data = {
			id: ticket.id,
			userGuid: ticket.userGuid,
			infoJson: JSON.stringify(infoJson),
		};
		axiosInstance({
			baseURL: server2URL,
			url: `/api/ticket`,
			method: "put",
			data,
		});
	};

	const deleteAttachment = (id: number | string) => {
		setAttachments((list) => {
			const index = list.findIndex((i) => i.id === id);
			if (index > -1) {
				list.splice(index, 1);
			}
			return list;
		});
	};

	const onChatInputKeyPress = (event: KeyboardEvent) => {
		if (
			(event.code === "Enter" || event.code === "NumpadEnter") &&
			!event.shiftKey
		) {
			if (message?.length && !submitLoading) {
				event.preventDefault();
				submit();
			}
		}
	};

	const onChangeChatInput = (event: ChangeEvent) => {
		// setMessage(sanitizeHtml((event.target as HTMLInputElement).value.replace(/<img[^>]*>/g,'')
		// .replace(/(https?\:\/\/)?(www\.)?[^\s]+\.[^\s]+/g, (matched) => {
		// 	let withProtocol = matched
		// 	if(!withProtocol.startsWith("http")) {
		// 		withProtocol = "http://" + matched
		// 	}
		// 	const newStr = `<a
		// 		class="text-link"
		// 		target="_blank"
		// 		href="${withProtocol}"
		// 	>
		// 		${matched}
		// 	</a>`
		// 	return newStr
		// })
		// ))
	};

	const addAudioElement = (blob: any) => {
		const url = URL.createObjectURL(blob);
		blob.name = `voice-${+new Date()}.mp3`;
		setVoiceRecorder({
			url,
			blob,
		});
		// const audio = document.createElement("audio");
		// audio.src = url;
		// audio.controls = true;
		// document.body.appendChild(audio);
	};

	const deleteVoice = () => {
		setVoiceRecorder(null);
	};

	useEffect(() => {
		// updateSeen();
		// @ts-ignore
		contentEditable?.current?.focus?.();
	}, []);

	const submit = async (
		msg?: string,
		inputFiles?: any[],
		doSilent: boolean = false
	) => {
		const iMessage = message || msg;
		let files: any[] = [{}];
		if (inputFiles) {
			files = inputFiles;
		}
		// if (voiceRecorder?.blob) {
		// 	files =
		// 		(
		// 			await uploadFile(
		// 				ticket.id as number,
		// 				[voiceRecorder.blob],
		// 				false,
		// 				true
		// 			)
		// 		)?.data?.files || [];
		// }

		if ((iMessage?.length || files?.length) && !submitLoading) {
			setSubmitLoading(true);

			const input = {
				fileJson: inputFiles ? JSON.stringify(inputFiles) : undefined,
				workId: work.id,
				senderId: isSender ? adminUser.id : user.id,
				receiverId: !isSender ? adminUser.id : user.id,
				text: iMessage,
				type: 2,
				// historyJson: JSON.stringify({}),
			};

			axiosInstance({
				url: `/workchat`,
				method: "post",
				data: input,
			})
				.then((res: any) => {
					if (res.data.result === "ok") {
						if (!doSilent) {
							enqueueSnackbar(t("Success"), {
								variant: "success",
							});
						}
						setMessage("");
						const workChat = res.data.workChat;
						inputFiles?.length && uploadFile(inputFiles, false, workChat);
						getWork.setData((data: any) => {
							return { ...data, chats: [...data.chats, workChat] };
						});

						setTimeout(() => {
							const chatsContainer: HTMLDivElement =
								document.querySelector(".chats-container")!;
							if (chatsContainer) {
								chatsContainer.scrollTo(0, chatsContainer.scrollHeight);
							}
						});
					} else {
						enqueueSnackbar("خطا", {
							variant: "error",
						});
					}
					setSubmitLoading(false);
				})
				.catch((e) => {
					console.log(e);
					setSubmitLoading(false);
					if (e.code === "ERR_NETWORK") {
						console.log("خطای دسترسی به شبکه");
						enqueueSnackbar("خطای دسترسی به شبکه", {
							variant: "error",
						});
					} else {
						enqueueSnackbar("خطا: ارسال پیغام انجام نشد!", {
							variant: "error",
						});
					}
				})
				.finally(() => {
					setVoiceRecorder(null);
					setSubmitLoading(false);
				});
		}
	};

	return (
		// @ts-ignore
		<Box sx={{ p: { md: "0 24px 16px" } }}>
			{/* @ts-ignore */}
			{/* {!!voiceRecorder && (<Box sx={{ display: 'flex', alignItems: 'center', backgroundColor: theme.palette.slate.deep }}>
				<TicketVoicePlayer url={voiceRecorder?.url || ''} deleteVoice={deleteVoice} />
			</Box>)} */}
			{/* @ts-ignore */}
			{(true || userInfo.IsManager || userInfo.Id === -1) && (
				<Box
					sx={{
						backgroundColor: { md: theme.palette.slate.deep },
						borderRadius: "8px",
						borderRadius: { md: "8px" },
						display: "flex",
						alignItems: "center",
						gap: { xs: "4px", md: 2 },
						py: 1,
						px: 2,
						borderTop: {
							xs: `1px solid ${theme.palette.slate.dark}`,
							md: "none",
						},
					}}
				>
					{/* @ts-ignore */}
					<LoadingButton
						onClick={() => submit()}
						loading={submitLoading}
						disabled={
							(!message.length && !voiceRecorder?.blob) || submitLoading
						}
						sx={{
							width: 32,
							height: 32,
							minWidth: 32,
							borderRadius: "100%",
							"& .MuiButton-startIcon": { m: 0 },
						}}
						loadingPosition="start"
						color="success"
						variant="text"
						startIcon={<SendIcon />}
					></LoadingButton>
					{/* @ts-ignore */}
					<Box
						sx={{
							flexGrow: 1,
							outline: "none",
							// @ts-ignore
							color: theme.palette.secondary.dimgray.main,
							fontSize: "14px",
							fontWeight: 400,
							"&:empty::before": {
								content: '""',
								// @ts-ignore
								color: theme.palette.secondary.gray.main,
							},
						}}
					>
						<InputBase
							multiline
							inputRef={contentEditable}
							value={message}
							fullWidth
							classes={{ input: "chat-input mui-scrollbar" }}
							// @ts-ignore
							sx={{
								fontWeight: 400,
								fontSize: "14px",
								color: theme.palette.secondary.dimgray.main,
							}}
							// @ts-ignore
							onKeyDown={onChatInputKeyPress}
							placeholder={t("Message") as string}
							onChange={(event: any) => setMessage(event.target.value)}
							maxRows={15}
						/>
						{/* <ContentEditable
						className="chat-input mui-scrollbar"
						innerRef={contentEditable}
						html={message}
						onKeyDown={onChatInputKeyPress}
						onChange={onChangeChatInput}
						// onPaste
					// placeholder={t('Message') as string}
					// multiline
					// maxRows={15}
					/> */}
					</Box>
					{/* <AudioRecorder
						onRecordingComplete={addAudioElement}
						audioTrackConstraints={{
							noiseSuppression: true,
							echoCancellation: true,
						}}
						downloadOnSavePress={false}
						downloadFileExtension="mp3"
						recorderControls={recorderControls}
						showVisualizer={true}
						classes={{
							AudioRecorderStartSaveClass: "audio-recorder-svg-color",
							AudioRecorderPauseResumeClass: "audio-recorder-svg-color",
							AudioRecorderDiscardClass: "audio-recorder-svg-color",
						}}
					/> */}
					<Box sx={{ display: "flex", alignItems: "center" }}>
						<Box className={classes.wrapper}>
							<input
								onChange={fileUploaderHandleChange}
								multiple
								className={classes.input}
								id="icon-button-file"
								type="file"
							/>
							<label htmlFor="icon-button-file">
								{/* @ts-ignore */}
								<IconButton
									component="span"
									className={classes.button}
									sx={{ color: theme.palette.info.blue }}
								>
									<AttachFileIcon />
								</IconButton>
							</label>
						</Box>
					</Box>
				</Box>
			)}
		</Box>
	);
};
export default TaskChatInput;
