import PropTypes from "prop-types";
import { useState, useEffect } from "react";
// @mui
import { useTheme, styled } from "@mui/material/styles";
import {
	Toolbar,
	Tooltip,
	IconButton,
	Typography,
	InputAdornment,
	MenuItem,
	Box,
} from "@mui/material";
// components
import Iconify from "../../../../components/Iconify";
import InputStyle from "../../../../components/InputStyle";
import MenuPopover from "../../../../components/MenuPopover";
// ----------------------------------------------------------------------

const RootStyle = styled(Toolbar)(({ theme }) => ({
	height: 96,
	display: "flex",
	justifyContent: "space-between",
	padding: theme.spacing(0, 1, 0, 3),
}));

// ----------------------------------------------------------------------

UserListToolbar.propTypes = {
	numSelected: PropTypes.number,
	filterName: PropTypes.string,
	onFilterName: PropTypes.func,
	onDeleteUsers: PropTypes.func,
	setFilterType: PropTypes.func,
	onSendSms: PropTypes.func,
	onUserDialog: PropTypes.func,
};

export default function UserListToolbar({
	numSelected,
	filterName,
	onFilterName,
	onDeleteUsers,
	setFilterType,
	onSendSms,
	onUserDialog,
}) {
	const theme = useTheme();
	const isLight = theme.palette.mode === "light";

	const [open, setOpen] = useState(null);

	const handleOpen = (event) => {
		setOpen(event.currentTarget);
	};

	const handleClose = () => {
		setOpen(null);
	};

	return (
		<RootStyle
			sx={{
				...(numSelected > 0 && {
					color: isLight ? "primary.main" : "text.primary",
					bgcolor: isLight ? "primary.lighter" : "primary.dark",
				}),
			}}
		>
			{numSelected > 0 ? (
				<Typography component="div" variant="subtitle1">
					{numSelected} انتخاب شده
				</Typography>
			) : (
				<InputStyle
					stretchStart={240}
					value={filterName}
					size="small"
					onChange={(event) => onFilterName(event.target.value)}
					placeholder="جست‌و‌جوی کاربران..."
					InputProps={{
						startAdornment: (
							<InputAdornment position="start">
								<Iconify
									icon={"eva:search-fill"}
									sx={{ color: "text.disabled", width: 20, height: 20 }}
								/>
							</InputAdornment>
						),
					}}
				/>
			)}

			{numSelected > 0 ? (
				<Box>
					<IconButton onClick={onSendSms}>
						<Iconify icon={"ic:send"} />
					</IconButton>

					<Tooltip title="حدف انتخاب شده ها">
						<IconButton onClick={onDeleteUsers}>
							<Iconify icon={"eva:trash-2-outline"} />
						</IconButton>
					</Tooltip>
				</Box>
			) : (
				<Box>
					<IconButton onClick={onUserDialog}>
						<Iconify icon={"ic:add-circle"} />
					</IconButton>

					<IconButton onClick={onSendSms}>
						<Iconify icon={"ic:send"} />
					</IconButton>

					<IconButton>
						<Iconify icon={"ic:round-filter-list"} onClick={handleOpen} />

						<MenuPopover
							open={Boolean(open)}
							anchorEl={open}
							onClose={handleClose}
							anchorOrigin={{ vertical: "top", horizontal: "left" }}
							transformOrigin={{ vertical: "top", horizontal: "right" }}
							arrow="left-top"
							sx={{
								mt: -1,
								width: 160,
								"& .MuiMenuItem-root": {
									px: 1,
									typography: "body2",
									borderRadius: 0.75,
								},
							}}
						>
							<MenuItem onClick={() => handleClose() || setFilterType("")}>
								همه کاربران
							</MenuItem>

							<MenuItem
								onClick={() => handleClose() || setFilterType("withPlan")}
							>
								کاربران دارای پلن
							</MenuItem>

							<MenuItem
								onClick={() => handleClose() || setFilterType("withOutPlan")}
							>
								کاربران بدون پلن
							</MenuItem>
						</MenuPopover>
					</IconButton>
				</Box>
			)}
		</RootStyle>
	);
}
