import { Dispatch, FC, SetStateAction, useState } from "react";
import { useTranslation } from "react-i18next";
import { Link as RouterLink } from "react-router-dom";
import { UserInfo } from "@/types";

// @mui
import {
	Button,
	Box,
	List,
	ListItem,
	Alert,
	Typography,
	IconButton,
} from "@mui/material";
import { useTheme } from "@mui/material/styles";

// components
import HeaderBreadcrumbs from "../../HeaderBreadcrumbs";

// icons
import AddIcon from "@mui/icons-material/Add"; // icons
import { ReactComponent as InfoIcon } from "../../../images/ic_info.svg";

const points = [
	"ResponseHours",
	"AverageResponseTime",
	"MoreTimeComplexTickets",
	"ExpertsResponseTicket",
];

const TicketHeaderToolbar: FC<{
	state: [boolean, Dispatch<SetStateAction<boolean>>];
	isSender: boolean;
}> = ({ state, isSender }) => {
	const { t } = useTranslation("task");
	const gt = useTranslation().t;
	const theme = useTheme();
	const [showPageInfo, setShowPageInfo] = useState<boolean>(
		localStorage.showPageInfo === "true"
	);
	const showPageInfoHandleChange = (val: boolean) => {
		setShowPageInfo(val);
		localStorage.setItem("showPageInfo", JSON.stringify(val));
	};

	const [show, setShow] = state || [];
	return (
		<Box>
			<Box
				sx={{
					width: "100%",
					display: "flex",
					alignItems: "center",
					justifyContent: "space-between",
				}}
			>
				<HeaderBreadcrumbs
					heading={t("TicketList")}
					links={[
						{ name: gt("Dashboard"), href: "/dashboard" },
						{ name: t("Tasks") },
					]}
				/>
				<Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
					<IconButton onClick={() => showPageInfoHandleChange(!showPageInfo)}>
						<InfoIcon />
					</IconButton>
					{/* @ts-ignore */}
					{isSender && (
						<Button
							onClick={() => {
								setShow(true);
							}}
							color="success"
							variant="contained"
							startIcon={<AddIcon />}
							sx={{
								color: "#fff",
								fontWeight: 700,
								boxShadow: "0px 8px 16px rgba(0, 171, 85, 0.24)",
								"&:hover": {
									backgroundColor: theme.palette.success.muted,
									boxShadow: "0px 8px 16px rgba(0, 123, 85, 0.24)",
								},
							}}
						>
							{t("NewTask")}
						</Button>
					)}
				</Box>
			</Box>

			{/* @ts-ignore */}
			{showPageInfo && (
				<Alert
					severity="info"
					icon={<InfoIcon />}
					sx={{ backgroundColor: theme.palette.info.pale, mb: 3 }}
				>
					<Box
						sx={{
							display: "flex",
							flexDirection: "column",
							justifyContent: "center",
						}}
					>
						{/* @ts-ignore */}
						<Typography
							variant="h3"
							sx={{
								fontSize: "16px",
								fontWeight: 700,
								color: theme.palette.secondary.primary.muted,
							}}
						>
							{t("TicketTitle")}
						</Typography>
						<List
							sx={{
								listStyleType: "disc",
								listStylePosition: "inside",
								// @ts-ignore
								color: theme.palette.secondary.primary.muted,
								p: 0,
							}}
						>
							{points.map((i) => (
								<ListItem key={i} sx={{ display: "list-item" }}>
									{t(i)}
								</ListItem>
							))}
						</List>
					</Box>
				</Alert>
			)}
		</Box>
	);
};
export default TicketHeaderToolbar;
