import { FC, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import axiosInstance, { userGuid } from '../utils/axios-instance';
import merge from 'lodash/merge';
import ReactApexChart from "react-apexcharts";
import { Report, ReportItemDetails } from '@/types'
import { NavLink as RouterLink } from 'react-router-dom';

// @mui
import { Box, Container, Typography, Card, TextField, Drawer, InputAdornment, IconButton } from '@mui/material';
import { useTheme, alpha } from '@mui/material/styles';

// components
import Page from "../components/Page";
import DashboardNavbar from "../components/dashboard/DashboardNavbar";
import DepotItemsTable from "../components/items/DepotItemsTable";
import { BaseOptionChart } from "../components/chart";
import Iconify from '../components/Iconify';
import InputStyle from '../components/InputStyle';

// icons
import { ReactComponent as ChevronLeftIcon } from '../images/ic_chevron-left.svg'
import { ReactComponent as ChevronRightIcon } from '../images/ic_chevron_right.svg'
import { toEnDigit } from '../utils/formatNumber';

// const CHART_DATA = [
//   {
//     year: 'Week',
//     data: [
//       { name: 'Buy', data: [10, 41, 35, 151, 49, 62, 69, 91, 48] },
//       { name: 'Sell', data: [10, 34, 13, 56, 77, 88, 99, 77, 45] },
//     ],
//   },
//   {
//     year: 'Month',
//     data: [
//       { name: 'Buy', data: [148, 91, 69, 62, 49, 51, 35, 41, 10] },
//       { name: 'Sell', data: [45, 77, 99, 88, 77, 56, 13, 34, 10] },
//     ],
//   },
//   {
//     year: 'Year',
//     data: [
//       { name: 'Buy', data: [76, 42, 29, 41, 27, 138, 117, 86, 63] },
//       { name: 'Sell', data: [80, 55, 34, 114, 80, 130, 15, 28, 55] },
//     ],
//   },
// ];

const DepotsItem: FC = () => {
  const { t } = useTranslation();
	const routeParams = useParams();
  const theme = useTheme();
  const [loadeds, setLoades] = useState<string[]>([])
  const [seriesData, setSeriesData] = useState('Year');
  const [searchPhrase, setSearchPhrase] = useState<string>('')
  const [reportList, setReportList] = useState<Report[]>([])
  const [reportInventoryList, setReportInventoryList] = useState<any[]>([])
  const [chartLoaded, setChartLoaded] = useState<boolean>(false)
  const [reportItemDetails, setReportItemDetails] = useState<ReportItemDetails>()
  const [chartData, setChartData] = useState<{
    year: string,
    data: { name: string, data: number[] }[],
  }[]>([])
  const filterdReportList = useMemo(() => reportList.filter(i => 
    i.Title?.includes(searchPhrase) || 
    i.Code?.includes(searchPhrase) || 
    i.Alias?.includes(searchPhrase) ||
    i.Barcode?.includes(searchPhrase) ||
    i.BrandCode?.includes(searchPhrase) ||
    String(i.Id)?.includes(searchPhrase) ||
    i.ModelNo?.includes(searchPhrase) ||
    String(i.TechnicalCode)?.includes(searchPhrase) ||
    String(i.WooCommerceProductId)?.includes(searchPhrase) ||
    String(i.WooCommerceVariableId)?.includes(searchPhrase)
  ), [reportList, searchPhrase])

  const [chartOptions, setChartOptions] = useState(merge(BaseOptionChart(), {
    chart: {
      id: 'mychart',
    },
    dataLabels: { enabled: false },
    colors: ['#5BE584', '#FFD666'],
    stroke: {
      show: true,
      width: 0,
      colors: ['#5BE584', '#FFD666'],
    },
    xaxis: {
      categories: [],
    },
    tooltip: {
      y: {
        formatter: (val: any) => `$${val}`,
      },
    },
  }));

  const handleChangeSeriesData = (event: any) => {
    setSeriesData(event.target.value);
  };

  const onFilterName = (search: string) => {
		setSearchPhrase(search);
	}

  const getChartData = (res: { depots: { Guid: string; }[]; buyList: any[]; }, current: string[], cIndex: number, type: 'buy' | 'sell') => res?.depots.map((j: { Guid: string; }) => {
    const ctor = {
      0: [0, 4],
      1: [4, 6],
      2: [6, 8]
    }
    // @ts-ignore
    const result = res?.[`${type}List`]?.filter((i: { DepotGuid: string, Total: number, date: string }) => {
      let condition = false
      switch (cIndex) {
        case 2:
          condition = +current[cIndex] >= +i.date.slice(ctor[cIndex][0], ctor[cIndex][1]) && +i.date.slice(ctor[cIndex][0], ctor[cIndex][1]) > +current[cIndex] - 7
          break
        default:
          // @ts-ignore
          condition = +current[cIndex] === +i.date.slice(ctor[cIndex][0], ctor[cIndex][1])
      }
      return i.DepotGuid === j.Guid && condition
    } ).map((i: { Total: any; }) => i.Total)
    return result.length ? result.reduce((s: number,x: number) => s + x, 0) / result.length : 0
  })

  useEffect(() => {
    axiosInstance.get('/Item/ReportList').then(res => {
      if (res.status === 200) {
        setReportList(res.data)
      }
    })
    axiosInstance.get(`/Item/ReportInventory?userGuid=${userGuid}`).then(res => {
      if (res.status === 200) {
        setReportInventoryList(res.data)
      }
    })
  }, [])

  useEffect(() => {
    axiosInstance.get(`/Item/ReportItemDetails?userGuid=${userGuid}&itemId=${routeParams.item}`).then(res => {
      if (res.status === 200) {
        setReportItemDetails(res.data)
        const newChartOptions = {...chartOptions}
        newChartOptions.xaxis.categories = res.data?.depots.map((i: { Title: any; }) => i.Title)
        setChartOptions(newChartOptions)
        const current = new Date().toLocaleDateString('fa').split('/').map(i => toEnDigit(i).padStart(2, '0'))
        const result = [
          {
            year: 'Week',
            data: [
              { name: t('Buy'), data: getChartData(res.data, current, 2, 'buy') },
              { name: t('Sell'), data: getChartData(res.data, current, 2, 'sell') },
            ],
          },
          {
            year: 'Month',
            data: [
              { name: t('Buy'), data: getChartData(res.data, current, 1, 'buy') },
              { name: t('Sell'), data: getChartData(res.data, current, 1, 'sell') },
            ],
          },
          {
            year: 'Year',
            data: [
              { name: t('Buy'), data: getChartData(res.data, current, 0, 'buy') },
              { name: t('Sell'), data: getChartData(res.data, current, 0, 'sell') },
            ],
          },
        ]
        // @ts-ignore
        setChartData(result)
        // CHART_DATA[0].data[0].data = res.data.buyList?.filter((i: { DepotGuid: string, Total: number, date: string }) => +current[2] >= +i.date.slice(4, 6) && +i.date.slice(4, 6) > (+current[2] - 7) )
        // CHART_DATA[0].data[1].data = res.data.sellList?.filter((i: { DepotGuid: string, Total: number, date: string }) => current[1] == i.date.slice(4, 6))
        // // @ts-ignore
        // ReactApexChart.exec('mychart', 'updateSeries', [{
        //   data: res.data.sellList?.filter((i: { DepotGuid: string, Total: number, date: string }) => current[1] == i.date.slice(4, 6))
        // }], true);
        // CHART_DATA[0].data[0].data = res.data.buyList?.filter((i: { DepotGuid: string, Total: number, date: string }) => current[1] == i.date.slice(4, 6))
        // CHART_DATA[0].data[1].data = res.data.sellList?.filter((i: { DepotGuid: string, Total: number, date: string }) => current[1] == i.date.slice(4, 6))

        // CHART_DATA[0].data[0].data = res.data.buyList?.filter((i: { DepotGuid: string, Total: number, date: string }) => current[1] == i.date.slice(4, 6))
        // CHART_DATA[0].data[1].data = res.data.sellList?.filter((i: { DepotGuid: string, Total: number, date: string }) => current[1] == i.date.slice(4, 6))
        setChartLoaded(true)
      }
    })
  }, [routeParams.item])
  
  return (
    <Page title={t('ItemWithInventoryAndPrice')}>
      <Box sx={{ display: 'flex' }}>
        <DashboardNavbar setLoades={setLoades} />
        <Container sx={{ display: 'flex', flexDirection: 'column', py: 3, ml: '127px', gap: 2 }}>
          {/* @ts-ignore */}
          <Box component={RouterLink} to={`/itemsPriceInventory`} sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', width: '32px', height: '32px', backgroundColor: '#fff', boxShadow: '0px 5px 12px -4px rgba(145, 158, 171, 0.31)', border: `1px solid ${theme.palette.secondary.slate.dark}`, borderRadius: '100%', position: 'fixed', zIndex: 9999, top: '20px', right: '300px' }}>
            <ChevronRightIcon />
          </Box>
          <Card>
            <Typography sx={{ fontSize: '14px', fontWeight: 700, py: 2, px: 3 }}>{t('StockInStock')}</Typography>
            <DepotItemsTable rows={reportItemDetails?.depots.map(i => {
              const sellListDepot = reportItemDetails?.sellList?.filter(j => j.DepotGuid === i.Guid && j.Total !== undefined)
              const buyListDepot = reportItemDetails?.buyList?.filter(j => j.DepotGuid === i.Guid && j.Total !== undefined)
              const SellAverage = Math.trunc(sellListDepot?.reduce((t, x) => t + x.Total, 0) / sellListDepot.length) || '-'
              const BuyAverage = Math.trunc(buyListDepot?.reduce((t, x) => t + x.Total, 0) / buyListDepot.length) || '-'
              return {
                ...i,
                DepotHoldings: reportItemDetails?.depotHoldings?.[i.Guid] || '-',
                SellAverage,
                BuyAverage
              }
            }) || []} />
          </Card>
          <Card>
            <Box sx={{ display: 'flex', alignItems: 'center', p: 3 }}>
              <Box sx={{ display: 'flex', flexDirection: 'column', flexGrow: 1 }}>
                <Typography sx={{ fontWeight: 700, fontSize: '16px' }}>{t('BuyAndSellChart')}</Typography>
                <Typography color="secondary.gray.main" sx={{ fontSize: '14px' }}>{t('BuyAndSellChartSubTitle')}</Typography>
              </Box>
              <Box>
                {/* <Select
                  labelId="period-label"
                  id="period"
                  value={seriesData}
                  onChange={(event: any) => setSeriesData(event.target.value)}
                  sx={{ '& .MuiSelect-select': { backgroundColor: '#919EAB29', padding: '4px 8px 4px 36px', color: '#212B36' } }}
                >
                  <MenuItem value={1}>{t('Montly')}</MenuItem>
                  <MenuItem value={2}>{t('Yearly')}</MenuItem>
                </Select> */}
                <TextField
                  select
                  fullWidth
                  value={seriesData}
                  SelectProps={{ native: true }}
                  onChange={handleChangeSeriesData}
                  sx={{
                    '& fieldset': { border: '0 !important' },
                    '& select': { 
                      pl: 1, 
                      py: 0.5, 
                      pr: '24px !important', 
                      typography: 'subtitle2',
                      // @ts-ignore
                      backgroundColor:  alpha(theme.palette.secondary.gray.light, 0.16),
                      borderRadius: '8px',
                      // @ts-ignore
                      color: theme.palette.secondary.dimgray.main 
                    },
                    '& .MuiOutlinedInput-root': { borderRadius: 0.75, bgcolor: 'background.neutral' },
                    '& .MuiNativeSelect-icon': { top: 4, right: 0, width: 20, height: 20 },
                  }}
                >
                  {chartData.map((option) => (
                    <option key={option.year} value={option.year}>
                      {t(option.year)}
                    </option>
                  ))}
                </TextField>
              </Box>
            </Box>
            {chartData.map((item) => (
              <Box key={item.year} sx={{ mt: 3, mx: 3 }} dir="ltr">
                {item.year === seriesData && (
                  <ReactApexChart type="bar" series={item.data} options={chartOptions as any} height={364} />
                )}
              </Box>
            ))}
            {/* @ts-ignore */}
            <Box sx={{ display: 'flex', alignItems: 'center', borderTop: `1px solid ${alpha(theme.palette.secondary.gray.light, 0.24)}`, p: 2 }}>
              <Box sx={{ flexGrow: 1 }}></Box>
              <Box component={RouterLink} to={`/`} sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Typography sx={{ fontWeight: 700 }}>{t('DisplayCharts')}</Typography>
                <ChevronLeftIcon />
              </Box>
            </Box>
          </Card>
        </Container>
        <Drawer
          anchor="right"
          open
          variant="permanent"
          // @ts-ignore
          sx={{ '& .MuiPaper-root': { borderLeftColor: theme.palette.secondary.gray.pastel, borderLeftStyle: 'dashed', width: '317px', maxWidth: '100%' } }}
        >
          <Box sx={{ p: 3 }}>
            <Typography sx={{ pb: 2 }} color="secondary.dimgray.main">{t('ItemWithInventoryAndPrice')}</Typography>
            {/* @ts-ignore */}
            <InputStyle
              stretchStart={240}
              value={searchPhrase}
              onChange={(event: { target: { value: string; }; }) => onFilterName(event.target.value)}
              fullWidth
              placeholder={t('SearchItem') as string}
              sx={{ '& .MuiOutlinedInput-root': { width: { xs: '100%', md: '100%' }, '&.Mui-focused': { width: { xs: '100%', md: '100%' } } } }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Iconify icon={'eva:search-fill'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
                  </InputAdornment>
                ),
                endAdornment: (
                  <InputAdornment position="end">
                    {searchPhrase?.trim()?.length > 0 && (
                      <IconButton onClick={() => onFilterName('')}>
                        <Iconify icon={'fluent-mdl2:clear'} sx={{ color: 'text.disabled', width: 20, height: 20 }} />
                      </IconButton>
                    )}
                  </InputAdornment>
                )
              }}
            />
            {/* @ts-ignore */}
            <Box sx={{ borderBottom: `1px dashed ${theme.palette.secondary.gray.pastel}`, my: 3 }}></Box>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: '12px' }}>
              {
                filterdReportList.map(i => (
                  // @ts-ignore
                  <Box component={RouterLink} to={`/DepotsItem/${i.Id}`} sx={{ border: `1px solid ${+routeParams.item === i.Id ? theme.palette.secondary.primary.main : 'transparent'}`, borderRadius: '8px', p: '12px' }}>
                    <Typography noWrap color={routeParams?.item && +routeParams?.item === i.Id ? 'secondary.primary.main' : 'secondary.dimgray.main'} sx={{ fontSize: '14px', fontWeight: 500 }}>{i.Title}</Typography>
                    <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                      <Typography color="secondary.gray.main" sx={{ fontSize: '12px' }}>{t('Code')}: {i.Code}</Typography>
                      {!!i.TechnicalCode && <Typography color="secondary.gray.main" sx={{ fontSize: '12px' }}>{t('TechnicalCode')}: {i.TechnicalCode}</Typography>}
                    </Box>
                  </Box>
                ))
              }
            </Box>
          </Box>
        </Drawer>
      </Box>
    </Page>
  )
}
export default DepotsItem