export interface Setting {
	Category: string;
	FormId: string;
	FormName: string;
	Name: string;
	Value: "True" | "False";
	IsCheckBox: boolean;
	IsSystem: boolean;
	Guid: string;
	Company: string;
	Branch: string;
}
export type OnChangeSetting = (e: any, data: Setting) => void;
export type GetTitle = (title: string) => string | JSX.Element;
export interface TabData {
	[key: string]: { [key: string]: Setting[] };
}
export interface TabComponent {
	orientation?: "horizontal" | "vertical";
	data: TabData;
	loading: boolean;
	branchList?: { name: string; value: string }[];
	onChange: OnChangeSetting;
}
export interface TabVerticalComponent {
	orientation?: "horizontal" | "vertical";
	data: { [key: string]: Setting[] };
	branchList?: { name: string; value: string }[];
	onChange: OnChangeSetting;
}
export interface SettingConfig {
	type?: string;
	options?:
		| {
				name: string;
				value: string;
		  }[]
		| string;
	info?: string | ((s: Setting) => string);
	hint?: string | ((s: Setting) => string);
	placeHolder?: string;
	label?: string | ((s: Setting) => string);
	min?: number;
	max?: number;
	class?: string;
	inputWrapperClass?: string;
}
export interface SettingConfigList {
	[key: string]: SettingConfig;
}
