import {
	SetStateAction,
	useCallback,
	useEffect,
	useRef,
	useState,
} from "react";
import { Link } from "react-router-dom";
import { useLocation, useNavigate } from "react-router-dom";
import { TextField, CircularProgress, Alert, Container } from "@mui/material";
import { MuiOtpInput } from "mui-one-time-password-input";
import KeyboardArrowLeftIcon from "@mui/icons-material/KeyboardArrowLeft";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";
import LanguageSelector from "../../components/LanguageSelector";

import Logo from "../../images/logo.svg";
import axios from "../../utils/axios-instance";
import { useSnackbar } from "notistack";
import { useTranslation } from "react-i18next";

const Verify = () => {
	const [otp, setOtp] = useState("");
	const [loading, setLoading] = useState(false);
	const [loadingResend, setLoadingResend] = useState(false);
	const [error, setError] = useState("");
	const { enqueueSnackbar } = useSnackbar();
	const navigate = useNavigate();
	const location = useLocation();
	const [timer, setTimer] = useState<string | null>(null);
	const { t } = useTranslation();
	const direction = localStorage.getItem("i18nextLng")
		? ["fa", "sa"].includes(localStorage.getItem("i18nextLng") as string)
			? "rtl"
			: "ltr"
		: "rtl";

	const handleChange = (value: SetStateAction<string>) => {
		setOtp(value);
	};

	const otpRef = useRef(null);

	const focusOtp = () => {
		//@ts-ignore
		otpRef.current?.querySelector("input")?.focus();
	};

	useEffect(() => {
		if (!location.state?.mobile) {
			enqueueSnackbar(t("registerFirst") as string, { variant: "error" });
			// navigate("/register");
		}

		focusOtp();
	}, []);

	const handleComplete = (value: any) => {
		setLoading(true);
		axios
			.post("user/login", {
				username: location.state.mobile,
				password: value,
			})
			.then((response) => {
				if (response.data?.result === "ok") {
					//set token
					window.localStorage.token = response.data?.token;
					// //check user status
					// //if had payment go create site
					// //if created site go to site
					// const user = response.data.user;
					// if (user && user.siteName) {
					// 	//user had site
					// 	return window.location.assign(
					// 		`https://${user.siteName}.clickmis.net`
					// 	);
					// }

					// if (user && user.planName && user.paymentStatus === "ok") {
					// 	//if had payment go create site
					// 	return navigate("/payment-result", { state: { user } });
					// }

					navigate(response.data?.isAdmin ? '/dashboard' : '/choose');
				} else {
					setError(t("invalidCode") as string);
				}
				setOtp("");
			})
			.catch((error) => {
				setOtp("");
				if (error._handled) setError(t("errorTryAgain") as string);
				else setError(t("loginCatchMessage") as string);
			})
			.finally(() => {
				setLoading(false);
				focusOtp();
			});
	};

	const countdown = useCallback((minutes: number, seconds: number) => {
		function tick() {
			setTimer(
				minutes.toString() + ":" + (seconds < 10 ? "0" : "") + String(seconds)
			);
			seconds--;
			if (seconds >= 0) {
				setTimeout(tick, 1000);
			} else {
				if (minutes >= 1) {
					// countdown(mins-1);
					setTimeout(function () {
						countdown(minutes - 1, 59);
					}, 1000);
				} else if (minutes === 0) {
					setTimer(null);
				}
			}
		}
		tick();
	}, []);

	const resendOtp = () => {
		setLoadingResend(false);
		axios
			.post("User/otp", {
				mobilePhone: location.state.mobile,
			})
			.then((response) => {
				if (response.data.result === "ok") {
					enqueueSnackbar(t("sendCodeSuccess"));
					countdown(2, 0);
				} else {
					setError(response.data.error || t("errorOccurred"));
				}
			})
			.catch((error) => {
				if (error._handled) setError(t("errorTryAgain") as string);
				else setError(error.response?.data?.error || t("mobileIncorrect"));
			})
			.finally(() => setLoadingResend(false));
	};

	return (
		<Container className="flex min-h-screen flex-col items-center lg:items-stretch">
			<div className="hidden justify-end pt-7 lg:flex">
				<LanguageSelector />
			</div>
			<main className="px-4 lg:px-6">
				<div
					className="mx-auto flex min-h-screen flex-col items-center py-24 lg:justify-center"
					style={{ maxWidth: "400px" }}
				>
					<Link to="/" className="mb-10">
						<img
							src={Logo}
							alt="نرم افزار حسابداری آنلاین و ابری کلیک"
							className="h-32"
						/>
					</Link>
					<h1 className="mb-4 text-2xl font-bold text-gray-800 lg:text-3xl">
						{location.state?.resetPassword
							? t("login")
							: t("mobileVerification")}
					</h1>

					<h2 className="mb-10 text-center text-sm text-gray-500 lg:text-base">
						{t("enterCode")}
					</h2>

					{error && (
						<Alert severity="error" className="mb-6 w-full">
							{error}
						</Alert>
					)}

					<TextField
						fullWidth
						label={t("mobile")}
						disabled
						value={location.state ? location.state.mobile : ""}
						className="mb-5"
					/>

					<div className="relative mb-6">
						<MuiOtpInput
							value={otp}
							style={{ direction: "ltr" }}
							TextFieldsProps={{
								placeholder: "-",
								type: "tel",
							}}
							ref={otpRef}
							onChange={handleChange}
							onComplete={handleComplete}
						/>

						{loading && (
							<div className="absolute inset-0 flex items-center justify-center rounded-lg bg-gray-500/30 text-white">
								<CircularProgress color="inherit" size={30} />
							</div>
						)}
					</div>

					{timer ? (
						<div className="mb-6 flex justify-center text-sm">
							ارسال مجدد کد : {timer}
						</div>
					) : (
						<div className="mb-6 flex justify-center gap-1 text-sm">
							<p className="ml-1 text-gray-800">{t("notReceiveCode")}</p>

							{!loadingResend ? (
								<span
									className="cursor-pointer font-semibold text-blue-700 hover:underline"
									onClick={resendOtp}
								>
									{t("resend")}
								</span>
							) : (
								<CircularProgress color="inherit" size={18} />
							)}
						</div>
					)}
					<Link
						to="/login"
						className="mb-6 flex items-center justify-center text-sm font-semibold text-gray-800 hover:underline"
					>
						{t("changeMobile")}
						{direction === "rtl" ? (
							<KeyboardArrowLeftIcon className="text-base" />
						) : (
							<KeyboardArrowRightIcon className="text-base" />
						)}
					</Link>
					<div className="flex justify-center lg:hidden">
						<LanguageSelector />
					</div>
				</div>
			</main>
		</Container>
	);
};

export default Verify;
