import { FC, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link as RouterLink } from "react-router-dom";
import { Help, Post, PostType } from 'types';
import axiosInstance from '../utils/axios-instance';
import { categories } from "../utils/blog";
import { setScreenLoading } from "../utils";
import _ from 'lodash';

// @mui
import { Box, Button, Card, Container, Grid, IconButton, OutlinedInput, Pagination, Skeleton, Stack, Typography } from '@mui/material';
import { styled, alpha } from "@mui/material/styles";

// icons
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import imgEmpty from '../images/icons/ic_image.svg'
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import AddIcon from "@mui/icons-material/Add";
import { ReactComponent as AllIcon } from "../images/icons/categories/ic_all.svg";
import ClearIcon from "@mui/icons-material/Clear";

// components
import Header from '../layouts/Header'
import Footer from '../components/layout/Footer'
import CLKSwitch from '../components/CLKSwitch';
import PostCard from '../components/post/PostCard';
import useResponsive from "../hooks/useResponsive";

const canEdit = window.localStorage.isAdmin === 'true';

const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: "100%",
  // [theme.breakpoints.up('sm')]: {
  //   marginLeft: theme.spacing(1),
  //   width: 'auto',
  // },
}));

const StyledInputBase = styled(OutlinedInput)(({ theme }) => ({
  color: "inherit",
  "& .MuiInputBase-input": {
    p: 2,
    // vertical padding + font size from searchIcon
    // paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      width: "12ch",
      "&:focus": {
        width: "20ch",
      },
    },
  },
}));

const blog: FC = () => {
  const { t } = useTranslation('blog')
  const isMobile: boolean = useResponsive('down', 'md') ?? false;
  const [postType, setPostType] = useState<PostType>(3)
  const [page, setPage] = useState<number>(+localStorage?.BlogPage || 1);
  const [rowsPerPage, setRowsPerPage] = useState<number>(6);
  const [postTypes, setPostTypes] = useState<{ title: string, value: number, disabled?: boolean }[]>([
    {
      title: t('TextArticles'),
      value: 3
    },
    {
      title: t('Podcast'),
      value: 4,
      disabled: true
    }
  ])
  const [posts, setPosts] = useState<Post[]>([])
  const [postsLoading, setPostsLoading] = useState<boolean>(true)
  const [activeCategory, setActiveCategory] = useState<string>(localStorage?.blogActiveCategory || 'all');
  const [search, setSearch] = useState<string>(localStorage.blogSearch || '');
  const [searchInput, setSearchInput] = useState<string>(localStorage.blogSearch || '');
  const [minimalSearch, setMinimalSearch] = useState<boolean>(isMobile);
  const [openMinimalSearch, setOpenMinimalSearch] = useState<boolean>(false);
  const [imageLoadError, setImageLoadError] = useState<{ [id: number]: boolean }>({})
  const postList = useMemo(() => activeCategory === 'all' ? posts : posts.filter(i => i.categoryTitle === activeCategory || i.categoryTitle === t(activeCategory)), [posts, activeCategory])
  const filteredPosts = useMemo(() => postList.filter(i => i.type === postType && ((search && (
    (i?.title.toLowerCase().trim().indexOf(search.toLowerCase().trim()) !== -1 ||
    (i?.summary && i?.summary.toLowerCase().trim().indexOf(search.toLowerCase().trim()) !== -1) ||
    (i?.categoryTitle && i?.categoryTitle.toLowerCase().trim().indexOf(search.toLowerCase().trim()) !== -1) ||
    (i?.tagsArray && i?.tagsArray.map(i => i.toLowerCase().trim()).some(x => x.includes(search.toLowerCase().trim()))))
  )) || !search)), [postList, postType, search])
  const postsPaging = useMemo(() => filteredPosts.slice(3).slice((page - 1) * rowsPerPage, (page - 1) * rowsPerPage + rowsPerPage), [filteredPosts, page, rowsPerPage])
  const categoryList = useMemo(() => [{ id: 'all', icon: AllIcon }, ...(posts.length ? categories.filter(i => posts.some(x => x.categoryTitle === i.id || x.categoryTitle === t(i.id))) : [])], [posts])

  const onDelete = (id: number) => {
    const conf = confirm("پاک شود؟!");
    if (!conf) return;

    setScreenLoading(true);
    axiosInstance
      .delete(`help`, { data: { id } })
      .then((res: any) => {
        window.location.reload();
      })
      .catch((e) => {
        console.log(e);
      })
      .finally(() => setScreenLoading(false));
  };

  const setPageHandle = (val: number) => {
    setPage(val)
    localStorage.setItem('page', String(val))
  }

  const handleSearchChange = _.debounce(
    async (event: { target: { value: string } }) => {
      setSearch(event.target.value);
      localStorage.setItem('blogSearch', event.target.value)
    },
    1000
  );

  useEffect(() => {
    axiosInstance({
      baseURL: 'https://clickmis.net/',
      url: '/api/help',
      method: 'get'
    })
      .then((res: any) => {
        const result = res.data.helpList.sort((a: { date: string }, b: { date: string }) => +new Date(b.date) - +new Date(a.date)).filter((i: { type: number; }) => [3, 4].includes(i.type)).map((post: Help) => ({
          id: post.id,
          title: post.title,
          summary: post.summary,
          date: new Date(post.date).toLocaleDateString("fa", { month: "2-digit", day: "2-digit", year: "numeric" }),
          readTime: post.readTime + " ثانیه",
          categoryTitle: post.category,
          imageUrl: post.imageUrl ? `https://clickmis.net/${post.imageUrl}` : null,
          categoryIcon: post.imageUrl,
          type: post.type,
          videoUrl: post.videoUrl,
          address: post.address,
          tagsArray: JSON.parse(post.tagsArray)
        }))
        // .sort(
        //   (a: Post, b: Post) => (categories.findIndex(i => i.id === a.categoryTitle || t(i.id) === a.categoryTitle) + 1 || 9) - (categories.findIndex(i => i.id === b.categoryTitle || t(i.id) === b.categoryTitle) + 1 || 9)
        // )
        setPosts(result)
      })
      .catch((e) => {
        console.log(e)
      }).finally(() => setPostsLoading(false));
  }, [])
  return (
    <Stack direction="column" sx={{ height: '100%', backgroundColor: '#fff' }}>
      <Header />
      <Container maxWidth="lg" sx={{ py: 13, pb: 2, flexGrow: 1 }}>
        <Stack direction={{ xs: 'column', sm: 'row' }} gap={2} alignItems="center" justifyContent="space-between" flexGrow={1} sx={{ mx: '12px' }}>
          <Typography variant="h1" sx={{ color: '#212B36', fontSize: 16, fontWeight: 500 }}>{t('Blog')}</Typography>
          <CLKSwitch options={postTypes} value={postType} onChange={setPostType} />
          <Stack direction="row" alignItems="center" sx={{ position: 'relative' }}>
            {canEdit && (<Button
              variant="contained"
              to={"/blogForm"}
              size="small"
              component={RouterLink}
              sx={{ position: 'absolute', right: '111%' }}
            >
              <AddIcon />
              افزودن
            </Button>)}
            <Button component={RouterLink} to="/" sx={{ borderColor: '#C4CDD5', color: '#212B36', fontSize: '16px' }} variant="outlined" endIcon={<ArrowBackIcon sx={{ color: '#212B36' }} />}>{t('Back')}</Button>
          </Stack>
        </Stack>

        {/* Search */}
        <Stack direction="row" alignItems="center" justifyContent="center" sx={{ pt: 3, px: '12px' }}>
          <Search
            onClick={() => minimalSearch && setOpenMinimalSearch(true)}
            className="cursor-pointer basis-full md:basis-1/4"
          >
            {/* ${minimalSearch && !openMinimalSearch ? " w-14" : "w-full"}
              ${(scrolling || isMobile) && !openMinimalSearch
                  ? " absolute left-[180px] sm:left-[270px] " +
                  (isLg ? "top-0" : "top-1.5")
                  : ""
                } */}
            {/* <SearchIconWrapper>
                <SearchIcon />
              </SearchIconWrapper> */}
            <StyledInputBase
              value={searchInput}
              placeholder="جستجو..."
              inputProps={{ "aria-label": "search" }}
              onChange={(event) => {
                setSearchInput(event.target.value);
                handleSearchChange(event);
              }}
              fullWidth
              classes={{
                input: `flex-grow`,
              }}
              // ${minimalSearch && !openMinimalSearch ? "hidden" : ""}
              endAdornment={
                !!search.length && (
                  <IconButton
                    onClick={() => {
                      setSearchInput('');
                      setSearch('');
                      localStorage.removeItem('blogSearch')
                    }}
                    aria-label="clear-search"
                  >
                    <ClearIcon />
                  </IconButton>
                )
              }
            />
          </Search>
        </Stack>

        {/* Categories */}
        <Stack direction="row" alignItems="center" justifyContent="center" sx={{ backgroundColor: '#0154A4', borderRadius: '8px', p: 4, mt: 3, mx: '12px' }}>
          {categoryList.map(i => (
            <Stack key={i.id} onClick={() => { localStorage.setItem('blogActiveCategory', i.id); setActiveCategory(i.id) }} direction="row" alignItems="center" justifyContent="center" gap={1} sx={{ cursor: activeCategory !== i.id ? 'pointer' : '', backgroundColor: activeCategory === i.id ? '#fff' : '', color: activeCategory === i.id ? '#0154A4' : '#fff', p: '8px 16px', borderRadius: '8px' }}>
              <Box className="svg-inline-color" component={i.icon} sx={{ color: activeCategory === i.id ? '#0154A4' : '#fff' }} />
              {t(i.id)}
            </Stack>
          ))}
        </Stack>
        <Grid container sx={{ py: 2 }}>
          {!postsLoading ? filteredPosts.slice(0, 2).map((i, j) => (<Grid item xs={12} sm={6} md={j === 0 ? 8 : 4} sx={{ p: '12px' }} key={i.id}>
            <Card sx={{ display: 'flex', alignItems: 'flex-end', height: 458, position: 'relative', p: 3, borderRadius: '16px', backgroundColor: '#D9F0FD', '&:before': { content: '""', width: 106, height: 90, backgroundImage: `url(${imgEmpty})`, display: !i.imageUrl || imageLoadError[i.id] ? 'block' : 'none', position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', zIndex: 0 } }}>
              <Box
                component="img"
                src={i.imageUrl}
                alt={i.title}
                title={i.title}
                loading="lazy"
                sx={{ objectFit: 'cover', position: 'absolute', inset: 0, width: '100%', height: '100%' }}
                onError={ e => { setImageLoadError(x => ({...x, [i.id]: true }) ); (e.target as HTMLImageElement).onerror = null }}
              />
              <Box sx={{ width: '100%', height: '100%', position: 'absolute', inset: 0, backgroundColor: 'rgba(22, 28, 36, 0.8)', zIndex: 0 }}></Box>
              {canEdit && (
                <Stack direction="row" alignItems="center" sx={{ position: 'absolute', top: 8, right: 8 }}>
                  <IconButton
                    aria-label="Edit"
                    sx={{ ml: "auto" }}
                    to={`/blogForm/${i.id}`}
                    component={RouterLink}
                  >
                    <EditIcon sx={{ width: 16, height: 16, color: '#fff' }} />
                  </IconButton>
                  <IconButton aria-label="Edit" onClick={() => onDelete(i.id)}>
                    <DeleteIcon sx={{ width: 16, height: 16, color: '#fff' }} />
                  </IconButton>
                </Stack>
              )}
              <Stack direction="column" alignSelf="flex-end" gap={2} sx={{ position: 'relative', color: '#fff', zIndex: 1 }}>
                <Typography component={RouterLink} to={`/blog/${i.title?.trim()?.replace(/\s+/g, '-')}`} variant="h3" sx={{ color: '#fff', fontSize: 22, fontWeight: 500, '&:hover': { color: '#2D99FF' } }} title={i.title}>{i.title}</Typography>
                <Stack direction="column" gap="4px">
                  <Typography variant="caption" sx={{ color: '#919EAB', fontSize: 14 }}>{i.date}</Typography>
                  <Typography variant="subtitle1" textAlign="justify" sx={{ height: 84, overflow: 'hidden' }}>{i.summary}</Typography>
                </Stack>
              </Stack>
            </Card>
          </Grid>)) : (
            <>
              <Grid item xs={12} sm={6} md={8} sx={{ p: '12px' }}>
                <Skeleton variant="rounded" width="100%" height={458} />
              </Grid>
              <Grid item xs={12} sm={6} md={4} sx={{ p: '12px' }}>
                <Skeleton variant="rounded" width="100%" height={458} />
              </Grid>
            </>
          )}
          {!postsLoading ? postsPaging.map((i, j) => (<Grid item xs={12} sm={6} md={4} sx={{ p: '12px' }} key={i.id}>
            <PostCard post={i} />
          </Grid>)) : [...Array(3)].map((_, index) => (
            <Grid item xs={12} sm={6} md={4} sx={{ p: '12px' }} key={index}>
              <Skeleton variant="rounded" width="100%" height={458} />
            </Grid>
          ))}
          {!postsLoading && !posts.length && (
            <Grid item xs={12}>
              <Typography>موردی یافت نشد</Typography>
            </Grid>
          )}
        </Grid>
        <Stack direction="row" alignItems="center" justifyContent="center">
          <Pagination sx={{ '& .MuiPaginationItem-root.Mui-selected': { backgroundColor: '#193766', color: '#fff' } }} count={Math.ceil(filteredPosts.slice(3).length / rowsPerPage)} page={page} onChange={(event: any, page: number) => setPageHandle(page)} />
        </Stack>
      </Container>
      <Footer />
    </Stack>
  )
}

export default blog