import { FC, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link as RouterLink, useParams } from "react-router-dom";
import { setScreenLoading } from "../utils";
import { Helmet } from 'react-helmet-async'
import { useSnackbar } from 'notistack';
import { copyTextToClipboard } from '../utils/help';

// @mui
import { Breadcrumbs, Button, Container, Stack, Typography, Link, Box, Card, IconButton, CardMedia, CardContent, Grid, Skeleton } from '@mui/material';
import axiosInstance from '../utils/axios-instance';
import { Post } from 'types';

//icon
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ShareIcon from '@mui/icons-material/Share';
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import imgEmpty from '../images/icons/ic_image.svg'
import { ReactComponent as LikeIcon } from '../images/icons/ic_like.svg'
import { ReactComponent as DisLikeIcon } from '../images/icons/ic_dislike.svg'

// components
import Header from '../layouts/Header';
import Footer from '../components/layout/Footer';
import PostCard from '../components/post/PostCard';
import setting from '../settings';

const canEdit = window.localStorage.isAdmin === 'true';
const postType = location.pathname.toLowerCase().includes('help') ? 'help' : 'blog'

const PostView: FC = () => {
  const { t } = useTranslation(postType)
  const { enqueueSnackbar } = useSnackbar();
  const routeParams = useParams();
  const [post, setPost] = useState<{
    id: number,
    title: string,
    content: string,
    date: string,
    image: string,
    videoUrl: string,
    summary: string,
    keywords: string[],
    like: number,
    dislike: number
  } | {}>({})
  const [postLoading, setPostLoading] = useState<boolean>(true)
  const [relatedPosts, setRelatedPosts] = useState<Post[]>([])
  const [relatedPostsLoading, setRelatedPostsLoading] = useState<boolean>(true)
  const [isLikedPost, setIsLikedPost] = useState<boolean>(post?.id && localStorage.likedPosts ? localStorage.likedPosts?.includes(post.id) : false)
  const [isDislikedPost, setIsDislikedPost] = useState<boolean>(post?.id && localStorage.dislikedPosts ? localStorage.dislikedPosts?.includes(post.id) : false)

  const toggleLink = (id: number, type: string) => {
    let undo: boolean = false
    if (type === 'like') {
      if (isLikedPost) {
        undo = true
        setIsLikedPost(false)
      } else {
        undo = false
        setIsLikedPost(true)
        if (isDislikedPost) {
          axiosInstance.post(`/Help/like?id=${id}&type=dislike&undo=true`).then(res => {
            if (res.data.result === 'ok') {
              setIsLikedPost(false)
            }
          })
        }
      }
    } else {
      if (isDislikedPost) {
        undo = true
        setIsDislikedPost(false)
      } else {
        undo = false
        setIsDislikedPost(true)
        if (isLikedPost) {
          axiosInstance.post(`/Help/like?id=${id}&type=like&undo=true`).then(res => {
            if (res.data.result === 'ok') {
              setIsDislikedPost(false)
            }
          })
        }
      }
    }
    axiosInstance.post(`/Help/like?id=${id}&type=${type}&undo=${undo}`).then(res => {
      if (res.data.result === 'ok') {
        enqueueSnackbar(t('PostLikeSuccess'), { variant: 'success' })
        setPost(p => ({ ...p, like: res.data.help.like, dislike: res.data.help.dislike }))
      } else {
        enqueueSnackbar(t('PostLikeFailed'), { variant: 'error' })
      }
    }).catch(e => {
      console.log(e)
      enqueueSnackbar(t('PostLikeFailed'), { variant: 'error' })
    })
  }

  useEffect(() => {
    const lastLikedPosts = JSON.parse(localStorage.likedPosts || '[]')
    if (!lastLikedPosts.includes(post.id)) {
      if (isLikedPost) {
        lastLikedPosts.push(post.id)
      }
    } else if (!isLikedPost) {
      lastLikedPosts.splice(lastLikedPosts.indexOf(post.id), 1)
    }
    localStorage.setItem('likedPosts', JSON.stringify(lastLikedPosts))
  }, [isLikedPost])
  const onDelete = (id: number) => {
    const conf = confirm("پاک شود؟!");
    if (!conf) return;

    setScreenLoading(true);
    axiosInstance
      .delete(`help`, { data: { id } })
      .then((res: any) => {
        window.location.reload();
      })
      .catch((e) => {
        console.log(e);
      })
      .finally(() => setScreenLoading(false));
  };

  useEffect(() => {
    document.querySelector('meta[name="description"]')?.remove()
  }, [])

  useEffect(() => {
    if (routeParams.id) {
      axiosInstance({
        baseURL: 'https://clickmis.net/',
        url: `/api/help/link?${routeParams.id}`,
        method: 'get'
      })
        .then((res: any) => {
          const postItem = res.data;
          let keywords = []
          if (postItem?.tagsArray) {
            try {
              keywords = JSON.parse(postItem?.tagsArray)
            } catch (e) {
              console.log(e)
            }
          }
          setPost({
            id: postItem.id,
            title: postItem.title,
            content: postItem.textHtml,
            date: new Date(postItem.date).toLocaleDateString("fa"),
            image: `https://clickmis.net/${postItem.imageUrl}`,
            videoUrl: postItem.videoUrl,
            summary: postItem.summary,
            category: postItem.category,
            keywords
          });
          axiosInstance({
            baseURL: 'https://clickmis.net/',
            url: `/api/help/RelatedList?category=${postItem.category}&type=${location.pathname.toLowerCase().includes('help') ? [1, 2] : [3, 4]}`,
            method: 'get'
          })
            .then(({ data }: any) => {
              if (data.result === 'ok') {
                setRelatedPosts(data.helpList.filter((i: { id: number }) => i.id !== postItem.id).slice(0, 3).map((i: { date: string; readTime: string; imageUrl: string, category: string }) => ({
                  ...i,
                  categoryTitle: i.category,
                  imageUrl: i.imageUrl ? `https://clickmis.net/${i.imageUrl}` : null,
                  date: new Date(i.date).toLocaleDateString("fa", { month: "2-digit", day: "2-digit", year: "numeric" }),
                  readTime: i.readTime + " ثانیه"
                })));
              }
            })
            .catch((e) => {
              console.log(e);
            })
            .finally(() => setRelatedPostsLoading(false));
        })
        .catch((e) => {
          console.log(e);
        })
        .finally(() => setPostLoading(false));
    }
  }, [routeParams?.id])
  return (
    <Stack direction="column" sx={{ height: '100%', backgroundColor: '#fff' }}>
      <Helmet>
        <title>{`${post?.title || (postType === 'blog' ? 'راهنما' : 'بلاگ')} | کلیــــک`}</title>
        <meta name="title" content={post?.title || 'کلیک'} />
        <meta name="description" content={post?.summary || 'نرم افزار حسابداری کلیک'} />
        <meta name="keywords" content={post?.keywords?.join(', ') || 'حسابداری'} />
      </Helmet>
      <Header />
      <Container maxWidth="lg" sx={{ py: 13, pb: 2, flexGrow: 1 }}>
        <Stack direction={{ xs: 'column', sm: 'row' }} gap={2} alignItems="center" justifyContent="space-between" flexGrow={1}>
          <Breadcrumbs separator={<Box sx={{ width: 4, height: 4, borderRadius: '100%', backgroundColor: '#212B36' }} />} aria-label="breadcrumb">
            <Link underline="hover" color="inherit" href={postType === 'blog' ? '/blog' : '/helpList'}>
              <Typography variant="h1" sx={{ color: '#212B36', fontSize: 16, fontWeight: 500 }}>{t(postType === 'blog' ? 'Blog' : 'Help')}</Typography>
            </Link>
            {!!post?.title && <Typography color="text.primary">{post.title}</Typography>}
          </Breadcrumbs>
          <Button component={RouterLink} to={postType === 'blog' ? '/blog' : '/helpList'} sx={{ borderColor: '#C4CDD5', color: '#212B36', fontSize: '16px' }} variant="outlined" endIcon={<ArrowBackIcon sx={{ color: '#212B36' }} />}>{t('Back')}</Button>
        </Stack>
        <Box sx={{ py: 5 }}>
          {!postLoading && post?.id ? (<Card sx={{ display: 'flex', alignItems: 'flex-end', height: 458, position: 'relative', p: 3, borderRadius: '16px', background: post.image ? `url(${post.image})` : '#D9F0FD', backgroundRepeat: 'no-repeat', backgroundSize: 'cover', backgroundPosition: 'center', '&:before': { content: '""', width: 106, height: 90, backgroundImage: `url(${imgEmpty})`, display: 'block', position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', zIndex: 0 } }}>
            <Box sx={{ width: '100%', height: '100%', position: 'absolute', inset: 0, backgroundColor: 'rgba(22, 28, 36, 0.8)', zIndex: 0 }}></Box>

            <Stack direction="row" alignItems="center" sx={{ position: 'absolute', top: 8, right: 8 }}>
                <IconButton
                  aria-label="Share"
                  sx={{ ml: "auto" }}
                  onClick={() => copyTextToClipboard(`${setting.home}/${postType === 'help' ? 'newHelp' : postType}/${post?.title?.trim()?.replace(/ /g, '-')}`)}
                ><ShareIcon sx={{ width: 16, height: 16, color: '#fff' }} /></IconButton>
              {canEdit && (
                <>
                  <IconButton
                    aria-label="Edit"
                    sx={{ ml: "auto" }}
                    to={`/${postType}Form/${post.id}`}
                    component={RouterLink}
                  >
                    <EditIcon sx={{ width: 16, height: 16, color: '#fff' }} />
                  </IconButton>
                  <IconButton aria-label="Edit" onClick={() => onDelete(post.id)}>
                    <DeleteIcon sx={{ width: 16, height: 16, color: '#fff' }} />
                  </IconButton>
                </>
              )}
            </Stack>
            <Stack direction="column" alignSelf="flex-end" gap={2} sx={{ position: 'relative', color: '#fff', zIndex: 1 }}>
              <Typography variant="h1" sx={{ color: '#fff', fontSize: 22, fontWeight: 500 }} title={post.title}>{post.title}</Typography>
              <Stack direction="column" gap="4px">
                <Typography variant="caption" sx={{ color: '#919EAB', fontSize: 14 }}>{post.date}</Typography>
                <Typography variant="subtitle1" textAlign="justify" sx={{ maxHeight: 84, overflow: 'hidden' }}>{post.summary}</Typography>
              </Stack>
            </Stack>
          </Card>) : (
            <Skeleton variant="rounded" width="100%" height={458} />
          )}
        </Box>
        {!!post?.videoUrl && (
          <Box dangerouslySetInnerHTML={{ __html: post.videoUrl }} sx={{ mb: 3 }}></Box>
        )}
        <div
          className="content"
          dangerouslySetInnerHTML={{ __html: post.content }}
        ></div>
        {!postLoading && !post?.content && <div>در این صفحه محتوایی وجود ندارد!</div>}
        <Stack direction="column" sx={{ borderWidth: '1px 0', borderStyle: 'dashed', borderColor: '#e5e8eb', my: 5, pb: 3 }}>
          <Stack direction="row" alignItems="center" gap="12px" sx={{ py: 3 }}>
            {post?.keywords?.map((i: string, j: number) => (
              <Typography key={j} sx={{ backgroundColor: '#ebebeb', color: '#212121', borderRadius: '16px', px: 2, py: '4px', fontSize: '13px', fontWeight: 400 }}>
                {i}
              </Typography>
            ))}
          </Stack>
          {!postLoading && post?.id && (<Stack direction="row" alignItems="center" gap={3}>
            <Button onClick={() => toggleLink(post?.id, 'like')} startIcon={<LikeIcon />} sx={{ borderRadius: '100%', width: 48, minWidth: 48, height: 48 }}>{post.like || 0}</Button>
            <Button onClick={() => toggleLink(post?.id, 'dislike')} startIcon={<DisLikeIcon />} sx={{ borderRadius: '100%', width: 48, minWidth: 48, height: 48 }}>{post.dislike || 0}</Button>
          </Stack>)}
        </Stack>
        {!!(relatedPostsLoading || relatedPosts.length) && (<Stack direction="column">
          <Typography sx={{ fontWeight: 700 }}>{t('RelatedPosts')}</Typography>
          <Grid container sx={{ py: 3 }}>
            {!relatedPostsLoading ? relatedPosts.map((i, j) => (<Grid item xs={12} sm={6} md={4} sx={{ p: '12px' }} key={i.id}>
              <PostCard post={i} />
            </Grid>)) : [...Array(3)].map((_, index) => (
              <Grid item xs={12} sm={6} md={4} sx={{ p: '12px' }} key={index}>
                <Skeleton variant="rounded" width="100%" height={458} />
              </Grid>
            ))}
            {!relatedPostsLoading && !relatedPosts.length && (
              <Grid item xs={12}>
                <Typography>موردی یافت نشد</Typography>
              </Grid>
            )}
          </Grid>
        </Stack>)}
      </Container>
      <Footer />
    </Stack>
  )
}
export default PostView