import { useEffect, useMemo, useState } from "react";
import { Link } from "react-router-dom";
import {
	Container,
	Grid,
	Box,
	Typography,
	TextField,
	Alert,
	Divider,
} from "@mui/material";
import { LoadingButton } from "@mui/lab";
import { useFormik } from "formik";
import { object, string } from "yup";
import { useLocation, useNavigate } from "react-router-dom";

import Logo from "../images/logo.png";
import { useSnackbar } from "notistack";
import { ArrowForward, Refresh } from "@mui/icons-material";
import axiosInstance from "../utils/axios-instance";
import ScreenLoading from "../components/ScreenLoading";

let irYear = "1402";
try {
	irYear = new Intl.DateTimeFormat("en-US-u-ca-persian", {
		year: "numeric",
	})
		.format(new Date())
		.replace(/\D/g, "");
} catch {
	irYear = "1402";
}

type _plans = {
	[key: string]: string;
};

const _getPlanDescription = (name: string, number: number) => {
	const termName = ["1 ماهه", "3 ماهه", "6 ماهه", "سالیانه", "دائمی"];

	return `${name || "پلن نامعلوم"} ${termName[number - 1] || "مدت نامعلوم"}`;
};

const PaymentResult = () => {
	const [loading, setLoading] = useState(false);
	const [validateloading, setValidateloading] = useState(true);
	const [success, setSuccess] = useState(true);
	const [showPayment, setShowPayment] = useState(true);
	const [screenLoading, setScreenLoading] = useState(false);
	const [planDescription, setPlanDescription] = useState("");
	const [refId, setRefId] = useState("");
	const location = useLocation();
	const { enqueueSnackbar, closeSnackbar } = useSnackbar();
	const navigate = useNavigate();

	const queryParams = useMemo(
		() =>
			Object.fromEntries(
				location.search
					.replace("?", "")
					.split("&")
					.map((i) => i.split("="))
			),
		[location]
	);

	const createForm = useFormik({
		initialValues: {
			domainName: "",
			companyName: "",
		},
		validationSchema: object({
			domainName: string()
				.required("نام دامنه را وارد کنید")
				.max(20, "حداکثر 20 حرف")
				.matches(
					/[a-zA-Z0-9]$/,
					// /[a-zA-Z0-9_-]$/,
					"تنها حروف انگلیسی، اعداد و خط تیره مجاز می‌باشد"
				),
			companyName: string().required("نام مجموعه را وارد کنید"),
		}),
		onSubmit: (values) => {
			setLoading(true);
			let key = enqueueSnackbar(
				"عملیات ممکن است چند دقیقه زمان ببرد، شکیبا باشید",
				{
					variant: "warning",
					key: "error",
				}
			);

			axiosInstance
				.post("User/createSite", {
					domain: values.domainName,
					company: values.companyName,
				})
				.then((response) => {
					if (response.data.result === "ok") {
						setScreenLoading(true);
						window.document.cookie =
							response.data?.registerResponse?.cookie + ";domain=clickmis.ir";

						setTimeout(() => {
							window.location.assign(
								`http://${values.domainName}.clickmis.ir/`
							);
						});
						return;
					}

					closeSnackbar(key);
					enqueueSnackbar(
						response?.data?.error ||
							"عملیات به مشکلی برخورد، با دامنه‌ای دیگر مجدد امتحان نمایید!",
						{ variant: "error" }
					);
				})
				.catch((error) => {
					if (!error._handled) {
						closeSnackbar(key);
						enqueueSnackbar(
							error.response?.data?.error ||
								"عملیات به مشکلی برخورد، با دامنه‌ای دیگر مجدد امتحان نمایید!",
							{ variant: "error" }
						);
					}
				})
				.finally(() => setLoading(false));
		},
	});

	useEffect(() => {
		console.log("useEffect result");
		// queryParams[]
		if (queryParams?.Status?.toLowerCase() === "ok") {
			setValidateloading(true);
			axiosInstance
				.post("Payment/validate", {
					authority: queryParams.Authority,
				})
				.then((response) => {
					console.log(response);
					setRefId(response.data?.verification?.refId);

					if (response?.data?.result === "ok") {
						if (response?.data?.payment?.payType?.toLowerCase() == "extend") {
							return window.location.assign(
								`http://${response?.data?.user.siteName}.clickmis.ir/`
							);
						}

						setSuccess(true);
						setPlanDescription(
							_getPlanDescription(
								response?.data?.user.planName,
								response?.data?.user.planNumber
							)
						);

						return;
					}
					setSuccess(false);
					enqueueSnackbar("پرداخت معتبر نمی‌باشد", {
						variant: "error",
					});
				})
				.catch((error) => {
					setSuccess(false);
					if (!error._handled && error.response?.data?.error)
						enqueueSnackbar(error.response?.data?.error, {
							variant: "error",
						});
				})
				.finally(() => setValidateloading(false));
		} else {
			setValidateloading(false);
			setSuccess(false);

			if (location.state?.user) {
				//validated just create site
				setShowPayment(false);
				setSuccess(true);

				const user = location.state?.user;

				setPlanDescription(_getPlanDescription(user.planName, user.planNumber));
			}
		}
	}, []);

	return (
		<>
			<Link to="/" className="absolute top-3 right-4 lg:top-10 lg:right-10">
				<img
					src={Logo}
					alt="نرم افزار حسابداری آنلاین و ابری کلیک"
					className="h-16"
				/>
			</Link>

			{success && (
				<Container className="pt-32 pb-20">
					<Grid container spacing={3} className="justify-center">
						{showPayment && (
							<Grid item xs={12} md={4}>
								<Box className="flex min-h-[345px] flex-row items-center justify-center rounded-2xl bg-gray-100 p-10">
									{validateloading ? (
										<div className="lds-ripple">
											<div></div>

											<div></div>
										</div>
									) : (
										<div className="flex flex-col items-center">
											<svg
												width="96"
												height="96"
												viewBox="0 0 24 24"
												fill="none"
												xmlns="http://www.w3.org/2000/svg"
												className="mb-10"
											>
												<path
													d="M9 22H15C20 22 22 20 22 15V9C22 4 20 2 15 2H9C4 2 2 4 2 9V15C2 20 4 22 9 22Z"
													stroke="#166534"
													strokeWidth="1.5"
													strokeLinecap="round"
													strokeLinejoin="round"
												/>

												<path
													opacity="0.34"
													d="M7.75 12.0019L10.58 14.8319L16.25 9.17188"
													stroke="#166534"
													strokeWidth="1.5"
													strokeLinecap="round"
													strokeLinejoin="round"
												/>
											</svg>

											<h1 className="mb-4 text-2xl font-bold text-green-800 lg:text-3xl">
												پرداخت موفق
											</h1>

											<h2 className="mb-4 text-center text-sm text-gray-500 lg:text-base">
												پرداخت شما با موفقیت انجام شد
											</h2>

											{Boolean(refId) && (
												<>
													<Divider className="mb-3 w-full text-sm text-gray-800">
														شماره پیگیری
													</Divider>

													<span className="mb-10 text-xs text-gray-800">
														{refId || ""}
													</span>
												</>
											)}

											{/* <Divider className="mb-3 w-full border-dashed bg-gray-400/25" />

											<div className="flex w-full justify-between">
												<span className="text-sm text-gray-800">
													شماره پیگیری
												</span>

												<span className="flex h-6 items-center justify-center rounded-md bg-green-500/10 px-2 text-xs font-bold text-green-700">
													{refId || ""}
												</span>
											</div> */}
										</div>
									)}
								</Box>
							</Grid>
						)}

						{!validateloading && (
							<Grid item xs={12} md={4}>
								<Box className="grid gap-10 rounded-2xl lg:grid-cols-1 lg:border lg:border-dashed lg:border-gray-400/25 lg:p-6">
									<div>
										<Typography
											variant="h4"
											className="mb-6"
											sx={{ fontSize: "0.8rem" }}
										>
											برای راه‌اندازی برنامه خود اطلاعات زیر را کامل نمایید
										</Typography>

										<Alert
											severity="success"
											className="mb-4 h-fit text-justify text-xs leading-relaxed"
										>
											طرح: {planDescription}
										</Alert>

										<form onSubmit={createForm.handleSubmit}>
											<div className="mb-2">
												<div className="flex">
													<span className="ml-1 pt-2 text-gray-800">
														clickmis.ir.
													</span>

													<TextField
														fullWidth
														label="نام دامنه"
														className="mb-2"
														size="small"
														dir="auto"
														error={
															createForm.touched.domainName &&
															Boolean(createForm.errors.domainName)
														}
														helperText={
															createForm.touched.domainName &&
															createForm.errors.domainName
														}
														{...createForm.getFieldProps("domainName")}
													/>

													<span className="mr-1 pt-2 text-gray-800" dir="auto">
														https://
													</span>
												</div>

												<Alert
													severity="info"
													className="mb-4 h-fit text-justify text-xs leading-relaxed"
												>
													آدرس سایت ابری شما (قابل تغییر نمی‌باشد!)
												</Alert>

												<TextField
													size="small"
													fullWidth
													label="نام مجموعه"
													error={
														createForm.touched.companyName &&
														Boolean(createForm.errors.companyName)
													}
													helperText={
														createForm.touched.companyName &&
														createForm.errors.companyName
													}
													{...createForm.getFieldProps("companyName")}
												/>
											</div>

											<Alert
												severity="info"
												className="mb-4 h-fit text-justify text-xs leading-relaxed"
											>
												دوره مالی پیش‌فرض از ابتدا تا انتهای سال {irYear}{" "}
												می‌باشد
											</Alert>

											<LoadingButton
												disableElevation
												fullWidth
												variant="contained"
												size="large"
												type="submit"
												className="bg-blue-700 hover:bg-blue-900"
												loading={loading}
												sx={{
													"&:hover": {
														boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
													},
												}}
											>
												ساخت نرم افزار
											</LoadingButton>
										</form>
									</div>
								</Box>
							</Grid>
						)}
					</Grid>
				</Container>
			)}

			{!success && (
				<div className="mx-auto flex min-h-screen max-w-fit flex-col items-center py-24 lg:justify-center">
					<svg
						width="96"
						height="96"
						viewBox="0 0 24 24"
						fill="none"
						xmlns="http://www.w3.org/2000/svg"
						className="mb-10"
					>
						<g opacity="0.4">
							<path
								d="M9.16992 14.8319L14.8299 9.17188"
								stroke="#991b1b"
								strokeWidth="1.5"
								strokeLinecap="round"
								strokeLinejoin="round"
							/>

							<path
								d="M14.8299 14.8319L9.16992 9.17188"
								stroke="#991b1b"
								strokeWidth="1.5"
								strokeLinecap="round"
								strokeLinejoin="round"
							/>
						</g>

						<path
							d="M9 22H15C20 22 22 20 22 15V9C22 4 20 2 15 2H9C4 2 2 4 2 9V15C2 20 4 22 9 22Z"
							stroke="#991b1b"
							strokeWidth="1.5"
							strokeLinecap="round"
							strokeLinejoin="round"
						/>
					</svg>

					<h1 className="mb-4 text-2xl font-bold text-red-800 lg:text-3xl">
						پرداخت ناموفق
					</h1>

					<h2 className="mb-4 text-center text-sm text-gray-500 lg:text-base">
						پرداخت شما با خطا مواجه شد
					</h2>

					{queryParams?.Status?.toLowerCase() === "ok" && (
						<LoadingButton
							disableElevation
							fullWidth
							variant="text"
							onClick={() => {
								navigate(0);
							}}
							size="small"
							color="info"
							sx={{
								mb: 1,
								"&:hover": {
									boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
								},
							}}
						>
							<Refresh sx={{ mr: "4px" }} fontSize="small" />
							پیگیری مجدد تراکنش
						</LoadingButton>
					)}

					{Boolean(refId) && (
						<>
							<Divider className="mb-3 w-full text-sm text-gray-800">
								شماره پیگیری
							</Divider>

							<span className="mb-10 text-xs text-gray-800">{refId || ""}</span>
						</>
					)}

					<LoadingButton
						disableElevation
						fullWidth
						variant="text"
						size="small"
						className="text-gray-500"
						onClick={() => {
							navigate("/plans");
						}}
						sx={{
							mb: 1,
							"&:hover": {
								boxShadow: "rgba(32,101,209,0.24) 0 8px 16px 0",
							},
							px: 2,
						}}
					>
						<ArrowForward sx={{ mr: "4px" }} fontSize="small" />
						بازگشت به صفحه طرح‌ها
					</LoadingButton>
				</div>
			)}

			{screenLoading && <ScreenLoading />}
		</>
	);
};

export default PaymentResult;
