const QontoConnector = styled(StepConnector)(({ theme, color }) => ({
	[`&.${stepConnectorClasses.alternativeLabel}`]: {
		top: 10,
		left: "calc(-50% + 16px)",
		right: "calc(50% + 16px)",
	},
	[`&.${stepConnectorClasses.active}`]: {
		[`& .${stepConnectorClasses.line}`]: {
			borderColor: color,
		},
	},
	[`&.${stepConnectorClasses.completed}`]: {
		[`& .${stepConnectorClasses.line}`]: {
			borderColor: color,
		},
	},
	[`& .${stepConnectorClasses.line}`]: {
		borderColor:
			theme.palette.mode === "dark" ? theme.palette.grey[800] : "#eaeaf0",
		borderTopWidth: 3,
		borderRadius: 1,
	},
}));

function QontoStepIcon(props: any) {
	const { active, completed, className, color, Icon = Check } = props;

	return (
		<QontoStepIconRoot
			ownerState={{ active }}
			className={className}
			color={color}
		>
			{completed ? (
				// <Box position={"relative"}>
				// 	{Icon === PlayIcon && (
				// 		<CircularProgress
				// 			color="success"
				// 			thickness={2}
				// 			size={28}
				// 			sx={{ position: "absolute", top: "-5px", right: "-5px" }}
				// 		/>
				// 	)}
				// 	<Icon className="QontoStepIcon-completedIcon" />
				// </Box>
				<Icon className="QontoStepIcon-completedIcon" />
			) : (
				<div className="QontoStepIcon-circle" />
			)}
		</QontoStepIconRoot>
	);
}

const QontoStepIconRoot = styled("div")<{
	ownerState: { active?: boolean };
	color: any;
}>(({ theme, ownerState, color }) => ({
	color: theme.palette.mode === "dark" ? theme.palette.grey[700] : "#eaeaf0",
	display: "flex",
	height: 22,
	alignItems: "center",
	...(ownerState.active && {
		color: color,
	}),
	"& .QontoStepIcon-completedIcon": {
		color: color,
		zIndex: 1,
		fontSize: 18,
	},
	"& .QontoStepIcon-circle": {
		width: 8,
		height: 8,
		borderRadius: "50%",
		backgroundColor: "currentColor",
	},
}));

type GetData = {
	work: Work;
	workTimes: Array<WorkTime>;
	user: User;
	adminUser: User;
	chats: Array<WorkChat>;
	files: Array<Attachment>;
};

const useGet = (url: string, callback?: Function) => {
	const [loading, setLoading] = useState<boolean>(true);
	const [data, setData] = useState<GetData>({});
	const [error, setError] = useState<string>("");
	const timer = useRef<Timeout>(null);
	const datas = useRef<any>(data);
	datas.current = data;
	const fetchData = () => {
		clearTimeout(timer.current);
		setLoading(true);
		setError("");

		axiosInstance
			.get(url)
			.then((res) => {
				if (!_.isEqual(res.data, datas.current)) {
					setData(res.data || []); // true
					callback && callback(res);
				}
			})
			.catch((e) => setError(e))
			.finally(() => {
				setLoading(false);
				timer.current = setTimeout(fetchData, 11000);
			});
	};
	useEffect(() => {
		fetchData();
		return () => clearTimeout(timer.current);
	}, []);

	return { data, loading, error, fetchData, setData };
};

type GetWork = {
	data: GetData;
	loading: boolean;
	error: string;
	fetchData: () => void;
	setData: Dispatch<GetData>;
};

export const TaskContext = createContext<{ getWork: GetWork | null }>({
	getWork: null,
});

