		setLoading({ ...loading, start: true });
		axiosInstance
			.put("/work/start/" + work?.id)
			.then((res) => {
				if (res.data?.result == "ok") {
					setWork(res.data?.work);
				} else enqueueSnackbar(gt("errorTryAgain"), { variant: "error" });
			})
			.catch((e) => enqueueSnackbar(gt("errorTryAgain"), { variant: "error" }))
			.finally(() => setLoading({ ...loading, start: false }));

