const Timer = memo<{
	time: Date;
}>(({ time }) => {
	const [timing, setTiming] = useState(0);
	useEffect(() => {
		const id = setInterval(() => setTiming((c) => c + 1), 1000);
		() => clearInterval(id);
	}, []);
	return <span> {toTimeString((getNow() - time) / 1000, true)}</span>;
});

const CircleProgressBar = memo<{
	work: Work;
	pauseTimes: number;
	pauseTime: WorkTime;
}>(({ work, pauseTimes, pauseTime }) => {
	const disabled = work?.status == TaskStatus.pause;
	var stopTimed = getNow();
	if (pauseTime && !pauseTime.toDate) {
		stopTimed = new Date(pauseTime.startDate || pauseTime.createDate);
	}
	if (work?.status == TaskStatus.done) {
		stopTimed = new Date(work.endDate);
	}

	let passedTime = (stopTimed - new Date(work?.startDate)) / 1000;
	if (work?.status == TaskStatus.pending) passedTime = 0;
	passedTime -= pauseTimes || 0;
	const remainTime = work.finalTime - passedTime;
	const percent = parseInt(
		remainTime < 0 ? 100 : (remainTime / work.finalTime) * 100
	);

	var color = "#00AB55"; //green
	if (percent > 25 && percent < 75) color = "#FFE16A"; //blue ,to =>  #B78103
	if (percent <= 25 || remainTime < 0) color = "#B72136"; //red

	const [timing, setTiming] = useState(0);
	useEffect(() => {
		const id = setInterval(() => {
			work?.status == TaskStatus.doing && setTiming((c) => c + 1);
		}, 1000);
		return () => {
			clearInterval(id);
		};
	}, [work]);

	const { t } = useTranslation("task");
	const gt = useTranslation().t;

	const chartData = {
		series: [percent],
		options: {
			timing,
			work,
			chart: {
				width: "200px",
				height: "200px",
				type: "radialBar",
				fontFamily:
					'Vazirmatn RD UI FD, Vazirmatn, "Vazirmatn RD UI FD", "Vazirmatn"',
				toolbar: {
					show: false,
				},
			},
			colors: [disabled ? statusColor[TaskStatus.pause] : color],
			plotOptions: {
				radialBar: {
					hollow: {
						margin: 0,
						size: "65%",
						background: "#fff",
						image: undefined,
						imageOffsetX: 0,
						imageOffsetY: 0,
						position: "front",
						dropShadow: {
							enabled: true,
							top: 3,
							left: 0,
							blur: 4,
							opacity: 0.2,
						},
					},
					track: {
						background: "#fff",
						strokeWidth: "55%",
						margin: 0, // margin is in pixels
						dropShadow: {
							enabled: true,
							top: -3,
							left: 0,
							blur: 4,
							opacity: 0.15,
						},
					},

					dataLabels: {
						show: true,
						name: {
							offsetY: -10,
							show: true,
							color: "#b72136",
							fontSize: "14px",
						},
						value: {
							formatter: function () {
								if (work?.status == TaskStatus.pending)
									return toTimeString(work.finalTime);

								// if (work?.status == TaskStatus.done)
								// 	return toTimeString(work.finalTime);
								// return parseInt(work?.finalTime / 3600) + t("Hour");
								return toTimeString(Math.abs(remainTime), true);
							},
							color: "#b72136",
							fontSize: "16px",
							offsetY: 0,
							fontFamily:
								'Vazirmatn RD UI FD, Vazirmatn, "Vazirmatn RD UI FD", "Vazirmatn"',
							show: true,
						},
					},
				},
			},
			fill: {
				type: "gradient",
				gradient: {
					shade: "dark",
					type: "horizontal",
					shadeIntensity: 0.33,
					gradientToColors: undefined,
					inverseColors: true,
					opacityFrom: 1,
					opacityTo: 1,
					stops: [0, 100],
				},
			},
			stroke: {
				lineCap: "round",
			},
			labels: [remainTime < 0 ? t("Delay") : t("Remain")],
		},
	};

	return (
		<ReactApexChart
			options={chartData.options}
			series={chartData.series}
			type="radialBar"
			width={"200px"}
			height={"200px"}
		/>
	);
});

