import { FC, useEffect, useState } from "react";
import { useParams, Link as RouterLink } from "react-router-dom";
import { Helmet } from 'react-helmet-async'
import { useTranslation } from "react-i18next";
import axiosInstance from "../utils/axios-instance";
import "../assets/css/post.css";
import { Box, Container, IconButton, Stack, Tooltip, Typography } from "@mui/material";
import Header from "../layouts/Header";
import { setScreenLoading } from "../utils";
import Image from "../components/Image";
import EventIcon from "@mui/icons-material/Event";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import Footer from "../components/layout/Footer";

const backURL = location.pathname.toLowerCase().includes('help') ? '/helpList' : '/blog'
const HelpView: FC = () => {
	const routeParams = useParams();
	const { t } = useTranslation('help')
	const [post, setPost] = useState<{
		title: string;
		content: string;
		date: string;
		image: string;
		videoUrl?: string;
		keywords?: string[];
		summary?: string;
	} | null>(null);
	useEffect(() => {
		document.querySelector('meta[name="description"]')?.remove()
		if (routeParams.id) {
			setScreenLoading(true);
			const postType = location.pathname.includes("help") ? "docs" : "posts";
			// axiosInstance.get(`help/${routeParams.id}`)
			axiosInstance({
				baseURL: 'https://clickmis.net/',
				url: `/api/help/link?${routeParams.id}`,
				method: 'get'
			})
				.then((res: any) => {
					const help = res.data;
					let keywords = []
					if (help?.tagsArray) {
						try {
							keywords = JSON.parse(help?.tagsArray)
						} catch (e) {
							console.log(e)
						}
					}
					setPost({
						title: help.title,
						content: help.textHtml,
						date: new Date(help.date).toLocaleDateString("fa"),
						image: `https://clickmis.net/${help.imageUrl}`,
						videoUrl: help.videoUrl,
						summary: help.summary,
						keywords
					});
				})
				.catch((e) => {
					console.log(e);
				})
				.finally(() => setScreenLoading(false));
		}
	}, [routeParams.id]);
	return (
		<Stack direction="column" alignItems="flex-start" sx={{ height: '100%' }}>
			<Helmet>
				<title>{`${post?.title || 'راهنما'} | کلیــــک`}</title>
				<meta name="title" content={post?.title || 'کلیک'} />
				<meta name="description" content={post?.summary || 'نرم افزار حسابداری کلیک'} />
				<meta name="keywords" content={post?.keywords?.join(', ') || 'حسابداری'} />
			</Helmet>
			<Container maxWidth="xl" sx={{ flexGrow: 1, alignItems: 'flex-start' }} className="single-article-content pt-56 sm:pt-40 md:pt-24">
				<Header />
				{!!post && (
					<div className="flex flex-col w-full justify-center main-content article-content">
						<div className="flex flex-col justify-center items-start gap-2 sm:flex-row sm:items-center sm:justify-between py-8">
							<Stack direction="row" alignItems="center">
								<Tooltip title="بازگشت به لیست">
									<IconButton to={backURL} aria-label="back" component={RouterLink}>
										<ArrowForwardIcon />
									</IconButton>
								</Tooltip>
								<div>
									<Typography variant="h1" sx={{ fontSize: '20px', fontWeight: 500, color: '#383838', lineHeight: '40px' }}>{post.title}</Typography>
									<div className="flex items-center gap-1 text-[#9e9e9e]">
										<EventIcon sx={{ fontSize: 18 }} />
										<Typography variant="caption">{post.date}</Typography>
									</div>
								</div>
							</Stack>
							<div>
								<Image
									src={post.image}
									alt={post.title}
									ratio="4/3"
									sx={{
										width: "140px",
										height: "105px",
										borderRadius: "3px",
										border: "1px solid #bdbdbd",
										background:
											"#e5e5e5 url(https://old.clickmis.net/wp-content/uploads/2021/06/background-postsimage.svg) no-repeat top 28px center !important",
									}}
								/>
							</div>
						</div>
						{!!post?.videoUrl && (
							<Box dangerouslySetInnerHTML={{ __html: post.videoUrl }} sx={{ mb: 3 }}></Box>
						)}
						<div
							className="content"
							dangerouslySetInnerHTML={{ __html: post.content }}
						></div>
						{!post.content && <div>در این صفحه محتوایی وجود ندارد!</div>}
						<Stack direction="row" alignItems="center" spacing={2} sx={{ pb: 4 }}>
							<Box>{t('Tags')}:</Box>
							{post?.keywords?.map(i => (
								<Typography sx={{ backgroundColor: '#ebebeb', color: '#212121', borderRadius: '16px', px: 2, py: '4px', fontSize: '13px', fontWeight: 400 }}>
									{i}
								</Typography>
							))}
						</Stack>
					</div>
				)}
			</Container>
			<Footer />
		</Stack>
	);
};
export default HelpView;
