import {
	FC,
	Fragment,
	useContext,
	useEffect,
	useMemo,
	useRef,
	useState,
} from "react";
import { useTranslation } from "react-i18next";
import { getFormatedDate } from "../../utils/formatNumber";
import { useSnackbar } from "notistack";
import Lightbox from "react-awesome-lightbox";
import "react-awesome-lightbox/build/style.css";

//@mui
import { Box, Typography, Card, Divider, Avatar } from "@mui/material";
import { useTheme } from "@mui/material/styles";

import TaskChatInput from "./TaskChatInput";
import { TaskContext } from "../../pages/TaskView";
import TicketFile from "./TicketFile";
// import TicketVoicePlayerChat from "./TicketVoicePlayerChat";
import { TaskStatus, Work, User, Chat, File } from "../../types";
import utils, { getTokenUser, toDateTimeString } from "../../utils/utils";
import { getTicketTypeFromFileName } from "../../utils/formatNumber";

const getFormatedTime = (t) => {
	return t;
};
const tiketStatus = {
	1: "ResponseWaiting",
	2: "TicketIsOpen",
	3: "TocketClosed",
};
const currentUserId = 1;
const TaskChat: FC<{}> = ({}) => {
	const { ticket, attachments } = {
		ticket: {},
		attachments: [],
	};
	const theme = useTheme();
	const { t } = useTranslation("ticket");
	const chatsContainer = useRef();
	const { enqueueSnackbar } = useSnackbar();

	const [showFile, setShowFile] = useState<File | null>(null);
	const [notLoadedImages, setNotLoadedImages] = useState<number[] | []>([]);

	const getShortProfileName = (name: string) => {
		const nameA = name.split(" ");
		if (nameA.length > 1) {
			return (
				nameA[0]!.slice(0, 1) +
				"‌" +
				nameA[nameA.length - 1]!.slice(0, 1).toUpperCase()
			);
		} else if (nameA.length === 1) {
			return nameA[0]!.slice(0, 1);
		} else {
			return name;
		}
	};

	async function downloadFile(url: string, filename: string) {
		try {
			// Fetch the file
			const response = await fetch("/" + url);

			// Check if the request was successful
			if (response.status !== 200) {
				enqueueSnackbar(t("DownloadFileError"), { variant: "error" });
				throw new Error(
					`Unable to download file. HTTP status: ${response.status}`
				);
			}

			// Get the Blob data
			const blob = await response.blob();

			// Create a download link
			const downloadLink = document.createElement("a");
			downloadLink.href = URL.createObjectURL(blob);
			downloadLink.download = filename;

			// Trigger the download
			document.body.appendChild(downloadLink);
			downloadLink.click();

			// Clean up
			setTimeout(() => {
				URL.revokeObjectURL(downloadLink.href);
				document.body.removeChild(downloadLink);
			}, 100);
		} catch (error: any) {
			console.error("Error downloading the file:", error.message);
			enqueueSnackbar(t("DownloadFileError"), { variant: "error" });
		}
	}
	const openFile = (file: File) => {
		// @ts-ignore
		if (
			getTicketTypeFromFileName(file.name) == 3 ||
			!notLoadedImages.includes(file.id)
		) {
			setShowFile(file);
		} else if (file.url) {
			downloadFile(file.url, file.name);
		}
	};

	useEffect(() => {
		if (chatsContainer && chatsContainer.current) {
			(chatsContainer.current as HTMLDivElement).scrollTo(
				0,
				(chatsContainer.current as HTMLDivElement).scrollHeight
			);
		}
	}, []);

	///farjad
	const { getWork } = useContext(TaskContext);
	if (getWork == null) return <></>;

	const { user, adminUser, chats, files, work } = getWork.data;

	const _user = getTokenUser();
	const isSender = _user?.userId == -1;

	const images = files
		.filter((f) => getTicketTypeFromFileName(f.name) == 3)
		.map((f) => "/" + f.url);

	return (
		<Box
			className="chats-container mui-scrollbar"
			ref={chatsContainer}
			sx={{
				display: "flex",
				flexDirection: "column",
				flexGrow: 1,
				overflow: "auto",
				height: "100%",
			}}
		>
			<Box sx={{ display: "flex", flexDirection: "column", gap: 3, p: 3 }}>
				{/* @ts-ignore */}
				<Card
					variant="outlined"
					sx={{
						maxWidth: "100%",
						backgroundColor: theme.palette.info.pale,
						borderRadius: "16px",
						padding: 2,
						border: "none",
						boxShadow: "none",
					}}
				>
					{/* @ts-ignore */}
					<Typography
						variant="body1"
						align="justify"
						sx={{
							whiteSpace: "pre-wrap",
							fontSize: "14px",
							color: theme.palette.secondary.dimgray.main,
						}}
						dangerouslySetInnerHTML={{ __html: work.description }}
					></Typography>
				</Card>

				{chats.map((chat, index: number) => {
					const chatUser = chat.senderId == user.id ? user : adminUser;
					const isSelf = chat.senderId == _user.userId;
					const userName = chatUser
						? chatUser.name + " " + chatUser.lastName
						: t("NoName");
					const chatFiles = files.filter((f) => f.reference == chat.id);

					if (!chatFiles.length && !chat.text) return;

					return (
						<Fragment key={chat.id}>
							<Box
								sx={{
									alignSelf: { md: isSelf ? "flex-start" : "flex-end" },
									maxWidth: "740px",
								}}
							>
								<Box
									sx={{
										display: "flex",
										alignItems: "center",
										justifyContent: "space-between",
										gap: 3,
										px: 2,
										pb: "12px",
										direction: isSelf ? "ltr" : "rtl",
									}}
								>
									<Box
										sx={{
											display: "flex",
											alignItems: "center",
											gap: 1,
											flexShrink: 0,
										}}
									>
										{/* @ts-ignore */}
										<Avatar
											sx={{
												bgcolor: theme.palette.secondary.blue.main,
												color: "#fff",
												fontSize: "12px",
												fontWeight: 700,
											}}
										>
											{getShortProfileName(userName)}
										</Avatar>
										<Typography
											color="secondary.gray.main"
											noWrap
											sx={{ fontSize: "12px" }}
										>
											{userName.trim()}
										</Typography>
									</Box>
									<Box
										sx={{
											display: "flex",
											flexDirection: { xs: "column-reverse", sm: "row" },
											alignItems: "center",
											gap: { sm: 1 },
										}}
									>
										<Typography
											variant="caption"
											sx={{
												fontSize: "12px",
												color: theme.palette.secondary.gray.main,
											}}
										>
											{toDateTimeString(chat.createDate)}
										</Typography>
									</Box>
								</Box>
								{/* @ts-ignore */}
								<Card
									variant="outlined"
									sx={{
										minWidth: { md: "256px" },
										maxWidth: "100%",
										backgroundColor: isSelf
											? theme.palette.info.pale
											: theme.palette.slate.muted,
										borderRadius: "16px",
										padding: 2,
										border: "none",
										boxShadow: "none",
									}}
								>
									{chatFiles.map((file) => (
										<TicketFile
											key={file.id}
											file={file}
											openFile={openFile}
											setNotLoadedImages={setNotLoadedImages}
										/>
									))}

									{/* @ts-ignore */}
									<Typography
										variant="body1"
										align="justify"
										sx={{
											whiteSpace: "pre-wrap",
											fontSize: "14px",
											color: theme.palette.secondary.dimgray.main,
											pt: chatFiles.length ? 2 : 0,
										}}
										dangerouslySetInnerHTML={{ __html: chat.text }}
									></Typography>
								</Card>
							</Box>
							{!!(index === 0 && ticket.fastReply) && (
								<>
									<Divider
										orientation="vertical"
										variant="middle"
										flexItem
										sx={{
											margin: "24px 0 16px",
											borderWidth: "0px 0px thin 0px",
											borderColor: "rgba(145, 158, 171, 0.24)",
											alignSelf: "stretch",
											borderStyle: "dashed",
										}}
									/>
									<Box
										sx={{
											display: "flex",
											alignItems: "center",
											justifyContent: "center",
										}}
									>
										<Typography
											color="secondary.gray.main"
											sx={{ fontSize: "14px" }}
										>
											{ticket.fastReply}
										</Typography>
									</Box>
								</>
							)}
						</Fragment>
					);
				})}
			</Box>

			{/* <Divider
				orientation="vertical"
				variant="middle"
				flexItem
				sx={{
					margin: "24px 0 16px",
					borderWidth: "0px 0px thin 0px",
					borderColor: "rgba(145, 158, 171, 0.24)",
					alignSelf: "stretch",
					borderStyle: "dashed",
				}}
			/> */}

			{/* <Box
				sx={{ display: "flex", alignItems: "center", justifyContent: "center" }}
			>
				<Typography color="secondary.gray.main" sx={{ fontSize: "14px" }}>
					{t(tiketStatus[ticket.status as 1 | 2 | 3] as string)}
				</Typography>
			</Box> */}

			<Box sx={{ flex: 1 }}></Box>

			{showFile && (
				<Lightbox
					images={images}
					startIndex={files
						.filter((f) => getTicketTypeFromFileName(f.name) == 3)
						.indexOf(showFile)}
					onClose={() => setShowFile(null)}
				></Lightbox>
			)}
		</Box>
	);
};

export default TaskChat;
