import { AppBar, Box, Button, Container, Toolbar, Typography } from "@mui/material";
import { FC, useEffect, useState } from "react";
import Logo from "../images/logo.png";
import MainMenu from "./MainMenu";


const Header: FC = () => {
  const [ scrolling, setScrolling ] = useState<boolean>(false)
  const handleScroll = () => {
    setScrolling(window.scrollY > 80)
  }
  useEffect(() => {
    window.addEventListener('scroll', handleScroll, false);
    return () => {
      window.removeEventListener('scroll', handleScroll, false);
    }
  }, [])
  return (
    <AppBar sx={{ boxShadow: 0, bgcolor: 'transparent' }}>
      <Toolbar
        disableGutters
        className="relative"
        sx={{
          boxShadow: '0px 5px 12px -4px rgba(145, 158, 171, 0.31)',
          backgroundColor: '#fff',
          transition: 'height 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, background-color 200ms cubic-bezier(0.4, 0, 0.2, 1) 0ms',
          height: {
            md: scrolling ? '72px' : '88px'
          }
        }}
      >
        <div className={`bg-white/80 backdrop-blur-[6px] absolute inset-0 ${!scrolling ? 'hidden' : '' }`}></div>
        <Container
         maxWidth="lg"
          sx={{
            zIndex: 1
          }}
        >
          <div className="flex flex-row items-center justify-between rounded-md px-[12px]">
            <Box className="flex items-center" sx={{
              flexDirection: {
                xs: 'row-reverse',
                md: 'row'
              }
            }}>
              <div className="flex items-center pl-8 lg:pl-16">
                <a href="/" className="flex-shrink-0">
                  <img
                    src={Logo}
                    alt="نرم افزار حسابداری آنلاین و ابری کلیک"
                    className="h-12 outline-none"
                  />
                </a>
                <a href="/" className="hidden sm:block">
                  <Typography variant="h6" className="font-medium pr-2 text-xl" sx={{ color: '#212B36' }}>حسابداری آنلاین کلیک</Typography>
                </a>
              </div>
              <MainMenu />
            </Box>
            <Box sx={{ flexGrow: 1 }} />
            <Button
              variant="contained"
              target="_blank"
              rel="noopener"
              href="/choose"
              sx={{ backgroundColor: '#2D99FF', textAlign: 'center' }}
            >
							۱۵ روز استفاده رایگان
            </Button>
          </div>
        </Container>
      </Toolbar>
      <Box sx={{
        left: "0px",
        right: "0px",
        bottom: "0px",
        height: "24px",
        zIndex: "-1",
        margin: "auto",
        borderRadius: "50%",
        position: "absolute",
        width: "calc(100% - 48px)",
        boxShadow: 'rgba(145, 158, 171, 0.16) 0px 8px 16px 0px',
        display: !scrolling ? 'none' : ''
      }}></Box>
    </AppBar>
  )
}
export default Header;