import {
	BrowserRouter as Router,
	Routes,
	Route,
	Navigate,
	HashRouter,
	Outlet,
} from "react-router-dom";
import "./locales/i18n";

import React from "react";
import axiosInstance from "./utils/axios-instance";
import { useSnackbar } from "notistack";
import Notfound from "./pages/Notfound";
import UserList from "./pages/UserList";
import Payment from "./pages/Payment";
import Blog from "./pages/Blog";
import Post from "./pages/Post";
import UserForm from "./pages/UserForm";
import SettingView from "./pages/Settings";
import HelpForm from "./pages/HelpForm";
import AdminDashboard from "./pages/AdminDashboard";
import HelpList from "./pages/HelpList";
import HelpView from "./pages/HelpView";
import Dashboard from "./pages/Dashboard";
import ItemsPriceInventory from "./pages/ItemsPriceInventory";
import LeaderDetails from "./pages/LeaderDetails";
import DepotsItem from "./pages/DepotsItem";
import TaskList from "./pages/TaskList";
import TaskView from "./pages/TaskView";
import NewBlog from "./pages/NewBlog";
import HelpController from "./pages/HelpController";
import Help from "./pages/Help";
import Home from "./pages/Home";
import Features from "./pages/Features";
import NewPlans from "./pages/NewPlans";
import BlogForm from "./pages/BlogForm";
import BlogList from "./pages/BlogList";
import PostView from "./pages/PostView";
import Register from "./pages/auth/Register";
import Login from "./pages/auth/Login";
import Choose from "./pages/auth/Choose";
import Verify from "./pages/auth/Verify";
import ResetPassword from "./pages/auth/ResetPassword";
import Plans from "./pages/Plans";
import PaymentResult from "./pages/PaymentResult";
import LoginRegister from "./pages/auth/LoginRegister";

const components = {
	Register,
	Login,
	Choose,
	Verify,
	ResetPassword,
	Plans,
	PaymentResult,

	Notfound,
	UserList,
	Payment,
	Blog,
	Post,
	UserForm,
	SettingView,
	HelpForm,
	AdminDashboard,
	HelpList,
	HelpView,
	Dashboard,
	ItemsPriceInventory,
	LeaderDetails,
	DepotsItem,
	TaskList,
	TaskView,
	NewBlog,
	HelpController,
	Help,
	Features,
	NewPlans,
	BlogForm,
	BlogList,
	PostView,
	LoginRegister,
};

const SiteRoutes = React.memo<{
	routeName: string;
}>(({ routeName }) => {
	var Component = components[routeName] || Notfound;
	return <Component />;
});

export default SiteRoutes;
