const SettingForm: FC<{
	data: Setting[];
	onChange: OnChangeSetting;
	branchList?: { name: string; value: string }[];
	searchValue?: string;
}> = (props) => {
	const { data, onChange, branchList, searchValue } = props;
	return (
		<>
			{data.map((x: Setting, y: number) => {
				const settingConfig = config[x.Name];
				const commonClassName =
					settingConfig?.class ||
					"basis-full md:basis-1/2 flex items-start p-3";
				if (settingConfig?.info && typeof settingConfig.info == "function") {
					settingConfig.info = settingConfig.info(x);
				}
				if (settingConfig?.label && typeof settingConfig.label == "function") {
					settingConfig.label = settingConfig.label(x);
				}
				const PreTemplate: FC = () =>
					settingConfig?.info ? (
						<Alert className="mb-4" severity="warning">
							{(settingConfig?.info as string) || ""}
						</Alert>
					) : (
						<></>
					);

				const getTitle = (title: string)=> getHighlightedText(title, searchValue)

				// if (settingConfig) {
				// 	//@ts-ignore
				// 	settingConfig.label = getHighlightedText(
				// 		//@ts-ignore
				// 		settingConfig.label || x.FormName,
				// 		searchValue
				// 	);
				// }

				if (settingConfig?.type) {
					switch (settingConfig.type) {
						case "select":
							let options: { name: string; value: string }[] | [] = [];
							if (settingConfig?.options === "BranchGetAPI") {
								options = branchList as { name: string; value: string }[];
							} else {
								options = settingConfig.options as {
									name: string;
									value: string;
								}[];
							}
							return (
								<div className={commonClassName} key={y}>
									<PreTemplate />
									<SelectCLK
										config={{ ...settingConfig, options }}
										data={x}
										onChange={(e) => onChange(e, x)}
										getTitle={getTitle}
									/>
								</div>
							);
						case "codeGenrator":
							return (
								<div className={commonClassName} key={y}>
									<PreTemplate />
									<CodeGenrator
										config={settingConfig}
										data={x}
										onChange={onChange}
									/>
								</div>
							);
						case "date":
							return (
								<div className={commonClassName} key={y}>
									<PreTemplate />
									<DatePickerCLK
										config={settingConfig}
										data={x}
										onChange={onChange}
										getTitle={getTitle}
									/>
								</div>
							);
					}
				}
				return x.IsCheckBox ? (
					<div className={commonClassName} key={y}>
						<PreTemplate />
						<FormControlLabel
							control={
								<Android12Switch
									onChange={(e) => onChange(e, x)}
									checked={x.Value === "True"}
									disabled={false}
									name={`${x.Name}_${x.Guid}`}
								/>
							}
							label={getTitle(x.FormName)}
							classes={{ root: "text-justify" }}
							key={y}
						/>
					</div>
				) : (
					<div className={commonClassName} key={y}>
						<PreTemplate />
						<TextFieldCLK
							type={settingConfig?.type || "text"}
							data={x}
							config={settingConfig}
							onChange={(e) => onChange(e, x)}
							getTitle={getTitle}
						/>
						{/* <TextField label={x.FormName} variant="outlined" key={y} defaultValue={x.Value} disabled={x.IsSystem} className="w-full" onChange={onChange} /> */}
					</div>
				);
			})}
		</>
	);
};
export default SettingForm;
