import { FC, useEffect, useState } from "react";
import { useParams, Link as RouterLink } from "react-router-dom";
import axiosInstance from "../utils/axios-instance";
import "../assets/css/post.css";
import { Container, IconButton, Tooltip, Typography } from "@mui/material";
import Header from "../layouts/Header";
import { setScreenLoading } from "../utils";
import Image from "../components/Image";
import EventIcon from "@mui/icons-material/Event";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";

const Post: FC = () => {
	const routeParams = useParams();
	const [post, setPost] = useState<{
		title: string;
		content: string;
		date: string;
		image: string;
	} | null>(null);
	useEffect(() => {
		if (routeParams.id) {
			setScreenLoading(true);
			const postType = location.pathname.includes("help") ? "docs" : "posts";
			axiosInstance
				.get(`https://old.clickmis.net/wp-json/wp/v2/${postType}/${routeParams.id}`)
				.then((res: any) => {
					setPost({
						title: res.data.title.rendered,
						content: res.data.content.rendered,
						date: new Date(res.data.date).toLocaleDateString("fa"),
						image: res.data?.yoast_head_json?.og_image?.[0]?.url || "_NOIMAGE",
					});
				})
				.catch((e) => {
					console.log(e);
				})
				.finally(() => setScreenLoading(false));
		}
	}, [routeParams.id]);
	return (
		<Container className="single-article-content pt-56 sm:pt-40 md:pt-24">
			<Header />
			{!!post && (
				<div className="flex flex-col justify-center main-content article-content">
					<div className="flex flex-col justify-center items-start gap-2 sm:flex-row sm:items-center sm:justify-between pb-8">
						<div className="flex">
							<Tooltip title="بازگشت به لیست">
								<IconButton to="/blog" aria-label="back" component={RouterLink}>
									<ArrowForwardIcon />
								</IconButton>
							</Tooltip>
							<div>
								<Typography variant="h6">{post.title}</Typography>
								<div className="flex items-center gap-1 text-[#9e9e9e]">
									<EventIcon sx={{ fontSize: 18 }} />
									<Typography variant="caption">{post.date}</Typography>
								</div>
							</div>
						</div>
						<div>
							<Image
								src={post.image}
								alt={post.title}
								ratio="4/3"
								sx={{
									width: "140px",
									height: "105px",
									borderRadius: "3px",
									border: "1px solid #bdbdbd",
									background:
										"#e5e5e5 url(https://old.clickmis.net/wp-content/uploads/2021/06/background-postsimage.svg) no-repeat top 28px center !important",
								}}
							/>
						</div>
					</div>
					<div
						className="content"
						dangerouslySetInnerHTML={{ __html: post.content }}
					></div>
					{!post.content && <div>در این صفحه محتوایی وجود ندارد!</div>}
				</div>
			)}
		</Container>
	);
};
export default Post;
