import SearchIcon from "@mui/icons-material/Search";
import InputBase from "@mui/material/InputBase";
import { styled, alpha } from "@mui/material/styles";
import { useTranslation } from "react-i18next";
import React, { KeyboardEvent, FC, useRef, ChangeEventHandler } from "react";
import Box from "@mui/material/Box";
import Popper from "@mui/material/Popper";
import type { OnChangeSetting, TabData } from "@/types";
import SettingForm from "./SettingForm";
import Fade from "@mui/material/Fade";
import Paper from "@mui/material/Paper";
import LoadingButton from "@mui/lab/LoadingButton";
import SaveIcon from "@mui/icons-material/Save";
import ClickAwayListener from "@mui/base/ClickAwayListener";
import SearchNotFound from "./SearchNotFound";
// import AppBar from '@mui/material/AppBar';
// import Box from '@mui/material/Box';
// import Toolbar from '@mui/material/Toolbar';
// import IconButton from '@mui/material/IconButton';
// import Typography from '@mui/material/Typography';
// import MenuIcon from '@mui/icons-material/Menu';

const Search = styled('div')(({ theme }) => ({
	position: 'relative',
	borderRadius: theme.shape.borderRadius,
	backgroundColor: alpha(theme.palette.common.white, 0.15),
	'&:hover': {
		backgroundColor: alpha(theme.palette.common.white, 0.25),
	},
	// marginRight: theme.spacing(2),
	marginRight: 0,
	marginLeft: 0,
	width: '100%',

	[theme.breakpoints.up('sm')]: {
		// marginLeft: theme.spacing(3),
		width: 'auto',
	},
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
	padding: theme.spacing(0, 2),
	height: '100%',
	position: 'absolute',
	pointerEvents: 'none',
	display: 'flex',
	alignItems: 'center',
	justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
	color: 'inherit',
	background: '#f0f0f088',
	minWidth: 256,
	borderRadius: theme.shape.borderRadius,
	[theme.breakpoints.up('md')]: {
		width: 600,
	},

	'& .MuiInputBase-input': {
		padding: theme.spacing(1, 1, 1, 0),
		// vertical padding + font size from searchIcon
		paddingLeft: `calc(1em + ${theme.spacing(4)})`,
		transition: theme.transitions.create('width'),
		width: '100%',
		// [theme.breakpoints.up('md')]: {
		// 	width: '20ch',
		// },
	},
}));
const FabLoadingButton = styled(LoadingButton)(({ theme }) => ({
	minWidth: 48,
	width: 48,
	height: 48,
	borderRadius: '100%',
	'& .MuiButton-startIcon': {
		margin: 0,
	},
}));

const Header: FC<{
	handleSearchKeyDown: (e: any) => TabData;
	onChange: OnChangeSetting;
	save: () => void;
	saveLoading: boolean;
	saveButtonDisabled: boolean;
}> = (props) => {
	const {
		handleSearchKeyDown,
		onChange,
		save,
		saveLoading,
		saveButtonDisabled,
	} = props;
	const { t } = useTranslation();
	const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
	const open = Boolean(anchorEl);
	const [searchData, setSearchData] = React.useState<TabData>();
	const searchRef = useRef();
	const id = open ? 'simple-popper' : undefined;
	const handleSearchChange: ChangeEventHandler<
		HTMLInputElement | HTMLTextAreaElement
	> = (event) => {
		if ((event.target.value?.length || 0) < 3) return setAnchorEl(null);
		setSearchData(handleSearchKeyDown(event));
		setAnchorEl(event.currentTarget);
	};

	const handleKeyDown = (
		event: KeyboardEvent<HTMLElement & HTMLInputElement>
	) => {
		// if (event.key === 'Enter') {
		//@ts-ignore
		if ((event.target.value?.length || 0) < 2) return;
		setSearchData(handleSearchKeyDown(event));
		setAnchorEl(event.currentTarget);
		//@ts-ignore
		console.log(event.target.value);
		// }
	};
	const handleClickAway = () => {
		console.log('clicked');
		setAnchorEl(null);
		const ref = searchRef.current as unknown as HTMLInputElement;
		if (searchRef && searchRef.current && ref.value) ref.value = '';
	};

	//@ts-ignore
	const getSearchValue = () => searchRef?.current?.value || '';

	return (
		<div className="flex relative">
			<Search className="flex-grow">
				<SearchIconWrapper>
					<SearchIcon />
				</SearchIconWrapper>
				<StyledInputBase
					// onKeyDown={handleKeyDown}
					onChange={handleSearchChange}
					placeholder={t("Search")!}
					inputRef={searchRef}
					inputProps={{ "aria-label": "search" }}
				/>
			</Search>
			<ClickAwayListener onClickAway={handleClickAway}>
				<Popper
					id={id}
					open={open}
					anchorEl={anchorEl}
					transition
					className="z-[100] w-[600px] max-w-full"
					placement="bottom-start"
				>
					{({ TransitionProps }) => (
						<Fade {...TransitionProps} timeout={350}>
							<Paper>
								<Box
									className="relative shadow-md"
									sx={{
										width: "100%",
										height: "100%",
										maxHeight: "600px",
										overflowY: "auto",
										p: 1,
									}}
								>
									{searchData && Object.keys(searchData)?.length ? (
										Object.keys(searchData).map((i: string, j: number) =>
											Object.keys(searchData[i]).map((x: string, y: number) => (
												<div key={`${j}-${y}`}>
													<div className="sticky top-0 right-0 bg-[#f6f8f9] rounded-md p-2 z-10">
														{t(i)} / {t(x)}
													</div>
													<div className="flex flex-col px-4">
														<SettingForm
															data={searchData[i][x]}
															key={j + y}
															onChange={onChange}
															searchValue={getSearchValue()}
														/>
													</div>
												</div>
											))
										)
									) : (
										<SearchNotFound
											//@ts-ignore
											searchQuery={searchRef?.current?.value || ""}
										/>
									)}
								</Box>
							</Paper>
						</Fade>
					)}
				</Popper>
			</ClickAwayListener>
			<FabLoadingButton
				onClick={save}
				loading={saveLoading}
				variant="contained"
				classes={{ startIcon: "m-0" }}
				disabled={saveButtonDisabled}
				startIcon={<SaveIcon style={{ fontSize: 32 }} />}
				className="z-[1]"
				sx={{ position: "absolute", top: 8, right: 0, zIndex: 10 }}
			></FabLoadingButton>
			{/* <AppBar position="static">
          <Toolbar>
            <IconButton
              size="large"
              edge="start"
              color="inherit"
              aria-label="open drawer"
              sx={{ mr: 2 }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
              variant="h6"
              noWrap
              component="div"
              sx={{ display: { xs: 'none', sm: 'block' } }}
            >
              MUI
            </Typography>
            <Search>
              <SearchIconWrapper>
                <SearchIcon />
              </SearchIconWrapper>
              <StyledInputBase
                placeholder="Search…"
                inputProps={{ 'aria-label': 'search' }}
              />
            </Search>
            <Box sx={{ flexGrow: 1 }} />
          </Toolbar>
        </AppBar> */}
		</div>
	);
};

export default Header;
